/**
 * 图片浏览
 */

import { PureComponent } from 'react';
import { connect } from 'react-redux';
import * as IConstant from '../../core/utils/IConstant';
import {setPhotoBrowerIframeStatusAction} from '../../store/actions/photoBrowserAction';
import '../../assets/css/picture.css';

class PhotoBrowerView extends PureComponent 
{
    constructor(props)
    {
        super(props);
        this.closePhotosToIframe = this.closePhotosToIframe.bind(this);
    }
    componentDidMount()
    {
        if (!this.props.hasloadPhotoBrowerIframe) 
        {
            window.loadPhotoBrowerIframeScript();
            this.props.dispatch(setPhotoBrowerIframeStatusAction());
            // 防止查看大图超时
            this.timeoutListener = setTimeout(this.closePhotosToIframe, 5000);
        }
    }
    componentWillUnmount()
    {
        this.clearTimer();
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(this.props.photoBrowserStatus !== prevProps.photoBrowserStatus
            && this.props.photoBrowserStatus === IConstant.PHOTO_BROWSER_MESSAGE_TYPE.PHOTO_BROWSER_LOADING)
        {
        }
        if(this.props.photoBrowserStatus !== prevProps.photoBrowserStatus
            && this.props.photoBrowserStatus === IConstant.PHOTO_BROWSER_MESSAGE_TYPE.PHOTO_BROWSER_OPENED)
        {
            this.clearTimer();
        }
        else if(this.props.photoBrowserStatus !== prevProps.photoBrowserStatus
            && this.props.photoBrowserStatus === IConstant.PHOTO_BROWSER_MESSAGE_TYPE.PHOTO_BROWSER_CLOSED)
        {
            this.closePhotosToIframe();
        }
    }
    render() 
    {
        return null;
    }
    clearTimer()
    {
        if(this.timeoutListener)
        {
            clearTimeout(this.timeoutListener);
            this.timeoutListener = undefined;
        }
    }
    sendPhotosToIframe(imageList, activeIndex = 0)
    {
        let photoBrowser = document.getElementById('photoBrowser');

        if(photoBrowser && photoBrowser.contentWindow)
        {
            photoBrowser.contentWindow.postMessage(JSON.stringify({
                type: 'photo_browser_photos',
                data: imageList,
                activeIndex,
            }), '*');
            photoBrowser.style.display = '';
        }
    }
    closePhotosToIframe()
    {
        let photoBrowser = document.getElementById('photoBrowser');
        photoBrowser.style.display = 'none';
    }
    open(imageList, activeIndex = 0) 
    {
        this.sendPhotosToIframe(imageList, activeIndex);
    }
}

const mapStateToProps = function (state) {
    return {
        photoBrowserStatus: state.photoBrowser.photoBrowserStatus,
        hasloadPhotoBrowerIframe: state.photoBrowser.hasloadPhotoBrowerIframe
    };
};

PhotoBrowerView = connect(mapStateToProps, null, null, { withRef: true })(PhotoBrowerView);

export default PhotoBrowerView;


