import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import * as IConstant from '../../core/utils/IConstant';
import { asyncGetFriendsInfoAction } from '../../store/actions/userProfile/userProfileAction';
import queryString from 'query-string';
import SysUtil from '../../core/utils/SysUtil';

export class FriendInfoScreen extends PureComponent 
{
    constructor(props) 
    {
        super(props);
        this.goBack = this.goBack.bind(this);
    }

    componentDidMount()
    {
        if(!this.props.targetUserInfo)
        {
            this.props.dispatch(asyncGetFriendsInfoAction(this.props.targetUserId,this.props.userId));
        }
    }

    render() 
    {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    用户资料
                </NavBar>
                <Content style={{ backgroundColor: '#efeff4' }}>
                    <div >
                        {
                            this.renderHeaderImage()
                        }
                        {
                            this.renderItem('昵称:', this.props.targetUserInfo ? this.props.targetUserInfo.Nickname : '', 0)
                        }
                        {
                            this.renderItem('真实姓名:', this.props.targetUserInfo ?  this.props.targetUserInfo.Name : '', 4)
                        }
                        {
                            this.renderItem('性别:', this.props.targetUserInfo ? (this.props.targetUserInfo.Sex ? '男':'女') : '', 1)
                        }
                        {
                            this.renderItem('常住地:', this.props.targetUserInfo ? this.props.targetUserInfo.CityName : '', 6)
                        }
                    </div>
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderHeaderImage()
    {
        return (
            <div style={{ backgroundColor: '#ffffff', position: 'relative' }}>
                <div className='userInfoItem'>
                    <div className='userInfoItemLeft'>
                        头像
                    </div>
                    <div className='userInfoItemRight'>
                        <div className="userInfoHeaderIcon" style={{backgroundImage: `url(${this.props.targetUserInfo ? this.props.targetUserInfo.GetUserAvatar : ''})`}}/>
                    </div>
                </div>
                <div className="userInfoSplitBottom"></div>
            </div>
        );
    }
    renderItem(leftContent, rightContent, i) {
        return (
            <div style={{ backgroundColor: '#ffffff' }}>
                <div className='userInfoItem'>
                    <div className='userInfoItemLeft'>
                        {leftContent}
                    </div>
                    <div className='userInfoItemRight'>
                        <span className='userInfoItemLeftContent'>{rightContent}</span>
                    </div>
                </div>
                <div className="userInfoSplitBottom"></div>
            </div>
        );
    }
    goBack() 
    {
        this.props.history.goBack();
    }
}

const mapStateToProps = function (state,ownProps) 
{
    let searchParams = queryString.parse(ownProps.location.search);
    let userId = SysUtil.getUserId(state);
    let profileName = searchParams.targetUserId + '_' + userId;
    let profileObj = state.userProfile[profileName] ? state.userProfile[profileName] : {};
    let targetUserInfo = profileObj[IConstant.FRIEND_PROFILE_DATA_TYPE.TARGETINFO] ? profileObj[IConstant.FRIEND_PROFILE_DATA_TYPE.TARGETINFO] : null;
    return {
        targetUserInfo,
        targetUserId: searchParams.targetUserId,
        userId
    };
};
FriendInfoScreen = connect(mapStateToProps)(FriendInfoScreen);

export default FriendInfoScreen;


