import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import HotelListView from '../../views/hotel/HotelListView';
import ScenicListView from '../../views/scenic/ScenicListView';
import FunListView from '../../views/fun/FunListView';
// import FarmHouseListView from '../../views/farm-house/FarmHouseListView';
import ShoppingListView from '../../views/shopping/ShoppingListView';
import RestaurantListView from '../../views/restaurant/RestaurantListView';
import UIUtil from '../../core/utils/UIUtil';
import { SpecialStoreKey } from '../../core/utils/IConstant';
import { setSelectedTabIndexAction, goBackAction } from '../../store/actions/near-by/listAction';

class NearByListScreen extends PureComponent
{
    tabs = [
        {title: '景区', tabIndex: 3, component: ScenicListView, storeKey: SpecialStoreKey.ME_NEAR_BY_SCENIC},
        {title: '酒店', tabIndex: 3, component: HotelListView, storeKey: SpecialStoreKey.ME_NEAR_BY_HOTEL},
        {title: '餐厅', tabIndex: 2, component: RestaurantListView, storeKey: SpecialStoreKey.ME_NEAR_BY_RESTAURANT},
        {title: '娱乐', tabIndex: 3, component: FunListView, storeKey: SpecialStoreKey.ME_NEAR_BY_FUN},
        /*{title: '乡村游', tabIndex: 2, component: FarmHouseListView, storeKey: SpecialStoreKey.ME_NEAR_BY_FARM_HOUSE},*/
        {title: '购物', tabIndex: 2, component: ShoppingListView, storeKey: SpecialStoreKey.ME_NEAR_BY_SHOPPING},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);

        this.centerLat = undefined;
        this.centerLng = undefined;
        if(this.props.history.location && this.props.history.location.state)
        {
            this.centerLat = this.props.history.location.state.centerLat;
            this.centerLng = this.props.history.location.state.centerLng;
        }
    }
    render()
    {
        let tabHeight = window.innerHeight - 45;
        let listViewHeight = window.innerHeight - (45 + 44);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    周边
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(getStoreKey(this.props), index));
                            this.searchByKeyword(index);
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=>{
                                let NearByListView = item.component;
                                return <NearByListView key={index} 
                                    ref={(listView)=>{
                                        if(listView)
                                        {
                                            this.tabs[index].listView = listView.getWrappedInstance();
                                        }
                                    }}
                                    storeKey={item.storeKey}
                                    centerLat={this.centerLat}
                                    centerLng={this.centerLng}
                                    onItemClick={this.onItemClick}
                                    tabIndex={item.tabIndex} 
                                    height={listViewHeight}/>;
                            })
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    searchByKeyword(index = undefined)
    {
        index = (index === undefined) ? this.props.selectedTabIndex : index;
        let listView = this.tabs[index].listView;
        if(!listView)
        {
            return;
        }
        listView.searchByKeyword();
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        if(this.props.selectedTabIndex === 0)
        {
            this.props.history.push(`/scenic/detail/${item.Code}`);
        }
        else if(this.props.selectedTabIndex === 1)
        {
            this.props.history.push(`/hotel/detail/${item.Code}`);
        }
        else if(this.props.selectedTabIndex === 2)
        {
            this.props.history.push(`/restaurant/detail/${item.Code}`);
        }
        else if(this.props.selectedTabIndex === 3)
        {
            this.props.history.push(`/fun/detail/${item.Code}`);
        }
        /*
        else if(this.props.selectedTabIndex === 4)
        {
            this.props.history.push(`/farm-house/detail/${item.Code}`);
        }*/
        else if(this.props.selectedTabIndex === 4)
        {
            this.props.history.push(`/shopping/detail/${item.Code}`);
        }
    }
    goBack()
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack(getStoreKey(this.props));
        dispatch(goBackAction());
    }
}

function getStoreKey(props)
{
    let centerLat = undefined;
    let centerLng = undefined;
    if(props.history.location && props.history.location.state)
    {
        centerLat = props.history.location.state.centerLat;
        centerLng = props.history.location.state.centerLng;
    }
    if(centerLat && centerLng)
    {
        return `selectedTabIndex_${centerLat}_${centerLng}`;
    }
    return 'selectedTabIndex';
}

const mapStateToProps = function(state, ownProps)
{
    let selectedTabIndex = state.userNearby.selectedTabIndexGroup[getStoreKey(ownProps)];
    return {
        selectedTabIndex: selectedTabIndex ? selectedTabIndex : 0,
    };
};

NearByListScreen = connect(mapStateToProps)(NearByListScreen);

export default NearByListScreen;


