import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function setSelectedTabIndexAction(index)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.LIST.SET_SELECTED_TAB_INDEX,
        index: index
    };
}

export function asyncGetListAction(sortBy, pageIndex)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(sortBy, pageIndex));
        return _fetchList(sortBy, pageIndex).then((resp)=>{
            dispatch(_getListSuccessAction(sortBy, pageIndex, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(sortBy, error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.LIST.GO_BACK,
    };
}

export function asyncDeleteListAction(sortBy)
{
    return function(dispatch, getState)
    {
        dispatch(deleteListAction(sortBy));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction(sortBy)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.LIST.DELETE,
        sortBy,
    };
}

export function setScrollerPositionAction(sortBy, offset)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.LIST.SET_LISTVIEW_OFFSET,
        sortBy,
        listViewOffset: offset
    };
}

function _fetchList(sortBy, pageIndex)
{
    let kvDict = {
        Type: sortBy, 
        Index: pageIndex, 
        Size: IConstant.PAGE_SIZE,
        Time:'',
        Order:'',
        Query:''
    };

    return HttpClient.getFromPortal(IConstant.PortalApis.NOTES.GetNotes, kvDict)
        .then((resp) => resp.json());
}

function _getListRequestAction(sortBy, pageIndex)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.LIST.GET_LIST_REQUEST,
        sortBy,
        pageIndex,
    };
}

function _getListSuccessAction(sortBy, pageIndex, result)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.LIST.GET_LIST_SUCCESS,
        sortBy,
        pageIndex,
        result
    };
}

function _getListFailureAction(sortBy, error)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.LIST.GET_LIST_FAILURE,
        sortBy,
        message: error
    };
}


