import { Actions, STORAGE_KEY } from '../../../core/utils/IConstant';
import WebDB from '../../../core/utils/WebDB';

export default function userInfoReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.ME.USER_INFO.LOGIN_REQUEST:
        return {
            ...state, 
            isFetching: true,
        };
    case Actions.ME.USER_INFO.LOGIN_SUCCESS:
        let userInfo = action.userInfo ? action.userInfo : {};
        if(userInfo.results)
        {
            let account = WebDB.getItem(STORAGE_KEY.WX_ACCOUNT);
            let openId = WebDB.getItem(STORAGE_KEY.WX_USER_OPENID);
            if (account && account.length > 0) 
            {
                userInfo.results.Account = account;
            }
            if (openId && openId.length > 0) 
            {
                userInfo.results.OpenId = openId;
            }
        }
        return {
            ...state,
            isFetching: false,
            ...userInfo
        };
    case Actions.ME.USER_INFO.LOGIN_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.ME.USER_INFO.EXIT_LOGIN:
        return {};
    case Actions.ME.USER_INFO.UPDATE_INFO_REQUEST:
        return {
            ...state, 
            isFetching: true,
            updateSuccess: false
        };
    case Actions.ME.USER_INFO.UPDATE_INFO_SUCCESS:
        return {
            ...state,
            isFetching: false,
            results:{
                ...action.userInfo.results
            },
            tmpUserInfo: {
                ...state.tmpUserInfo,
                updateSuccess: true
            },
        };
    case Actions.ME.USER_INFO.UPDATE_INFO_FAILURE:
        return {
            ...state,
            isFetching: false,
            updateSuccess: false
        };
    case Actions.ME.USER_INFO.CLONE_TEMP_INFO:
        return {
            ...state,
            tmpUserInfo: { ...state.results },
        };
    case Actions.ME.USER_INFO.CLEAR_TEMP_INFO:
        delete state.tmpUserInfo;
        return {
            ...state,
        };
    case Actions.ME.USER_INFO.UPDATE_TEMP_INFO:
        return {
            ...state,
            tmpUserInfo: {
                ...state.tmpUserInfo,
                ...action.configItem
            },
        };
    case Actions.ME.USER_INFO.GET_NUMBERS_REQUEST:
        return {
            ...state, 
            isFetching: true,
        };
    case Actions.ME.USER_INFO.GET_NUMBERS_SUCCESS:
        return {
            ...state,
            isFetching: false,
            results: {
                ...state.results,
                ...action.results
            },
        };
    case Actions.ME.USER_INFO.GET_NUMBERS_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.ME.USER_INFO.SET_USER_INFO:
        let results = state.results;
        return {
            ...state,
            results: {
                ...results,
                ...action.userInfo
            }
        };
    default:
        return state;
    }
}


