import { Actions } from '../../../core/utils/IConstant';

export default function elementMapReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.MAP.ELEMENTMAP.GO_BACK:
        return {};
    case Actions.MAP.ELEMENTMAP.IS_PANEL_SHOWING_CHANGE:
        return {
            ...state,
            isPanelShowing:action.isPanelShowing
        };
    case Actions.MAP.ELEMENTMAP.IS_FETCHING_CHANGE:
        return {
            ...state,
            isFetching: action.isFetching
        };
        
    case Actions.MAP.ELEMENTMAP.KEYWORDS_CHANGE:
        return {
            ...state,
            keywords:action.keywords
        };
    case Actions.MAP.ELEMENTMAP.MAPZOOM_AND_CENTERLOCATION_CHANGE:
        return {
            ...state,
            mapZoom: action.mapZoom,
            centerLat: action.centerLat,
            centerLng: action.centerLng
        };
    case Actions.MAP.ELEMENTMAP.SEARCHTYPE_CHANGE:
        return {
            ...state,
            searchType:action.searchType
        };
    case Actions.MAP.ELEMENTMAP.GET_LIST_REQUEST:
        return {
            ...state,
            isFetching: true
        };
    case Actions.MAP.ELEMENTMAP.GET_LIST_SUCCESS:
        let dataList = action.result.results;
        return {
            ...state,
            dataList: dataList,
            isFetching: false,
            keywords:action.keywords,
            searchType:action.searchType
        };
    case Actions.MAP.ELEMENTMAP.GET_LIST_FAILURE:
        return {
            ...state,
            isFetching: false
        };
    case Actions.MAP.ELEMENTMAP.SELECTEDITEM_CHANGE:
        return {
            ...state,
            selectedItem: action.selectedItem
        };
    default:
        return state;
    }
}


