import { Actions } from '../../../core/utils/IConstant';

export default function createReducer (state = {}, action)
{
    switch(action.type)
    {
    case Actions.FOOTMARK.CREATE.GO_BACK:
        return {};
    case Actions.FOOTMARK.CREATE.UPDATE_FOOTMARK_ITEM:
        return {
            ...state,
            footmarkItem: action.footmarkItem,
            tabs: action.tabs,
            siteIndex: action.siteIndex
        };
    case Actions.FOOTMARK.CREATE.UPDATE_SITE_INDEX:
        return {
            ...state,
            siteIndex: action.siteIndex
        };
    case Actions.FOOTMARK.CREATE.UPDATE_SITE_ITEM:
        let _state = {...state};
        _state.footmarkItem.GetShowList[action.siteIndex] = action.siteItem;
        return {
            ..._state,
        };
    case Actions.FOOTMARK.CREATE.UPDATE_FOOTMARK_TITLE:
        return {
            ...state,
            Title: action.title
        };
    case Actions.FOOTMARK.CREATE.IS_FETCHING_CHANGE:
        return {
            ...state,
            isFetching: action.isFetching
        };
        
    default:
        return state;
    }
}



