import { Actions } from '../../../core/utils/IConstant';

export default function addPointReducer(state = {}, action)
{
    let obj = state[action.journeyId];
    switch(action.type)
    {
    case Actions.JOURNEY.ADDPOINT.GO_BACK:
        let _state = {...state};
        delete _state[action.journeyId];
        return {
            ..._state
        };
    case Actions.JOURNEY.ADDPOINT.SELECT_POINT_DONE:
        return {
            ...state,
            groupList:action.groupList,
            [action.journeyId]: {
                ...obj
            }
        };
    case Actions.JOURNEY.ADDPOINT.DELETE_ADDPOINT_GROUPLIST:
        let _newState = {...state};
        delete _newState.groupList;
        return {
            ..._newState
        };
    case Actions.JOURNEY.ADDPOINT.IS_FETCHING_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                isFetching:action.isFetching
            }
        };
    case Actions.JOURNEY.ADDPOINT.GET_LIST_REQUEST:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                isFetching:true,
                pageIndex:action.pageIndex
            }
        };
    case Actions.JOURNEY.ADDPOINT.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        if(action.result)
        {
            dataList = dataList.concat(action.result);
        }
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                isFetching:false,
                dataList: dataList,
                pageIndex:action.pageIndex
            }
        };
    case Actions.JOURNEY.ADDPOINT.GET_LIST_FAILURE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                isFetching:false,
                pageIndex:action.pageIndex
            }
        };
    case Actions.JOURNEY.ADDPOINT.DATALIST_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                dataList:action.dataList
            }
        };
    case Actions.JOURNEY.ADDPOINT.SELECTLIST_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                selectList:action.selectList
            }
        };
    case Actions.JOURNEY.ADDPOINT.SET_SCROLL_OFFSET:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                scrollViewOffset: action.scrollViewOffset,
            }
        };
    default:
        return {
            ...state
        };
    }
}


