import * as IConstant from '../../../core/utils/IConstant';

export function goBackAction(journeyId)
{
    return {
        type: IConstant.Actions.JOURNEY.EDIT.GO_BACK,
        journeyId,
    };
}

export function dataListChangeAction(journeyId,dataList)
{
    return {
        type: IConstant.Actions.JOURNEY.EDIT.DATA_LIST_CHANGE,
        journeyId,
        dataList
    };
}

export function beginDateChangeAction(journeyId,beginDate)
{
    return {
        type: IConstant.Actions.JOURNEY.EDIT.BEGIN_DATE_CHANGE,
        journeyId,
        beginDate
    };
}

export function deleteAddPointGroupListAction(journeyId)
{
    return {
        type: IConstant.Actions.JOURNEY.ADDPOINT.DELETE_ADDPOINT_GROUPLIST,
        journeyId
    };
}

export function setScrollPositionAction(journeyId,offset)
{
    return {
        type: IConstant.Actions.JOURNEY.EDIT.SET_SCROLL_OFFSET,
        scrollViewOffset: offset,
        journeyId,
    };
}




