import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function selectedTabIndexReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.ORDER.LIST.SET_SELECTED_TAB_INDEX:
        return action.index;
    case Actions.ORDER.LIST.GO_BACK:
        return 0;
    default:
        return state;
    }
}

function groupReducer(state = {}, action)
{
    let obj = state[action.orderType];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.ORDER.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [action.orderType]: {
                ...obj,
                isFetching: true,
                pageIndex: action.pageIndex,
                keyword: action.keyword,
            }
        };
    case Actions.ORDER.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 0 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        if(action.result.results)
        {
            dataList = dataList.concat(action.result.results);
        }
        return {
            ...state,
            [action.orderType]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
                total: action.result.total,
                pageIndex: action.pageIndex,
                keyword: action.keyword,
                hasMore: (action.result.total > dataList.length)
            }
        };
    case Actions.ORDER.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [action.orderType]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.ORDER.LIST.GO_BACK:
    case Actions.COMMENTS.ADD_ORDER_COMMENTS_SUCCESS:
        return {};
    case Actions.ORDER.LIST.DELETE:
        let _state = {...state};
        delete _state[action.orderType];
        return {
            ..._state
        };
    case Actions.ORDER.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [action.orderType]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    default:
        return state;
    }
}

const listReducer = combineReducers({
    selectedTabIndex: selectedTabIndexReducer,
    groups: groupReducer,
});

export default listReducer;


