import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function goBackAction(id)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.GO_BACK,
        id,
    };
}

// 新闻详情
export function asyncGetDetailAction(id)
{
    return function(dispatch, getState)
    {
        let detailItem = undefined;
        let groups = getState().travelNotes.listScreen.groups;
        for(let k in groups)
        {
            let v = groups[k].dataList;
            if(v && v.length > 0)
            {
                for(let i = 0; i < v.length; i++)
                {
                    let item = v[i];
                    if(item.ID === parseInt(id, 10))
                    {
                        detailItem = item;
                        break;
                    }
                }
            }
        }

        dispatch(_getDetailRequestAction(id));
        return _fetchDetail(id).then((resp)=>{
            dispatch(_getDetailSuccessAction(id, detailItem, resp ? resp.results : []));
        }).catch((error)=>{
            dispatch(_getDetailFailureAction(id, error));
        });
    };
}

function _fetchDetail(id)
{
    let kvDict = {
        NoteID: id,
    };
    return HttpClient.getFromPortal(IConstant.PortalApis.NOTES.GetNote, kvDict)
        .then((resp) => resp.json());
}

function _getDetailRequestAction(id)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.GET_DETAIL_REQUEST,
        id,
    };
}

function _getDetailSuccessAction(id, detailItem, resp)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.GET_DETAIL_SUCCESS,
        id,
        detailItem: detailItem,
        detailItems: resp
    };
}

function _getDetailFailureAction(id, error)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.GET_DETAIL_FAILURE,
        id,
        message: error
    };
}

// 添加点评
export function asyncSendCommentsAction(code, kvDict)
{
    return function(dispatch, getState)
    {
        dispatch(_sendCommentsRequestAction(code));
        return _sendComents(code, kvDict).then((resp)=>{
            let data = resp.results ? resp.results : {};
            dispatch(_sendCommentsSuccessAction(code, data));
        }).catch((error)=>{
            dispatch(_sendCommentsFailureAction(code, error));
        });
    };
}

function _sendComents(code, kvDict)
{
    return HttpClient.postToPortal(IConstant.PortalApis.NOTES.InsertComment, kvDict, undefined, true)
        .then((resp) => resp.json());
}

function _sendCommentsRequestAction(code)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.SEND_COMMENT_REQUEST,
        id: code,
    };
}

function _sendCommentsSuccessAction(code, data)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.SEND_COMMENT_SUCCESS,
        id: code,
    };
}

function _sendCommentsFailureAction(code, error)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.SEND_COMMENT_FAILURE,
        id: code,
        message: error
    };
}


// 查询点评
export function asyncGetCommentsAction(code, kvDict)
{
    return function(dispatch, getState)
    {
        dispatch(_getCommentsRequestAction(code));
        return _getComents(code, kvDict).then((resp)=>{
            let data = (resp.results && resp.results.length > 0) ? resp.results : {};
            dispatch(_getCommentsSuccessAction(code, data));
        }).catch((error)=>{
            dispatch(_getCommentsFailureAction(code, error));
        });
    };
}

function _getComents(code, kvDict)
{
    return HttpClient.postToPortal(IConstant.PortalApis.NOTES.QueryComment, kvDict, undefined, true)
        .then((resp) => resp.json());
}

function _getCommentsRequestAction(code)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.GET_COMMENT_REQUEST,
        id: code,
    };
}

function _getCommentsSuccessAction(code, data)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.GET_COMMENT_SUCCESS,
        id: code,
        results: data,
    };
}

function _getCommentsFailureAction(code, error)
{
    return {
        type: IConstant.Actions.TRAVEL_NOTES.DETAIL.GET_COMMENT_FAILURE,
        id: code,
        message: error
    };
}


