import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    let obj = state[action.code];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.SCENIC.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state,
            [action.code]: {
                isFetching: true,
            }
        };
    case Actions.SCENIC.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
                detailItem: action.detailItem
            },
        };
    case Actions.SCENIC.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
            }
        };
    case Actions.SCENIC.DETAIL.GO_BACK:
        let _state = {...state};
        delete _state[action.code];
        return {
            ..._state
        };
    case Actions.SCENIC.DETAIL.GET_TICKET_LIST_REQUEST:
        return {
            ...state, 
            [action.code]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.SCENIC.DETAIL.GET_TICKET_LIST_SUCCESS:
        return {
            ...state, 
            [action.code]: {
                ...obj,
                ticketList: action.ticketList,
                isFetching: false,
            }
        };
    case Actions.SCENIC.DETAIL.GET_TICKET_LIST_FAILURE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.SCENIC.DETAIL.GET_SCENERY_LIST_REQUEST:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.SCENIC.DETAIL.GET_SCENERY_LIST_SUCCESS:
        return {
            ...state,
            [action.code]: {
                ...obj,
                sceneryList: action.sceneryList,
                isFetching: false,
            }
        };
    case Actions.SCENIC.DETAIL.GET_SCENERY_LIST_FAILURE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.SCENIC.DETAIL.SET_DETAIL_SCROLL_OFFSET:
        return {
            ...state,
            [action.code]: {
                ...obj,
                scrollViewOffset: action.scrollViewOffset,
            }
        };
    case Actions.SCENIC.DETAIL.SET_SCENERY_LIST_SCROLL_OFFSET:
        return {
            ...state,
            [action.code]: {
                ...obj,
                sceneryListScreenScrollViewOffset: action.scrollViewOffset,
            }
        };
    case Actions.SCENIC.DETAIL.SET_SCENERY_DETAIL_SCROLL_OFFSET:
        let key = 'sceneryDetailScreenScrollViewOffset_' + action.sceneryCode;
        return {
            ...state,
            [action.code]: {
                ...obj,
                [key]: action.scrollViewOffset,
            }
        };
    default:
        return state;
    }
}


