import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncDeleteListAction, setScrollerPositionAction } from '../../store/actions/line/listAction';
import EmptyView from '../../views/common/EmptyView';

// 综合、评分
const sortFields: Array = ['Sort', 'ScoreSort', 'PriceOrder', 'ModifyTime'];
const sortTypes: Array = ['desc', 'desc', 'asc', 'desc'];

class LineListView extends PureComponent
{
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList)
        {
            this.loadData(this.props.inputKeyword);
        }
        if(this.listView && !isNaN(this.props.listViewOffset))
        {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(this.props.dataList !== prevProps.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : [])});
        }
    }
    render()
    {
        return (
            <div className="scenicListBox" style={{height: this.props.height}}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh(this.props.inputKeyword)}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 0)
                        ?
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                        :null
                }
            </div>
        );
    }
    renderListView()
    {
        if(this.props.dataList && this.props.dataList.length > 0)
        {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );
    
            return (
                <ListView
                    ref={el => this.listView = el}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}
                    />}
                    onEndReached={this.loadNextPage}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20}/>
            );
        }
    }
    searchByKeyword(keyword)
    {
        if(keyword === this.props.keyword || (this.isEmptyString(keyword) && this.isEmptyString(this.props.keyword)))
        {
            return;
        }
        this.onRefresh(keyword);
    }
    isEmptyString(str)
    {
        return !str || str.length === 0;
    }
    onRefresh(keyword)
    {
        this.props.dispatch(asyncDeleteListAction(sortFields[this.props.tabIndex], sortTypes[this.props.tabIndex])).then(()=>{
            this.loadData(keyword);
        });
    }
    loadNextPage()
    {
        this.loadData();
    }
    renderFooter()
    {
        if(!this.props.isFetching || this.props.hasMore !== true)
        {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID)
    {
        let imgUrl = item.GetImage ? item.GetImage.ImageFrontUrl : '';
        let price = (item.Price <= 0 ? '免费' :  ('¥' + item.Price + '起/人'));
        return (
            <div key={rowID} 
                className="scenicListItem"
                onClick={()=>{
                    if(this.props.onItemClick)
                    {
                        this.props.onItemClick(item);
                    }
                }}>
                <div style={{backgroundImage: `url(${imgUrl})`}} className="scenicListItemImg"/>
                <div className="scenicListItemTextBox">
                    <div className="lineListItemName">{item.CnName}</div>
                    <div className="lineListTravelName">{item.TravelName}</div>
                    <div className="lineListTrafficDayRow">
                        <div className="scenicListItemOthers">{item.Characterystic}</div>
                        <div style={{flex: 1}}/>
                        <div className="scenicListItemOthers">{item.DaysType}天</div>
                    </div>
                    <div className="lineListTrafficDayRow">
                        <div className="scenicListItemOthers">{item.Score}分</div>
                        <div style={{flex: 1}}/>
                        <div className="scenicListItemOthers">{price}</div>
                    </div>
                </div>
            </div>
        );
    }
    loadData(keyword)
    {
        if(this.props.isFetching)
        {
            return;
        }
        if(this.props.hasMore === false)
        {
            return;
        }

        let pageIndex = this.props.pageIndex >= 0 ? (this.props.pageIndex + 1) : 0;
        this.props.dispatch(asyncGetListAction(sortFields[this.props.tabIndex], sortTypes[this.props.tabIndex], pageIndex, keyword ? keyword : this.props.keyword));
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(sortFields[this.props.tabIndex], sortTypes[this.props.tabIndex], offsetY));
    }
}

LineListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

const mapStateToProps = function(state, ownProps)
{
    let obj = state.line.listScreen.groups[sortFields[ownProps.tabIndex]];
    return {
        selectedTabIndex: state.line.listScreen.selectedTabIndex,
        ...obj,
        inputKeyword: state.line.listScreen.keyword,
    };
};

LineListView = connect(mapStateToProps, null, null, {withRef: true})(LineListView);

export default LineListView;


