import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon} from 'antd-mobile';
import Content from '../../views/common/Content';
import FriendListView from '../../views/userProfile/FriendListView';
import { asyncDeleteFriendsListAction } from '../../store/actions/userProfile/userProfileAction';
import '../../assets/css/journey.css';
import SysUtil from '../../core/utils/SysUtil';
import queryString from 'query-string';
import * as IConstant from '../../core/utils/IConstant';

class FriendListScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }
    
    render()
    {
        let listViewHeight = window.innerHeight - 45;
        let title = (this.props.friendsType === IConstant.FRIEND_PROFILE_DATA_TYPE.FANS ? '粉丝' : '关注') + '列表' ;
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.props.dispatch(asyncDeleteFriendsListAction(this.props.targetUserId,this.props.userId,this.props.friendsType));
                        this.props.history.goBack();
                    }}>
                    {title}
                </NavBar>
                <Content>
                    <FriendListView
                        friendsType={this.props.friendsType}
                        targetUserId={this.props.targetUserId}
                        onItemClick={this.onItemClick}
                        height={listViewHeight}
                        ref={(listView) => {
                            if (listView) {
                                this.listView = listView.getWrappedInstance();
                            }
                        }} />
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        this.props.history.push({
            pathname: '/userProfile/friendProfile',
            search: `?targetUserId=${item.UserID}`,
        });
    }
}

const mapStateToProps = function(state, ownProps)
{
    let searchParams = queryString.parse(ownProps.location.search);
    return {
        userId: SysUtil.getUserId(state),
        ...searchParams
    };
};

FriendListScreen = connect(mapStateToProps)(FriendListScreen);

export default FriendListScreen;


