import React from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon, SearchBar } from 'antd-mobile';
import Content from '../../views/common/Content';
import NativeAppScreen from '../common/NativeAppScreen';
import TravelAgentListView from '../../views/travel-agent/TravelAgentListView';
import UIUtil from '../../core/utils/UIUtil';
import { setSelectedTabIndexAction, goBackAction, setKeywordAction } from '../../store/actions/travel-agent/listAction';
import '../../assets/css/travel-agent.css';

/**
 * 旅行社
 */
class TravelAgentListScreen extends NativeAppScreen
{
    tabs = [
        {title: '综合', tabIndex: 0},
        {title: '热度', tabIndex: 1},
        {title: '距离', tabIndex: 2},
        {title: '最新', tabIndex: 3},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    componentDidMount()
    {
        this.updateNativeTitle('旅行社');
    }
    render()
    {
        let navBarHeight = this.props.isFromNativeApp ? 0 : 45;
        let tabHeight = window.innerHeight - (navBarHeight + 44);
        let listViewHeight = window.innerHeight - (navBarHeight + 44 + 44);

        return (
            <div className={ this.props.isFromNativeApp ? 'isFromNativeApp' : null }>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark" 
                            className="navBar"
                            icon={<Icon type="left" />}
                            onLeftClick={this.goBack}>
                            旅行社
                        </NavBar>
                }
                <Content>
                    <SearchBar placeholder="请输入关键字" 
                        maxLength={20} 
                        value={this.props.keyword}
                        onChange={(val)=>{this.updateKeyword(val);}}
                        onSubmit={(val)=>{
                            this.updateKeyword(val);
                            this.searchByKeyword(val);
                        }}/>
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                            this.searchByKeyword(this.props.keyword, index);
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><TravelAgentListView key={index} 
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                tabIndex={item.tabIndex} 
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    updateKeyword(keyword)
    {
        this.props.dispatch(setKeywordAction(keyword));
    }
    searchByKeyword(keyword, index = undefined)
    {
        index = (index === undefined) ? this.props.selectedTabIndex : index;
        let listView: TravelAgentListView = this.tabs[index].listView;
        if(!listView)
        {
            return;
        }
        listView.searchByKeyword(keyword);
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: TravelAgentListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push(`/travel-agent/detail/${item.Code}`);
    }
    goBack()
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }
}

const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.travelAgent.listScreen.selectedTabIndex,
        keyword: state.travelAgent.listScreen.keyword,
        isFromNativeApp: state.native.isFromNativeApp
    };
};

TravelAgentListScreen = connect(mapStateToProps)(TravelAgentListScreen);

export default TravelAgentListScreen;


