import React, { PureComponent } from 'react';
import { NavBar, Icon, ActivityIndicator } from 'antd-mobile';
import { connect } from 'react-redux';
import Content from '../../views/common/Content';
import '../../assets/css/sign.css';
import '../../core/utils/DateUtil';
import WebDB from '../../core/utils/WebDB';
import { asyncGetDataAction, goBackAction } from '../../store/actions/sign/signAction';
import HttpClient from '../../core/utils/HttpClient';
import SysUtil from '../../core/utils/SysUtil';
import * as IConstant from '../../core/utils/IConstant';

class SignScreen extends PureComponent {
    constructor(props) {
        super(props);
        this.onSign = this.onSign.bind(this);
        let today = new Date().formatDate('YYYY-MM-DD');

        this.state = {
            weekDay: [],
            today: today,
            isLastMonth: true,
        };
    }
    componentDidMount() {
        if (!this.props.dataInfo) {
            this.loadData();
        }
    }
    render() {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    签到
                </NavBar>
                <Content>
                    {
                        this.renderPage()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }
    renderPage() {
        if (!this.props.dataInfo) {
            return;
        }
        let unsignButton = require('../../assets/images/sign/btn-signin.png');
        let signButton = require('../../assets/images/sign/btn-signedin.png');

        let signScoreBg = require('../../assets/images/sign/bg-top.jpg');
        let singRuleBg = require('../../assets/images/sign/bg-rule.png');

        let todayScore = 0;//今日获得积分
        let signDay = 0;//连续签到天数

        if (!this.props.dataInfo.CanSignIn && this.props.dataInfo.Logs && this.props.dataInfo.Logs.length > 0) {//已签到
            todayScore = this.props.dataInfo.Logs[0].BaseIntegration + this.props.dataInfo.Logs[0].RuleIntegration;
        }

        if (this.props.dataInfo.Logs && this.props.dataInfo.Logs.length > 0) {
            signDay = this.props.dataInfo.Logs[0].SignInCount;
        }

        return (
            <div className='signBox'>
                <div className='signScoreBox' style={{ backgroundImage: `url(${signScoreBg})` }}>
                    <div className='signScoreFlex'>
                        <div className='signScoreToday'>
                            <div className='signScoreTodayTip'>今日获得</div>
                            <div className='signScoreTodayValue'>{todayScore}积分</div>
                        </div>
                        <div className='signScoreButton'>
                            {
                                this.props.dataInfo.CanSignIn
                                    ?
                                    <img onClick={this.onSign} src={unsignButton} alt='' className='signButton' />
                                    :
                                    <img src={signButton} alt='' className='signButton' />
                            }
                        </div>
                        <div className='signScoreDayTimes'>
                            <div className='signScoreDayTimesTip'>连续签到</div>
                            <div className='signScoreDayTimesValue'>{signDay}天</div>
                        </div>
                    </div>
                    <div className='signTips'>每日签到送<b>{this.props.dataInfo.Info ? this.props.dataInfo.Info.Integration : ''}积分</b></div>
                </div>
                <div className='signCalendarBox'>
                    {
                        this.renderCalendar()
                    }
                </div>
                <div className='signRuleBox' style={{ backgroundImage: `url(${singRuleBg})` }}>
                    <h5 className='signRuleTip'>签到说明规则</h5>
                    <div className='signRuleContent'>
                        <p>1、每日签到可获得{this.props.dataInfo.Info ? this.props.dataInfo.Info.Integration : ''}积分</p>
                        {
                            this.props.dataInfo.Rules
                                ? this.props.dataInfo.Rules.map((item, index) =>
                                    <p key={index}>{index + 2}、连续签到{item.SignInCount}次，可获得{item.Integration}积分</p>
                                )
                                : null
                        }
                    </div>
                </div>
            </div>
        );
    }
    renderCalendar() {
        let weekTitle = ['周日', '周一', '周二', '周三', '周四', '周五', '周六'];

        let signCheck = require('../../assets/images/sign/day-active.png');

        let preMonth = require('../../assets/images/sign/calendar_last_unfocused.png');
        let nextMonth = require('../../assets/images/sign/calendar_next_unfocused.png');

        return (
            <div className='signCalendar'>
                <div className='signCalendarToday'>
                    <img src={preMonth} alt='' className='signCalendarMonthButton' onClick={() => this.getCalendarData(-1,false)} />
                    <span className='signCalendarDate'>{this.state.today}</span>
                    {
                        this.state.isLastMonth
                            ?
                            ''
                            :
                            <img src={nextMonth} alt='' className='signCalendarMonthButton' onClick={() => this.getCalendarData(1,false)} />
                    }
                </div>
                <div className='signCalendarContent'>
                    <ul className='weekTitleBox'>
                        {
                            weekTitle.map((item, index) =>
                                <li className='weekTitle' key={index}>
                                    <span>{item}</span>
                                </li>
                            )
                        }
                    </ul>
                    <ul className='weekDayBox'>
                        {
                            this.state.weekDay.map((item, index) =>
                                <li className='weekDayTitle' key={index}>
                                    {
                                        item.isSign
                                            ?
                                            <div className='signCalendarCheck' style={{ backgroundImage: `url(${signCheck})` }}>{item.value}</div>
                                            :
                                            <div>{item.value}</div>
                                    }
                                </li>
                            )
                        }
                    </ul>
                </div>
            </div>
        );
    }
    //获取当前月份日历数据
    getCalendarData(addMonth,isReset) {
        if (!this.props.dataInfo) {
            return;
        }
        let today;
        if(isReset){
            today = new Date().formatDate('YYYY-MM-DD');
        }else{
            today = new Date(this.state.today);
            today = new Date(today.setMonth(today.getMonth() + addMonth)).formatDate('YYYY-MM-DD');
        }
        

        let weekDay = [];
        let currDate = new Date(today);
        let firstDay = new Date(currDate.getFullYear(), currDate.getMonth(), 1); // 当前月的第一天
        let endDay = new Date(currDate.getFullYear(), (currDate.getMonth() + 1), 0); // 当前月的最后一天
        let days = endDay.getDate(); // 当前月的天数
        let week = firstDay.getDay();  // 当前月的第一天日期

        if (week > 0) {
            for (let i = 0; i < firstDay.getDay(); i++) {
                weekDay[i] = { isSign: false, value: '', date: '' };
            }
        }
        for (let i = 1; i <= days; i++) {
            let date = firstDay.addDays(i - 1).formatDate('YYYY-MM-DD');
            weekDay[i + firstDay.getDay() - 1] = { isSign: false, value: i + '', date: date };
        }

        if (this.props.dataInfo.Logs && this.props.dataInfo.Logs.length > 0) {
            for (let i = 0; i < this.props.dataInfo.Logs.length; i++) {
                let date = new Date(this.props.dataInfo.Logs[i].CreateTime).formatDate('YYYY-MM-DD');
                for (let j = 0; j < weekDay.length; j++) {
                    if (weekDay[j].date === date) {
                        weekDay[j].isSign = true;
                    }
                }
            }
        }

        if (new Date().formatDate('YYYY-MM') === new Date(today).formatDate('YYYY-MM')) {
            this.setState({
                isLastMonth: true,
                today: new Date().formatDate('YYYY-MM-DD'),
                weekDay: weekDay
            });
        } else {
            this.setState({
                isLastMonth: false,
                today: new Date(today).formatDate('YYYY-MM'),
                weekDay: weekDay
            });
        }
    }
    loadData() {
        this.props.dispatch(asyncGetDataAction()).then(() => {
            this.getCalendarData(0,true);
        });
    }
    onSign() 
    {
        let accountId = WebDB.getItem(IConstant.STORAGE_KEY.WX_ACCOUNT);
        let openId = WebDB.getItem(IConstant.STORAGE_KEY.WX_USER_OPENID);

        let url = HttpClient.WECHAT_HANDLER;
        let kvDict = {
            type: '7',
            action: '2',
            conditionParm: JSON.stringify({
                AccountGuid: accountId,
                OpenId: openId,
                Longitude: this.props.location.lng,
                Latitude: this.props.location.lat
            })
        };

        HttpClient.getData(url, kvDict).then((resp) => resp.json()).then((resp) => {
            if (resp.success) {
                this.loadData();
            }
        });
    }
}

const mapStateToProps = function (state) {
    return {
        ...state.sign.signScreen,
        location: SysUtil.getLocation(state),
    };
};

SignScreen = connect(mapStateToProps)(SignScreen);

export default SignScreen;


