import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

// 评论列表
export function asyncGetListAction(code, pageIndex)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(code, pageIndex));
        return _fetchList(code, pageIndex).then((resp)=>{
            dispatch(_getListSuccessAction(code, pageIndex, resp ? resp.results : {}));
        }).catch((error)=>{
            dispatch(_getListFailureAction(code, error));
        });
    };
}

export function goBackAction(code)
{
    return {
        type: IConstant.Actions.COMMENTS.LIST.GO_BACK,
        code,
    };
}

export function asyncDeleteListAction(code)
{
    return function(dispatch, getState)
    {
        dispatch(_deleteListAction(code));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

function _deleteListAction(code)
{
    return {
        type: IConstant.Actions.COMMENTS.LIST.DELETE,
        code,
    };
}

function _fetchList(code, pageIndex)
{
    let jsonDict = {
        Code: code,
        PageIndex: pageIndex,
        PageSize: IConstant.PAGE_SIZE
    };
    return HttpClient.postToPortal(IConstant.PortalApis.ME.GetCommentList,jsonDict,null,true)
        .then((resp) => resp.json());
}

function _getListRequestAction(code, pageIndex)
{
    return {
        type: IConstant.Actions.COMMENTS.LIST.GET_LIST_REQUEST,
        code,
        pageIndex,
    };
}

function _getListSuccessAction(code, pageIndex, resp)
{
    return {
        type: IConstant.Actions.COMMENTS.LIST.GET_LIST_SUCCESS,
        code,
        pageIndex,
        result: resp,
    };
}

function _getListFailureAction(code, error)
{
    return {
        type: IConstant.Actions.COMMENTS.LIST.GET_LIST_FAILURE,
        code,
        message: error
    };
}


