import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Modal, Toast, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/footmark.css';
import SysUtil from '../../core/utils/SysUtil';
import * as IConstant from '../../core/utils/IConstant';
import {
    goBackAction ,
    asyncUpdateFootmarkItemAndSiteIndexAction,
    updateFootmarkItemAndSiteIndexAction, 
    updateSiteIndexAction ,
    isFetchingChangeAction
} from '../../store/actions/footmark/createAction';
import { clearChooseAddressAction } from '../../store/actions/map/chooseAddressAction';
import CreateFootmarkView from '../../views/footmark/CreateFootmarkView';
import PhotoBrowserView from '../../views/photo-brower/PhotoBrowerView';
import HttpClient from '../../core/utils/HttpClient';

const FootmarkMaxSiteCount  = 5; //最多站点数
const SCREEN_WIDTH = window.innerWidth;
const TOPBAR_BOX_WIDTH = SCREEN_WIDTH - 20;
const TOPBAR_STEP_WIDTH = 100; // 头部站点  宽度

/** 创建/编辑  足迹
 *  isEdit 选填， 是否编辑， 默认false
 *  footmarkItem  编辑的足迹原始数据， 如果是编辑，该参数必填，
 */
class CreateFootmarkScreen extends PureComponent
{
    isEdit:boolean = false;
    constructor(props) {
        super(props);
        this.isEdit = this.props.history.location.state && this.props.history.location.state.isEdit ? 
            this.props.history.location.state.isEdit 
            : 
            false ;
        this.addNewSiteClicking = this.addNewSiteClicking.bind(this);
        this.deleteSiteClicking = this.deleteSiteClicking.bind(this);
        this.onChangeSiteIndexClicking = this.onChangeSiteIndexClicking.bind(this);
        this.showImageBrowser = this.showImageBrowser.bind(this);
        this.submitButtonClicking = this.submitButtonClicking.bind(this);
        this.onLocationSelectClick = this.onLocationSelectClick.bind(this);
    }
    componentDidMount()
    {
        if(!this.props.footmarkItem)
        {
            let footmarkItem = this.initFootmarkItem();
            this.props.dispatch(updateFootmarkItemAndSiteIndexAction(footmarkItem,0));
        }
        if (this.props.selectPoint && this.props.selectPoint !== undefined && this.props.footmarkItem)
        {
            let selectPoint = {...this.props.selectPoint};
            let footItem = {...this.props.footmarkItem};
            let siteItem = {...(footItem.GetShowList[this.props.siteIndex])};
            siteItem.Address = selectPoint.name;
            siteItem.Latitude = selectPoint.location.lat;
            siteItem.Longitude = selectPoint.location.lng;
            footItem.GetShowList[this.props.siteIndex] = siteItem;
            this.props.dispatch(clearChooseAddressAction());
            this.props.dispatch(updateFootmarkItemAndSiteIndexAction(footItem,this.props.siteIndex));
        }
    }

    initFootmarkItem()
    {
        let footmarkItem = this.props.footmarkItem ? 
            this.props.footmarkItem
            :
            (
                this.isEdit && this.props.history.location.state.footmarkItem ? 
                    this.props.history.location.state.footmarkItem
                    :
                    {
                        CreateTime: new Date().formatDate('YYYY-MM-DD HH:mm:ss') ,
                        Title: '',
                        GetShowList: [
                            this.initSiteItem()
                        ]
                    }
            );
        return footmarkItem;
    }
    initSiteItem()
    {
        let siteItem = {
            GetImage: {
                ImageList: []
            },
            CreateTime: new Date().formatDate('YYYY-MM-DD HH:mm'),
            Latitude: this.props.latitude,
            Longitude: this.props.longitude,
            Address: this.props.address,
            Content: ''
        };
        return siteItem;
    }
    
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => this.goBackClicking()}
                    rightContent={this.renderRightMenu()}>
                    {
                        this.isEdit ? ' 编辑足迹' : '发布足迹'
                    }
                </NavBar>
                <Content style={{backgroundColor: '#296fb1'}}>
                    <div style={{margin:'10px',backgroundColor:'#fff',borderRadius:'3px',height:'calc( 100% - 20px )'}}>
                        {
                            this.renderTabbarView()
                        }
                        {
                            this.renderTabContentView()
                        }
                    </div>
                    {
                        this.props.isFetching ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :
                            null
                    }
                    <PhotoBrowserView
                        ref={(view) => { this.photoBrowserView = view ? view.getWrappedInstance() : this.scrollView; }}
                    />
                </Content>
            </div>
        );
    }
    renderRightMenu()
    {
        return (
            <div onClick={this.submitButtonClicking} style={{ backgroundColor: '#00c6ad', fontSize: '14px', borderRadius: '3px', padding: '6px', color: '#fff' }} >
                发布
            </div>
        );
    }
    
    renderTabbarView()
    {
        if (!this.props.tabs || this.props.tabs.length <= 0)
        {
            return null;
        }
        let siteCount = this.props.tabs.length;
        return (
            <div ref={(view) => { this.topScrollView = view;}}
                style={{overflow:'scroll', display:'flex',flexDirection:'row',alignItems:'center',flex:1,backgroundColor:'#e1f0ff',borderRadius:'3px'}}>
                {
                    this.props.tabs.map((item,index)=><div key={index} onClick={()=>this.onChangeSiteIndexClicking(index)}
                        style={{ minWidth:TOPBAR_STEP_WIDTH + 'px' ,backgroundColor: index === this.props.siteIndex ? '#fff' : '#e1f0ff',display:'flex',flexDirection:'row',alignItems:'center',justifyContent:'space-around',height:'45px',borderRadius:'3px',borderTopRightRadius:'15px'}}>
                        <div style={{color:'#3f7ab2',fontSize:'14px',textAlign:'center',flex:2.5,width:'80px'}}>{item.title}</div>
                        {
                            !(siteCount === 1 && index === 0) ?
                                <div onClick={(e) => {
                                    e.stopPropagation();
                                    e.nativeEvent.stopImmediatePropagation();
                                    this.deleteSiteClicking(index);
                                }}
                                style={{ flex:1, height:'45px',marginRight:'5px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                    <img style={{ width: '17px' }} src={require('../../assets/images/footmark/footmark_ico-delete@2x.png')} alt='' />
                                </div>
                                :
                                null
                        }
                        {
                            index >= (this.props.siteIndex - 1) && index < (this.props.siteIndex + 1) ?
                                null
                                :
                                <img style={{ width: '1px',height:'30px' }} src={require('../../assets/images/footmark/footmark_send-dashed@2x.png')} alt='' />
                        }
                    </div>)
                }
                {
                    siteCount < FootmarkMaxSiteCount ?
                        <div style={{paddingLeft:'10px',paddingRight:'10px',height:'45px',display:'flex',alignItems:'center',justifyContent:'center'}} 
                            onClick={this.addNewSiteClicking}>
                            <img style={{width:'20px'}} src={require('../../assets/images/footmark/footmark_add@2x.png')} alt='' />
                        </div>
                        :
                        null
                }
            </div>
        );
    }

    renderTabContentView()
    {
        if (!this.props.tabs || this.props.tabs.length <= 0)
        {
            return null;
        }
        return <CreateFootmarkView siteIndex={this.props.siteIndex}
            showImageBrowser={this.showImageBrowser}
            onLocationSelectClick={this.onLocationSelectClick}/>;
    }

    goBackClicking() {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }

    onLocationSelectClick(siteItem)
    {
        this.props.history.push(`/map/chooseAddress/${siteItem.Address}/${siteItem.Latitude}/${siteItem.Longitude}`);
    }

    addNewSiteClicking()
    {
        let footmarkItem = this.props.footmarkItem;
        footmarkItem.GetShowList.push(this.initSiteItem());
        let siteIndex = footmarkItem.GetShowList.length - 1 ;
        
        this.props.dispatch(asyncUpdateFootmarkItemAndSiteIndexAction(footmarkItem,siteIndex)).then(()=>{
            let topMoveX = (TOPBAR_STEP_WIDTH * this.props.footmarkItem.GetShowList.length );
            if (TOPBAR_BOX_WIDTH < topMoveX) {
                this.topScrollView.scrollLeft = topMoveX - TOPBAR_BOX_WIDTH + 70;
            }
        });
    }

    deleteSiteClicking(index)
    {
        let currentSelect = this.props.siteIndex;
        let nextSelect = (currentSelect === index
            ?
            (index > 0 ? index - 1 : index)
            :
            (currentSelect > index ? currentSelect - 1 : currentSelect));

        let footmarkItem = this.props.footmarkItem;
        footmarkItem.GetShowList.splice(index,1);
        this.props.dispatch(updateFootmarkItemAndSiteIndexAction(footmarkItem,nextSelect));
    }

    onChangeSiteIndexClicking(index)
    {
        if (index === this.props.siteIndex)
        {
            return;
        }
        this.props.dispatch(updateSiteIndexAction(index));
    }
    showImageBrowser(imageList,index){
        if(this.photoBrowserView)
        {
            this.photoBrowserView.open(imageList, index);
        }
    }
    submitButtonClicking()
    {
        let inValuidIndex = this.isInputValid();
        if (inValuidIndex >= 0) {
            Modal.alert('提示', ('请填写完整' + this.props.tabs[inValuidIndex].title + '!'), [
                { text: '确定', style: 'default' },
            ]);
            return;
        }

        if ((this.props.footmarkItem.GetShowList.length > 0 && (!this.props.footmarkItem.Title || this.props.footmarkItem.Title === null || this.props.footmarkItem.Title.length === 0)) || (this.isEdit && this.props.footmarkItem.Title.length > 0)) {
            //标题输入
            Modal.prompt(
                '足迹标题',
                '请输入您的足迹标题',
                [
                    { text: '取消' },
                    {
                        text: '确定', onPress: value => {
                            let footmarkItem = {...this.props.footmarkItem};
                            footmarkItem.Title = value;
                            this.props.dispatch(asyncUpdateFootmarkItemAndSiteIndexAction(footmarkItem,this.props.siteIndex))
                                .then(()=> {
                                    if (!value || value.length <= 0) {
                                        Toast.info('足迹标题不能为空!', 1);
                                        return;
                                    }
                                    else {
                                        this.submitViewTapped();
                                    }
                                });
                        }
                    },
                ],
                'default',
                this.props.footmarkItem.Title
            );
            return;
        }
        this.submitViewTapped();
        
    }
    isInputValid() {
        //检测 数据输入 是否完整
        let isValidIndex = -1;
        for (let i = 0; i < this.props.footmarkItem.GetShowList.length; i++) {
            let item = this.props.footmarkItem.GetShowList[i];
            let isValid = item.Content && item.Content.length > 0 &&
                item.Address && item.Address.length > 0 &&
                item.CreateTime && item.CreateTime.length > 0 &&
                item.GetImage && item.GetImage.ImageList && item.GetImage.ImageList.length > 0;
            if (!isValid) {
                isValidIndex = i;
                break;
            }
        }
        return isValidIndex;
    }
    submitViewTapped()
    {
        let showList = [];
        let fileList = [];
        for (let i = 0; i < this.props.footmarkItem.GetShowList.length; i++) {
            let siteItem = this.props.footmarkItem.GetShowList[i];
            let showSiteItem = {
                'Code': siteItem.Code && siteItem.Code.length > 0 ? siteItem.Code : '',
                'Content': siteItem.Content,
                'Address': siteItem.Address,
                'CreateTime': siteItem.CreateTime,
                'Longitude': siteItem.Longitude,
                'Latitude': siteItem.Latitude,
                'Sort': (i + 1).toString(),
            };
            showList.push(showSiteItem);
            if (siteItem && siteItem.GetImage
                && siteItem.GetImage.ImageList
                && siteItem.GetImage.ImageList.length > 0) {
                for (let j = 0; j < siteItem.GetImage.ImageList.length; j++) {
                    let imageItem = siteItem.GetImage.ImageList[j];
                    let fileName = 'site_' + (i + 1) + '_' + (j + 1) + '.jpg';
                    let streamItem;
                    if (imageItem.file) {
                        // 构造新file , 修改name 属性, 如果直接赋值file, name 是只读的
                        // streamItem = new File([imageItem.file.file],fileName,{type: 'image/jpeg'});
                        streamItem = {
                            isRenameFile: true,
                            file: imageItem.file.file,
                            fileName: fileName
                        };
                    } else if (imageItem.SmallImgSrc && imageItem.SmallImgSrc.length > 0) {
                        // imageItem.SmallImgSrc.replace('Small', 'Large')
                        // streamItem = new HttpStreamFormModel('file', imageItem.ImgSrc);
                    }

                    if (!streamItem) {
                        continue;
                    }
                    streamItem.fieldName = fileName;
                    fileList.push(streamItem);
                }
            }
        }

        let themeCode = this.isEdit ? this.props.footmarkItem.Code : '';
        let jsonDict = {
            'jsonParams': {
                'Code':themeCode,
                'Title': this.props.footmarkItem.Title && this.props.footmarkItem.Title !== null  ? this.props.footmarkItem.Title : '',
                'UserID': this.props.userId,
                'CreateTime': this.props.footmarkItem.CreateTime,
                'ShowList': showList
            },
            'type': this.isEdit ? 1 : 0
        };
        this.props.dispatch(isFetchingChangeAction(true));

        HttpClient.postToPortal(IConstant.PortalApis.FOOTMARK.PUBLISH, jsonDict, null, true, fileList)
            .then((resp) => resp.json())
            .then((respJson) => {
                this.props.dispatch(isFetchingChangeAction(false));
                if(respJson.status === '0'){
                    Toast.info((this.isEdit ? '编辑' : '发布') + '足迹成功!', 1);
                    this.goBackClicking();
                }
                else
                {
                    Toast.info((this.isEdit ? '编辑' : '发布') + '足迹失败!', 1);
                }
            })
            .catch((error)=>{
                this.props.dispatch(isFetchingChangeAction(false));
                Toast.info('接口异常，请重试', 1);
            });
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.footmark.createScreen;
    return {
        ...obj,
        latitude: state.system.latitude,
        longitude: state.system.longitude,
        address: state.system.address, 
        userId: SysUtil.getUserId(state),
        selectPoint: state.map.chooseAddress.selectPoint
    };
};

CreateFootmarkScreen = connect(mapStateToProps)(CreateFootmarkScreen);

export default CreateFootmarkScreen;


