import React, { PureComponent } from 'react';
import { Toast } from 'antd-mobile';
import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import PropTypes from 'prop-types';
import SysUtil from '../../core/utils/SysUtil';
import HttpClient from '../../core/utils/HttpClient';
import * as Iconstant from '../../core/utils/IConstant';

import '../../assets/css/common.css';

class ElementOperationView extends PureComponent
{
    TYPE_ADD:number = 1;
    TYPE_CANCEL:number = 0;
    constructor(props)
    {
        super(props);
        this.state = {
            isCollect: false,
        };
        this.onCollectTapped = this.onCollectTapped.bind(this);
        this.onCommentsTapped = this.onCommentsTapped.bind(this);
    }
    render()
    {
        let leftIcon = require('../../assets/images/ico_surrond@2x.png');
        let leftTitle = '周边';
        if (this.props.entityType === Iconstant.EntityType.EntityType_Route)
        {
            leftIcon = require('../../assets/images/ico_notice@2x.png');
            leftTitle = '预定须知';
        }
        return (
            <div className="elementOperationView">
                <div className="elementOperationItem"
                    onClick={this.props.onNearByTapped}>
                    <img src={leftIcon} alt=''/>
                    <span>{leftTitle}</span>
                </div>
                <div className="elementOperationItem"
                    onClick={this.onCommentsTapped}>
                    <img src={require('../../assets/images/ico_comment@2x.png')} alt=''/>
                    <span>评论</span>
                </div>
                <div className="elementOperationItem"
                    onClick={this.onCollectTapped}>
                    {
                        this.state.isCollect
                            ? <img src={require('../../assets/images/ico_collect_active@2x.png')} alt=''/>
                            : <img src={require('../../assets/images/ico_collect@2x.png')} alt=''/>
                    }
                    <span>收藏</span>
                </div>
            </div>
        );
    }
    componentDidMount()
    {
        if(this.props.code && !isNaN(this.props.userId))
        {
            this.checkCollection();
        }
    }
    checkCollection()
    {
        let kvDict = {
            Code: this.props.code,
            UserID: this.props.userId
        };
        HttpClient.postToPortal(Iconstant.PortalApis.ME.IsCollect, kvDict, null, true)
            .then((resp) => resp.json())
            .then((resp)=>{
                if(resp)
                {
                    this.setState({ isCollect: resp.results });
                }
            });
    }
    onCollectTapped()
    {
        if(isNaN(this.props.userId))
        {
            this.props.history.push('/me/login');
            return;
        }
        let kvDict = {
            'Code': this.props.code,
            'UserID': this.props.userId,
            'Type': this.state.isCollect ? this.TYPE_CANCEL : this.TYPE_ADD, 
            'LikeType':4, 
            'IP':''
        };
        HttpClient.postToPortal(Iconstant.PortalApis.ME.AddCollect, kvDict, null, true)
            .then((resp) => resp.json())
            .then((resp)=>{
                if(resp)
                {
                    if ( resp.status === '0' )
                    {
                        this.setState({ isCollect: !this.state.isCollect });
                    }
                    Toast.success(resp.results, undefined, undefined, false);
                }
            });
    }
    onCommentsTapped()
    {
        this.props.history.push(`/comments/${this.props.code}`);
    }
}

ElementOperationView.propTypes = {
    // 周边点击事件
    onNearByTapped: PropTypes.func,
    code: PropTypes.string,
    //实体类别
    entityType: PropTypes.number,
};

const mapStateToProps = function(state, ownProps)
{
    let userId = SysUtil.getUserId(state);
    return {
        userId,
    };
};

ElementOperationView = connect(mapStateToProps)(withRouter(ElementOperationView));

export default ElementOperationView;


