import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, SegmentedControl } from 'antd-mobile';
import Content from '../../views/common/Content';
import FunRecommendListView from '../../views/fun/FunRecommendListView';
import {  
    goBackAction
} from '../../store/actions/fun/recommendListAction';
import '../../assets/css/fun.css';
import LeftContentView from '../../views/common/LeftContentView';

class FunRecommendListScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.showCommonList = this.showCommonList.bind(this);
    }
    render()
    {
        let listViewHeight = window.innerHeight - 45;
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    leftContent={<LeftContentView
                        ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                        goBackAction={goBackAction}
                        history={this.props.history}
                    />}
                    onLeftClick={() => {
                        this.LeftContentView.goHome();
                    }}>
                    <SegmentedControl values={['非玩不可', '全部娱乐']} 
                        tintColor="white" 
                        selectedIndex={0}
                        onValueChange={this.showCommonList}/>
                </NavBar>
                <Content>
                    <FunRecommendListView ref={(listView)=>{
                        if(listView)
                        {
                            this.listView = listView.getWrappedInstance();
                        }
                    }}
                    onItemClick={this.onItemClick}
                    height={listViewHeight}/>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item || !item.GetRelation || !item.GetRelation.Code)
        {
            return;
        }
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        this.props.history.push(`/fun/detail/${item.GetRelation.Code}`);
    }
    showCommonList()
    {
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        this.props.history.push('/fun/list?segment=true');
    }
}

FunRecommendListScreen = connect()(FunRecommendListScreen);

export default FunRecommendListScreen;


