import * as IConstant from '../../../core/utils/IConstant';
import '../../../core/utils/DateUtil';

export function setSelectedTabIndexAction(storeKey, index)
{
    return {
        type: IConstant.Actions.ME.NEAR_BY.SET_SELECTED_TAB_INDEX,
        index: index,
        storeKey,
    };
}

export function goBackAction(storeKey)
{
    return {
        type: IConstant.Actions.ME.NEAR_BY.GO_BACK,
        storeKey,
    };
}


