import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, ActivityIndicator, Picker,List, TextareaItem,Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import LeftContentView from '../../views/common/LeftContentView';
import '../../assets/css/forum.css';
import { asyncGetGroupList } from '../../store/actions/forum/listAction';
import HttpClient from '../../core/utils/HttpClient';
import * as IConstant from '../../core/utils/IConstant';
import SysUtil from '../../core/utils/SysUtil';

class PostForumScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.postTypeOnOK = this.postTypeOnOK.bind(this);
        this.submitTapped = this.submitTapped.bind(this);
        this.state = {
            postTypeSectionID: 0,
            postTitle: '',
            postContent: '',
            isLoading: false
        };
    }
    componentDidMount()
    {
        if(!this.props.groups.dataList)
        {
            this.loadData();
        }
    }
    render()
    {
        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark" 
                            className="navBar"
                            leftContent={<LeftContentView
                                ref={(view) => { this.leftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                                goBackAction={()=>{}}
                                history={this.props.history}
                            />}
                            onLeftClick={() => {
                                this.leftContentView.goHome();
                            }}
                            rightContent={<div className="postForum_rightButton" onClick={this.submitTapped} >
                                发布
                            </div>}
                        >
                            发布帖子
                        </NavBar>
                }
                <Content>
                    {
                        this.renderPostInputView()
                    }
                    {
                        this.props.groups.isFetching || this.state.isLoading
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderPostInputView()
    {
        if(!this.props.groups.postTypePickerDataList || this.props.groups.postTypePickerDataList.length === 0)
        {
            return null;
        };
        return (
            <div>
                <Picker data={this.props.groups.postTypePickerDataList}
                    title='帖子类型' 
                    cols={1}
                    extra="请选择(必选)"
                    value={[this.state.postTypeSectionID]}
                    onOk={this.postTypeOnOK}
                >
                    <List.Item arrow="horizontal">帖子类型</List.Item>
                </Picker>
                <div className="postForum_spliteView" />
                <TextareaItem
                    title="帖子标题"
                    placeholder="请输入帖子标题"
                    value={this.state.postTitle}
                    count={30}
                    onChange={val => this.setState({postTitle: val})}
                    rows={2}
                />
                <div className="postForum_spliteView" />
                <TextareaItem
                    title="帖子内容"
                    placeholder="请输入帖子内容"
                    value={this.state.postContent}
                    count={200}
                    onChange={val => this.setState({postContent: val})}
                    rows={5}
                />
                <div className="postForum_spliteView" />
            </div>);
    }
    loadData()
    {
        this.props.dispatch(asyncGetGroupList());
    }
    postTypeOnOK(val)
    {
        this.setState({postTypeSectionID: val[0]});
    }
    toLoginPage()
    {
        this.props.history.push('/me/login');
    }
    //发布
    submitTapped()
    {
        if(!this.props.userId)
        {
            this.toLoginPage();
            return;
        }
        if (this.state.postTypeSectionID === 0)
        {
            Toast.info('请选择帖子类型!', 1);
            return;
        }
        if (this.state.postTitle.length <= 0)
        {
            Toast.info('帖子标题不能为空!', 1);
            return;
        }
        if (this.state.postContent.length <= 0)
        {
            Toast.info('帖子内容不能为空!', 1);
            return;
        }
        this.postForumRequest();
    }
    postForumRequest()
    {
        this.setState({isLoading: true});
        let jsonDict = {
            sid: this.state.postTypeSectionID,
            title: this.state.postTitle,
            uid: this.props.userId,
            content: this.state.postContent,
            state: 1
        };
        HttpClient.postToPortal(IConstant.PortalApis.FORUM.InsertThread, jsonDict)
            .then((resp) => resp.json())
            .then((respJson) => {
                this.setState({isLoading: false});
                if(respJson.status === '0'){
                    Toast.info( '发布论坛帖子成功!', 1);
                    //进入帖子详情
                    let postId = respJson.results;
                    this.props.history.push(`/forum/detail/${postId}`);
                }
                else
                {
                    Toast.info('发布论坛帖子失败!', 1);
                }
            })
            .catch((error)=>{
                this.setState({isLoading: false});
                Toast.info('接口异常，请重试', 1);
            });
    }
}

const mapStateToProps = function(state)
{
    return {
        ...state.forum.listScreen,
        userId: SysUtil.getUserId(state),
        isFromNativeApp: state.native.isFromNativeApp
    };
};

PostForumScreen = connect(mapStateToProps)(PostForumScreen);

export default PostForumScreen;



