import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon, SearchBar, SegmentedControl } from 'antd-mobile';
import Content from '../../views/common/Content';
import ShoppingListView from '../../views/shopping/ShoppingListView';
import UIUtil from '../../core/utils/UIUtil';
import queryString from 'query-string';
import { setSelectedTabIndexAction, goBackAction, setKeywordAction } from '../../store/actions/shopping/listAction';
import '../../assets/css/shopping.css';

class ShoppingListScreen extends PureComponent
{
    tabs = [
        {title: '综合', tabIndex: 0},
        {title: '热度', tabIndex: 1},
        {title: '距离', tabIndex: 2},
        {title: '最新', tabIndex: 3},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render()
    {
        let tabHeight = window.innerHeight - (45 + 44);
        let listViewHeight = window.innerHeight - (45 + 44 + 44);

        let urlSearch = queryString.parse(this.props.location.search);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    {
                        (urlSearch && urlSearch.segment)
                            ?
                            <SegmentedControl values={['非逛不可', '全部购物']} 
                                tintColor="white" 
                                selectedIndex={1}
                                onValueChange={this.goBack}/>
                            :
                            '购物列表'
                    }
                </NavBar>
                <Content>
                    <SearchBar placeholder="请输入关键字" 
                        maxLength={20} 
                        value={this.props.keyword}
                        onChange={(val)=>{this.updateKeyword(val);}}
                        onSubmit={(val)=>{
                            this.updateKeyword(val);
                            this.searchByKeyword(val);
                        }}/>
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                            this.searchByKeyword(this.props.keyword, index);
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><ShoppingListView key={index} 
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                tabIndex={item.tabIndex} 
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    updateKeyword(keyword)
    {
        this.props.dispatch(setKeywordAction(keyword));
    }
    searchByKeyword(keyword, index = undefined)
    {
        index = (index === undefined) ? this.props.selectedTabIndex : index;
        let listView: ShoppingListView = this.tabs[index].listView;
        if(!listView)
        {
            return;
        }
        listView.searchByKeyword(keyword);
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: ShoppingListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push(`/shopping/detail/${item.Code}`);
    }
    goBack()
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }
}

const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.shopping.listScreen.selectedTabIndex,
        keyword: state.shopping.listScreen.keyword
    };
};

ShoppingListScreen = connect(mapStateToProps)(ShoppingListScreen);

export default ShoppingListScreen;


