import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.FESTIVAL_NEWS.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state,
            [action.code]: {
                isFetching: true,
            }
        };
    case Actions.FESTIVAL_NEWS.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
                detailItem: action.detailItem
            },
        };
    case Actions.FESTIVAL_NEWS.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
            }
        };
    case Actions.FESTIVAL_NEWS.DETAIL.GO_BACK:
        let _state = {...state};
        delete _state[action.code];
        return {
            ..._state
        };
    default:
        return state;
    }
}


