import { Actions } from '../../../core/utils/IConstant';

export default function mapNavReducer(state = {}, action)
{
    let obj = state[action.code];
    obj = obj ? obj : {};

    switch(action.type)
    {
    case Actions.MAP.MAPNAV.GO_BACK:
        let _state = {...state};
        delete _state[action.code];
        return {
            ..._state
        };
    case Actions.MAP.MAPNAV.UPDATE_FROM_TO_LOCATION:
        return {
            ..._state,
            [action.code]: {
                ...obj,
                fromLat:action.fromLat,
                fromLng:action.fromLng,
                toLat:action.toLat,
                toLng:action.toLng,
                isPanelShowing:false,
                navType:action.navType,
                navMethodIndex:0
            }
        };
    case Actions.MAP.MAPNAV.IS_FETCHING_CHANGE:
        return {
            ..._state,
            [action.code]: {
                ...obj,
                isFetching:action.isFetching,
            }
        };
    case Actions.MAP.MAPNAV.SELECT_NAV_TYPE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                navType:action.navType,
                navMethodIndex: 0
            }
        };
    case Actions.MAP.MAPNAV.IS_PANEL_SHOWING_CHANGE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isPanelShowing:action.isPanelShowing
            }
        };
    case Actions.MAP.MAPNAV.NAV_SEARCH_REQUEST:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching:true,
            }
        };
    case Actions.MAP.MAPNAV.NAV_SEARCH_SUCCESS:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching:false,
                navResults:action.results,
                totalTime: action.totalTime,
                totalDistance: action.totalDistance,
                dataIsValid: action.dataIsValid
            }
        };
    case Actions.MAP.MAPNAV.NAV_SEARCH_FAILURE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching:false,
                navResults: action.results,
                totalTime: '',
                totalDistance: '',
                dataIsValid: false
            }
        };
    case Actions.MAP.MAPNAV.NAV_METHOD_INDEX_CHANGE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                navMethodIndex:action.navMethodIndex
            }
        };
    case Actions.MAP.MAPNAV.MAPZOOM_AND_CENTERLOCATION_CHANGE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                mapZoom: action.mapZoom,
                centerLat: action.centerLat,
                centerLng: action.centerLng
            }
        };
        
    default:
        return state;
    }
}


