import React, { PureComponent } from 'react';

export default class EmptyView extends PureComponent
{
    render()
    {
        let className = `emptyView ${this.props.className}`;
        return (
            <div className={className} style={this.props.style} onClick={this.props.onClick}>
                <img className="emptyViewImage" src={require('../../assets/images/icon_Sad@2x.png')} alt=''/>
            </div>
        );
    }
}



