import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import UIUtil from '../../core/utils/UIUtil';
import * as IConstant from '../../core/utils/IConstant';
import { setSelectedTabIndexAction, goBackAction } from '../../store/actions/collect/listAction';
import '../../assets/css/collect.css';
import CollectListView from '../../views/collect/CollectListView';

class CollectListScreen extends PureComponent
{
    tabs = [
        {title: '景区', entityType: IConstant.EntityType.EntityType_Attraction},
        {title: '酒店', entityType: IConstant.EntityType.EntityType_Hotel},
        {title: '线路', entityType: IConstant.EntityType.EntityType_Route},
        {title: '餐厅', entityType: IConstant.EntityType.EntityType_Restaurant},
        {title: '购物', entityType: IConstant.EntityType.EntityType_Shopping},
        {title: '娱乐', entityType: IConstant.EntityType.EntityType_Entertainment}
        /*{title: '乡村游', entityType: IConstant.EntityType.EntityType_FarmHouse}*/
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render()
    {
        let tabHeight = window.innerHeight - 45;
        let listViewHeight = window.innerHeight - (45 + 44);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    我的收藏
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><CollectListView key={index} 
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                entityType={item.entityType} 
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: OrderListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        let entityType = this.tabs[this.props.selectedTabIndex].entityType;
        if(entityType === IConstant.EntityType.EntityType_Attraction)
        {
            this.props.history.push(`/scenic/detail/${item.Code}`);
        }
        else if (entityType === IConstant.EntityType.EntityType_Hotel)
        {
            this.props.history.push(`/hotel/detail/${item.Code}`);
        }
        else if (entityType === IConstant.EntityType.EntityType_Route)
        {
            this.props.history.push(`/line/detail/${item.Code}`);
        }
        else if (entityType === IConstant.EntityType.EntityType_Restaurant)
        {
            this.props.history.push(`/restaurant/detail/${item.Code}`);
        }
        else if (entityType === IConstant.EntityType.EntityType_Shopping)
        {
            this.props.history.push(`/shopping/detail/${item.Code}`);
        }
        else if (entityType === IConstant.EntityType.EntityType_Entertainment)
        {
            this.props.history.push(`/fun/detail/${item.Code}`);
        }
        else if (entityType === IConstant.EntityType.EntityType_FarmHouse)
        {
            this.props.history.push(`/farm-house/detail/${item.Code}`);
        }
    }
    goBack()
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }
}

const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.me.collect.selectedTabIndex,
    };
};

CollectListScreen = connect(mapStateToProps)(CollectListScreen);

export default CollectListScreen;


