import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function selectedTabIndexReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.FOOTMARK.LIST.SET_SELECTED_TAB_INDEX:
        return action.index;
    case Actions.FOOTMARK.LIST.GO_BACK:
        return 0;
    default:
        return state;
    }
}

function groupReducer(state = {}, action)
{
    let obj = state[getFootmarkGroupKey(action)];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.FOOTMARK.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [getFootmarkGroupKey(action)]: {
                ...obj,
                isFetching: true,
                pageIndex: action.pageIndex,
            }
        };
    case Actions.FOOTMARK.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        if(action.result.results)
        {
            dataList = dataList.concat(action.result.results);
        }
        return {
            ...state,
            [getFootmarkGroupKey(action)]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
                total: action.result.total,
                pageIndex: action.pageIndex,
                hasMore: (action.result.total > dataList.length)
            }
        };
    case Actions.FOOTMARK.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [getFootmarkGroupKey(action)]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.FOOTMARK.LIST.GO_BACK:
        return {};
    case Actions.FOOTMARK.LIST.DELETE:
        return {
            ...state,
            [getFootmarkGroupKey(action)]: {
                isFetching: false,
                dataList: [],
            }
        };
    case Actions.FOOTMARK.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [getFootmarkGroupKey(action)]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    case Actions.FOOTMARK.LIST.TOGGLE_EXPAND:
        let _dataList = obj.dataList;
        let item = _dataList[action.index];
        item = {
            ...item,
            isExpand: !item.isExpand
        };
        _dataList = _dataList.slice(0, action.index).concat(item).concat(_dataList.slice(action.index + 1));
        return {
            ...state, 
            [getFootmarkGroupKey(action)]: {
                ...obj,
                dataList: _dataList,
            }
        };
    case Actions.FOOTMARK.LIST.PRAISE_SUCCESS:
    {
        let dataList = obj.dataList;
        let item = dataList[action.index];
        item = {
            ...item,
            IsLike: item.IsLike === 0 ? 1 : 0,
            LikeCount: item.IsLike === 0 ? (item.LikeCount + 1) : (item.LikeCount -1)
        };
        dataList = dataList.slice(0, action.index).concat(item).concat(dataList.slice(action.index + 1));
        return {
            ...state, 
            [getFootmarkGroupKey(action)]: {
                ...obj,
                dataList: dataList
            }
        };
    }
    case Actions.FOOTMARK.LIST.COLLECT_SUCCESS:
    {
        let dataList = obj.dataList;
        let item = dataList[action.index];
        item = {
            ...item,
            IsCollection: item.IsCollection === 0 ? 1 : 0,
        };
        dataList = dataList.slice(0, action.index).concat(item).concat(dataList.slice(action.index + 1));
        return {
            ...state, 
            [getFootmarkGroupKey(action)]: {
                ...obj,
                dataList: dataList
            }
        };
    }
    default:
        return state;
    }
}

function getFootmarkGroupKey(action)
{
    return action.footmarkType + '_' + action.userId;
}

const listReducer = combineReducers({
    selectedTabIndex: selectedTabIndexReducer,
    groups: groupReducer,
});

export default listReducer;


