import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {Toast } from 'antd-mobile';
// eslint-disable-next-line
import DateUtil from '../../core/utils/DateUtil';
import SysUtil from '../../core/utils/SysUtil';
import {_asyncUpdateSelectVisitorAction, _asyncUpdateVisitorAction} from '../../store/actions/order/bookingAction';

class OrderBookingVisitorView extends PureComponent
{
    render()
    {
        return (
            <div className='orderBookingVisitorBox'>
                {
                    this.renderHeader()
                }
                {
                    this.renderVisitorList()
                }
            </div>
        );
    }
    renderHeader()
    {
        // if(!this.props.dataList || !this.props.selectDataList){
        //     return;
        // }
        return (
            <div className='orderBookingVisitorHeader'>
                <div className='orderBookingVisitorHeaderLeft'>
                    <img src={require('../../assets/images/order/book-tourism@2x.png')} alt='' className='orderBookingUserInfoHeaderLeftIcon' />
                    <span className="orderBookingVisitorHeaderText">游客信息</span>
                    {
                        this.props.selectDataList && this.props.selectDataList.length !== this.props.orderNumber
                            ?
                            <span className="orderBookingVisitorHeaderInfo">(还需添加
                                {this.props.orderNumber - this.props.selectDataList.length}
                                位游玩人信息)</span>
                            :
                            null
                    }
                </div>
                <div className='orderBookingVisitorHeaderRight' onClick={() => this.props.showVisitor()}>
                    <span>选择游客</span>
                    <img src={require('../../assets/images/order/arrow_right_green.png')} alt='' className='orderBookingVisitorHeaderRightIcon' />
                </div>
            </div>
        );
    }
    renderVisitorList(){
        if(!this.props.dataList || !this.props.selectDataList){
            return;
        }
        let corner=require('../../assets/images/order/corner@2x.png');

        return(
            <div className='orderBookingVisitorListBox'>
                <div className='orderBookingVisitorList'>
                    {
                        this.props.dataList.map((item, index) =>
                            this.ifVisitorChecked(item)
                                ?
                                <div key={index} className='orderBookingVisitorListItemChecked' onClick={()=>this.itemSelect(item)}>
                                    {item.UserName}
                                    <img src={require('../../assets/images/order/ticket_checked@2x.png')} alt='' className='orderBookingVisitorListItemCheckIcon' />
                                </div>
                                :
                                <div key={index} className='orderBookingVisitorListItem' onClick={()=>this.itemSelect(item)}>
                                    {item.UserName}
                                </div>
                        )
                    }
                </div>
                <div className='orderBookingVisitorFormList'>
                    {
                        this.props.selectDataList.map((item,index)=>
                            <div key={index} className='orderBookingVisitorFormListItem'>
                                <div className='orderBookingVisitorFormListItemContent'>
                                    <div className='orderBookingVisitorFormListItemContentName'>
                                        <div className='orderBookingVisitorFormListItemContentNameTitle'>姓名</div>
                                        <input 
                                            type='text' 
                                            placeholder='请输入姓名' 
                                            value={item.UserName}  
                                            className='orderBookingVisitorFormListItemContentNameInput' 
                                            onChange={(e)=>this.onChangeName(e,index)}
                                            onBlur={(e)=>this.onBlur(index)}/>
                                    </div>
                                    <div className='orderBookingVisitorFormListItemContentName'>
                                        <div className='orderBookingVisitorFormListItemContentNameTitle'>身份证号</div>
                                        <input 
                                            type='text' 
                                            placeholder='请输入身份证号' 
                                            value={item.IDCard}  
                                            className='orderBookingVisitorFormListItemContentNameInput' 
                                            onChange={(e)=>this.onChangeIdCard(e,index)}
                                            onBlur={(e)=>this.onBlur(index)}/>
                                    </div>
                                    <div className='orderBookingVisitorFormListItemCornerBg' style={{backgroundImage:`url(${corner})`}}>
                                        <div className="orderBookingVisitorFormListItemCornerNum">{index+1}</div>
                                    </div>
                                </div>
                            </div>
                        )
                    }
                </div>
            </div>
        );
    } 
    //游客是否被选中
    ifVisitorChecked(item){
        for(let i=0;i<this.props.selectDataList.length;i++){
            if (this.props.selectDataList[i].UserName === item.UserName && this.props.selectDataList[i].IDCard === item.IDCard) {
                return true;
            }
        }
        return false;
    }
    itemSelect(item){
        let index = 0;
        this.props.selectDataList.forEach((_item) => {
            if (_item.UserName !== '' && _item.IDCard !== '') {
                index++;
            }
        });
        let selectDataList=this.props.selectDataList;
        if (!this.ifVisitorChecked(item)) {
            if (index === this.props.orderNumber) {
                Toast.info('您只需要选择' + this.props.orderNumber + '位游玩人');
            } else {
                selectDataList[index] = {...item};
            }
        }else{
            for (let i = 0; i < selectDataList.length; i++) {
                if (selectDataList[i].UserName === item.UserName && selectDataList[i].IDCard === item.IDCard) {
                    selectDataList.splice(i, 1);
                    selectDataList[selectDataList.length] = {
                        ID: '',
                        UserName: '',
                        IDCard: ''
                    };
                    break;
                }
            }
        }
        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
    }
    onChangeName(e,index){
        let selectDataList=this.props.selectDataList;
        selectDataList[index].UserName=e.target.value;
        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
    }
    onChangeIdCard(e,index){
        let selectDataList=this.props.selectDataList;
        selectDataList[index].IDCard=e.target.value;
        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
    }
    onBlur(index){
        let obj={...this.props.selectDataList[index]};
        let dataList=this.props.dataList;
        let selectDataList=this.props.selectDataList;
        if(obj.UserName === ''){
            return;
        }
        if(obj.IDCard===''){
            return;
        }
        if (!SysUtil.ValidateIdcard(obj.IDCard)) {
            Toast.info('身份证号格式不正确!');
            obj={
                ID:'',
                UserName:obj.UserName,
                IDCard:''
            };
            selectDataList[index]=obj;
            this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
            return;
        }
        if(dataList.length===0){
            dataList.push({
                ID:'',
                UserName:obj.UserName,
                IDCard:obj.IDCard
            });
        }else{
            for(let i=0;i<dataList.length;i++){
                if (dataList[i].UserName === obj.UserName && dataList[i].IDCard === obj.IDCard) {
                    let num = 0;
                    for (let j = 0; j < selectDataList.length; j++) {
                        if (selectDataList[j].UserName === obj.UserName && selectDataList[j].IDCard === obj.IDCard) {
                            num++;
                        }
                    }
                    if (num >= 2) {
                        obj = {
                            ID: '',
                            UserName: '',
                            IDCard: ''
                        };
                        Toast.info('游客信息重复');
                        selectDataList[index] = obj;
                        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
                    }
                    break;
                }
                if(i===dataList.length-1){
                    dataList.push({
                        ID: '',
                        UserName: obj.UserName,
                        IDCard: obj.IDCard
                    });
                }
            }
        }
        this.props.dispatch(_asyncUpdateVisitorAction(dataList));
    }
}

OrderBookingVisitorView.propTypes = {
    showVisitor: PropTypes.func,
};

const mapStateToProps = function(state, ownProps)
{
    return {
        ...state.order.bookingScreen.visitor,
        orderNumber:state.order.bookingScreen.bookingItem.orderNumber
    };
};

OrderBookingVisitorView = connect(mapStateToProps, null, null, {withRef: true})(OrderBookingVisitorView);

export default OrderBookingVisitorView;


