import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ActivityIndicator, Popover, Toast, Modal } from 'antd-mobile';
import Content from '../../views/common/Content';
import { ItemType } from '../../core/utils/IConstant';
import { goBackAction, dataListChangeAction,beginDateChangeAction, deleteAddPointGroupListAction,setScrollPositionAction } from '../../store/actions/journey/editAction';
import '../../assets/css/journey.css';
import HttpClient from '../../core/utils/HttpClient';
import ReactDOM from 'react-dom';
import * as IConstant from '../../core/utils/IConstant';
import { clearDetailDataAction } from '../../store/actions/journey/detailAction';

const Item = Popover.Item;
const alert = Modal.alert;

class JourneyEditScreen extends PureComponent {
    code;
    groupList;
    headerInfo;
    constructor(props) {
        super(props);
        this.code = this.props.history.location.state.code;
        this.groupList = this.props.history.location.state.groupList;
        this.headerInfo = this.props.history.location.state.headerInfo;

        this.journeyMapTapped = this.journeyMapTapped.bind(this);
        this.submitViewTapped = this.submitViewTapped.bind(this);
        this.renderRow = this.renderRow.bind(this);
        this.renderHeader = this.renderHeader.bind(this);
        this.onDayMenuSelected = this.onDayMenuSelected.bind(this);
        this.showDayMenu = this.showDayMenu.bind(this);
        this.handleDayVisibleChange = this.handleDayVisibleChange.bind(this);
        this.showRowMenu = this.showRowMenu.bind(this);
        this.onRowMenuSelected = this.onRowMenuSelected.bind(this);
        this.handleRowVisibleChange = this.handleRowVisibleChange.bind(this);
        this.addPointTapped = this.addPointTapped.bind(this);
        this.pointDetailTapped = this.pointDetailTapped.bind(this);

        let dataList = this.props.dataList ? this.props.dataList : this.groupList;
        let menuVisibleList = [];
        let dayRowVisibleList = [];
        for (let i = 0; i < dataList.length; i++) {
            menuVisibleList.push(false);
            let dayVisibleList = [];
            for (let j = 0; j < dataList[i].length; j++) {
                dayVisibleList.push(false);
            }
            dayRowVisibleList.push(dayVisibleList);
        }
        this.state = {
            menuVisibleList: menuVisibleList,
            dayRowVisibleList: dayRowVisibleList,
            isLoading: false
        };
    }

    componentDidMount() {
        if (!this.props.dataList) {
            let beginDate = this.headerInfo && this.headerInfo.BeginDate.length > 0 ? new Date(this.headerInfo.BeginDate.replace(' ', 'T')).formatDate('YYYY-MM-DD') : new Date().formatDate('YYYY-MM-DD');
            this.props.dispatch(beginDateChangeAction(this.code, beginDate));
            this.props.dispatch(dataListChangeAction(this.code, JSON.parse(JSON.stringify(this.groupList))));
        }
        if (this.props.addPointGroupList)
        {
            let addPointGroupList = [...this.props.addPointGroupList];
            //删除已存在的
            this.props.dispatch(deleteAddPointGroupListAction(this.code));
            // 展示 已修改后的
            this.props.dispatch(dataListChangeAction(this.code, addPointGroupList));
        }
        this.restoreScrollViewPosition();
    }

    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if (this.props.dataList !== prevProps.dataList)
        {
        }
    }


    render() {
        return (
            <div className="journeyEdit">
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let code = this.code;
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction(code));
                    }}
                    rightContent={<div style={{color:'#fff',backgroundColor:'#00c6ad',fontSize:'14px',borderRadius:'4px',padding:'7px'}} onClick={()=>this.submitViewTapped()}>
                        完成
                    </div>}>
                    编辑行程
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    {
                        this.renderListView()
                    }
                    {
                        (this.state.isLoading )
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }

    renderListView() {
        if (this.props.dataList && this.props.dataList.length > 0) {
            return (
                <div className="listView" style={{ padding: '10px' }}  >
                    {
                        this.renderHeader()
                    }
                    <div className="rowBox">
                        {
                            this.props.dataList.map((item, index) => this.renderRow(item, index))
                        }
                    </div>
                </div>
            );
        }
    }

    renderHeader() {
        let pointCount = this.loadAllPointCount();
        return (
            <div className="headerBox" onClick={() => this.journeyMapTapped()}>
                <img className="headerBgImage" src={require('../../assets/images/journey/route-map@2x.png')} alt='' />
                <div className="headerTitle">已添加{pointCount}个地点，在地图中查看</div>
            </div>
        );
    }

    renderRow(item, dayIndex) {
        let pointCount = (item && item.length > 0 ? item.length : 0);
        let dayCount = this.props.dataList.length;
        let dayDate = this.getDayDate(dayIndex);
        let popoverOverlay = [
            <Item key="0">向上增一天</Item>,
            <Item key="1">向下增一天</Item>
        ];
        if (!(dayCount === 1 && dayIndex === 0)) {
            popoverOverlay.push(<Item key="2">删除</Item>);
        }
        if (dayIndex !== 0) {
            popoverOverlay.push(<Item key="3">向上移一天</Item>);
        }
        if ((dayCount - 1) !== dayIndex) {
            popoverOverlay.push(<Item key="4">向下移一天</Item>);
        }

        return (
            <div className="dayBox" key={dayIndex}>
                <div className="headBox">
                    <div className="headLeftBox">
                        <img className="leftIconImage" src={require('../../assets/images/journey/img-item@2x.png')} alt='' />
                        <div className="headDayInfo">
                            <div className="dayTitle">第{parseInt(dayIndex, 10) + 1}天</div>
                            <div className="dayDateStr">{dayDate}</div>
                        </div>
                    </div>
                    <Popover mask
                        overlayClassName="fortest"
                        overlay={popoverOverlay}
                        visible={this.state.menuVisibleList[dayIndex]}
                        align={{
                            overflow: { adjustY: 0, adjustX: 0 },
                            offset: [-10, 0],
                        }}
                        onSelect={(opt) => this.onDayMenuSelected(opt, dayIndex)}
                        onVisibleChange={(visible) => this.handleDayVisibleChange(visible, dayIndex)}>

                        <div onClick={() => this.showDayMenu(dayIndex)} style={{ padding: '10px', width: '30px', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                            <img src={require('../../assets/images/journey/menu@2x.png')} alt='' />
                        </div>
                    </Popover>

                </div>
                <div className="pointBox">
                    {
                        pointCount > 0 ?
                            item.map((pointItem, index) => this.renderPointItemRow(pointItem, index, ((pointCount - 1) === index), parseInt(dayIndex, 10)))
                            :
                            null
                    }
                </div>
                {
                    this.renderAddPointBox(parseInt(dayIndex, 10))
                }
            </div>
        );
    }

    renderPointItemRow(pointItem, rowIndex, isLast, dayIndex) {
        let pointStyle = isLast ? 'pointRowNoSplite' : 'pointRow';
        let popoverOverlay = [
            <Item key="0">删除</Item>
        ];
        if (rowIndex !== 0) {
            popoverOverlay.push(<Item key="1">向上移</Item>);
        }
        if (!isLast) {
            popoverOverlay.push(<Item key="2">向下移</Item>);
        }
        return (
            <div className={pointStyle} key={rowIndex}>
                <div className="projectName" onClick={()=>this.pointDetailTapped(pointItem)}>{pointItem.Name}</div>
                <Popover mask
                    overlayClassName="fortest"
                    overlay={popoverOverlay}
                    visible={this.state.dayRowVisibleList[dayIndex][rowIndex]}
                    align={{
                        overflow: { adjustY: 0, adjustX: 0 },
                        offset: [-10, 0],
                    }}
                    onSelect={(opt) => this.onRowMenuSelected(opt, dayIndex,rowIndex)}
                    onVisibleChange={(visible) => this.handleRowVisibleChange(visible, dayIndex,rowIndex)}>
                    <div onClick={() => this.showRowMenu(dayIndex,rowIndex)} style={{ padding: '10px', width: '30px', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <img style={{ width: '30px', height: '20px', marginRight: '10px' }} src={require('../../assets/images/journey/sequence@2x.png')} alt='' />
                    </div>
                </Popover>
                
            </div>
        );
    }

    renderAddPointBox(dayIndex) {
        return (
            <div className="addPointBox" >
                <div style={{ flex: 1, }}>去添加: </div>
                <div className="addPointScroll">
                    <div className="selectPointBox"
                        onClick={() => this.addPointTapped(ItemType.You, dayIndex)}>
                        <img className="selectIcon" src={require('../../assets/images/journey/icon_plan_since.png')} alt='' />
                        <div className="selectTypeName">附近景区</div>
                    </div>
                    <div className="selectPointBox"
                        onClick={() => this.addPointTapped(ItemType.Zhu, dayIndex)}>
                        <img className="selectIcon" src={require('../../assets/images/journey/icon_plan_hotel.png')} alt='' />
                        <div className="selectTypeName">附近酒店</div>
                    </div>
                    <div className="selectPointBox"
                        onClick={() => this.addPointTapped(ItemType.CanTing, dayIndex)}>
                        <img className="selectIcon" src={require('../../assets/images/journey/icon_plan_food.png')} alt='' />
                        <div className="selectTypeName">附近餐厅</div>
                    </div>
                    <div className="selectPointBox"
                        onClick={() => this.addPointTapped(ItemType.Gou, dayIndex)}>
                        <img className="selectIcon" src={require('../../assets/images/journey/icon_plan_business.png')} alt='' />
                        <div className="selectTypeName">附近购物</div>
                    </div>
                    <div className="selectPointBox"
                        onClick={() => this.addPointTapped(ItemType.Yu, dayIndex)}>
                        <img className="selectIcon" src={require('../../assets/images/journey/icon_plan_amusement.png')} alt='' />
                        <div className="selectTypeName">附近娱乐</div>
                    </div>
                </div>
            </div>
        );
    }

    getDayDate(index) {
        return new Date(this.props.beginDate).addDays(index).formatDate('YYYY-MM-DD');
    }

    loadAllPointCount() {
        let count = 0;
        if (this.props.dataList && this.props.dataList.length > 0) {
            for (let i = 0; i < this.props.dataList.length; i++) {
                let dayList = this.props.dataList[i];
                count += (dayList && dayList.length > 0 ? dayList.length : 0);
            }
        }
        return count;
    }

    updateAllMenuVisibleList(dataList)
    {
        let menuVisibleList = [];
        let dayRowVisibleList = [];
        for (let i = 0; i < dataList.length; i++) {
            menuVisibleList.push(false);
            let dayVisibleList = [];
            for (let j = 0; j < dataList[i].length; j++) {
                dayVisibleList.push(false);
            }
            dayRowVisibleList.push(dayVisibleList);
        }
        this.setState({menuVisibleList:menuVisibleList,dayRowVisibleList:dayRowVisibleList});
    }

    showDayMenu(dayIndex) {
        let menuVisibleList = [...this.state.menuVisibleList];
        menuVisibleList[dayIndex] = true;
        this.setState({ menuVisibleList: menuVisibleList });
    }

    handleDayVisibleChange(visible, dayIndex) {
        let menuVisibleList = [...this.state.menuVisibleList];
        menuVisibleList[dayIndex] = visible;
        this.setState({ menuVisibleList: menuVisibleList });
    }

    onDayMenuSelected(opt, dayIndex) {
        let index = parseInt(opt.key, 10);
        let dataList = [...this.props.dataList];
        if (index === 0) {
            if ( dataList.length >= this.props.journeyMaxDayCount )
            {
                this.updateAllMenuVisibleList(dataList);
                Modal.alert('提示', '最多只能添加' + this.props.journeyMaxDayCount + '天的行程!' , [
                    { text: 'OK', style: 'default', onPress: () => {} },
                ]);
                return;
            }
            if (dayIndex === 0) {
                //如果是 第一天， 向上增一天，开始日期 提前一天
                let beginDate = new Date(this.props.beginDate).addDays(-1).formatDate('YYYY-MM-DD');
                this.props.dispatch(beginDateChangeAction(this.code, beginDate));
            }
            //向上增 一天
            dataList.splice(dayIndex, 0, []);
            this.props.dispatch(dataListChangeAction(this.code, dataList));
        }
        else if (index === 1) {
            if ( dataList.length >= this.props.journeyMaxDayCount )
            {
                this.updateAllMenuVisibleList(dataList);
                Modal.alert('提示', '最多只能添加' + this.props.journeyMaxDayCount + '天的行程!' , [
                    { text: 'OK', style: 'default', onPress: () => {} },
                ]);
                return;
            }
            //向下增 一天
            dataList.splice(dayIndex + 1, 0, []);
            this.props.dispatch(dataListChangeAction(this.code, dataList));
        }
        else if (index === 2) {
            //删除
            dataList.splice(dayIndex, 1);
            this.props.dispatch(dataListChangeAction(this.code, dataList));
        }
        else if (index === 3) {
            //向上移 一天
            let firstItem = dataList[dayIndex - 1];
            let secondItem = dataList[dayIndex];
            dataList.splice(dayIndex - 1, 1, secondItem);
            dataList.splice(dayIndex, 1, firstItem);
            this.props.dispatch(dataListChangeAction(this.code, dataList));
        }
        else if (index === 4) {
            //向下移 一天
            let firstItem = dataList[dayIndex];
            let secondItem = dataList[dayIndex + 1];
            dataList.splice(dayIndex + 1, 1, firstItem);
            dataList.splice(dayIndex, 1, secondItem);
            this.props.dispatch(dataListChangeAction(this.code, dataList));
        }
        this.updateAllMenuVisibleList(dataList);
    }

    showRowMenu(dayIndex,rowIndex) {
        let dayRowVisibleList = [...this.state.dayRowVisibleList];
        dayRowVisibleList[dayIndex][rowIndex] = true;
        this.setState({ dayRowVisibleList: dayRowVisibleList });
    }

    handleRowVisibleChange(visible, dayIndex, rowIndex){
        let dayRowVisibleList = [...this.state.dayRowVisibleList];
        dayRowVisibleList[dayIndex][rowIndex] = false;
        this.setState({ dayRowVisibleList: dayRowVisibleList });
    }

    onRowMenuSelected(opt, dayIndex,rowIndex){
        let dataList = [...this.props.dataList];
        let index = parseInt(opt.key, 10);
        if (index === 0) {
            //删除
            dataList[dayIndex].splice(rowIndex,1);
            this.props.dispatch(dataListChangeAction(this.code, dataList));
        }
        else if (index === 1) {
            //向上移
            let dayItem = dataList[dayIndex];
            let firstPointItem = dayItem[rowIndex - 1];
            let secondPointItem = dayItem[rowIndex];
            dayItem.splice(rowIndex - 1, 1, secondPointItem);
            dayItem.splice(rowIndex, 1, firstPointItem);
            this.props.dispatch(dataListChangeAction(this.code, dataList));
        }
        else if (index === 2) {
            //向下移
            let dayItem = dataList[dayIndex];
            let firstPointItem = dayItem[rowIndex];
            let secondPointItem = dayItem[rowIndex + 1];
            dayItem.splice(rowIndex + 1, 1, firstPointItem);
            dayItem.splice(rowIndex, 1, secondPointItem);
            this.props.dispatch(dataListChangeAction(this.code, dataList));
        }
        this.updateAllMenuVisibleList(dataList);
    }
    
    //进入 行程地图
    journeyMapTapped()
    {
        this.cacheScrollOffset();
        this.props.history.push({pathname:'/journey/mapLine',state:{code:this.code,groupList:this.props.dataList}});
    }

    // 新增兴趣点
    addPointTapped(pointType,dayIndex)
    {
        this.cacheScrollOffset();
        this.props.history.push({pathname:'/journey/addPoint',state:{code:this.code,pointType:pointType,groupList:this.props.dataList,dayIndex:dayIndex}});
    }

    pointDetailTapped(item)
    {
        //兴趣点详情
        this.cacheScrollOffset();
        let code = item.ProjectCode;
        if (code.indexOf('JQ') >= 0) {
            this.props.history.push(`/scenic/detail/${code}`);
        }
        else if(code.indexOf('JD') >= 0)
        {
            this.props.history.push(`/hotel/detail/${code}`);
        }
        else if(code.indexOf('CT') >= 0)
        {
            this.props.history.push(`/restaurant/detail/${code}`);
        }
        else if(code.indexOf('SD') >= 0 || code.indexOf('SQ') >= 0)
        {
            this.props.history.push(`/shopping/detail/${code}`);
        }
        else if(code.indexOf('YL') >= 0)
        {
            this.props.history.push(`/fun/detail/${code}`);
        }
    }

    // 编辑完成
    submitViewTapped()
    {
        //没有 兴趣点
        if (this.loadAllPointCount() <= 0) {
            Toast.info('请至少选择一个兴趣点!',2);
            return;
        }

        //是否 有空 日期 
        let isHasNullDayCount = 0;
        let dataList = this.props.dataList;
        for (let i = 0; i < dataList.length; i++) {
            let dayList = dataList[i];
            if (dayList.length === 0) {
                isHasNullDayCount += 1;
            }
        }
        if (isHasNullDayCount > 0)
        {
            alert('提示','存在没有兴趣点的日期,该日期将被删除，是否保存?',[
                { text:'取消', onPress:()=>{}},
                { text:'确定', onPress:()=>{
                    let dataList = [...this.props.dataList];
                    let beginDate = this.props.beginDate;
                    for (let i = 0 ; i < isHasNullDayCount; i++)
                    {
                        let newDataList = [...dataList];
                        for (let j = 0 ; j < newDataList.length; j++)
                        {
                            if (newDataList[j].length <= 0)
                            {
                                if(j === 0)
                                {
                                    //如果是 第一个， 开始日期 向后挪动一天
                                    beginDate = new Date(beginDate).addDays(1).formatDate('YYYY-MM-DD');
                                    
                                }
                                dataList.splice(j,1);
                                break;
                            }
                        }
                    }
                    this.updateAllMenuVisibleList(dataList);
                    this.props.dispatch(beginDateChangeAction(this.code,beginDate));
                    this.props.dispatch(dataListChangeAction(this.code,dataList));
                }}
            ]);
        }
        else
        {
            //判断是否 有修改 ， 没有 给提示
            let dataList = this.props.dataList;
            let isHasChange = (dataList.length !== this.groupList.length);
            if (!isHasChange) {
                for (let i = 0; i < dataList.length; i++) {
                    let contentItem = dataList[i];
                    let oldContentItem = this.groupList[i];
                    isHasChange = (contentItem.length !== oldContentItem.length);
                    if (isHasChange) {
                        break;
                    } else {
                        for (let j = 0; j < contentItem.length; j++) {
                            let pointItem = contentItem[j];
                            let oldPointItem = oldContentItem[j];
                            isHasChange = (pointItem.Code !== oldPointItem.Code);
                            if (isHasChange) {
                                break;
                            }
                        }
                    }
                    if (isHasChange) {
                        break;
                    }
                }
            }
            if (isHasChange) {
                this.doSubmitEditDone();
            } else {
                Toast.info('请修改行程后选择保存!',2);
            }
        }    
    }

    getEditJsonParmString() {
        let jsonList = [];
        let contentList = this.props.dataList;
        for (let i = 0; i < contentList.length; i++) {
            let contentItem = contentList[i];
            let dayIndexStr = (i + 1).toString();
            for (let j = 0; j < contentItem.length; j++) {
                let pointItem = contentItem[j];
                let PX = (j + 1).toString();
                let jsonItem = {
                    'day': dayIndexStr,
                    'protype': pointItem.projecttype,
                    'procode': (pointItem.Code && pointItem.Code.length > 0 ? pointItem.Code : ''),
                    'proname': (pointItem.Name && pointItem.Name.length > 0 ? pointItem.Name : ''),
                    'proshortname': (pointItem.ShortName && pointItem.ShortName.length > 0 ? pointItem.ShortName : ''),
                    'price': (pointItem.Price && pointItem.Price.length > 0 ? pointItem.Price : '0.00'),
                    'px': PX,
                    'Remark': (pointItem.remark && pointItem.remark.length > 0 ? pointItem.remark : ''),
                    'times': (pointItem.PlayTime && pointItem.PlayTime.length > 0 ? pointItem.PlayTime : ''),
                    'district': (pointItem.District && pointItem.District.length > 0 ? pointItem.District : ''),
                    'planproid': (pointItem.PlanProjectId && pointItem.PlanProjectId.length > 0 ? pointItem.PlanProjectId : ''),
                    'plandayid': (pointItem.plandayId && pointItem.plandayId.length > 0 ? pointItem.plandayId : '' ),
                    'latitude': (pointItem.Latitude && pointItem.Latitude.length > 0 ? pointItem.Latitude : ''),
                    'longitude': (pointItem.Longitude && pointItem.Longitude.length > 0 ? pointItem.Longitude : ''),
                    'phone': (pointItem.Telephone && pointItem.Telephone.length > 0 ? pointItem.Telephone : ''),
                    'address': (pointItem.Address && pointItem.Address.length > 0 ? pointItem.Address : ''),
                    'imgurl': (pointItem.GetImage && pointItem.GetImage && pointItem.GetImage.MiddleImageFrontUrl ? pointItem.GetImage.MiddleImageFrontUrl : '')
                };
                jsonList.push(jsonItem);
            }
        }

        let jsonTourApi = {
            curTime: this.props.beginDate,
            planMainID : this.headerInfo.PlanMainID,
            strJson: jsonList,
            ApiCode: 'EditTour'
        };
        return jsonTourApi;
    }
    doSubmitEditDone()
    {
        this.setState({isLoading: true});
        let kvDict = this.getEditJsonParmString();
        HttpClient.postToPortal(IConstant.PortalApis.JOURNEY.TourApi, kvDict, null, false)
            .then((resp) => resp.json()).then((resp) => {
                this.setState({isLoading: false});
                if (resp && resp.status && parseInt(resp.status,10) === 0) {
                    alert('提示', '修改行程成功', [
                        {
                            text: 'OK', style: 'default', onPress: () => {
                                //返回
                                let dispatch = this.props.dispatch;
                                this.props.history.goBack();

                                //清除 行程详情数据
                                dispatch(clearDetailDataAction(this.code));
                                dispatch(goBackAction(this.code));
                            }
                        },
                    ]);
                } else {
                    alert('提示', '修改行程失败', [
                        { text: 'OK', style: 'default' },
                    ]);
                }
            })
            .catch((error) => {
                this.setState({isLoading: false});
            });
    }

    // 缓存位置
    cacheScrollOffset() {
        let offsetY = ReactDOM.findDOMNode(this.scrollView).scrollTop;
        this.props.dispatch(setScrollPositionAction(this.code, offsetY));
    }
    restoreScrollViewPosition() {
        if (this.scrollView) {
            if (!isNaN(this.props.scrollViewOffset)) {
                this.scrollView.scrollToY(this.props.scrollViewOffset);
            }
            else {
                this.scrollView.scrollToY(0);
            }
        }
    }
}

const mapStateToProps = function (state, ownProps) {
    let obj = state.journey.editScreen[ownProps.history.location.state.code];
    let addPointGroupList = state.journey.addPointScreen.groupList;
    return {
        ...obj,
        journeyMaxDayCount: state.system.journeyMaxDayCount,
        addPointGroupList
    };
};


JourneyEditScreen = connect(mapStateToProps)(JourneyEditScreen);

export default JourneyEditScreen;


