import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.FORUM.LIST.GET_GROUP_LIST_REQUEST:
        return {
            ...state, 
            isFetching: true,
            dataList: [],
        };
    case Actions.FORUM.LIST.GET_GROUP_LIST_SUCCESS:
    {
        let postTypePickerDataList = [];
        for (let i = 0; i < action.groupList.length; i++)
        {
            let item = action.groupList[i];
            postTypePickerDataList.push({
                label: item.SectionName,
                value: item.SectionID + '',
            });
        }
        return {
            ...state,
            isFetching: false,
            dataList: action.groupList,
            selectedIndex: 0,
            postTypePickerDataList: postTypePickerDataList
        };
    }
    case Actions.FORUM.LIST.GET_GROUP_LIST_FAILURE:
        return {
            ...state,
            isFetching: false,
            dataList: [],
        };
    case Actions.FORUM.LIST.SET_SELECTED_GROUP_INDEX:
        return {
            ...state, 
            selectedIndex: action.index,
        };
    case Actions.FORUM.LIST.GO_BACK:
        return {};
    default:
        return state;
    }
}

function tabReducer(state = {}, action)
{
    let obj = state[action.sectionID];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.FORUM.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [action.sectionID]: {
                ...obj,
                isFetching: true,
                pageIndex: action.pageIndex,
            }
        };
    case Actions.FORUM.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        if(action.result.results)
        {
            dataList = dataList.concat(action.result.results);
        }
        return {
            ...state,
            [action.sectionID]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
                total: action.result.total,
                pageIndex: action.pageIndex,
                hasMore: (action.result.total > dataList.length)
            }
        };
    case Actions.FORUM.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [action.sectionID]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.FORUM.LIST.GO_BACK:
        return {};
    case Actions.FORUM.LIST.DELETE:
        return {
            ...state,
            [action.sectionID]: {
                isFetching: false,
                dataList: [],
            }
        };
    case Actions.FORUM.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [action.sectionID]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    default:
        return state;
    }
}

const listReducer = combineReducers({
    tabs: tabReducer,
    groups: groupReducer,
});

export default listReducer;



