import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';
import scenicListReducer from './scenicListReducer';
import hotelListReducer from './hotelListReducer';
import funListReducer from './funListReducer';
import farmHouseListReducer from './farmHouseListReducer';
import shoppingListReducer from './shoppingListReducer';
import restaurantListReducer from './restaurantListReducer';

function selectedTabIndexReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.ME.NEAR_BY.SET_SELECTED_TAB_INDEX:
    {
        let _state = {...state};
        _state[action.storeKey] = action.index;
        return _state;
    }
    case Actions.ME.NEAR_BY.GO_BACK:
    {
        let _state = {...state};
        delete _state[action.storeKey];
        return _state;
    }
    default:
        return state;
    }
}

const nearByReducer = combineReducers({
    scenicList: scenicListReducer,
    hotelList: hotelListReducer,
    funList: funListReducer,
    farmHouseList: farmHouseListReducer,
    selectedTabIndexGroup: selectedTabIndexReducer,
    shoppingList: shoppingListReducer,
    restaurantList: restaurantListReducer,
});

export default nearByReducer;


