import React from 'react';
import { connect } from 'react-redux';
import { NavBar, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import NativeAppScreen from '../common/NativeAppScreen';
import { 
    asyncGetDetailAction, 
    goBackAction, 
} from '../../store/actions/news/detailAction';

import '../../assets/css/news.css';
import * as IConstant from '../../core/utils/IConstant';
import LeftContentView from '../../views/common/LeftContentView';

class NewsDetailScreen extends NativeAppScreen
{
    componentDidMount()
    {
        this.updateNativeTitle('详情');
        this.loadData();
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(this.props.isFromNativeApp && !prevProps.isNativeAppReady && this.props.isNativeAppReady)
        {
            this.sendMessageToNative(IConstant.NATIVE_MESSAGE_TYPE.SHOW_SHARE, {isShowShare: true , url: window.location.href});
        }
    }
    render()
    {
        return (
            <div className={ this.props.isFromNativeApp ? 'isFromNativeApp' : null }>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark" 
                            className="navBar"
                            leftContent={<LeftContentView
                                ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                                goBackAction={() => goBackAction(this.props.match.params.code)}
                                history={this.props.history}
                            />}
                            onLeftClick={() => {
                                this.LeftContentView.goHome();
                            }}>
                            详情
                        </NavBar>
                }
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    {
                        this.renderHeaderView()
                    }
                    {
                        this.renderContentView()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderHeaderView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div>
                <div className="newsDetailHeaderRow">
                    <div className="newsDetailHeaderName">{this.props.detailItem.Head}</div>
                    <div className="newsListSecondRow">
                        <img className="newsListIcon" src={require('../../assets/images/time.png')} alt=''/>
                        <div className="newsListItemOthers">{this.props.detailItem.CreateDate}</div>
                        <div style={{flex: 1}}/>
                        <img className="newsListIcon" src={require('../../assets/images/eye.png')} alt=''/>
                        <div className="newsListItemOthers">{this.props.detailItem.ClickCount}</div>
                    </div>
                </div>
                <div className="newsDetailSplitLine"/>
            </div>
        );
    }
    renderContentView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div className="newsDetailHtml" dangerouslySetInnerHTML={{__html: this.props.detailItem.content}}></div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetDetailAction(this.props.match.params.code));
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.news.detailScreen[ownProps.match.params.code];
    return {
        ...obj,
        isFromNativeApp: state.native.isFromNativeApp,
        isNativeAppReady: state.native.isNativeAppReady,
    };
};

NewsDetailScreen = connect(mapStateToProps)(NewsDetailScreen);

export default NewsDetailScreen;


