import { Actions } from '../../../core/utils/IConstant';

export default function listReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.JOURNEY.MINE.GET_LIST_REQUEST:
        return {
            ...state,
            isFetching: true,
            pageIndex: action.pageIndex
        };
    case Actions.JOURNEY.MINE.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : state.dataList;
        dataList = dataList ? dataList : [];
        if(action.resp)
        {
            dataList = dataList.concat(action.resp);
        }
        return {
            ...state,
            dataList: dataList,
            isFetching: false,
            pageIndex: action.pageIndex,
            hasMore: (action.resp.length >= action.pageSize)
        };
    case Actions.JOURNEY.MINE.GET_LIST_FAILURE:
        return {
            isFetching: false,
        };
    case Actions.JOURNEY.MINE.DELETE:
        return {
            isFetching: false,
            dataList: [],
        };
    case Actions.JOURNEY.MINE.SET_SCROLL_OFFSET:
        return {
            ...state,
            listViewOffset:action.scrollViewOffset
        };
    case Actions.JOURNEY.MINE.GO_BACK:
        return {};
    default:
        return {
            ...state
        };
    }
}



