import { combineReducers } from 'redux';
import { Actions, SpecialStoreKey } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    // 我的周边列表
    if(!action.extraParams || action.extraParams.storeKey !== SpecialStoreKey.ME_NEAR_BY_SCENIC)
    {
        return {...state};
    }
    let reduxStoreKey = action.orderFiled;
    if(action.extraParams && action.extraParams.lat && action.extraParams.lng)
    {
        reduxStoreKey = `${reduxStoreKey}_${action.extraParams.lat}_${action.extraParams.lng}`;
    }
    if(action.type === Actions.ME.NEAR_BY.GO_BACK)
    {
        let _state = {...state};
        delete _state[reduxStoreKey];
        return {..._state};
    }
    let obj = state[reduxStoreKey];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.SCENIC.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [reduxStoreKey]: {
                ...obj,
                isFetching: true,
                pageIndex: action.pageIndex,
                keyword: action.keyword,
            }
        };
    case Actions.SCENIC.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 0 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        if(action.result.results)
        {
            dataList = dataList.concat(action.result.results);
        }
        return {
            ...state,
            [reduxStoreKey]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
                total: action.result.total,
                pageIndex: action.pageIndex,
                keyword: action.keyword,
                hasMore: (action.result.total > dataList.length)
            }
        };
    case Actions.SCENIC.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [reduxStoreKey]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.SCENIC.LIST.GO_BACK:
        let _state = {...state};
        delete _state[reduxStoreKey];
        return {..._state};
    case Actions.SCENIC.LIST.DELETE:
        return {
            ...state,
            [reduxStoreKey]: {
                isFetching: false,
                dataList: [],
            }
        };
    case Actions.SCENIC.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [reduxStoreKey]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    default:
        return state;
    }
}

const listReducer = combineReducers({
    groups: groupReducer,
});

export default listReducer;


