import * as IConstant from '../../../core/utils/IConstant';
import Config from '../../../core/utils/Config';
import HttpClient from '../../../core/utils/HttpClient';
import WebDB from '../../../core/utils/WebDB';

export function goBackAction()
{
    return {
        type: IConstant.Actions.WEATHER.GO_BACK
    };
}

// 获取天气数据
export function asyncGetWeatherAction()
{
    return function(dispatch, getState)
    {
        dispatch(_getWeatherRequestAction());
        return _fetchWeather().then((resp)=>{
            WebDB.setItem('HOME_WEATHER', resp);
            WebDB.setItem('HOME_WEATHER_TIME', new Date().getTime());
            resp = resp.results && resp.results.length > 0  ? resp.results[0] : null;
            dispatch(_getWeatherSuccessAction(resp));
        }).catch((error)=>{
            dispatch(_getWeatherFailureAction(error));
        });
    };
}

function _fetchWeather()
{
    let url = HttpClient.HOST_WEATHER;
    let kvDict = {
        location: Config.instance.weatherCityName,
        output: 'json',
        ak: 'wzkXpn7CyH8zOpOfFzUYaXnr96M6pq3e'
    };
    return HttpClient.getDataWithJsonp(url, kvDict);
}

function _getWeatherRequestAction()
{
    return {
        type: IConstant.Actions.WEATHER.GET_WEATHER_REQUEST
    };
}

function _getWeatherSuccessAction(resp)
{
    return {
        type: IConstant.Actions.WEATHER.GET_WEATHER_SUCCESS,
        resp
    };
}

function _getWeatherFailureAction(error)
{
    return {
        type: IConstant.Actions.WEATHER.GET_WEATHER_FAILURE,
        message: error
    };
}


