import { Actions } from '../../../core/utils/IConstant';

export default function recommendListReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.HOTEL.RECOMMEND_LIST.GET_LIST_REQUEST:
        return {
            isFetching: true
        };
    case Actions.HOTEL.RECOMMEND_LIST.GET_LIST_SUCCESS:
        return {
            isFetching: false,
            dataList: action.dataList,
        };
    case Actions.HOTEL.RECOMMEND_LIST.GET_LIST_FAILURE:
        return {
            isFetching: false
        };
    case Actions.HOTEL.RECOMMEND_LIST.GO_BACK:
        return {};
    case Actions.HOTEL.RECOMMEND_LIST.DELETE:
        return {
            isFetching: false,
            dataList: [],
        };
    case Actions.HOTEL.RECOMMEND_LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state,
            listViewOffset: action.listViewOffset,
        };
    default:
        return {...state};
    }
}


