import { Actions } from '../../../core/utils/IConstant';

function InfoReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.TICKET_PURCHASE.GET_INFO_REQUEST:
        return {
            ...state,
            isFetching:true
        };
    case Actions.TICKET_PURCHASE.GET_INFO_SUCCESS:
        return {
            ...state,
            dataInfo:action.result.datas,
            isFetching:false
        };
    case Actions.TICKET_PURCHASE.GET_INFO_FAILURE:
        return {
            ...state,
            isFetching: false
        };
    case Actions.TICKET_PURCHASE.GO_BACK:
        return {}; 
    case Actions.TICKET_PURCHASE.SET_SCROLL_OFFSET:
        return {
            ...state,
            scrollViewOffset: action.scrollViewOffset,
        };
    default:
        return {
            ...state
        };
    }
}

export default InfoReducer;


