import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    let obj = state[action.journeyId];
    switch(action.type)
    {
    case Actions.JOURNEY.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state,
            [action.journeyId]: {
                isFetching: true,
            }
        };
    case Actions.JOURNEY.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state,
            [action.journeyId]: {
                isFetching: false,
                headerInfo: action.headerInfo,
                groupList: action.groupList,
                trafficInfos: action.trafficInfos,
            }
        };
    case Actions.JOURNEY.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state,
            [action.journeyId]: {
                isFetching: false,
            }
        };
    case Actions.JOURNEY.DETAIL.SET_SCROLL_OFFSET:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                scrollViewOffset: action.scrollViewOffset,
            }
        };
    case Actions.JOURNEY.DETAIL.GO_BACK:
        let _state = {...state};
        delete _state[action.journeyId];
        return {
            ..._state
        };
    case Actions.JOURNEY.DETAIL.SET_TRAFFIC_INFOS:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                trafficInfos: action.trafficInfos,
            }
        };
    case Actions.JOURNEY.DETAIL.COPY_TO_MINE_REQUEST:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.JOURNEY.DETAIL.COPY_TO_MINE_SUCCESS:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                targetJourneyId: action.targetJourneyId,
                isFetching: false,
            }
        };
    case Actions.JOURNEY.DETAIL.COPY_TO_MINE_FAILURE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                isFetching: false,
            }
        };
    default:
        return {
            ...state
        };
    }
}


