import { Actions } from '../../../core/utils/IConstant';

export default function editReducer(state = {}, action)
{
    let obj = state[action.journeyId];
    switch(action.type)
    {
    case Actions.JOURNEY.EDIT.GO_BACK:
        let _state = {...state};
        delete _state[action.journeyId];
        return {
            ..._state
        };
    case Actions.JOURNEY.EDIT.DATA_LIST_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                dataList: action.dataList
            }
        };
    case Actions.JOURNEY.EDIT.BEGIN_DATE_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                beginDate: action.beginDate
            }
        };
    case Actions.JOURNEY.EDIT.SET_SCROLL_OFFSET:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                scrollViewOffset: action.scrollViewOffset,
            }
        };
    default:
        return {
            ...state
        };
    }
}


