import { Actions } from '../../core/utils/IConstant';

export default function systemReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.SYSTEM.BAIDU_MAP_INITED:
        return {
            ...state,
            baiduMapInited: true,
        };
    case Actions.SYSTEM.GET_USER_LOCATION_REQUEST:
        return {
            ...state,
            isFetching: true,
        };
    case Actions.SYSTEM.GET_USER_LOCATION_SUCCESS:
        return {
            ...state,
            latitude: action.lat,
            longitude: action.lng,
            address: action.address,
            isFetching: false,
        };
    case Actions.SYSTEM.GET_USER_LOCATION_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.SYSTEM.WINDOW_SIZE_CHANGED:
        return {
            ...state,
            windowSize: {...action.windowSize},
        };
    case Actions.SYSTEM.SET_CONFIG_ITEM:
        return {
            ...state,
            ...action.configItem,
        };
    case Actions.SYSTEM.GET_JOURNEY_MAX_DAYCOUNT_REQUEST:
        return {
            ...state,
            isFetching: true,
        };
    case Actions.SYSTEM.GET_JOURNEY_MAX_DAYCOUNT_SUCCESS:
        return {
            ...state,
            journeyMaxDayCount: action.dayCount,
            isFetching: false,
        };
    case Actions.SYSTEM.GET_JOURNEY_MAX_DAYCOUNT_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    default:
        return state;
    }
}


