import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function selectedTabIndexReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.TRAVEL_NOTES.LIST.SET_SELECTED_TAB_INDEX:
        return action.index;
    case Actions.TRAVEL_NOTES.LIST.GO_BACK:
        return 0;
    default:
        return state;
    }
}

function listGroupReducer(state = {}, action)
{
    let obj = state[action.sortBy];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.TRAVEL_NOTES.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [action.sortBy]: {
                ...obj,
                isFetching: true,
                pageIndex: action.pageIndex,
            }
        };
    case Actions.TRAVEL_NOTES.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        if(action.result.results)
        {
            dataList = dataList.concat(action.result.results);
        }
        return {
            ...state,
            [action.sortBy]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
                total: action.result.total,
                pageIndex: action.pageIndex,
                hasMore: (action.result.total > dataList.length)
            }
        };
    case Actions.TRAVEL_NOTES.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [action.sortBy]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.TRAVEL_NOTES.LIST.GO_BACK:
        return {};
    case Actions.TRAVEL_NOTES.LIST.DELETE:
        return {
            ...state,
            [action.sortBy]: {
                isFetching: false,
                dataList: [],
            }
        };
    case Actions.TRAVEL_NOTES.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [action.sortBy]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    default:
        return state;
    }
}

const listReducer = combineReducers({
    selectedTabIndex: selectedTabIndexReducer,
    groups: listGroupReducer,
});

export default listReducer;


