import { Actions } from '../../../core/utils/IConstant';

export default function mapLineReducer(state = {}, action)
{
    let obj = state[action.journeyId];
    switch(action.type)
    {
    case Actions.JOURNEY.MAPLINE.GO_BACK:
        let _state = {...state};
        delete _state[action.journeyId];
        return {
            ..._state
        };
    case Actions.JOURNEY.MAPLINE.IS_FETCHING_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                isFetching:action.isFetching
            }
        };
    case Actions.JOURNEY.MAPLINE.DAYINDEX_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                dayIndex: action.dayIndex,
                showList: action.showList
            }
        };
    case Actions.JOURNEY.MAPLINE.MAPZOOM_AND_CENTERLOCATION_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                mapZoom: action.mapZoom,
                centerLat: action.centerLat,
                centerLng: action.centerLng
            }
        };
    case Actions.JOURNEY.MAPLINE.TOP_BOTTOM_SCROLLX_CHANGE:
        return {
            ...state,
            [action.journeyId]: {
                ...obj,
                topScrollX: action.topScrollX,
                bottomScrollX: action.bottomScrollX,
            }
        };
        
    default:
        return {
            ...state
        };
    }
}


