import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function asyncGetTicketListAction(AccountGuid,PanicBuyingActiveGuid) {
    return function (dispatch, getState) {
        dispatch(_getTicketListRequestAction(AccountGuid,PanicBuyingActiveGuid));
        return _fetchTicketList(AccountGuid,PanicBuyingActiveGuid).then((resp) => {
            dispatch(_getTicketListSuccessAction(resp));
        }).catch((error) => {
            dispatch(_getTicketListFailureAction(error));
        });
    };
}

export function _getTicketListRequestAction(AccountGuid,PanicBuyingActiveGuid) {
    return {
        type: IConstant.Actions.TICKET_PURCHASE.GET_LIST_REQUEST,
        AccountGuid,
        PanicBuyingActiveGuid
    };
}

export function _fetchTicketList(AccountGuid,PanicBuyingActiveGuid) {
    
    let url = HttpClient.WECHAT_HANDLER;
    let kvDict = {
        type: '34',
        action: '11',
        conditionParm: JSON.stringify({
            AccountGuid: AccountGuid,
            PanicBuyingActiveGuid: PanicBuyingActiveGuid
        })
    };

    return HttpClient.getData(url, kvDict).then((resp) => resp.json());
}

export function _getTicketListSuccessAction(result) {
    return {
        type: IConstant.Actions.TICKET_PURCHASE.GET_LIST_SUCCESS,
        result
    };
}

export function _getTicketListFailureAction(error) {
    return {
        type: IConstant.Actions.TICKET_PURCHASE.GET_LIST_FAILURE,
        error,
    };
}


