import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function asyncGetFriendsListAction(userId,targetUserId,dataType,pageIndex)
{
    return function(dispatch, getState)
    {
        dispatch(_getFriendsListRequestAction(userId,targetUserId,dataType, pageIndex));
        return _fetchFriendsList(userId,targetUserId,dataType, pageIndex).then((resp)=>{
            dispatch(_getFriendsListSuccessAction(userId,targetUserId,dataType, pageIndex, resp));
        }).catch((error)=>{
            dispatch(_getFriendsListFailureAction(userId,targetUserId,dataType, error));
        });
    };
}

export function asyncGetFriendsInfoAction(targetUserId,userId)
{
    return function(dispatch, getState)
    {
        dispatch(_getFriendsInfoRequestAction(targetUserId,userId));
        return _fetchFriendsInfo(targetUserId).then((resp)=>{
            dispatch(_getFriendsInfoSuccessAction(targetUserId,userId,resp.results && resp.results.length > 0 ? resp.results[0] : null));
        }).catch((error)=>{
            dispatch(_getFriendsInfoFailureAction(targetUserId,userId, error));
        });
    };
}

