function _fetchFriendsList(userId,targetUserId,dataType, pageIndex)
{
    let type = 0;
    
    if( dataType === IConstant.FRIEND_PROFILE_DATA_TYPE.ATTENTIONS )
    {
        type = 0;
    }
    else if ( dataType === IConstant.FRIEND_PROFILE_DATA_TYPE.FANS)
    {
        type = 1;
    }
    let kvDict = {
        type: type,
        UserID: '',
        ReUserID: '',
        conditionParams: {
            Page: {
                PageIndex: pageIndex,
                PageSize: IConstant.PAGE_SIZE,
            },
        }
    };
    /*    一、关注列表type:0
    1.未登录状态查看别人——UserID为空；ReUserID有值

    2.登录状态查看自己的——UserID有值；ReUserID为空

    3.登录状态查看别人的——UserID有值；ReUserID有值

    二、粉丝列表type:1

    1.未登录状态查看别人——UserID为空；ReUserID有值

    2.登录状态查看自己的——UserID有值；ReUserID为空

    3.登录状态查看别人的——UserID有值；ReUserID有值
    */
    if (userId !== undefined)
    {
        kvDict.UserID = userId;
        if ( userId + '' !== targetUserId + '')
        {
            kvDict.ReUserID = targetUserId;
        }
    }
    else
    {
        kvDict.ReUserID = targetUserId;
    }
    return HttpClient.getFromPortal(IConstant.PortalApis.FOOTMARK.ATTENTION_LIST, kvDict)
        .then((resp) => resp.json());
}

