function _fetchUpdateFollow(targetUserId,userId,isFollowed)
{
    let kvDict = {
        jsonParams: {
            ReUserID: targetUserId,
            UserID: userId,
            IsDelete: isFollowed ? '1' : '0',
        }
    };
    return HttpClient.postToPortal(IConstant.PortalApis.FOOTMARK.UPDATE_ATTENTION,kvDict,null,true)
        .then((resp) => resp.json());
}

