import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function goBackAction(code)
{
    return {
        type: IConstant.Actions.LINE.DETAIL.GO_BACK,
        code,
    };
}

// 线路详情
export function asyncGetDetailAction(code)
{
    return function(dispatch, getState)
    {
        dispatch(_getDetailRequestAction(code));
        return _fetchDetail(code).then((resp)=>{
            resp = (resp && resp.results && resp.results.length > 0) ? resp.results[0] : resp;
            dispatch(_getDetailSuccessAction(code, resp));
        }).catch((error)=>{
            dispatch(_getDetailFailureAction(code, error));
        });
    };
}

// 酒店详情页面滚动条位置
export function setDetailScrollerPositionAction(code, offset)
{
    return {
        type: IConstant.Actions.LINE.DETAIL.SET_DETAIL_SCROLL_OFFSET,
        code,
        scrollViewOffset: offset,
    };
}

function _fetchDetail(code)
{
    let kvDict = {
        ConditionParams: {
            Params:{ code: code }
        }
    };
    return HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS,IConstant.PortalApis.ELEMENTS.LineDetail,kvDict)
        .then((resp) => resp.json());
}

function _getDetailRequestAction(code)
{
    return {
        type: IConstant.Actions.LINE.DETAIL.GET_DETAIL_REQUEST,
        code,
    };
}

function _getDetailSuccessAction(code, resp)
{
    return {
        type: IConstant.Actions.LINE.DETAIL.GET_DETAIL_SUCCESS,
        code,
        detailItem: resp
    };
}

function _getDetailFailureAction(code, error)
{
    return {
        type: IConstant.Actions.LINE.DETAIL.GET_DETAIL_FAILURE,
        code,
        message: error
    };
}


