import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, InputItem } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/mine.css';
import { updateTempUserInfoAction } from '../../store/actions/me/userInfoAction';

export class UpdateUserInfoItemScreen extends PureComponent 
{
    constructor(props)
    {
        super(props);
        this.state = {
            value: this.props.history.location.state.value
        };
        this.submitInfo = this.submitInfo.bind(this);
    }
    render()
    {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    rightContent={this.renderRightMenu()}
                    onLeftClick={()=>this.props.history.goBack()}>
                    { this.props.history.location.state.title }
                </NavBar>
                <Content style={{ backgroundColor: '#efeff4', fontWeight: '300' }}>
                    <InputItem
                        placeholder={'请输入您的' + this.props.history.location.state.title}
                        clear
                        defaultValue={this.props.history.location.state.value}
                        onChange={(v) => { this.setState({value: v}); }}
                        onBlur={(v) => { console.log('onBlur', v); }}>
                        { this.props.history.location.state.title }
                    </InputItem>
                </Content>
            </div>
        );
    }
    renderRightMenu()
    {
        if(!this.state.value || this.state.value.length === 0)
        {
            return null;
        }
        return (
            <div className="updateUserInfoItemButton" onClick={this.submitInfo}>
                确定
            </div>
        );
    }
    submitInfo()
    {
        this.props.dispatch(updateTempUserInfoAction({
            [this.props.history.location.state.key]: this.state.value,
        }));
        this.props.history.goBack();
    }
}

const mapStateToProps = function (state) 
{
    return {
        ...state.me.userInfo.tmpUserInfo,
    };
};
UpdateUserInfoItemScreen = connect(mapStateToProps)(UpdateUserInfoItemScreen);

export default UpdateUserInfoItemScreen;


