import React, { PureComponent } from 'react';
import { NavBar, Icon } from 'antd-mobile';
import { connect } from 'react-redux';
import Content from '../../views/common/Content';

class PayResultScreen extends PureComponent 
{
    render() {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.props.history.goBack();
                    }}>
                    支付结果
                </NavBar>
                <Content>
                    {
                        this.renderPage()
                    }
                </Content>
            </div>
        );
    }
    renderPage(){
        return(
            <div className='payResultBox'>
                <div></div>
            </div>
        );
    }
}

const mapStateToProps = function (state) {
    return {};
};

PayResultScreen = connect(mapStateToProps)(PayResultScreen);

export default PayResultScreen;


