import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Toast, Calendar } from 'antd-mobile';
import { 
    updateUserInfoAction,
    updateBookingItemAction,
    _asyncUpdateSelectVisitorAction
} from '../../store/actions/order/bookingAction';
// eslint-disable-next-line
import DateUtil from '../../core/utils/DateUtil';
import UIUtil from '../../core/utils/UIUtil';
import zhCN from 'antd-mobile/lib/calendar/locale/zh_CN';

class OrderBookingHotelRoomBasicView extends PureComponent
{
    minDate;
    maxDate;
    constructor(props)
    {
        super(props);
        this.state = {
            showDatePicker: false,
        };
        this.diffOrderNumber = this.diffOrderNumber.bind(this);
        this.increaseOrderNumber = this.increaseOrderNumber.bind(this);
        this.onDateChooserCancel = this.onDateChooserCancel.bind(this);
        this.onDateChooserConfirm = this.onDateChooserConfirm.bind(this);
        this.showDateChooser = this.showDateChooser.bind(this);
        this.handlePhoneNumberChange = this.handlePhoneNumberChange.bind(this);
        this.handleUserNameChange = this.handleUserNameChange.bind(this);
    }
    render()
    {
        let priceItem = this.props.price ? this.props.price.priceItem : null;

        if(priceItem)
        {
            this.minDate = new Date(priceItem.UpTime);
            this.maxDate = new Date(priceItem.DownTime);

            if(new Date().getTime() >= this.minDate.getTime())
            {
                this.minDate = new Date();
            }
        }

        return (
            <div className="inlineCalendar">
                {
                    this.renderUserInfoViews()
                }
                {
                    this.renderRoomInfo()
                }
                {
                    this.renderBookingInfo()
                }
                <Calendar
                    type="range"
                    title="请选择入离日期"
                    ref={(r)=>UIUtil.fixCalendarHeightIssue(r, window.innerHeight - 45)}
                    locale={zhCN}
                    visible={this.state.showDatePicker}
                    onCancel={this.onDateChooserCancel}
                    onConfirm={this.onDateChooserConfirm}
                    getDateExtra={(date)=>{
                        if(!this.minDate || !this.maxDate)
                        {
                            return null;
                        }
                        if((date.getTime() >= this.minDate.getTime() && date.getTime() <= this.maxDate.getTime()) || date.formatDate('YYYY-MM-DD') === this.minDate.formatDate('YYYY-MM-DD'))
                        {
                            return {
                                info: '¥' + this.getDatePrice(date.formatDate('YYYY-MM-DD')),
                            };
                        }
                    }}
                    minDate={this.minDate}
                    maxDate={this.maxDate}/>
            </div>
        );
    }
    getDatePrice(date)
    {
        if(this.props.price.priceItem.NewPriceList && this.props.price.priceItem.NewPriceList.length > 0)
        {
            for(let i = 0; i < this.props.price.priceItem.NewPriceList.length; i++)
            {
                let item = this.props.price.priceItem.NewPriceList[i];
                if(item.ToDay === date)
                {
                    return item.NewPrice;
                }
            }
        }
        return this.props.price.priceItem.DefaultPrice;
    }
    renderBookingInfo()
    {
        return (
            <div className="orderBookingNewDiv">
                <div className="orderBookingUserInfoHeader">
                    <span className="orderBookingUserInfoHeaderText">预订信息</span>
                </div>
                <div className="orderBookingLeftRightRow" onClick={this.showDateChooser}>
                    <div className="orderBookingLeft">入离时间</div>
                    <div className="orderBookingNumberRow">
                        {this.props.bookingItem.startDate} ~ {this.props.bookingItem.endDate}
                        <img className="orderBookingDateIcon" src={require('../../assets/images/dateChange@2x.png')} alt=''/>
                    </div>
                </div>
                <div className="orderBookingLeftRightRow">
                    <div className="orderBookingLeft">数量</div>
                    <div className="orderBookingNumberRow">
                        <div className="orderBookingNumberButton orderBookingNumberButtonLeft" onClick={this.diffOrderNumber}>-</div>
                        <div className="orderBookingNumberText">{this.props.bookingItem.orderNumber}</div>
                        <div className="orderBookingNumberButton orderBookingNumberButtonRight" onClick={this.increaseOrderNumber}>+</div>
                    </div>
                </div>
            </div>
        );
    }
    diffOrderNumber()
    {
        if(this.props.bookingItem.orderNumber <= 1)
        {
            return;
        }
        this.props.dispatch(updateBookingItemAction({
            orderNumber: this.props.bookingItem.orderNumber - 1
        }));
        let selectDataList = this.props.visitor.selectDataList;
        selectDataList.splice(selectDataList.length-1, 1);
        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
    }
    increaseOrderNumber()
    {
        if(this.props.bookingItem.orderNumber >= 10)
        {
            Toast.show('最多选择10间房');
            return;
        }
        this.props.dispatch(updateBookingItemAction({
            orderNumber: this.props.bookingItem.orderNumber + 1
        }));
        let selectDataList = this.props.visitor.selectDataList;
        let obj = {
            ID: '',
            UserName: '',
            IDCard: ''
        };
        selectDataList.push(obj);
        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
    }
    renderRoomInfo()
    {
        if(!this.props.price.priceItem)
        {
            return null;
        }
        return (
            <div className="orderBookingNewDiv">
                <div className="orderBookingUserInfoHeader">
                    <span className="orderBookingUserInfoHeaderText orderBookingHotelRoomName">{this.props.price.priceItem.CnName}</span>
                </div>
                <div className="orderBookingLeftRightRow">
                    <div className="orderBookingLeft">入住天数</div>
                    <div className="orderBookingNumberRow">{this.getRoomDaysLabel()}</div>
                </div>
                <div className="orderBookingLeftRightRow">
                    <div className="orderBookingLeft">单价</div>
                    <div className="orderBookingNumberRow orderBookingMoney">¥{this.props.bookingItem.unitPrice.toFixed(2)}</div>
                </div>
            </div>
        );
    }
    getRoomDays()
    {
        if(!this.props.bookingItem.startDate || !this.props.bookingItem.endDate)
        {
            return;
        }
        return (new Date(this.props.bookingItem.endDate).getTime() - new Date(this.props.bookingItem.startDate).getTime()) / (24 * 60 * 60 * 1000);
    }
    getRoomDaysLabel()
    {
        return this.getRoomDays() + '天';
    }
    renderUserInfoViews()
    {
        if(!this.props.bookingItem.userInfo)
        {
            return null;
        }
        return (
            <div className="orderBookingNewDiv">
                <div className="orderBookingUserInfoHeader">
                    <span className="orderBookingUserInfoHeaderText">联系人信息</span>
                </div>
                <div className="orderBookingUserPhoneRow">
                    <div className="orderBookingUserPhoneLabel">姓名：</div>
                    <input
                        value={this.props.bookingItem.userInfo.Name}
                        className='orderBookingUserPhoneInput'
                        placeholder="请填写真实姓名"
                        type='text'
                        onChange={this.handleUserNameChange} />
                </div>
                <div className="orderBookingUserPhoneRow">
                    <div className="orderBookingUserPhoneLabel">电话：</div>
                    <input
                        value={this.props.bookingItem.userInfo.PhoneNumber}
                        className='orderBookingUserPhoneInput'
                        placeholder="请填写联系电话"
                        type='number'
                        onChange={this.handlePhoneNumberChange} />
                </div>
            </div>
        );
    }
    onDateChooserCancel()
    {
        this.setState({showDatePicker: false});
    }
    onDateChooserConfirm(startTime, endTime)
    {
        let startDate = startTime.formatDate('YYYY-MM-DD');
        let endDate = endTime.formatDate('YYYY-MM-DD');
        if(startDate === endDate)
        {
            Toast.fail('入离时间不能相同', undefined, undefined, false);
            return;
        }
        this.setState({showDatePicker: false});
        if(startTime && endTime)
        {
            this.props.dispatch(updateBookingItemAction({
                startDate: startDate, 
                endDate: endDate,
                unitPrice: this.getUnitPrice(startTime, endTime),
                orderDay: (endTime.getTime() - startTime.getTime()) / (24 * 60 * 60 * 1000),
            }));
        }
    }
    getUnitPrice(startTime, endTime)
    {
        let day = (endTime.getTime() - startTime.getTime()) / (24 * 60 * 60 * 1000);
        let totalPrice = 0;
        for(let i = 0; i < day; i++)
        {
            let date = startTime.addDays(i);
            let price = parseFloat(this.getDatePrice(date.formatDate('YYYY-MM-DD')));
            totalPrice += price;
        }
        let unitPrice = totalPrice * 1.0 / day;
        // return parseFloat(unitPrice = unitPrice.toFixed(2));
        return unitPrice;
    }
    showDateChooser()
    {
        this.setState({showDatePicker: true});
    }
    handlePhoneNumberChange(event)
    {
        let userItem = {...this.props.bookingItem.userInfo };
        userItem.PhoneNumber = event.target.value;
        userItem.Phone = event.target.value;
        this.props.dispatch(updateUserInfoAction(userItem));
    }
    handleUserNameChange(event)
    {
        let userItem = {...this.props.bookingItem.userInfo };
        userItem.Name = event.target.value;
        this.props.dispatch(updateUserInfoAction(userItem));
    }
}

const mapStateToProps = function(state, ownProps)
{
    let bookingScreen = state.order.bookingScreen;
    return {
        ...bookingScreen,
    };
};

OrderBookingHotelRoomBasicView = connect(mapStateToProps, null, null, {withRef: true})(OrderBookingHotelRoomBasicView);

export default OrderBookingHotelRoomBasicView;


