import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ActivityIndicator, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import * as IConstant from '../../core/utils/IConstant';
import { 
    asyncGetPriceAction, 
    asyncGetVisitorsAction,
    goBackAction,
    asyncBackupUserInfoAction,
    updateBookingItemAction,
    _asyncUpdateSelectVisitorAction,
    // updateOrderDetailItemAction
} from '../../store/actions/order/bookingAction';
import '../../assets/css/order.css';
import SysUtil from '../../core/utils/SysUtil';
import OrderBookingTicketBasicView from '../../views/order/OrderBookingTicketBasicView';
import OrderBookingVisitorView from '../../views/order/OrderBookingVisitorView';
import OrderBookingHotelRoomBasicView from '../../views/order/OrderBookingHotelRoomBasicView';
import OrderBookingLineBasicView from '../../views/order/OrderBookingLineBasicView';
import HttpClient from '../../core/utils/HttpClient';
// import Base64 from '../../core/utils/Base64';

class OrderBookingScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.showVisitor=this.showVisitor.bind(this);
        this.goOrderBookTapped = this.goOrderBookTapped.bind(this);
    }
    componentDidMount()
    {
        this.props.dispatch(asyncBackupUserInfoAction()).then(()=>{
            if(!this.props.price.priceItem)
            {
                this.loadData();
            }
            else
            {
                let dataList=this.props.visitor.dataList;
                let selectDataList=this.props.visitor.selectDataList;
                for(let i=0;i<selectDataList.length;i++)
                {
                    let ifexit=false;
                    for(let j=0;j<dataList.length;j++)
                    {
                        if(selectDataList[i].UserName === dataList[j].UserName && selectDataList[i].IDCard === dataList[j].IDCard){
                            ifexit=true;
                            break;
                        }
                    }
                    if(!ifexit)
                    {
                        if(selectDataList[i].UserName !=='' && selectDataList[i].IDCard!=='')
                        {
                            dataList.push({...selectDataList[i]});
                        }
                    }
                }
            }
        });
        if(this.props.orderType === IConstant.ORDER_TYPE.HOTEL && !this.props.bookingItem.startDate)
        {
            if(this.props.history.location
                && this.props.history.location.state
                && this.props.history.location.state.startDate
                && this.props.history.location.state.endDate)
            {
                this.props.dispatch(updateBookingItemAction(this.props.history.location.state));   
            }
            else
            {
                this.props.dispatch(updateBookingItemAction({
                    startDate: new Date().formatDate('YYYY-MM-DD'),
                    endDate: new Date().addDays(1).formatDate('YYYY-MM-DD'),
                    hotelName:this.props.history.location.state.hotelName
                }));
            }
        }
    }
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    { this.getBookingTitle() }
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}} 
                    style={{backgroundColor: '#f3f3f3'}}>
                    {
                        this.renderBasicViews()
                    }
                    {
                        this.renderVisitorViews()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                    <div className="orderBookingFooterBar">
                        <div className="orderBookingFooterItem orderBookingFooterItemLeft">
                            <div className="orderBookingFooterPriceLabel">订单总价：<span className="orderBookingFooterPriceLabel2">¥</span></div>
                            <div className="orderBookingFooterPrice">{this.props.bookingItem.totalPrice.toFixed(2)}</div>
                        </div>
                        <div className="orderBookingFooterItem orderBookingFooterItemRight" onClick={this.goOrderBookTapped}>
                            立即支付
                        </div>
                    </div>
                </Content>
            </div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetPriceAction(this.props.productCode, this.props.orderType)).then(()=>{
            if(!this.props.visitor.dataList)
            {
                this.props.dispatch(asyncGetVisitorsAction(this.props.userId)).then(()=>{
                    let selectDataList=[];
                    if(this.props.visitor.dataList && this.props.visitor.dataList.length > 0){
                        selectDataList[selectDataList.length]={...this.props.visitor.dataList[0]};
                    }
                    else
                    {
                        selectDataList.push({
                            ID: '',
                            UserName: '',
                            IDCard: ''
                        });
                    }
                    this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
                });
            }
            if(this.props.orderType === IConstant.ORDER_TYPE.HOTEL)
            {
                let startDate = new Date(this.props.bookingItem.startDate);
                let endDate = new Date(this.props.bookingItem.endDate);
                this.props.dispatch(updateBookingItemAction({
                    unitPrice: this.basicView.getUnitPrice(startDate, endDate),
                    orderDay: (endDate.getTime() - startDate.getTime()) / (24 * 60 * 60 * 1000),
                }));
            }
            if(this.props.orderType === IConstant.ORDER_TYPE.TICKET || this.props.orderType === IConstant.ORDER_TYPE.LINE)
            {
                this.props.dispatch(updateBookingItemAction({
                    unitPrice: parseFloat(this.basicView.getDatePrice(this.props.bookingItem.playDate))
                }));
            }
        });
    }
    // 基本信息
    renderBasicViews()
    {
        if(this.props.orderType === IConstant.ORDER_TYPE.HOTEL)
        {
            return <OrderBookingHotelRoomBasicView ref={(r)=>this.basicView = r ? r.getWrappedInstance() : undefined}/>;
        }
        if(this.props.orderType === IConstant.ORDER_TYPE.TICKET)
        {
            return <OrderBookingTicketBasicView ref={(r)=>this.basicView = r ? r.getWrappedInstance() : undefined}/>;
        }
        if(this.props.orderType === IConstant.ORDER_TYPE.LINE)
        {
            return <OrderBookingLineBasicView ref={(r)=>this.basicView = r ? r.getWrappedInstance() : undefined}/>;
        }
    }
    // 游客信息
    renderVisitorViews()
    {
        return <OrderBookingVisitorView
            showVisitor={this.showVisitor}
        />;
    }
    showVisitor(){
        this.props.history.push('/order/booking/visitor');
    }
    getBookingTitle()
    {
        if(this.props.orderType === IConstant.ORDER_TYPE.HOTEL)
        {
            return '酒店预订';
        }
        if(this.props.orderType === IConstant.ORDER_TYPE.TICKET)
        {
            return '门票预订';
        }
        if(this.props.orderType === IConstant.ORDER_TYPE.LINE)
        {
            return '线路预订';
        }
    }

    goOrderBookTapped()
    {
        if(!this.checkFormValid())
        {
            return;
        }
        if(this.props.orderType === IConstant.ORDER_TYPE.HOTEL)
        {
            this.requestHotelOrderBooking();
        }
        if(this.props.orderType === IConstant.ORDER_TYPE.TICKET)
        {
            this.requestTicketOrderBooking();
        }
        if(this.props.orderType === IConstant.ORDER_TYPE.LINE)
        {
            this.requestLineOrderBooking();
        }
    }

    checkFormValid()
    {
        for (let i = 0; i < this.props.visitor.selectDataList.length; i++) 
        {
            let touristItem = this.props.visitor.selectDataList[i];
            if(!touristItem.UserName || touristItem.UserName.length === 0
                || !touristItem.IDCard || touristItem.IDCard.length === 0)
            {
                Toast.fail('请完善游客信息', undefined, undefined, false);
                return false;
            }
        }
        if (this.props.bookingItem.userInfo.PhoneNumber.length !== 11) {
            Toast.fail('请完善手机号信息', undefined, undefined, false);
            return false;
        }
        if ((this.props.orderType === IConstant.ORDER_TYPE.LINE || this.props.orderType === IConstant.ORDER_TYPE.HOTEL)  && this.props.bookingItem.userInfo.Name === 0) {
            Toast.fail('请完善姓名信息', undefined, undefined, false);
            return false;
        }
        return true;
    }

    requestTicketOrderBooking()
    {
        let firstUserIdCard = '';
        let firstUserName = '';
        let customerList = [];
        for (let i = 0; i < this.props.visitor.selectDataList.length; i++) {
            let touristItem = this.props.visitor.selectDataList[i];
            customerList.push({
                IsSave: '1',
                CustomerName: touristItem.UserName,
                CustomerIDCard: touristItem.IDCard,
                CustomerID: touristItem.ID && touristItem.ID !== undefined && parseInt(touristItem.ID, 10) > 0 ? touristItem.ID : ''
            });
            if (i === 0) {
                firstUserIdCard = touristItem.IDCard;
                firstUserName = touristItem.UserName;
            }
        }
        let userPhone = this.props.bookingItem.userInfo.PhoneNumber;
        if(this.props.bookingItem.userInfo)
        {
            firstUserName = this.props.bookingItem.userInfo.Name;
        }

        let orderJson = {
            OrderNo: '',
            OrderName: this.props.price.priceItem.CnName,
            OrderType: '0',
            OrderSource: IConstant.ORDER_SOUCE_TYPE.WeiXin,
            GroupNo:'',
            RecommendCode:'',
            TotalSalePrice: this.props.bookingItem.totalPrice,
            PayType: IConstant.ORDER_PAY_TYPE.WeiXine,
            UserID: this.props.userId,
            IDCard: firstUserIdCard,
            UserPhone: userPhone,
            Person: firstUserName,
            LeaveMsg: '',
            Product: [
                {
                    ProductNo: this.props.productCode,
                    ProductName: this.props.price.priceItem.CnName,
                    TokenID: '',
                    PlayDate: this.props.bookingItem.playDate,
                    SalePrice: this.props.bookingItem.unitPrice,
                    OrderNumber: this.props.bookingItem.orderNumber,
                    Customer: customerList,
                    Business: [
                        {
                            BusinessCode: this.props.shopCode,
                            BusinessName: this.props.history.location.state.scenicName || '',
                            BusinessType: '7',
                            BusinessRelation:'',
                            LocalTravelAgency:''
                        }
                    ]
                }
            ]
        };
        let kvDict = {
            ConditionParams: {
                Params: orderJson
            }
        };
        HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS,IConstant.PortalApis.ELEMENTS.TicketOrder,kvDict)
            .then((resp) => resp.json())
            .then((respJson)=>{
                if(respJson.status === '0')
                {
                    let results = respJson.results;
                    this.getOrderDetail(results.OrderNo && results.OrderNo.length > 0 ? results.OrderNo[0] : '');
                }
                else
                {
                    Toast.fail(respJson.message);
                }
            })
            .catch((error) => {
                console.log('error: ' + error);
                this.setState({ isLoading: false });
            });
    }

    requestHotelOrderBooking()
    {
        let userIdCard = '';
        let userName = '';
        let Roomer = [];
        for (let i = 0; i < this.props.visitor.selectDataList.length; i++) {
            let touristItem = this.props.visitor.selectDataList[i];
            Roomer.push({
                CusName: touristItem.UserName,
                CusIDCard: touristItem.IDCard,
                CusID: touristItem.ID && touristItem.ID !== undefined && parseInt(touristItem.ID, 10) > 0 ? touristItem.ID : ''
            });
            if (i === 0) {
                userIdCard = touristItem.IDCard;
                userName = touristItem.UserName;
            }
        }
        let userPhone = this.props.bookingItem.userInfo.PhoneNumber;
        let priceDays = [];
        let dayCount = (new Date(this.props.bookingItem.endDate).getTime() - new Date(this.props.bookingItem.startDate).getTime()) / (24 * 60 * 60 * 1000);
        for (let i = 0 ; i < dayCount; i++)
        {
            let newDate = new Date(this.props.bookingItem.startDate).addDays(i);
            let salePrice = this.basicView.getDatePrice(newDate.formatDate('YYYY-MM-DD'));
            priceDays.push({
                SalePrice: salePrice,
                StayDay:  newDate.formatDate('YYYY-MM-DD')
            });
        }

        if(this.props.bookingItem.userInfo)
        {
            userName = this.props.bookingItem.userInfo.Name;
        }

        let orderJson = {
            OrderName: this.props.price.priceItem.CnName,
            OrderType: '1',
            OrderSource: IConstant.ORDER_SOUCE_TYPE.WeiXin,
            TotalSalePrice: this.props.bookingItem.totalPrice.toFixed(2),
            PayType: IConstant.ORDER_PAY_TYPE.WeiXine,
            UserID: this.props.userId,
            IDCard: userIdCard,
            UserPhone: userPhone,
            Person: userName,
            RecommendCode:'',
            LeaveMsg: '',
            Hotel: {
                HotelCode: this.props.shopCode,
                HotelName: this.props.bookingItem.hotelName,
                HotelType: '1',
                Room:{
                    RoomNo: this.props.productCode,
                    RoomName: this.props.price.priceItem.CnName,
                    RoomCount: this.props.bookingItem.orderNumber,
                    PriceDays: priceDays,
                    Roomer: Roomer
                }
            }
        };
        let kvDict = {
            ConditionParams: {
                Params: orderJson
            }
        };
        HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS,IConstant.PortalApis.ELEMENTS.CreateHotelOrder,kvDict)
            .then((resp) => resp.json())
            .then((respJson)=>{
                if(respJson.status === '0')
                {
                    let results = respJson.results;
                    this.getOrderDetail(results.OrderNo && results.OrderNo.length > 0 ? results.OrderNo[0] : '');
                }
                else
                {
                    Toast.fail(respJson.message);
                }
            })
            .catch((error) => {
                console.log('error: ' + error);
                this.setState({ isLoading: false });
            });
    }

    requestLineOrderBooking()
    {
        let firstUserIdCard = '';
        let firstUserName = '';
        let Customer = [];
        for (let i = 0; i < this.props.visitor.selectDataList.length; i++) {
            let touristItem = this.props.visitor.selectDataList[i];
            Customer.push({
                IsSave: '1',                              
                CusName: touristItem.UserName,
                CusIDCard: touristItem.IDCard,
                CusID: touristItem.ID && touristItem.ID !== undefined && parseInt(touristItem.ID, 10) > 0 ? touristItem.ID : ''
            });
            if (i === 0) {
                firstUserIdCard = touristItem.IDCard;
                firstUserName = touristItem.UserName;
            }
        }
        let userPhone = this.props.bookingItem.userInfo.PhoneNumber;

        if(this.props.bookingItem.userInfo)
        {
            firstUserName = this.props.bookingItem.userInfo.Name;
        }

        let orderJson = {
            OrderName: this.props.price.priceItem.CnName,
            OrderType: '2',
            OrderSource: IConstant.ORDER_SOUCE_TYPE.WeiXin,
            TotalSalePrice: this.props.bookingItem.totalPrice,
            PayType: IConstant.ORDER_PAY_TYPE.WeiXine,
            UserID: this.props.userId,
            IDCard: firstUserIdCard,
            UserPhone: userPhone,
            Person: firstUserName,
            LeaveMsg: '',
            Product: [
                {
                    ProductNo: this.props.productCode,
                    ProductName: this.props.price.priceItem.CnName,
                    PlayDate: this.props.bookingItem.playDate,
                    SalePrice: this.props.bookingItem.unitPrice,
                    OrderNumber: this.props.bookingItem.orderNumber,
                    Customer: Customer,
                    Business: [
                        {
                            BusinessCode: this.props.shopCode,
                            BusinessName: this.props.history.location.state.lineName || '',
                            BusinessType: '8'
                        }
                    ]
                }
            ]
        };
        let kvDict = {
            ConditionParams: {
                Params: orderJson
            }
        };
        HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS,IConstant.PortalApis.ELEMENTS.CreateLineOrder,kvDict)
            .then((resp) => resp.json())
            .then((respJson)=>{
                if(respJson.status === '0')
                {
                    let results = respJson.results;
                    this.getOrderDetail(results.OrderNo && results.OrderNo.length > 0 ? results.OrderNo[0] : '');
                }
                else
                {
                    Toast.fail(respJson.message);
                }
            })
            .catch((error) => {
                console.log('error: ' + error);
                this.setState({ isLoading: false });
            });
    }

    // 获取订单详情
    getOrderDetail(orderNo)
    {
        //进入 订单详情页面
        this.showOrderDetailScreen(orderNo);
    }

    goToPay()
    {
        /*
        let b = new Base64();
        let base64_OrderNo = b.encode(this.props.orderDetailItem.OrderNo);
        let base64_sumPrice = b.encode(this.props.orderDetailItem.SumPrice.toString());
        let base64_actionStatus = b.encode('10');

        window.open(HttpClient.HOST_PORTAL + '/Content/Handler/Interface/WebSite/ZYEBank_pay_url.aspx?orderNo=' + base64_OrderNo + '&refund_amount=' + base64_sumPrice + '&actionStatus=' + base64_actionStatus);
        */
    }

    showOrderDetailScreen(orderNo)
    {
        this.props.history.push({
            pathname: '/order/detail',
            search: `?orderNo=${orderNo}&orderType=${this.props.orderType}`,
            state: {
                basicInfo: undefined
            },
        });
    }

}

const mapStateToProps = function(state, ownProps)
{
    let bookingScreen = state.order.bookingScreen;
    return {
        ...bookingScreen,
        userInfo: SysUtil.getUserInfo(state),
        userId: SysUtil.getUserId(state),
        shopCode: ownProps.match.params.shopCode,
        productCode: ownProps.match.params.productCode,
        orderType: ownProps.match.params.orderType,
        isFetching: bookingScreen.price.isFetching || bookingScreen.visitor.isFetching,
    };
};

OrderBookingScreen = connect(mapStateToProps)(OrderBookingScreen);

export default OrderBookingScreen;


