import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Button, Toast, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/me.css';
import { asyncLoginAction } from '../../store/actions/me/userInfoAction';
import HttpClient from '../../core/utils/HttpClient';
import * as IConstant from '../../core/utils/IConstant';
import SysUtil from '../../core/utils/SysUtil';

class LoginScreen extends PureComponent {
    pwd = '';
    constructor(props) {
        super(props);
        this.state = {
            mode: 1,  //0 验证码登录  1 密码登录
            phoneNumber: '',
            password: '',
            checkCode: '',
            timeCount: 60,  //计数器
            isTimerTicking: false
        };
        this.goBack = this.goBack.bind(this);
        this.handlePhoneNumberChange = this.handlePhoneNumberChange.bind(this);
        this.handlePasswordChange = this.handlePasswordChange.bind(this);
        this.scheduleTimer = this.scheduleTimer.bind(this);
    }


    handlePhoneNumberChange(event) {
        this.setState({ phoneNumber: event.target.value });
    };

    handlePasswordChange(event) {
        this.setState({ password: event.target.value });
    };

    render() {
        return (
            <div className="whiteTopNavBar">
                <NavBar mode="light"
                    className="navBar"
                    icon={<Icon type="left" />}
                    leftContent='返回'
                    rightContent={this.renderRightMenu()}
                    onLeftClick={this.goBack}>
                    登录
                </NavBar>
                <Content>
                    <div className='loginInput' style={{ marginTop: '50px' }}>
                        <div className='inputPhoneDiv'>
                            <input
                                className='inputPhone'
                                placeholder="请输入手机号"
                                type='number'
                                onChange={this.handlePhoneNumberChange} />
                        </div>
                        <div className="loginSendCode">
                            <input
                                className='inputPwd'
                                type={this.state.mode === 0 ? 'number' : 'password'}
                                placeholder={this.state.mode === 0 ? '请输入验证码' : '请输入密码'}
                                onChange={this.handlePasswordChange} />
                            {
                                this.state.mode === 0
                                    ?
                                    <div
                                        className='sendCodeButton'
                                        style={{ color: this.state.isTimerTicking ? '#999999' : '#58c0e7', borderColor: this.state.isTimerTicking ? '#999999' : '#58c0e7' }}
                                        onClick={() => this.sendCode()}>
                                        {this.state.isTimerTicking ? '重新获取(' + this.state.timeCount + 's)' : '发送验证码'}
                                    </div>
                                    :
                                    null
                            }
                        </div>
                    </div>
                    <Button style={{ margin: '10px 10px 0px 10px', background: '#58c0e7' }} type="primary" onClick={() => this.doLogin()}>登录</Button>
                    <div className='loginBottomText'>
                        <div className='loginBottomTextLeft' onClick={() => this.changeLoginWay()}>{this.state.mode === 0 ? '密码登录' : '验证码登录'}</div>
                        <div className='loginBottomTextRight' onClick={() => this.findPassword()}>找回密码</div>
                    </div>

                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="登录中..." />
                            : null
                    }
                </Content>
            </div>
        );
    }
    renderRightMenu() {
        return (
            <div onClick={() => this.register()}>
                注册
            </div>
        );
    }

    goBack() {
        // let dispatch = this.props.dispatch;
        this.props.history.goBack();
        // dispatch(goBackAction());
    }

    doLogin() {
        if (this.state.phoneNumber.length === 0) {
            Toast.show('请输入手机号', 1);
            return;
        }
        if (this.state.phoneNumber.length !== 11) {
            Toast.show('请输入11位手机号', 1);
            return;
        }
        if (this.state.password.length === 0) {
            if (this.state.mode === 0) {
                Toast.show('请输入验证码', 1);
            } else {
                Toast.show('请输入密码', 1);
            }
            return;
        }

        if (this.state.mode === 1) {
            this.props.dispatch(asyncLoginAction(this.state.phoneNumber, this.state.password)).then(() => {
                if (this.props.status && this.props.status === '0') {
                    Toast.success('登录成功', 1);
                    this.goBack();
                } else {
                    Toast.fail(this.props.message, 1);
                }

            });
        } else {
            if (this.state.checkCode === this.state.password) {
                this.props.dispatch(asyncLoginAction(this.state.phoneNumber, this.pwd)).then(() => {
                    if (this.props.status && this.props.status === '0') {
                        Toast.success('登录成功', 1);
                        this.goBack();
                    } else {
                        Toast.fail(this.props.message, 1);
                    }
                });
            } else {
                Toast.show('验证码不正确', 1);
            }
        }
    }

    changeLoginWay() {
        if (this.state.mode === 0) {
            this.setState({
                mode: 1
            });
        } else {
            this.setState({
                mode: 0
            });
        }

    }

    findPassword() {
        this.props.history.push(`/me/findPasswordAndRegist/${0}`);
    }
    register() {
        this.props.history.push(`/me/findPasswordAndRegist/${1}`);
    }
    sendCode() {
        if (!this.state.isTimerTicking) {
            if (this.state.phoneNumber.length === 0) {
                Toast.show('请输入手机号', 1);
                return;
            }
            if (this.state.phoneNumber.length !== 11) {
                Toast.show('请输入11位手机号', 1);
                return;
            }
            //验证手机号是否已注册
            let kvDict = {
                phone: this.state.phoneNumber,
            };
            HttpClient.postToPortal(IConstant.PortalApis.ME.GetUserInfoByPhone, kvDict, null, true).then((resp) => resp.json()).then((resp) => {
                if (resp && resp.results && resp.results.length > 0)
                {
                    //获取密码
                    this.pwd = resp.results.Pwd;
                    this.setState({
                        checkCode: SysUtil.RndNum(5)
                    });
                    this.fetchSendCode(this.state.phoneNumber, this.state.checkCode);
                }
                else
                {
                    Toast.fail('该用户不存在', undefined, undefined, false);
                }
            });
        }
    }

    fetchSendCode(phoneNumber, code)
    {
        let kvDict = {
            phone: phoneNumber,
            type: '1',
            code: code,
        };
        return HttpClient.postToPortal(IConstant.PortalApis.ME.SendMessage, kvDict, null, true).then((resp) => resp.json()).then((resp) => {
            if (resp.status === '0')
            {
                Toast.success('验证码已发送', undefined, undefined, false);
                this.setState({
                    timeCount: 60,
                    isTimerTicking: true
                });
                this.scheduleTimer();
            }
            else
            {
                Toast.fail('验证码发送失败', undefined, undefined, false);
            }
        });
    }

    scheduleTimer()
    {
        this.setState({ timeCount: (this.state.timeCount - 1) });
        if (this.state.timeCount > 0)
        {
            setTimeout(this.scheduleTimer, 1000);
        }
        else 
        {
            this.setState({ isTimerTicking: false });
        }
    }

    componentWillUnmount()
    {
        this.setState({ timeCount: 60, isTimerTicking: false });
    }
}
const mapStateToProps = function (state, ownProps) {
    let obj = state.me.userInfo;
    return {
        ...obj,
    };
};

LoginScreen = connect(mapStateToProps)(LoginScreen);

export default LoginScreen;



