import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import SysUtil from '../../../core/utils/SysUtil';

export function asyncGetDetailAction(journeyId)
{
    return function(dispatch, getState)
    {
        dispatch(_getDetailRequestAction(journeyId));
        return _fetchDetail(journeyId).then((resp)=>{
            let obj = {
                journeyId: journeyId
            };
            if(resp && resp.results && resp.results.length >= 0)
            {
                let detailItem = resp.results[0];
                obj.headerInfo = detailItem;
                let groupList = [];
                for(let i = 0; i < detailItem.PlanProList.length; i++)
                {
                    let item = detailItem.PlanProList[i];
                    let index = parseInt(item.dayname, 10) - 1;
                    let group = groupList[index];
                    group = group ? group : [];
                    group.push(item);
                    groupList[index] = group;
                }
                obj.groupList = groupList;
        
                let trafficInfos = {};
                for(let i = 0; i < groupList.length; i++)
                {
                    let itemList = groupList[i];
                    for(let j = 0; j < itemList.length - 1; j++)
                    {
                        trafficInfos[i + '_' + j] = '正在规划驾车路线...';
                    }
                }
                obj.trafficInfos = trafficInfos;
            }
            _loadJourneyUserDetailInfoData(obj.headerInfo.UserID)
                .then((respJson) => {
                    if (respJson && respJson.results && respJson.results.length > 0)
                    {
                        obj.headerInfo.headImg = respJson.results[0].GetUserAvatar;
                    }
                    dispatch(_getDetailSuccessAction(journeyId, obj));
                })
                .catch((error) => {
                    dispatch(_getDetailSuccessAction(journeyId, obj));
                });
        }).catch((error)=>{
            dispatch(_getDetailFailureAction(journeyId, error));
        });
    };
}

export function goBackAction(journeyId)
{
    return {
        type: IConstant.Actions.JOURNEY.DETAIL.GO_BACK,
        journeyId,
    };
}

export function setDetailScrollerPositionAction(journeyId, offset)
{
    return {
        type: IConstant.Actions.JOURNEY.DETAIL.SET_SCROLL_OFFSET,
        scrollViewOffset: offset,
        journeyId,
    };
}

export function setTrafficInfosAction(journeyId, trafficInfos)
{
    return {
        type: IConstant.Actions.JOURNEY.DETAIL.SET_TRAFFIC_INFOS,
        journeyId,
        trafficInfos,
    };
}

export function asyncCopyToMineAction(journeyId)
{
    return function(dispatch, getState)
    {
        let state = getState();
        let userId = SysUtil.getUserId(state);
        dispatch(_copyToMineRequestAction(journeyId));
        return _copyToMine(journeyId, userId).then((resp)=>{
            dispatch(_copyToMineSuccessAction(journeyId, resp));
        }).catch((error)=>{
            dispatch(_copyToMineFailureAction(journeyId, error));
        });
    };
}

function _copyToMine(journeyId, userId)
{
    let kvDict = {
        userID: userId,
        planMainID: journeyId,
        favoriteType: 1
    };
    return HttpClient.postToPortal(IConstant.PortalApis.JOURNEY.CopyTravelPlan, kvDict, null, true)
        .then((resp) => resp.json());
}

function _copyToMineSuccessAction(journeyId, resp)
{
    if(resp && parseInt(resp.status,10) === 0 && resp.results && resp.results.length > 0)
    {
        return {
            type: IConstant.Actions.JOURNEY.DETAIL.COPY_TO_MINE_SUCCESS,
            journeyId,
            targetJourneyId: resp.results,
        };
    }
    else
    {
        return _copyToMineFailureAction(journeyId, new Error('行程拷贝失败...'));
    }
}

function _copyToMineRequestAction(journeyId)
{
    return {
        type: IConstant.Actions.JOURNEY.DETAIL.COPY_TO_MINE_REQUEST,
        journeyId,
    };
}

function _copyToMineFailureAction(error)
{
    return {
        type: IConstant.Actions.JOURNEY.DETAIL.COPY_TO_MINE_FAILURE,
        message: error
    };
}

function _fetchDetail(journeyId)
{
    let kvDict = {
        'jsonParm': journeyId
    };
    return HttpClient.postToPortal(IConstant.PortalApis.JOURNEY.GetTourPlanByID, kvDict, null, true)
        .then((resp) => resp.json());
}

function _getDetailRequestAction(journeyId)
{
    return {
        type: IConstant.Actions.JOURNEY.DETAIL.GET_DETAIL_REQUEST,
        journeyId,
    };
}

function _getDetailSuccessAction(journeyId, obj)
{
    obj.type = IConstant.Actions.JOURNEY.DETAIL.GET_DETAIL_SUCCESS;
    return obj;
}

function _getDetailFailureAction(error)
{
    return {
        type: IConstant.Actions.JOURNEY.DETAIL.GET_DETAIL_FAILURE,
        message: error
    };
}
export function clearDetailDataAction(journeyId)
{
    return {
        type: IConstant.Actions.JOURNEY.DETAIL.GO_BACK,
        journeyId 
    };
}
// 获取 用户信息
function _loadJourneyUserDetailInfoData(userId)
{
    let kvDict = {
        id: userId
    };
    return HttpClient.postToPortal(IConstant.PortalApis.ME.GetUserInfo,kvDict,null,true)
        .then((resp) => resp.json());
}


