import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Toast, ActivityIndicator, TextareaItem } from 'antd-mobile';
import * as IConstant from '../../core/utils/IConstant';
import Content from '../../views/common/Content';
import '../../assets/css/order.css';
import SysUtil from '../../core/utils/SysUtil';
import HttpClient from '../../core/utils/HttpClient';
import {
    addOrderCommentsSuccessAction,
} from '../../store/actions/comments/commentsAction';

class OrderCommentsScreen extends PureComponent 
{
    constructor(props)
    {
        super(props);
        this.state = {
            grades: [5, 5, 5, 5],
            comments: '',
            isFetching: false,
        };
        this.submitComments = this.submitComments.bind(this);
    }
    render()
    {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.props.history.goBack();
                    }}>
                    订单点评
                </NavBar>
                <Content style={{ backgroundColor: '#f3f3f3', marginLeft: '8px', marginRight: '8px' }}>
                    <div style={{height: '8px'}}/>
                    <TextareaItem
                        placeholder="我要点评..."
                        onChange={(val)=>this.setState({comments: val})}
                        rows={5}
                        count={100}
                    />
                    {
                        this.renderStarRows()
                    }
                    <div className="orderCommentsButton" onClick={this.submitComments}>点评</div>
                    {
                        this.state.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }
    renderStarRows()
    {
        return (
            <div className="orderCommentsStarRows">
                <div className="orderCommentsStarRow">
                    {
                        this.renderStarView(0)
                    }
                    <div className="orderCommentsStarLRSpace"/>
                    {
                        this.renderStarView(1)
                    }
                </div>
                <div className="orderCommentsStarRow">
                    {
                        this.renderStarView(2)
                    }
                    <div className="orderCommentsStarLRSpace"/>
                    {
                        this.renderStarView(3)
                    }
                </div>
            </div>
        );
    }
    renderStarView(index)
    {
        let titles = ['价格', '服务', '设施', '环境'];
        if(this.props.match.params.orderType === IConstant.ORDER_TYPE.HOTEL)
        {
            titles = ['卫生', '设施', '环境', '服务'];
        }
        else if(this.props.match.params.orderType === IConstant.ORDER_TYPE.LINE)
        {
            titles = ['行程', '交通', '住宿', '导游'];
        }
        return (
            <div className="orderCommentsStarItem">
                <div className="orderCommentsStarLabel">{titles[index]}</div>
                <div className="orderCommentsStars">
                    {
                        Array(5).fill(0).map((item, sIndex)=>this.renderStarImage(index, sIndex))
                    }
                </div>
            </div>
        );
    }
    renderStarImage(index, sIndex)
    {
        return (
            <div key={sIndex} 
                className="orderCommentsStarIcon" 
                onClick={()=>{
                    let grades = [...this.state.grades];
                    grades[index] = sIndex + 1;
                    this.setState({ grades: grades });
                }}>
                {
                    this.state.grades[index] >= (sIndex + 1)
                        ?
                        <img src={require('../../assets/images/hotel/ico_star_full@2x.png')} alt=''/>
                        :
                        <img src={require('../../assets/images/hotel/ico_star_empty@2x.png')} alt=''/>
                }
            </div>
        );
    }
    submitComments()
    {
        if(!this.state.comments || this.state.comments.length === 0)
        {
            Toast.info('请输入点评内容', undefined, undefined, false);
            return;
        }
        this.setState({isFetching: true});
        let kvDict = {
            UserID: this.props.userId,
            Code: this.props.match.params.shopCode,
            Content: this.state.comments,
            Grade1: this.state.grades[0],
            Grade2: this.state.grades[1],
            Grade3: this.state.grades[2],
            Grade4: this.state.grades[3],
            OrderNo: this.props.match.params.orderNo,
        };
        HttpClient.postToPortal(IConstant.PortalApis.ME.AddComment, kvDict, null, true)
            .then((resp) => resp.json()).then((resp) => {
                this.setState({ isFetching: false });
                if (resp.status === '0') {
                    this.props.dispatch(addOrderCommentsSuccessAction(this.props.match.params.orderNo));
                    Toast.success('点评成功', undefined, undefined, false);
                    this.props.history.goBack();
                }
                else {
                    Toast.fail(resp.results || resp.message || '点评失败', undefined, undefined, false);
                }
            });
    }
}
const mapStateToProps = function (state, ownProps) {
    return {
        userId: SysUtil.getUserId(state),
    };
};

OrderCommentsScreen = connect(mapStateToProps)(OrderCommentsScreen);

export default OrderCommentsScreen;



