import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function setSelectedTabIndexAction(index)
{
    return {
        type: IConstant.Actions.ME.COLLECT.LIST.SET_SELECTED_TAB_INDEX,
        index: index
    };
}

export function asyncGetListAction(userId, entityType, pageIndex)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(entityType, pageIndex));
        return _fetchList(userId, entityType, pageIndex).then((resp)=>{
            dispatch(_getListSuccessAction(entityType, pageIndex, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(entityType, error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.ME.COLLECT.LIST.GO_BACK,
    };
}

export function asyncDeleteListAction(entityType)
{
    return function(dispatch, getState)
    {
        dispatch(deleteListAction(entityType));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction(entityType)
{
    return {
        type: IConstant.Actions.ME.COLLECT.LIST.DELETE,
        entityType
    };
}

export function setScrollerPositionAction(entityType, offset)
{
    return {
        type: IConstant.Actions.ORDER.LIST.SET_LISTVIEW_OFFSET,
        entityType,
        listViewOffset: offset
    };
}

function _fetchList(userId, entityType, pageIndex)
{
    let kvDict = {
        UserID: userId,
        Type: entityType,
        PageIndex: pageIndex,
        PageSize: IConstant.PAGE_SIZE
    };
    return HttpClient.postToPortal(IConstant.PortalApis.ME.CollectList,kvDict,null,true)
        .then((resp) => resp.json());
}

function _getListRequestAction(entityType, pageIndex)
{
    return {
        type: IConstant.Actions.ME.COLLECT.LIST.GET_LIST_REQUEST,
        entityType,
        pageIndex,
    };
}

function _getListSuccessAction(entityType, pageIndex, result)
{
    return {
        type: IConstant.Actions.ME.COLLECT.LIST.GET_LIST_SUCCESS,
        entityType,
        pageIndex,
        result
    };
}

function _getListFailureAction(entityType, error)
{
    return {
        type: IConstant.Actions.ME.COLLECT.LIST.GET_LIST_FAILURE,
        entityType,
        message: error
    };
}


