import { Actions,FRIEND_PROFILE_DATA_TYPE } from '../../../core/utils/IConstant';

export default function userProfileReducer(state = {}, action) {
    let profileName = action.targetUserId + '_' + action.userId;
    let targetUserObj = state[profileName];
    let obj = targetUserObj ? targetUserObj[action.dataType] : null;
    obj = obj ? obj : {};
    let _state = { ...state };
    switch (action.type) {
    case Actions.USERPROFILE.GET_FRIENDS_REQUEST:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: true,
                    pageIndex: action.pageIndex,
                }
            }
        };
    case Actions.USERPROFILE.GET_FRIENDS_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        if (action.result.results) {
            dataList = dataList.concat(action.result.results);
        }
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: false,
                    dataList: dataList,
                    pageIndex: action.pageIndex,
                    total: action.result.total,
                    hasMore: (action.result.total > dataList.length)
                }
            }
        };
    case Actions.USERPROFILE.GET_FRIENDS_FAILURE:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: false,
                }
            }
        };
    case Actions.USERPROFILE.GO_BACK:
        delete _state[profileName];
        return {
            ..._state
        };
    case Actions.USERPROFILE.DELETE_FRIENDS_LIST:
        if (_state[profileName])
        {
            delete _state[profileName][action.dataType];
        }
        return {
            ..._state
        };
    case Actions.USERPROFILE.DELETE_NUMBERS_INFO:
        if (_state[profileName])
        {
            delete _state[profileName][action.dataType];
        }
        return {
            ..._state
        };
    case Actions.USERPROFILE.DELETE_FOOTMARK_LIST:
        if (_state[profileName])
        {
            delete _state[profileName][action.dataType];
        }
        return {
            ..._state
        };
    case Actions.USERPROFILE.SET_LISTVIEW_OFFSET:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    listViewOffset: action.listViewOffset
                }
            }
        };
    case Actions.USERPROFILE.SET_SELECTED_TAB_INDEX:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                selectedTabIndex: action.index
            }
        };
    case Actions.USERPROFILE.GET_PROFILE_REQUEST:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: true,
                }
            }
        };
    case Actions.USERPROFILE.GET_PROFILE_SUCCESS:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: false,
                    ...action.result
                }
            }
        };
    case Actions.USERPROFILE.GET_PROFILE_FAILURE:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: false,
                }
            }
        };
    case Actions.USERPROFILE.GET_FOOTMARK_REQUEST:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: true,
                    pageIndex: action.pageIndex,
                }
            }
        };
    case Actions.USERPROFILE.GET_FOOTMARK_SUCCESS:
        let footmarkList = action.pageIndex === 1 ? [] : obj.dataList;
        footmarkList = footmarkList ? footmarkList : [];
        if (action.result.results) {
            footmarkList = footmarkList.concat(action.result.results);
        }
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: false,
                    dataList: footmarkList,
                    pageIndex: action.pageIndex,
                    total: action.result.total,
                    hasMore: (action.result.total > footmarkList.length)
                }
            }
        };
    case Actions.USERPROFILE.GET_FOOTMARK_FAILURE:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: false,
                }
            }
        };
    case Actions.USERPROFILE.GET_NUMBERS_REQUEST:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: true,
                }
            }
        };
    case Actions.USERPROFILE.GET_NUMBERS_SUCCESS:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: false,
                    ...action.result
                }
            }
        };
    case Actions.USERPROFILE.GET_NUMBERS_FAILURE:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: false,
                }
            }
        };  
    case Actions.USERPROFILE.GET_ISFOLLOW_REQUEST:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                isFetching: true,
            }
        };
    case Actions.USERPROFILE.GET_ISFOLLOW_SUCCESS:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                isFetching: false,
                isFollowed: action.result
            }
        };
    case Actions.USERPROFILE.GET_ISFOLLOW_FAILURE:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                isFetching: false,
            }
        };  
    case Actions.USERPROFILE.GET_UPDATE_FOLLOW_REQUEST:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                isFetching: true,
            }
        };
    case Actions.USERPROFILE.GET_UPDATE_FOLLOW_SUCCESS:
    {
        // 列子:
        // targetUserId 305
        // userId  22

        // 1. userID 的访客视角 - 关注列表,numberInfo刷新 , 粉丝列表 update
        // 2. targetUserId 的访客视角 - 粉丝列表,numberInfo刷新,头部状态 , 关注列表 update
        // 3. 非 userID && targetUserId 的访客视角 - 粉丝列表, 关注列表 update
        let IsAttention = action.result;
        //轮询userProfile
        for (let n in state)
        {
            let profileObj = state[n];
            if ( (action.targetUserId + '_' + action.userId)  === n)
            {
                _state[n] = {
                    ...profileObj,
                    isFetching: false,
                    isFollowed: IsAttention ? true : false,
                    [FRIEND_PROFILE_DATA_TYPE.FANS]: {
                        isRefresh: true
                    },
                    [FRIEND_PROFILE_DATA_TYPE.NUMBERSINFO]: null
                };
            }
            //自己的
            else if ( (action.userId + '_' + action.userId)  === n )
            {
                _state[n] = {
                    ...profileObj,
                    isFetching: false,
                    [FRIEND_PROFILE_DATA_TYPE.ATTENTIONS]: {
                        isRefresh: true
                    },
                    [FRIEND_PROFILE_DATA_TYPE.NUMBERSINFO]: null
                };
            }
            profileObj = _state[n];
            for (let i in profileObj) {
                if (i !== FRIEND_PROFILE_DATA_TYPE.FANS && i !== FRIEND_PROFILE_DATA_TYPE.ATTENTIONS) {
                    continue;
                }
                let profileObjValue = profileObj[i];
                if (profileObjValue && profileObjValue.dataList) {
                    let friendDataList = [...profileObjValue.dataList];
                    for (let index = 0; index < friendDataList.length; index++) {
                        let friendItem = friendDataList[index];
                        if ((friendItem.UserID + '') === action.targetUserId) {
                            friendDataList[index] = {
                                ...friendItem,
                                IsAttention: IsAttention
                            };
                            break;
                        }
                    }
                    _state[n][i] = {
                        ...profileObjValue,
                        dataList: friendDataList
                    };
                }
            }
        }
        return {
            ..._state
        };
    }
    case Actions.USERPROFILE.GET_UPDATE_FOLLOW_FAILURE:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                isFetching: false,
            }
        }; 
    case Actions.USERPROFILE.GET_UPDATE_FRIEND_ITEM_FOLLOW_REQUEST:
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    isFetching: true,
                }
            }
        };
    case Actions.USERPROFILE.GET_UPDATE_FRIEND_ITEM_FOLLOW_SUCCESS:
    {
        let IsAttention = action.isFollowed === 0 ? 1 : 0;
        // 列子:
        // targetUserId 305
        // userId  22
        // itemUserId 77

        // 1. itemUserId 的访客视角 - 粉丝列表,numberInfo 刷新, 头部关注状态改变
        // 2. userID 的访客视角 - 关注列表,numberInfo刷新 , 粉丝列表 update
        // 3. 非 itemUserId && userID  的访客视角 - 粉丝列表,关注列表 update
        //轮询userProfile
        for (let n in state)
        {
            let profileObj = state[n];
            //itemUserId + userId, 刷新粉丝列表
            if ( (action.itemUserId + '_' + action.userId)  === n)
            {
                _state[n] = {
                    ...profileObj,
                    isFollowed: IsAttention ? true : false,
                    [FRIEND_PROFILE_DATA_TYPE.FANS]: {
                        isRefresh: true
                    },
                    [FRIEND_PROFILE_DATA_TYPE.NUMBERSINFO]: null
                };
            }
            else
            {
                if ( (action.userId + '_' + action.userId) === n )
                {
                    _state[n] = {
                        ...profileObj,
                        [FRIEND_PROFILE_DATA_TYPE.ATTENTIONS]: {
                            isRefresh: true
                        },
                        [FRIEND_PROFILE_DATA_TYPE.NUMBERSINFO]: null
                    };
                }
                profileObj = _state[n];
                let targetUserProfileName = (action.targetUserId + '_' + action.userId);
                for (let i in profileObj)
                {
                    if (i !== FRIEND_PROFILE_DATA_TYPE.FANS && i !== FRIEND_PROFILE_DATA_TYPE.ATTENTIONS)
                    {
                        continue;
                    }
                    let profileObjValue = profileObj[i];
                    if ( profileObjValue && profileObjValue.dataList )
                    {
                        let friendDataList = [...profileObjValue.dataList];
                        for (let index = 0; index < friendDataList.length; index++ )
                        {
                            let friendItem = friendDataList[index];
                            if (friendItem.UserID === action.itemUserId)
                            {
                                friendDataList[index] = {
                                    ...friendItem,
                                    IsAttention: IsAttention
                                };
                                break;
                            }
                        }
                        _state[n][i] = {
                            ...profileObjValue,
                            dataList: friendDataList
                        };
                    }
                    if  (targetUserProfileName === n && i === action.dataType )
                    {
                        _state[n][i].isFetching = false;
                    }
                }
            }
        }
        return {
            ..._state
        };
    }
    case Actions.USERPROFILE.SET_FRIEND_FOOTMARK_ITEM_PRAISE:
    {
        let dataList = obj.dataList;
        let item = dataList[action.index];
        item = {
            ...item,
            IsLike: item.IsLike === 0 ? 1 : 0,
            LikeCount: item.IsLike === 0 ? (item.LikeCount + 1) : (item.LikeCount -1)
        };
        dataList = dataList.slice(0, action.index).concat(item).concat(dataList.slice(action.index + 1));
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    dataList: dataList
                }
            }
        };
    }
    case Actions.USERPROFILE.SET_FRIEND_FOOTMARK_ITEM_COLLECT:
    {
        let dataList = obj.dataList;
        let item = dataList[action.index];
        item = {
            ...item,
            IsCollection: item.IsCollection === 0 ? 1 : 0,
        };
        dataList = dataList.slice(0, action.index).concat(item).concat(dataList.slice(action.index + 1));
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    dataList: dataList
                }
            }
        };
    }
    case Actions.USERPROFILE.FOOTMARK_TOGGLE_EXPAND:
    {
        let _dataList = obj.dataList;
        let item = _dataList[action.index];
        item = {
            ...item,
            isExpand: !item.isExpand
        };
        _dataList = _dataList.slice(0, action.index).concat(item).concat(_dataList.slice(action.index + 1));
        return {
            ...state,
            [profileName]: {
                ...targetUserObj,
                [action.dataType]: {
                    ...obj,
                    dataList: _dataList
                }
            }
        };
    }
    default:
        return state;
    }
}


