import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import UIUtil from '../../core/utils/UIUtil';
import EmptyView from '../../views/common/EmptyView';
import { asyncGetListAction, asyncDeleteListAction, goBackAction } from '../../store/actions/comments/listAction';

import '../../assets/css/comments.css';

class CommentsListScreen extends PureComponent
{
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList || this.props.dataList.length === 0)
        {
            this.loadData();
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(this.props.dataList !== prevProps.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : [])});
        }
    }
    render()
    {
        let listViewHeight = window.innerHeight - 45;
        return (          
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let code = this.props.code;
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction(code));
                    }}>
                    评论列表
                </NavBar>
                <Content style={{ backgroundColor: '#efeff4' }}>
                    {
                        this.renderListView()
                    }
                    {
                        !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                            ?
                            <EmptyView style={{height: listViewHeight + 'px'}} onClick={this.onRefresh}/>
                            :
                            null
                    }
                    {
                        (this.props.isFetching && this.props.pageIndex === 1)
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderListView()
    {
        if(!this.props.dataList || this.props.dataList.length === 0)
        {
            return null;
        }

        let listViewHeight = window.innerHeight - 45;
        return (
            <ListView
                ref={el => {
                    UIUtil.fixListViewHeightIssue(el, listViewHeight);
                }}
                dataSource={this.state.dataSource}
                renderRow={this.renderRow}
                style={{
                    height: listViewHeight + 'px',
                    overflow: 'auto',
                    backgroundColor: '#efeff4',
                }}
                pullToRefresh={<PullToRefresh
                    refreshing={this.props.isFetching}
                    onRefresh={this.onRefresh}
                />}
                onEndReached={this.loadNextPage}
                renderFooter={this.renderFooter}
                initialListSize={this.props.dataList.length}
                pageSize={20}
                scrollRenderAheadDistance={500}
                scrollEventThrottle={50}
                onEndReachedThreshold={20}/>
        );
    }
    onRefresh()
    {
        this.props.dispatch(asyncDeleteListAction(this.props.code)).then(()=>{
            this.loadData();
        });
    }
    loadNextPage()
    {
        this.loadData();
    }
    renderFooter()
    {
        if(!this.props.isFetching || this.props.hasMore !== true)
        {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID)
    {
        return (
            <div key={rowID} className="commentsListItem">
                <div style={{ backgroundColor: '#efeff4', height: '10px' }}/>
                <div className="commentsListItemTop">
                    <div className="commentsListItemIcon" style={item.GetUserAvatar ? { backgroundImage: `url(${item.GetUserAvatar})` } : null}/>
                    <div className="commentsListItemTopRight">
                        <div className="commentsListItemTopFirstLine">
                            <div className="commentsListItemName">{ item.NickName ? item.NickName : '佚名' }</div>
                            <div style={{flex: 1}}/>
                            <div className="commentsListItemTime">{ item.CoomentDate }</div>
                        </div>
                        <div className="commentsListItemTopSecondLine">
                            评分&nbsp;:&nbsp;
                        </div>
                    </div>
                </div>
                <div className="commentsListItemBottom">
                    { item.CommentContent }
                </div>
            </div>
        );
    }
    loadData()
    {
        if(this.props.isFetching)
        {
            return;
        }
        if(this.props.hasMore === false)
        {
            return;
        }

        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        this.props.dispatch(asyncGetListAction(this.props.code, pageIndex));
    }
}

const mapStateToProps = function(state, ownProps)
{
    let code = ownProps.match.params.code;
    return {
        code,
        ...state.comments.listScreen.groups[code],
    };
};

CommentsListScreen = connect(mapStateToProps)(CommentsListScreen);

export default CommentsListScreen;


