import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function setSelectedTabIndexAction(index)
{
    return {
        type: IConstant.Actions.ORDER.LIST.SET_SELECTED_TAB_INDEX,
        index: index
    };
}

export function asyncGetListAction(userId, orderType, pageIndex)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(orderType, pageIndex));
        return _fetchList(userId, orderType, pageIndex).then((resp)=>{
            dispatch(_getListSuccessAction(orderType, pageIndex, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(orderType, error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.ORDER.LIST.GO_BACK,
    };
}

export function asyncDeleteListAction(orderType)
{
    return function(dispatch, getState)
    {
        dispatch(deleteListAction(orderType));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction(orderType)
{
    return {
        type: IConstant.Actions.ORDER.LIST.DELETE,
        orderType
    };
}

export function setScrollerPositionAction(orderType, offset)
{
    return {
        type: IConstant.Actions.ORDER.LIST.SET_LISTVIEW_OFFSET,
        orderType,
        listViewOffset: offset
    };
}

function _fetchList(userId, orderType, pageIndex)
{
    let kvDict = {
        ConditionParams: {
            Page: {
                PageSize: 10,
                PageIndex: pageIndex
            },
            Search: [],
            Params: {
                UserID: userId
            }
        }
    };
    let portalApi;
    if (orderType === IConstant.ORDER_TYPE.HOTEL) {
        portalApi = IConstant.PortalApis.ELEMENTS.Hotel_GetOrderList;
    }
    else if (orderType === IConstant.ORDER_TYPE.TICKET) {
        portalApi = IConstant.PortalApis.ELEMENTS.AttractionsOrderList;
    }
    else if (orderType === IConstant.ORDER_TYPE.LINE) {
        portalApi = IConstant.PortalApis.ELEMENTS.GetLineOrderList;
    }
    return HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS,portalApi,kvDict)
        .then((resp) => resp.json());
}

function _getListRequestAction(orderType, pageIndex)
{
    return {
        type: IConstant.Actions.ORDER.LIST.GET_LIST_REQUEST,
        orderType,
        pageIndex,
    };
}

function _getListSuccessAction(orderType, pageIndex, result)
{
    return {
        type: IConstant.Actions.ORDER.LIST.GET_LIST_SUCCESS,
        orderType,
        pageIndex,
        result
    };
}

function _getListFailureAction(orderType, error)
{
    return {
        type: IConstant.Actions.ORDER.LIST.GET_LIST_FAILURE,
        orderType,
        message: error
    };
}


