import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { TextareaItem, ImagePicker, DatePicker} from 'antd-mobile';
import { updateSiteItemAction } from '../../store/actions/footmark/createAction';
import * as IConstant from '../../core/utils/IConstant';
import HttpClient from '../../core/utils/HttpClient';

const FootmarkSiteMaxCount = 4; //每站最多图片数

const FootmarkDateChildren = ({ extra, onClick, children }) => (
    <div onClick={onClick} 
        style={{ padding: '10px', backgroundColor: '#e1f0ff', borderRadius: '3px', display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }} >
        <div style={{ display: 'flex' }} >
            <img src={require('../../assets/images/footmark/footmark_time@2x.png')} alt='' style={{ height: '18px', marginRight: '10px' }} />
            <div style={{ color: '#7eaad4', fontSize: '13px' }} >{extra && extra.length > 0 ? extra : '请选择时间'}</div>
        </div>
        <img src={require('../../assets/images/arrow@2x.png')} alt='' style={{ height: '12px' }} />
    </div>
);

class CreateFootmarkView extends PureComponent
{
    copyOfImageList = []; //记录当前已选图片列表
    isFirst = true;
    constructor(props)
    {
        super(props);
        this.inputOnChange = this.inputOnChange.bind(this);
        this.showImageBrowser = this.showImageBrowser.bind(this);
        this.onImageChange = this.onImageChange.bind(this);
        this.onLocationClicking = this.onLocationClicking.bind(this);
        this.onTimeClicking = this.onTimeClicking.bind(this);
        this.removecopyOfImageList = this.removecopyOfImageList.bind(this);
    }
    render()
    {
        return (
            <div className="createFootmarkView"
                style={{backgroundColor:'#fff',height:'calc( 100% - 70px )',padding:'10px'}}
                ref={el => {
                    this.listView = el;
                }}>
                {
                    this.renderContentTextView()
                }
                {
                    this.renderImagePickerView()
                }
                {
                    this.renderSiteInfoSelectView()
                }
                
            </div>
        );
    }

    renderContentTextView()
    {
        return (<div>
            <TextareaItem
                value={this.props.siteItem.Content && this.props.siteItem.Content.length > 0 ? this.props.siteItem.Content : ''}
                placeholder='在此输入您想说的话'
                rows={3}
                count={300}
                onChange={(val) => this.inputOnChange(val)}
            />
            <img src={require('../../assets/images/footmark/footmark_input-box@2x.png')} alt=''style={{width:'100%'}} />
        </div>);
    }
    renderImagePickerView()
    {
        let files = this.getImageFileList();
        return (<div>
            <ImagePicker
                files={files}
                onChange={(files,operationType,index)=>this.onImageChange(files,operationType,index)}
                onImageClick={(index, fs) => this.showImageBrowser(index, fs)}
                selectable={files.length < FootmarkSiteMaxCount}
                multiple={true}
            />
            <div>最多可发{FootmarkSiteMaxCount}张</div>
        </div>);
    }
    getImageFileList()
    {
        let files = [];
        let ImageList = this.props.siteItem.GetImage && this.props.siteItem.GetImage.ImageList && this.props.siteItem.GetImage.ImageList.length > 0 ? this.props.siteItem.GetImage.ImageList : [];
        for (let i = 0 ;i < ImageList.length; i++)
        {
            let imageItem = ImageList[i];
            files.push(imageItem.file ? imageItem.file : {
                url: imageItem.SmallImgSrc
            });

            if(this.isFirst){
                this.copyOfImageList.push({'PicID': imageItem.PicID});
            }
        }
        this.isFirst = false;
        return files;
    }
    renderSiteInfoSelectView()
    {
        let CreateTime = this.props.siteItem.CreateTime && this.props.siteItem.CreateTime.length > 0 ? this.props.siteItem.CreateTime : '';
        CreateTime = CreateTime.length > 0 && CreateTime.indexOf('-') >= 0 ? CreateTime.replace(/-/g ,'/') : CreateTime;
        return (<div style={{marginTop:'20px',marginBottom:'10px'}} >
            <div onClick={this.onLocationClicking} 
                style={{padding:'10px',marginBottom:'10px',backgroundColor:'#e1f0ff',borderRadius:'3px',display:'flex',flexDirection:'row',justifyContent:'space-between',alignItems:'center'}} >
                <div style={{display:'flex'}} >
                    <img src={require('../../assets/images/footmark/footmark_position@2x.png')} alt='' style={{height:'18px',marginRight:'10px'}} />
                    <div style={{color:'#00c6ad',fontSize:'13px'}} >{this.props.siteItem.Address && this.props.siteItem.Address.length > 0 ? this.props.siteItem.Address : '请选择地点'}</div>
                </div>
                <img src={require('../../assets/images/arrow@2x.png')} alt='' style={{height:'12px'}} />
            </div>
            <DatePicker key={1}
                value={new Date(CreateTime)}
                onChange={date => this.onTimeClicking(date)}>
                <FootmarkDateChildren />
            </DatePicker>
        </div>);
    }
    inputOnChange(val)
    {
        let siteItem = {...this.props.siteItem};
        siteItem.Content = val;
        this.props.dispatch(updateSiteItemAction(this.props.siteIndex,siteItem));
    }
    onImageChange(files,operationType,index)
    {
        if(operationType === 'remove' && index + 1 <= this.copyOfImageList.length && this.copyOfImageList[index].PicID){
            // Modal.alert('提示','是否删除足迹远程图片?',[
            //     { text: '取消', style: 'default' },
            //     { text: '确定', onPress: () => {
            this.deleteImageRequest(this.copyOfImageList[index],index);
            //     }}
            // ]);
        }
        let siteItem = {...this.props.siteItem};
        let imageList = [];
        for (let i = 0; i < files.length; i++)
        {
            let fileItem = files[i];
            imageList.push(fileItem.file ? {
                file: fileItem
            }: {
                SmallImgSrc: fileItem.url
            });
        }
        siteItem.GetImage.ImageList = imageList;
        this.props.dispatch(updateSiteItemAction(this.props.siteIndex,siteItem));
    }

    deleteImageRequest(imageItem,index)
    {
        let jsonDict = {
            ID: imageItem.PicID
        };
        HttpClient.postToPortal(IConstant.PortalApis.FOOTMARK.DeleteImage, jsonDict,null,true)
            .then((resp) => resp.text())
            .then((respJson) => {
                let isSuccess = (respJson === 'true');
                // Toast.show('删除足迹远程图片' + ( isSuccess ? '成功!' : '失败!'));
                if(isSuccess){
                    this.removecopyOfImageList(index);
                }
            })
            .catch((error) => {
                // Toast.show('接口报错!');
            });
    }
    
    removecopyOfImageList(index){
        if (index > -1) {
            this.copyOfImageList.splice(index, 1);
        }
    }
    showImageBrowser(index,fs)
    {
        let imageList = [];
        for (let i = 0; i < this.props.siteItem.GetImage.ImageList.length; i++)
        {
            let imageItem = this.props.siteItem.GetImage.ImageList[i];
            let url = imageItem.file ? imageItem.file.url : imageItem.SmallImgSrc.replace('Small', 'Large');
            imageList.push(url);
        }
        this.props.showImageBrowser(imageList,index);
    }
    onLocationClicking()
    {
        this.props.onLocationSelectClick(this.props.siteItem);
    }
    onTimeClicking(val)
    {
        //日期选择
        let siteItem = {...this.props.siteItem};
        siteItem.CreateTime = val.formatDate('YYYY-MM-DD HH:mm');
        this.props.dispatch(updateSiteItemAction(this.props.siteIndex,siteItem));
    }
}

CreateFootmarkView.propTypes = {
    // 位置点击事件
    onLocationSelectClick: PropTypes.func,
    //图片展示
    showImageBrowser: PropTypes.func,
    // 站点
    siteIndex: PropTypes.number,
};

const mapStateToProps = function(state, ownProps)
{
    let obj = state.footmark.createScreen.footmarkItem.GetShowList[ownProps.siteIndex];
    return {
        siteItem: obj,
    };
};

CreateFootmarkView = connect(mapStateToProps, null, null, {withRef: true})(CreateFootmarkView);

export default CreateFootmarkView;


