import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ActivityIndicator, Modal, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import * as IConstant from '../../core/utils/IConstant';
import queryString from 'query-string';
import {
    asyncGetDetailAction,
    goBackAction,
    asyncCheckStatusAction,
    asyncGetPayParamsAction,
    asyncSendWechatPaySuccessMessageAction,
} from '../../store/actions/order/detailAction';
import '../../assets/css/order.css';
import SysUtil from '../../core/utils/SysUtil';
import HttpClient from '../../core/utils/HttpClient';
// import Base64 from '../../core/utils/Base64';
import {
    asyncDeleteListAction
} from '../../store/actions/order/listAction';
import WebDB from '../../core/utils/WebDB';

class OrderDetailScreen extends PureComponent 
{
    constructor(props) 
    {
        super(props);
        this.showProductDetail = this.showProductDetail.bind(this);
        this.orderDetailOperateTapped = this.orderDetailOperateTapped.bind(this);
        this.searchParams = queryString.parse(props.location.search);
    }
    componentDidMount() 
    {
        if (!this.props.detailFetched) 
        {
            this.loadOrderDetail();
        }
    }
    componentWillUnmount()
    {
        if(this.modalDialog)
        {
            this.modalDialog.close();
            this.modalDialog = undefined;
        }
    }
    render() 
    {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let orderNo = this.searchParams.orderNo;
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction(orderNo));
                    }}>
                    订单详情
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}
                    style={{ backgroundColor: '#f3f3f3' }}>
                    {
                        this.renderOrderDetails()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                    {
                        this.props.operateList && this.props.operateList.length > 0 ?
                            <div className="orderDetailFooterBar">
                                {
                                    this.props.operateList.map((operateItem, index) => <div key={index} className="orderDetailFooterItem orderDetailFooterItemButton" onClick={() => this.orderDetailOperateTapped(operateItem)}>
                                        {operateItem.operateTitle}
                                    </div>)
                                }
                            </div>
                            :
                            null
                    }
                </Content>
            </div>
        );
    }
    renderOrderDetails() {
        if (!this.props.detailItem) {
            return null;
        }
        let contentBottom = this.props.operateList && this.props.operateList.length > 0 ? 49 : 0;
        return (
            <div className="orderDetailContentView" style={{bottom: contentBottom + 'px'}}>
                {
                    this.renderStatusView()
                }
                {
                    this.renderProductView()
                }
                {
                    this.renderOrderBasicInfoView()
                }
                {
                    this.renderTicketVistorViews()
                }
            </div>
        );
    }
    renderOrderBasicInfoView() {
        let detailItem = this.props.detailItem;
        return (
            <div className="orderDetailBasicInfoRows">
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">购买数量</div>
                    <div className="orderDetailBasicInfoValue">{this.getOrderNumber()}</div>
                </div>
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">单价</div>
                    <div className="orderDetailBasicInfoValue">{this.getUnitPrice()}</div>
                </div>
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">{this.getOrderPlayDateLabel()}</div>
                    <div className="orderDetailBasicInfoValue">{this.getOrderPlayDate()}</div>
                </div>
                {
                    this.renderLineTravelName()
                }
                <div className="orderDetailBasicInfoSplit" />
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">订单号</div>
                    <div className="orderDetailBasicInfoValue">{detailItem.OrderNo}</div>
                </div>
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">创建时间</div>
                    <div className="orderDetailBasicInfoValue">{detailItem.OrderTime}</div>
                </div>
                <div className="orderDetailBasicInfoSplit" />
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">购买人</div>
                    <div className="orderDetailBasicInfoValue">{detailItem.Creator}</div>
                </div>
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">联系电话</div>
                    <div className="orderDetailBasicInfoValue">{detailItem.TelPhone}</div>
                </div>
            </div>
        );
    }
    renderTicketVistorViews() {
        let orderType = this.searchParams.orderType;
        let detailItem = this.props.detailItem;
        if (orderType !== IConstant.ORDER_TYPE.TICKET || !detailItem.GetTicketList || detailItem.GetTicketList.length === 0) {
            return null;
        }
        let status = this.props.detailItem.OrderStatus;
        return (
            <div className="orderDetailBasicInfoRows">
                <div className="orderDetailStatusFlag">{SysUtil.getStatusName(status)}</div>
                <div className="orderDetailBasicInfoSplit" />
                {
                    detailItem.GetTicketList.map((item, index) => this.renderTicketVisitorView(item, index, index === detailItem.GetTicketList.length - 1))
                }
            </div>
        );
    }
    renderTicketVisitorView(item, index, isLast) {
        let infos = item.VisitorInfos ? item.VisitorInfos.split(',') : null;
        if (!infos || infos.length < 2) {
            return null;
        }
        return (
            <div className="orderDetailTicketVisitorRow" key={index}>
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">游玩人</div>
                    <div className="orderDetailBasicInfoValue">{infos[0]}</div>
                </div>
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">身份证</div>
                    <div className="orderDetailBasicInfoValue">{infos[1]}</div>
                </div>
                {
                    !isLast
                        ? <div className="orderDetailBasicInfoSplit" />
                        : null
                }
            </div>
        );
    }
    showProductDetail() 
    {
        let orderType = this.searchParams.orderType;
        if (orderType === IConstant.ORDER_TYPE.HOTEL) 
        {
            this.props.history.push(`/hotel/detail/${this.getShopCode()}`);
        }
        else if (orderType === IConstant.ORDER_TYPE.TICKET) 
        {
            this.props.history.push(`/scenic/detail/${this.getShopCode()}`);
        }
        else if (orderType === IConstant.ORDER_TYPE.LINE) 
        {
            this.props.history.push(`/line/detail/${this.getShopCode()}`);
        }
    }
    getShopCode()
    {
        let orderType = this.searchParams.orderType;
        let detailItem = this.props.detailItem;
        if (orderType === IConstant.ORDER_TYPE.HOTEL) 
        {
            return this.props.detailItem.Code;
        }
        else if (orderType === IConstant.ORDER_TYPE.TICKET) 
        {
            return this.props.detailItem.ShopCode;
        }
        else if (orderType === IConstant.ORDER_TYPE.LINE) 
        {
            let line0 = detailItem.GetLineList ? detailItem.GetLineList[0] : null;
            return line0.ProductNo;
        }
    }
    renderLineTravelName() 
    {
        let orderType = this.searchParams.orderType;
        let detailItem = this.props.detailItem;
        let line0 = detailItem.GetLineList ? detailItem.GetLineList[0] : null;
        if (orderType === IConstant.ORDER_TYPE.LINE && line0) {
            return (
                <div className="orderDetailBasicInfoRow">
                    <div className="orderDetailBasicInfoLabel">旅行社</div>
                    <div className="orderDetailBasicInfoValue">{line0.TravelName}</div>
                </div>
            );
        }
        return null;
    }
    getOrderPlayDateLabel() 
    {
        let orderType = this.searchParams.orderType;
        if (orderType === IConstant.ORDER_TYPE.HOTEL) {
            return '入住时间';
        }
        else if (orderType === IConstant.ORDER_TYPE.TICKET) {
            return '使用日期';
        }
        else if (orderType === IConstant.ORDER_TYPE.LINE) {
            return '线路班期';
        }
        return '';
    }
    getOrderPlayDate() {
        let orderType = this.searchParams.orderType;
        let detailItem = this.props.detailItem;
        if (orderType === IConstant.ORDER_TYPE.HOTEL) {
            return new Date(detailItem.PlaySDate).formatDate('YYYY-MM-DD') + ' ~ ' + new Date(detailItem.PlayEDate).formatDate('YYYY-MM-DD');
        }
        else if (orderType === IConstant.ORDER_TYPE.TICKET ) {
            return new Date(detailItem.PlaySDate).formatDate('YYYY-MM-DD');
        }
        else if (orderType === IConstant.ORDER_TYPE.LINE) {
            return new Date(detailItem.PlaySDate).formatDate('YYYY-MM-DD');
        }
        return '';
    }
    getOrderNumber() {
        let orderType = this.searchParams.orderType;
        let detailItem = this.props.detailItem;
        if (orderType === IConstant.ORDER_TYPE.HOTEL) {
            let roomNumber = detailItem.OrderNumber;
            let dayNumber =  new Date(detailItem.PlayEDate).diffDays(new Date(detailItem.PlaySDate));//detailItem.OrderNumber / roomNumber;
            return `${roomNumber}间${dayNumber}天`;
        }
        else if (orderType === IConstant.ORDER_TYPE.TICKET ) {
            return detailItem.OrderNumber+ '张';
        }
        else if (orderType === IConstant.ORDER_TYPE.LINE ) {
            return detailItem.OrderNumber;
        }
        return '';
    }
    getUnitPrice() {
        let orderType = this.searchParams.orderType;
        let detailItem = this.props.detailItem;
        let ticket0 = detailItem.GetTicketList ? detailItem.GetTicketList[0] : null;
        let hotel0 = detailItem.GetHotelList ? detailItem.GetHotelList[0] : null;
        let line0 = detailItem.GetLineList ? detailItem.GetLineList[0] : null;
        if (orderType === IConstant.ORDER_TYPE.HOTEL && hotel0) {
            return `¥${hotel0.SalePrice}`;
        }
        else if (orderType === IConstant.ORDER_TYPE.TICKET && ticket0) {
            return `¥${ticket0.SalePrice}`;
        }
        else if (orderType === IConstant.ORDER_TYPE.LINE && line0) {
            return `¥${line0.SalePrice}`;
        }
        return '';
    }
    renderProductView() {
        let imgUrl = this.props.detailItem.GetImage ? this.props.detailItem.GetImage.ImageFrontUrl : '';
        return (
            <div className="orderDetailProductRow" onClick={this.showProductDetail}>
                <div style={{ backgroundImage: `url(${imgUrl})` }} className="orderDetailProductImage" />
                <div className="orderDetailProductText">
                    <div className="orderDetailProductTitle">{this.props.detailItem.LineCnName ? this.props.detailItem.LineCnName : this.props.detailItem.OrderName}</div>
                    <div className="orderDetailProductPrice">订单总价：<span className="orderDetailProductPriceLabel">¥</span><span className="orderDetailProductPriceValue">{this.props.detailItem.AmountPrice}</span></div>
                </div>
                <div style={{ flex: 1 }} />
                <img className='arrowRight' src={require('../../assets/images/arrow@2x.png')} alt='' />
            </div>
        );
    }
    renderStatusView() {
        let status = this.props.detailItem.OrderStatus;
        let style = {
            backgroundColor: status >= 4 ? '#deffeb' : '#ffdfdd',
            color: status >= 4 ? '#43c276' : '#ef5848',
        };
        return (
            <div className="orderDetailStatusText" style={style}>
                {SysUtil.getStatusName(status)}
            </div>
        );
    }
    loadOrderDetail() 
    {
        let orderNo = this.searchParams.orderNo;
        let orderType = this.searchParams.orderType;
        this.props.dispatch(asyncGetDetailAction(orderNo, orderType));
    }

    orderDetailOperateTapped(operateItem) {
        let operateType = operateItem.operateType;
        if (operateType === IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_CANCEL) {
            this.orderCancelTapped();
        }
        else if (operateType === IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_GOPAY) {
            this.goPayTapped();
        }
        else if (operateType === IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_DELETE) {
            this.orderDeleteTapped();
        }
        else if (operateType === IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_REFUND) {
            this.orderRefundTapped();
        }
        else if (operateType === IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_BUYAGAIN) {
            this.buyAgainTapped();
        }
        else if (operateType === IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_REFUNDRESON) {
            this.refundReasonTapped();
        }
        else if (operateType === IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_GOCOMMENT) {
            this.goCommentTapped();
        }
        else if (operateType === IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_REFUND_REFUSERESON) {
            this.refundRefuseReasonTapped();
        }
    }

    //取消订单
    orderCancelTapped() {
        this.modalDialog = Modal.alert('取消订单', '确定取消订单?', [
            { text: '再考虑下', onPress: () => {
                this.modalDialog = undefined;
            } },
            {
                text: '确定', onPress: () => {
                    this.modalDialog = undefined;

                    let kvDict = {
                        ConditionParams: {
                            Params: {
                                OrderNo: this.props.detailItem.OrderNo,
                                UserID: this.props.userId,
                                Message: '行程变更'
                            }
                        }
                    };
                    let portalApi;
                    let orderType = this.searchParams.orderType;
                    if (orderType === IConstant.ORDER_TYPE.HOTEL) {
                        portalApi = IConstant.PortalApis.ELEMENTS.Hotel_CancelOrder;
                    }
                    else if (orderType === IConstant.ORDER_TYPE.TICKET) {
                        portalApi = IConstant.PortalApis.ELEMENTS.AttractionsCancelOrder;
                    }
                    else if (orderType === IConstant.ORDER_TYPE.LINE) {
                        portalApi = IConstant.PortalApis.ELEMENTS.CancelLineOrder;
                    }

                    HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS, portalApi, kvDict)
                        .then((resp) => resp.json()).then((resp) => {
                            if (resp && resp.status === '0') {
                                //清空对应类型订单列表 数据
                                this.props.dispatch(asyncDeleteListAction(this.searchParams.orderType));

                                this.loadOrderDetail();
                            }
                        });
                }
            },
        ]);
    }

    //立即支付
    goPayTapped() 
    {
        let openId = WebDB.getItem(IConstant.STORAGE_KEY.WX_USER_OPENID);
        if(!openId || openId.length === 0 || typeof WeixinJSBridge === 'undefined')
        {
            Toast.fail('请在微信客户端打开链接');
            return;
        }
        // safari对window.open函数的使用有限制。。。http://blog.csdn.net/dongguan_123/article/details/62416767
        // 检测订单当前状态是否改变
        let orderNo = this.searchParams.orderNo;
        let orderType = this.searchParams.orderType;
        this.props.dispatch(asyncCheckStatusAction(orderNo, orderType, this.props.detailItem.OrderSTATUS)).then(()=>{
            if(this.props.orderStatusChanged)
            {
                Toast.fail('订单状态已发生改变');
            }
            else
            {
                this.props.dispatch(asyncGetPayParamsAction(this.props.detailItem.OrderName, 
                    this.props.detailItem.OrderNo, 
                    this.props.detailItem.AmountPrice, 
                    openId)).then(()=>{
                    if(this.props.payParams)
                    {
                        // https://pay.weixin.qq.com/wiki/doc/api/jsapi.php?chapter=7_7&index=6
                        window.WeixinJSBridge.invoke('getBrandWCPayRequest', this.props.payParams, (res)=>{
                            if(res.err_msg === 'get_brand_wcpay_request:ok')
                            {
                                // 调用接口给用户发送支付成功的消息
                                asyncSendWechatPaySuccessMessageAction(this.props.detailItem, orderType);
                                Toast.success('支付成功!');
                                this.loadOrderDetail();
                            }
                            else
                            {
                                Toast.fail('支付失败!');
                            }
                            console.log('支付结果：' + JSON.stringify(res));
                        });
                    }
                    else if(this.props.payFailure)
                    {
                        Modal.alert('支付失败', this.props.payFailure);
                    }
                });
            }
        });
    }

    //删除
    orderDeleteTapped() {
        this.modalDialog = Modal.alert('删除订单', '确定删除订单?', [
            { text: '再考虑下', onPress: ()=>{
                this.modalDialog = undefined;
            } },
            {
                text: '确定', onPress: () => {
                    this.modalDialog = undefined;

                    let kvDict = {
                        ConditionParams: {
                            Params: {
                                OrderNo: this.props.detailItem.OrderNo,
                                UserID: this.props.userId,
                                Message: '行程变更'
                            }
                        }
                    };
                    let portalApi;
                    let orderType = this.searchParams.orderType;
                    if (orderType === IConstant.ORDER_TYPE.HOTEL) {
                        portalApi = IConstant.PortalApis.ELEMENTS.Hotel_DeleteOrder;
                    }
                    else if (orderType === IConstant.ORDER_TYPE.TICKET) {
                        portalApi = IConstant.PortalApis.ELEMENTS.AttractionsDeleteOrder;
                    }
                    else if (orderType === IConstant.ORDER_TYPE.LINE) {
                        portalApi = IConstant.PortalApis.ELEMENTS.DeleteLineOrder;
                    }

                    HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS, portalApi, kvDict)
                        .then((resp) => resp.json())
                        .then((resp) => {
                            if (resp && resp.status === '0') {
                                //清空对应类型订单列表 数据
                                this.props.dispatch(asyncDeleteListAction(this.searchParams.orderType));

                                //返回
                                let orderNo = this.searchParams.orderNo;
                                let dispatch = this.props.dispatch;
                                this.props.history.goBack();
                                dispatch(goBackAction(orderNo));
                            }
                        });
                }
            },
        ]);
    }

    //申请退款
    orderRefundTapped() {
        this.modalDialog = Modal.prompt('申请退款', '请输入您的申请退款原因', [
            { text: '取消', onPress: ()=>{
                this.modalDialog = undefined;
            } },
            {
                text: '确定', onPress: value => {
                    this.modalDialog = undefined;
                    if (value.length <= 0) {
                        Toast.info('申请退款原因不能为空!', undefined, undefined, false);
                        return;
                    }
                    let kvDict;
                    let portalApi;
                    let orderType = this.searchParams.orderType;
                    if (orderType === IConstant.ORDER_TYPE.HOTEL) {
                        portalApi = IConstant.PortalApis.ELEMENTS.Hotel_RefundApply;
                        kvDict = {
                            ConditionParams: {
                                Params: {
                                    OrderNo: this.props.detailItem.OrderNo,
                                    OrderStatus: this.props.detailItem.OrderStatus,
                                    Message: value
                                }
                            }
                        };
                    }
                    else if (orderType === IConstant.ORDER_TYPE.TICKET) {
                        portalApi = IConstant.PortalApis.ELEMENTS.AttractionsRefundApply;
                        kvDict = {
                            ConditionParams: {
                                Params: {
                                    OrderNo: this.props.detailItem.OrderNo,
                                    UserID: this.props.userId,
                                    Message: value
                                }
                            }
                        };
                    }
                    else if (orderType === IConstant.ORDER_TYPE.LINE) {
                        portalApi = IConstant.PortalApis.ELEMENTS.LineRefundApply;
                        kvDict = {
                            ConditionParams: {
                                Params: {
                                    OrderNo: this.props.detailItem.OrderNo,
                                    OrderStatus: this.props.detailItem.OrderStatus,
                                    Message: value
                                }
                            }
                        };
                    }
                    HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS, portalApi, kvDict)
                        .then((resp) => resp.json()).then((resp) => {
                            if (resp && resp.status === '0') {
                                //清空对应类型订单列表 数据
                                this.props.dispatch(asyncDeleteListAction(this.searchParams.orderType));

                                this.loadOrderDetail();
                            }
                            else {
                                Toast.fail('您已进行过此操作，不能重复申请！', undefined, undefined, false);
                            }
                        });
                }
            },
        ], 'default', '行程变更');
    }

    //再次购买
    buyAgainTapped() {
        this.showProductDetail();
    }

    //查看退款申请原因
    refundReasonTapped() 
    {
        this.modalDialog = Modal.alert('查看退款申请原因', this.props.detailItem.RefundAppCause, [
            {
                text: '确定', onPress: () => {
                    this.modalDialog = undefined;
                }
            },
        ]);
    }

    //查看退款拒绝原因
    refundRefuseReasonTapped() 
    {
        this.modalDialog = Modal.alert('查看退款拒绝原因', this.props.detailItem.RefundCause, [
            {
                text: '确定', onPress: () => {
                    this.modalDialog = undefined;
                }
            },
        ]);
    }

    //去点评 --- 暂无
    goCommentTapped()
    {
        this.props.history.push(`/order/comments/${this.getShopCode()}/${this.searchParams.orderNo}/${this.searchParams.orderType}`);
    }
}

const mapStateToProps = function (state, ownProps)
{
    let searchParams = queryString.parse(ownProps.location.search);

    let item = (ownProps.history.location && ownProps.history.location.state) ? ownProps.history.location.state.basicInfo : null;
    item = item ? { detailItem: item } : { detailItem: {} };
    let obj = state.order.detailScreen[searchParams.orderNo];
    item = Object.assign({}, item, obj ? obj : {});
    return {
        ...item,
        detailFetched: obj && obj.detailItem,
        userId: SysUtil.getUserId(state),
    };
};

OrderDetailScreen = connect(mapStateToProps)(OrderDetailScreen);

export default OrderDetailScreen;


