import { Actions } from '../../core/utils/IConstant';

export function clearConfigItemAction()
{
    return {
        type: Actions.PHOTO_BROWSER.CLEAR_CONFIG_ITEMS,
    };
}

export function setConfigItemAction(configItem)
{
    return {
        type: Actions.PHOTO_BROWSER.SET_CONFIG_ITEM,
        configItem
    };
}

export function setPhotoBrowerIframeStatusAction(){
    return {
        type: Actions.PHOTO_BROWSER.SET_PHOTOBROWER_IFRAME_LOAD
    };
}


