import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import SysUtil from '../../../core/utils/SysUtil';
import WebDB from '../../../core/utils/WebDB';

export function goBackAction(orderNo)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.GO_BACK,
        orderNo,
    };
}

// 订单详情
export function asyncGetDetailAction(orderNo, orderType)
{
    return function(dispatch, getState)
    {
        dispatch(_getDetailRequestAction(orderNo));
        return _fetchDetail(orderNo, orderType).then((resp)=>{
            resp = (resp && resp.results && resp.results.length > 0) ? resp.results[0] : resp;
            let orderStatus = _getOrderStatus(resp,orderType);
            if (resp && orderStatus && orderStatus !== undefined)
            {
                let operateList = getOrderOperateList(resp,orderStatus,orderType);
                dispatch(setDetailOrderOperateListAction(orderNo,operateList));
            }
            dispatch(_getDetailSuccessAction(orderNo, resp));
        }).catch((error)=>{
            dispatch(_getDetailFailureAction(orderNo, error));
        });
    };
}

// 发送支付成功的消息
export function asyncSendWechatPaySuccessMessageAction(orderItem, orderType)
{
    let accountId = WebDB.getItem(IConstant.STORAGE_KEY.WX_ACCOUNT);
    let openId = WebDB.getItem(IConstant.STORAGE_KEY.WX_USER_OPENID);
    if(!accountId || !openId)
    {
        console.error('微信用户信息不能为空！');
        return;
    }

    let kvDict;
    let jsonData;
    let conditionParm = {
        AccountGuid: accountId,
        OpenId: openId,
        url: '',
    };
    let orderName = orderItem.LineCnName ? orderItem.LineCnName : orderItem.OrderName;
    if (orderType === IConstant.ORDER_TYPE.HOTEL)
    {
        conditionParm.messageType = 'HotelBookingSuccess';
        jsonData = {
            first: '酒店房费支付成功',
            keyword1: orderItem.OrderNo, //订单号
            keyword2: new Date(orderItem.PlaySDate).formatDate('YYYY-MM-DD') + ' ~ ' + new Date(orderItem.PlayEDate).formatDate('YYYY-MM-DD'), //预达日期
            keyword3: orderName, //预购景点
            keyword4: orderItem.OrderNumber, //人数
            keyword5: '无', //订单识别码
            remark: '有任何疑问，请联系客服'
        };
    }
    else if (orderType === IConstant.ORDER_TYPE.TICKET)
    {
        conditionParm.messageType = 'TicketBookingSuccess';
        jsonData = {
            first: '门票支付成功',
            keyword1: orderItem.OrderNo, //订单号
            keyword2: new Date(orderItem.PlaySDate).formatDate('YYYY-MM-DD'), //预达日期
            keyword3: orderName, //预购景点
            keyword4: orderItem.OrderNumber, //人数
            keyword5: '无', //订单识别码
            remark: '有任何疑问，请联系客服'
        };
    }
    else if (orderType === IConstant.ORDER_TYPE.LINE)
    {
        conditionParm.messageType = 'LineBookingSuccess';
        jsonData = {
            first: '线路支付成功',
            keyword1: orderItem.OrderNo, //订单号
            keyword2: new Date(orderItem.PlaySDate).formatDate('YYYY-MM-DD'), //预达日期
            keyword3: orderName, //预购景点
            keyword4: orderItem.OrderNumber, //人数
            keyword5: '无', //订单识别码
            remark: '有任何疑问，请联系客服'
        };
    }
    if(!jsonData)
    {
        return;
    }
    conditionParm.jsonData = jsonData;
    kvDict = {
        type: 51,
        action: 20,
        conditionParm: conditionParm,
    };
    HttpClient.postData(HttpClient.WECHAT_HANDLER, kvDict)
        .then((resp) => resp.json());
}

// 检测订单状态
export function asyncCheckStatusAction(orderNo, orderType, orderStatus)
{
    return function(dispatch, getState)
    {
        dispatch(_checkStatusRequestAction(orderNo));
        return _fetchDetail(orderNo, orderType).then((resp)=>{
            resp = (resp && resp.results && resp.results.length > 0) ? resp.results[0] : resp;
            let orderStatusChanged = false;
            let newOrderStatus = _getOrderStatus(resp,orderType);
            if (resp && orderStatus && orderStatus !== undefined)
            {
                orderStatusChanged = (orderStatus !== newOrderStatus);
                let operateList = getOrderOperateList(resp,newOrderStatus);
                dispatch(setDetailOrderOperateListAction(orderNo,operateList));
            }
            dispatch(_checkStatusSuccessAction(orderNo, resp, orderStatusChanged));
        }).catch((error)=>{
            dispatch(_checkStatusFailureAction(orderNo, error));
        });
    };
}

// 酒店详情页面滚动条位置
export function setDetailScrollerPositionAction(orderNo, offset)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.SET_DETAIL_SCROLL_OFFSET,
        orderNo,
        scrollViewOffset: offset,
    };
}

// 设置 订单 可操作数据
export function setDetailOrderOperateListAction(orderNo, operateList)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.SET_DETAIL_ORDER_OPERATE_LIST,
        orderNo,
        operateList,
    };
}

// 获取微信支付参数
export function asyncGetPayParamsAction(orderName: string, orderNo: string, totalPrice: string, openId: string)
{
    return function(dispatch, getState)
    {
        dispatch(_fetchPayParamsRequestAction(orderNo));
        return _fetchPayParams(orderName, orderNo, totalPrice, openId).then((resp)=>{
            dispatch(_fetchPayParamsSuccessAction(orderNo, resp));
        }).catch((error)=>{
            dispatch(_fetchPayParamsFailureAction(orderNo, error));
        });
    };
}

function _fetchPayParams(orderName: string, orderNo: string, totalPrice: string, openId: string)
{
    return HttpClient.postToPayArgsData(orderName, orderNo, totalPrice, openId)
        .then((resp) => resp.json());
}

function _fetchPayParamsRequestAction(orderNo)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.GET_PAY_PARAMS_REQUEST,
        orderNo,
    };
}

function _fetchPayParamsSuccessAction(orderNo, resp)
{
    if (resp && resp.IsSuccess && resp.Result)
    {
        return {
            type: IConstant.Actions.ORDER.DETAIL.GET_PAY_PARAMS_SUCCESS,
            orderNo,
            payParams: resp.Result
        };
    }
    else
    {
        return {
            type: IConstant.Actions.ORDER.DETAIL.GET_PAY_PARAMS_FAILURE,
            orderNo,
            payFailure: (resp && resp.Message) ? resp.Message : '获取支付参数失败!'
        };
    }
}

function _fetchPayParamsFailureAction(orderNo, error)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.GET_PAY_PARAMS_FAILURE,
        orderNo,
        payFailure: '获取支付参数失败!'
    };
}

function _fetchDetail(orderNo, orderType)
{
    let kvDict = {
        ConditionParams: {
            Params: {
                OrderNo: orderNo
            }
        }
    };
    let portalApi;
    if (orderType === IConstant.ORDER_TYPE.HOTEL) {
        portalApi = IConstant.PortalApis.ELEMENTS.Hotel_GetOrderDetail; 
    }
    else if (orderType === IConstant.ORDER_TYPE.TICKET) {
        portalApi = IConstant.PortalApis.ELEMENTS.AttractionsOrderDetail;
    }
    else if (orderType === IConstant.ORDER_TYPE.LINE) {
        portalApi = IConstant.PortalApis.ELEMENTS.GetLineOrderDetail;
    }
   
    return HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS,portalApi,kvDict)
        .then((resp) => resp.json());
}

function _getOrderStatus(item,orderType)
{
    if (orderType === IConstant.ORDER_TYPE.SPECIAL) {
        return item.OrderSTATUS;
    }
    return item.OrderStatus;
}

function _getDetailRequestAction(orderNo)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.GET_DETAIL_REQUEST,
        orderNo,
    };
}

function _getDetailSuccessAction(orderNo, resp)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.GET_DETAIL_SUCCESS,
        orderNo,
        detailItem: resp
    };
}

function _getDetailFailureAction(orderNo, error)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.GET_DETAIL_FAILURE,
        orderNo,
        message: error
    };
}

function _checkStatusRequestAction(orderNo)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.CHECK_STATUS_REQUEST,
        orderNo,
    };
}

function _checkStatusSuccessAction(orderNo, resp, orderStatusChanged)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.CHECK_STATUS_SUCCESS,
        orderNo,
        detailItem: resp,
        orderStatusChanged,
    };
}

function _checkStatusFailureAction(orderNo, error)
{
    return {
        type: IConstant.Actions.ORDER.DETAIL.CHECK_STATUS_FAILURE,
        orderNo,
        message: error
    };
}

function getOrderOperateList(detailItem,orderStatus,orderType)
{
    let orderState = parseInt(orderStatus,10);
    
    let isHasRefundRefuseReason = detailItem.RefundCause && detailItem.RefundCause !== undefined;
    let isHasRefundReason = detailItem.RefundAppCause && detailItem.RefundAppCause !== undefined;
    let operateList = [];

    // 查看原因
    if (isHasRefundReason)//&& orderState !== IConstant.ORDER_STATE_TYPE.ORDER_STATUS_UnRefunded
    {
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_REFUNDRESON));
    }
    if (isHasRefundRefuseReason)
    {
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_REFUND_REFUSERESON));
    }

    //待付款 : 取消订单  立即支付  删除
    if (orderState === IConstant.ORDER_STATE_TYPE.ORDER_STATUS_PAY)
    {
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_CANCEL));
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_GOPAY));
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_DELETE));
    }
    //已支付: 申请退款（退款原因输入）   再次购买（进入下订单）
    else if (orderState === IConstant.ORDER_STATE_TYPE.ORDER_STATUS_PAID)
    {
        if (_isCanRefund())
        {
            operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_REFUND));
        }
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_BUYAGAIN));
    }
    //已取消:  删除
    else if (orderState === IConstant.ORDER_STATE_TYPE.ORDER_STATUS_CANCEL)
    {
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_DELETE));
    }
    //已消费 : 去点评  删除
    else if (orderState === IConstant.ORDER_STATE_TYPE.ORDER_STATUS_Consumption)
    {
        if (detailItem.IsComment <= 0)
        {
            operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_GOCOMMENT));
        }
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_DELETE));
    }
    //已退款: 删除
    else if (orderState === IConstant.ORDER_STATE_TYPE.ORDER_STATUS_REFUNDED)
    {
        operateList.push(getOrderOperateItem(IConstant.ORDER_OPERATE_TYPE.ORDER_OPERATE_DELETE));
    }
    return operateList;
}


function getOrderOperateItem(orderOperateType)
{
    let operateTitle = SysUtil.getOrderOperateTypeName(orderOperateType);
    let operateItem = {operateType: orderOperateType,operateTitle:operateTitle};
    return operateItem;
}

function _isCanRefund(detailItem,orderState,orderType):boolean
{
    if (orderState === IConstant.ORDER_STATE_TYPE.ORDER_STATUS_PAID)
    {
        if (orderType === IConstant.ORDER_TYPE.LINE)
        {
            //是否 在 发团日期前三天  a - 3 > b
            let playDate = new Date(detailItem.PlaySDate); // a
            let currentDate = new Date();  // b
            let maxDay = 3;
            let canRefund = playDate.addDays(-maxDay).isAfter(currentDate);
            return canRefund;
        }
    }
    return true;
}


