import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';
import WebDB from '../../../core/utils/WebDB';

export function asyncGetDataAction(AccountGuid, OpenId) {
    return function (dispatch, getState) {
        dispatch(_getDataRequestAction(AccountGuid, OpenId));
        return _fetchData(AccountGuid, OpenId).then((resp) => {
            dispatch(_getDataSuccessAction(resp));
        }).catch((error) => {
            dispatch(_getDataFailureAction());
        });
    };
}

export function _getDataRequestAction(AccountGuid, OpenId){
    return {
        type: IConstant.Actions.SIGN.GET_INFO_REQUEST
    };
}

export function _fetchData(AccountGuid, OpenId)
{
    let accountId = WebDB.getItem(IConstant.STORAGE_KEY.WX_ACCOUNT);
    let openId = WebDB.getItem(IConstant.STORAGE_KEY.WX_USER_OPENID);
    let url = HttpClient.WECHAT_HANDLER;
    let kvDict = {
        type: '7',
        action: '1',
        conditionParm: JSON.stringify({
            AccountGuid: accountId,
            OpenId: openId
        })
    };

    return HttpClient.getData(url, kvDict).then((resp) => resp.json());
}

export function _getDataSuccessAction(resp){
    return {
        type:IConstant.Actions.SIGN.GET_INFO_SUCCESS,
        resp
    };
}

export function _getDataFailureAction(){
    return {
        type:IConstant.Actions.SIGN.GET_INFO_FAILURE
    };
}

export function goBackAction(){
    return {
        type:IConstant.Actions.SIGN.GO_BACK
    };
}


