import HttpClient from '../../core/utils/HttpClient';
import { Actions, PortalApis } from '../../core/utils/IConstant';

export function asyncGetJourneyMaxDayCountAction()
{
    return function(dispatch, getState)
    {
        dispatch(_getJourneyMaxDayCountRequestAction());
        return _getJourneyMaxDayCount().then((resp)=>{
            dispatch(_getJourneyMaxDayCountSuccessAction(resp && resp.status === '0' ? resp.results : []));
        }).catch((error)=>{
            dispatch(_getJourneyMaxDayCountFailureAction(error));
        });
    };
}

function _getJourneyMaxDayCount()
{
    return HttpClient.postToPortal(PortalApis.JOURNEY.GetSettings)
        .then((resp) => resp.json());
}

function _getJourneyMaxDayCountRequestAction()
{
    return {
        type: Actions.SYSTEM.GET_JOURNEY_MAX_DAYCOUNT_REQUEST
    };
}

function _getJourneyMaxDayCountSuccessAction(dayCount)
{
    return {
        type: Actions.SYSTEM.GET_JOURNEY_MAX_DAYCOUNT_SUCCESS,
        dayCount
    };
}

function _getJourneyMaxDayCountFailureAction(error)
{
    return {
        type: Actions.SYSTEM.GET_JOURNEY_MAX_DAYCOUNT_FAILURE,
        message: error
    };
}


