import { combineReducers } from 'redux';
import listReducer from './listReducer';
import recommendListReducer from './recommendListReducer';
import detailReducer from './detailReducer';

const specialtyReducer = combineReducers({
    listScreen: listReducer,
    recommendListScreen: recommendListReducer,
    detailScreen: detailReducer,
});

export default specialtyReducer;


