import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator,Toast } from 'antd-mobile';
import { asyncGetFriendsListAction,asyncDeleteFriendsListAction, setScrollerPositionAction,asyncUpdateFriendsItemFollowAction } from '../../store/actions/userProfile/userProfileAction';
import EmptyView from '../../views/common/EmptyView';
import SysUtil from '../../core/utils/SysUtil';
import UIUtil from '../../core/utils/UIUtil';
import '../../assets/css/userProfile.css';

class FriendListView extends PureComponent
{
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList)
        {
            this.loadData();
        }
        if(this.listView && !isNaN(this.props.listViewOffset))
        {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(((prevProps.targetUserId !== this.props.targetUserId) || this.props.isRefresh ) && this.props.targetUserId && !this.props.dataList)
        {
            this.loadData();
        }
        if(this.props.dataList !== prevProps.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : [])});
        }
    }
    render()
    {
        return (
            <div className="friendListBox" style={{height: this.props.height}}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh(this.props.inputKeyword)}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator className="indicatorLoading"
                            toast
                            text="Loading..." />
                        :null
                }
            </div>
        );
    }
    renderListView()
    {
        if(this.props.dataList && this.props.dataList.length > 0)
        {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );
    
            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}
                    />}
                    onEndReached={this.loadNextPage}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20}/>
            );
        }
    }
    onRefresh()
    {
        this.props.dispatch(asyncDeleteFriendsListAction(this.props.targetUserId,this.props.userId, this.props.friendsType)).then(()=>{
            this.loadData();
        });
    }
    loadNextPage()
    {
        this.loadData();
    }
    renderFooter()
    {
        if(!this.props.isFetching || this.props.hasMore !== true)
        {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID)
    {
        let imgUrl = item.GetUserAvatar ? item.GetUserAvatar : '';
        let operationIcon = item.IsAttention ? require('../../assets/images/userProfile/ico_list_followed@2x.png') : require('../../assets/images/userProfile/ico_list_follow@2x.png');
        return (
            <div key={rowID} 
                className="friendsListItem"
                onClick={()=>{
                    if(this.props.onItemClick)
                    {
                        this.props.onItemClick(item);
                    }
                }}>
                <div className="friendsListLeftBox">
                    <div style={{backgroundImage: `url(${imgUrl})`}} className="friendsListItemImg"/>
                    <div className="friendsListItemName">{item.NickName}</div>
                </div>
                {
                    item.UserID !== this.props.userId ?
                        <div className="friendsListItemOperationBox" onClick={(e) => this.operatinButtonTapped(e,item)} >
                            <img className="friendsListOperationIcon" src={operationIcon} alt='' />
                        </div>
                        :
                        null
                }
            </div>
        );
    }
    loadData()
    {
        if(this.props.isFetching)
        {
            return;
        }
        if(this.props.hasMore === false)
        {
            return;
        }

        let pageIndex = this.props.pageIndex >= 1 ? (this.props.pageIndex + 1) : 1;
        this.props.dispatch(asyncGetFriendsListAction(this.props.userId,this.props.targetUserId, this.props.friendsType, pageIndex));
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(this.props.targetUserId,this.props.userId, this.props.friendsType, offsetY));
    }

    operatinButtonTapped(e,item)
    {
        e.stopPropagation();
        e.nativeEvent.stopImmediatePropagation();
        if(isNaN(this.props.userId))
        {
            this.props.toLoginPage();
            return;
        }
        this.props.dispatch(asyncUpdateFriendsItemFollowAction(this.props.targetUserId,item.UserID,this.props.userId, this.props.friendsType,item.IsAttention)).then(()=>{
            Toast.success(item.IsAttention ? '取消关注成功!' : '关注成功!', undefined, undefined, false);
            //刷新
            this.onRefresh();
        });
    }
}

FriendListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
    toLoginPage: PropTypes.func
};

const mapStateToProps = function(state, ownProps)
{
    let userId = SysUtil.getUserId(state);
    let profileName = ownProps.targetUserId + '_' + userId;
    let friendsObj = state.userProfile[profileName] ? state.userProfile[profileName][ownProps.friendsType] : null;
    return {
        ...friendsObj,
        userId,
        targetUserId: ownProps.targetUserId
    };
};

FriendListView = connect(mapStateToProps, null, null, {withRef: true})(FriendListView);

export default FriendListView;


