import { Actions } from '../../../core/utils/IConstant';

export default function listReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.TRAFFIC.LIST.GO_BACK:
        return { };
    case Actions.TRAFFIC.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            scrollViewOffset: action.scrollViewOffset,
        };
    default:
        return state;
    }
}


