import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';
import { friendsFootmarkItemPraiseSuccessAction, 
    friendsFootmarkItemCollectSuccessAction,
    setScrollerPositionAction as friendSetScrollerPositionAction,
    footmarkToggleExpandAction,
    deleteFriendFootmarkListAction,
    getFootmarkListRequestAction,
    getFootmarkListSuccessAction,
    getFootmarkListFailureAction
} from '../userProfile/userProfileAction';

export function setSelectedTabIndexAction(index)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.SET_SELECTED_TAB_INDEX,
        index: index
    };
}

export function asyncGetListAction(pageIndex, footmarkType, targetUserId, userId)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(pageIndex, footmarkType, targetUserId,userId));
        return _fetchList(pageIndex, footmarkType,targetUserId, userId).then((resp)=>{
            dispatch(_getListSuccessAction(pageIndex, footmarkType, targetUserId,userId, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(footmarkType, targetUserId,userId, error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.GO_BACK,
    };
}

export function asyncDeleteListAction(footmarkType,targetUserId, userId)
{
    return function(dispatch, getState)
    {
        if (_isFriendFootmarkType(footmarkType))
        {
            deleteFriendFootmarkListAction(targetUserId,userId);
        }
        else
        {
            dispatch(deleteListAction(footmarkType,targetUserId));
        }
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction(footmarkType, userId)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.DELETE,
        userId,
        footmarkType,
    };
}

export function setScrollerPositionAction(footmarkType,targetUserId, userId, offset)
{
    return function(dispatch, getState)
    {
        if (_isFriendFootmarkType(footmarkType))
        {
            dispatch(friendSetScrollerPositionAction(targetUserId, userId, IConstant.FRIEND_PROFILE_DATA_TYPE.FOOTMARK, offset));
        }
        else
        {
            dispatch(_setScrollerPositionAction(footmarkType,targetUserId,offset));
        }
        return new Promise((resolve)=>{
            resolve();
        });
    };
}
function _setScrollerPositionAction(footmarkType,userId,offset)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.SET_LISTVIEW_OFFSET,
        footmarkType,
        userId,
        listViewOffset: offset
    };
}

// 收起、展开
export function toggleExpandAction(footmarkType, targetUserId,userId, index)
{
    return function (dispatch, getState) {
        if (_isFriendFootmarkType(footmarkType))
        {
            dispatch(footmarkToggleExpandAction(targetUserId,userId,index));
        }
        else
        {
            dispatch(_toggleExpandAction(footmarkType,targetUserId,index));
        }
        return new Promise((resolve)=>{
            resolve();
        });
    };
}
function _toggleExpandAction(footmarkType,userId,index)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.TOGGLE_EXPAND,
        footmarkType,
        userId,
        index
    };
}

// 点赞
export function asyncPraiseAction(footmarkType,targetUserId, userId, code, isLike, index)
{
    return function(dispatch, getState)
    {
        return _fetchPraise(code, userId, isLike).then((resp)=>{
            if (_isFriendFootmarkType(footmarkType))
            {
                dispatch(friendsFootmarkItemPraiseSuccessAction(targetUserId,userId,index));
            }
            else
            {
                dispatch(_praiseSuccessAction(footmarkType, userId, index));
            }
        }).catch((error)=>{
        });
    };
}

// 收藏
export function asyncCollectAction(footmarkType,targetUserId ,userId, code, isCollect, index)
{
    return function(dispatch, getState)
    {
        return _fetchCollect(code, userId, isCollect).then((resp)=>{
            if (_isFriendFootmarkType(footmarkType))
            {
                dispatch(friendsFootmarkItemCollectSuccessAction(targetUserId,userId,index));
            }
            else
            {
                dispatch(_collectSuccessAction(footmarkType, userId, index));
            }
        }).catch((error)=>{
        });
    };
}

function _fetchCollect(code, userId, isCollect)
{
    let kvDict = {
        jsonParams: {
            ThemeCode: code, 
            UserID: userId,
            IsCollection: isCollect,
        },
    };
    return HttpClient.postToPortal(IConstant.PortalApis.FOOTMARK.COLLECT, kvDict, undefined, true)
        .then((resp) => resp.json());
}

function _fetchPraise(code, userId, isLike)
{
    let kvDict = {
        jsonParams: {
            ThemeCode: code, 
            UserID: userId,
            IsLike: isLike,
        },
    };
    return HttpClient.postToPortal(IConstant.PortalApis.FOOTMARK.LIKE, kvDict, undefined, true)
        .then((resp) => resp.json());
}

function _fetchList(pageIndex, footmarkType,targetUserId, userId)
{
    let conditionParams = {
        Page: {
            PageIndex: pageIndex,
            PageSize: IConstant.PAGE_SIZE
        },
    };
    let kvDict = {
        conditionParams: conditionParams,
        type: footmarkType,
        UserID: targetUserId,
    };
    if (_isFriendFootmarkType(footmarkType))
    {
        kvDict.MyUserID = userId;
    }

    return HttpClient.getFromPortal(IConstant.PortalApis.FOOTMARK.LIST, kvDict)
        .then((resp) => resp.json());
}

function _getListRequestAction(pageIndex, footmarkType, targetUserId,userId)
{
    return function (dispatch, getState) {
        if (_isFriendFootmarkType(footmarkType)) {
            dispatch(getFootmarkListRequestAction(targetUserId,userId,pageIndex));
        }
        else
        {
            dispatch(_getFootmarkIndexListRequestAction(footmarkType,targetUserId,pageIndex));
        }
        return new Promise((resolve)=>{
            resolve();
        });
    };
}
function _getFootmarkIndexListRequestAction(footmarkType,userId,pageIndex)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.GET_LIST_REQUEST,
        footmarkType,
        userId,
        pageIndex,
    };
}

function _getListSuccessAction(pageIndex, footmarkType,targetUserId, userId, result)
{
    return function (dispatch, getState) {
        if (_isFriendFootmarkType(footmarkType)) {
            dispatch(getFootmarkListSuccessAction(targetUserId, userId, pageIndex, result));
        }
        else
        {
            dispatch(_getFootmarkIndexListSuccessAction(footmarkType,targetUserId,pageIndex,result));
        }
        return new Promise((resolve)=>{
            resolve();
        });
    };
}
function _getFootmarkIndexListSuccessAction(footmarkType,userId,pageIndex,result)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.GET_LIST_SUCCESS,
        footmarkType,
        userId,
        pageIndex,
        result
    };
}

function _getListFailureAction(footmarkType,targetUserId, userId, error)
{
    return function (dispatch, getState) {
        if (_isFriendFootmarkType(footmarkType)) {
            dispatch(getFootmarkListFailureAction(targetUserId,userId,error));
        }
        else
        {
            dispatch(_getFootmarkIndexListFailureAction(footmarkType,targetUserId,error));
        }
        return new Promise((resolve)=>{
            resolve();
        });
    };
}
function _getFootmarkIndexListFailureAction(footmarkType,userId,error)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.GET_LIST_FAILURE,
        footmarkType,
        userId,
        message: error
    };
}

function _praiseSuccessAction(footmarkType, userId, index)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.PRAISE_SUCCESS,
        footmarkType,
        userId,
        index,
    };
}

function _collectSuccessAction(footmarkType, userId, index)
{
    return {
        type: IConstant.Actions.FOOTMARK.LIST.COLLECT_SUCCESS,
        footmarkType,
        userId,
        index,
    };
}

function _isFriendFootmarkType(footmarkType)
{
    return (footmarkType === 4);
}


