import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Toast, Calendar } from 'antd-mobile';
import { 
    updateUserInfoAction,
    updateBookingItemAction,
    _asyncUpdateSelectVisitorAction
} from '../../store/actions/order/bookingAction';
// eslint-disable-next-line
import DateUtil from '../../core/utils/DateUtil';
import zhCN from 'antd-mobile/lib/calendar/locale/zh_CN';
import UIUtil from '../../core/utils/UIUtil';

class OrderBookingTicketBasicView extends PureComponent
{
    minDate;
    maxDate;
    constructor(props)
    {
        super(props);
        this.state = {
            showDatePicker: false,
        };
        this.handlePhoneNumberChange = this.handlePhoneNumberChange.bind(this);
        this.diffOrderNumber = this.diffOrderNumber.bind(this);
        this.increaseOrderNumber = this.increaseOrderNumber.bind(this);
        this.showPlayDateChooser = this.showPlayDateChooser.bind(this);
        this.onDateChooserCancel = this.onDateChooserCancel.bind(this);
        this.onDateChooserConfirm = this.onDateChooserConfirm.bind(this);
    }
    render()
    {
        if(!this.props.price.priceItem)
        {
            return null;
        }
        return (
            <div className="inlineCalendar">
                {
                    this.renderOrderNameView()
                }
                {
                    this.renderUserInfoView()
                }
                {
                    (this.props.price && this.props.price.priceItem)
                        ?
                        <Calendar
                            type="one"
                            title="请选择游玩日期"
                            ref={(r)=>UIUtil.fixCalendarHeightIssue(r, window.innerHeight - 45)}
                            locale={zhCN}
                            visible={this.state.showDatePicker}
                            onCancel={this.onDateChooserCancel}
                            onConfirm={this.onDateChooserConfirm}
                            getDateExtra={(date)=>{
                                if((date.getTime() >= this.minDate.getTime() && date.getTime() <= this.maxDate.getTime()) || date.formatDate('YYYY-MM-DD') === this.minDate.formatDate('YYYY-MM-DD'))
                                {
                                    return {
                                        info: '¥' + this.getDatePrice(date.formatDate('YYYY-MM-DD')),
                                    };
                                }
                            }}
                            defaultDate={this.minDate}
                            minDate={this.minDate}
                            maxDate={this.maxDate}/>
                        : null
                }
            </div>
        );
    }
    getDatePrice(date)
    {
        if(this.props.price.priceItem.NewPriceList && this.props.price.priceItem.NewPriceList.length > 0)
        {
            for(let i = 0; i < this.props.price.priceItem.NewPriceList.length; i++)
            {
                let item = this.props.price.priceItem.NewPriceList[i];
                if(item.ToDay === date)
                {
                    return item.NewPrice;
                }
            }
        }
        return this.props.price.priceItem.DefaultPrice;
    }
    renderOrderNameView()
    {
        return (
            <div className="orderBookingOrderName">{ this.props.price.priceItem.CnName }</div>
        );
    }
    renderUserInfoView()
    {
        if(!this.props.bookingItem.userInfo)
        {
            return null;
        }
        return (
            <div className="orderBookingNewDiv">
                <div className="orderBookingUserInfoHeader">
                    <img className="orderBookingUserInfoHeaderLeftIcon" src={require('../../assets/images/order/book-information@2x.png')} alt=''/>
                    <span className="orderBookingUserInfoHeaderText">预订人信息</span>
                </div>
                <div className="orderBookingUserPhoneRow">
                    <div className="orderBookingUserPhoneLabel">手机号：</div>
                    <input
                        value={this.props.bookingItem.userInfo.PhoneNumber}
                        className='orderBookingUserPhoneInput'
                        placeholder="请输入预订人手机号"
                        type='number'
                        onChange={this.handlePhoneNumberChange} />
                </div>
                <div className="orderBookingUserInfoTips">
                *为确保能收到订票短信，请确保手机号真实有效
                </div>
                {
                    this.renderPlayDateViews()
                }
                {
                    this.renderTicketNumberView()
                }
            </div>
        );
    }
    onDateChooserCancel()
    {
        this.setState({showDatePicker: false});
    }
    onDateChooserConfirm(startTime, endTime)
    {
        this.setState({showDatePicker: false});
        if(startTime)
        {
            this.props.dispatch(updateBookingItemAction({playDate: startTime.formatDate('YYYY-MM-DD'), unitPrice: parseFloat(this.getDatePrice(startTime.formatDate('YYYY-MM-DD')))}));
        }
    }
    renderPlayDateViews()
    {
        let todayDate = new Date(new Date().formatDate('YYYY-MM-DD'));
        let tomorrowDate = new Date(new Date().addDays(1).formatDate('YYYY-MM-DD'));

        let todayText = todayDate.formatDate('MM-DD');
        let tomorrowText = tomorrowDate.formatDate('MM-DD');

        let todayFullText = todayDate.formatDate('YYYY-MM-DD');
        let tomorrowFullText = tomorrowDate.formatDate('YYYY-MM-DD');

        let priceItem = this.props.price ? this.props.price.priceItem : null;
        
        let todayEnable = false;
        let tomorrowEnabel = false;

        if(priceItem)
        {
            let upDate = new Date(priceItem.UpTime);
            let downDate = new Date(priceItem.DownTime);

            if(todayDate.getTime() >= upDate.getTime() && todayDate.getTime() <= downDate.getTime())
            {
                todayEnable = true;
                this.minDate = todayDate;
            }
            else
            {
                this.minDate = upDate;
            }
            if(tomorrowDate.getTime() >= upDate.getTime() && tomorrowDate.getTime() <= downDate.getTime())
            {
                tomorrowEnabel = true;
            }
            this.maxDate = downDate;
        }
        let selectedIndex = -1;
        if(this.props.bookingItem.playDate === todayFullText)
        {
            selectedIndex = 0;
        }
        else if(this.props.bookingItem.playDate === tomorrowFullText)
        {
            selectedIndex = 1;
        }
        else
        {
            selectedIndex = 2;
        }

        return (
            <div className="orderBookingLeftRightRow">
                <div className="orderBookingLeft">游玩时间</div>
                <div className="orderBookingTicketDateRow">
                    <div className={(selectedIndex === 0 && todayEnable) ? 'orderBookingTicketDateItem orderBookingTicketDateItemSelected' : 'orderBookingTicketDateItem'}
                        onClick={()=>this.choosePlayDate(todayFullText, todayEnable)}>
                        <div className="orderBookingTicketDateLabel">今天 {todayText}</div>
                        {
                            todayEnable
                                ?
                                <div className="orderBookingMoney">¥{this.getDatePrice(todayFullText)}</div>
                                :
                                <div className="orderBookingBottom">不可订</div>
                        }
                        {
                            (selectedIndex === 0 && todayEnable)
                                ?
                                <img className="orderBookingItemSelected" src={require('../../assets/images/order/ticket_checked@2x.png')} alt=''/>
                                :
                                null
                        }
                    </div>
                    <div className={(selectedIndex === 1 && todayEnable) ? 'orderBookingTicketDateItem orderBookingTicketDateItemSelected' : 'orderBookingTicketDateItem'}
                        onClick={()=>this.choosePlayDate(tomorrowFullText, tomorrowEnabel)}>
                        <div className="orderBookingTicketDateLabel">明天 {tomorrowText}</div>
                        {
                            tomorrowEnabel
                                ?
                                <div className="orderBookingMoney">¥{this.getDatePrice(tomorrowFullText)}</div>
                                :
                                <div className="orderBookingBottom">不可订</div>
                        }
                        {
                            (selectedIndex === 1 && todayEnable)
                                ?
                                <img className="orderBookingItemSelected" src={require('../../assets/images/order/ticket_checked@2x.png')} alt=''/>
                                :
                                null
                        }
                    </div>
                    <div className={selectedIndex === 2 ? 'orderBookingTicketDateItem orderBookingTicketDateItemSelected' : 'orderBookingTicketDateItem'} style={selectedIndex === 2 ? {} : {flexDirection: 'row'}}
                        onClick={this.showPlayDateChooser}>
                        {
                            selectedIndex === 2
                                ?
                                <div className="orderBookingTicketDateItemNormal">
                                    <div className="orderBookingTicketDateLabel">{this.props.bookingItem.playDate}</div>
                                    <div className="orderBookingMoney">¥{this.getDatePrice(this.props.bookingItem.playDate)}</div>
                                </div>
                                :
                                <div className="orderBookingTicketDateItemMore">
                                    <div className="orderBookingTicketDateLabel">更多日期</div>
                                    <img src={require('../../assets/images/arrow@2x.png')} alt=''/>
                                </div>
                        }
                        {
                            selectedIndex === 2
                                ?
                                <img className="orderBookingItemSelected" src={require('../../assets/images/order/ticket_checked@2x.png')} alt=''/>
                                :
                                null
                        }
                    </div>
                </div>
            </div>
        );
    }
    choosePlayDate(playDate, enabled)
    {
        if(!enabled || !playDate)
        {
            return;
        }
        this.props.dispatch(updateBookingItemAction({playDate: playDate, unitPrice: parseFloat(this.getDatePrice(playDate))}));
    }
    renderTicketNumberView()
    {
        return (
            <div className="orderBookingLeftRightRow">
                <div className="orderBookingLeft">预订票数</div>
                <div className="orderBookingNumberRow">
                    <div className="orderBookingNumberButton orderBookingNumberButtonLeft" onClick={this.diffOrderNumber}>-</div>
                    <div className="orderBookingNumberText">{this.props.bookingItem.orderNumber}</div>
                    <div className="orderBookingNumberButton orderBookingNumberButtonRight" onClick={this.increaseOrderNumber}>+</div>
                </div>
            </div>
        );
    }
    diffOrderNumber()
    {
        if(this.props.bookingItem.orderNumber <= 1)
        {
            return;
        }
        this.props.dispatch(updateBookingItemAction({
            orderNumber: this.props.bookingItem.orderNumber - 1
        }));
        let selectDataList = this.props.visitor.selectDataList;
        selectDataList.splice(selectDataList.length-1, 1);
        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
    } 
    increaseOrderNumber()
    {
        if(this.props.bookingItem.orderNumber >= 10)
        {
            Toast.show('最多选择10位游客');
            return;
        }
        this.props.dispatch(updateBookingItemAction({
            orderNumber: this.props.bookingItem.orderNumber + 1
        }));
        let selectDataList = this.props.visitor.selectDataList;
        let obj = {
            ID: '',
            UserName: '',
            IDCard: ''
        };
        selectDataList.push(obj);
        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
    }
    handlePhoneNumberChange(event)
    {
        let userItem = {...this.props.bookingItem.userInfo };
        userItem.PhoneNumber = event.target.value;
        userItem.Phone = event.target.value;
        this.props.dispatch(updateUserInfoAction(userItem));
    }
    showPlayDateChooser()
    {
        this.setState({showDatePicker: true});
    }
}

const mapStateToProps = function(state, ownProps)
{
    let bookingScreen = state.order.bookingScreen;
    return {
        ...bookingScreen,
    };
};

OrderBookingTicketBasicView = connect(mapStateToProps, null, null, {withRef: true})(OrderBookingTicketBasicView);

export default OrderBookingTicketBasicView;


