import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';
// eslint-disable-next-line
import DateUtil from '../../../core/utils/DateUtil';

function priceReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.ORDER.BOOKING.GET_PRICE_REQUEST:
        return {
            ...state,
            isFetching: true,
        };
    case Actions.ORDER.BOOKING.GET_PRICE_SUCCESS:
        return {
            ...state,
            isFetching: false,
            priceItem: action.priceItem
        };
    case Actions.ORDER.BOOKING.GET_PRICE_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.ORDER.BOOKING.GO_BACK:
        return {};
    default:
        return state;
    }
}

function visitorListReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.ORDER.BOOKING.GET_VISITORS_REQUEST:
        return {
            ...state,
            isFetching: true,
        };
    case Actions.ORDER.BOOKING.GET_VISITORS_SUCCESS:
        return {
            ...state,
            isFetching: false,
            dataList: action.dataList
        };
    case Actions.ORDER.BOOKING.GET_VISITORS_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.ORDER.BOOKING.UPDATE_SELECT_VISITOR:
        let arr=[].concat(action.resp);
        return{
            ...state,
            selectDataList:arr
        };
    case Actions.ORDER.BOOKING.UPDATE_VISITOR:
        let dataList=[].concat(action.resp);
        return{
            ...state,
            dataList:dataList
        };
    case Actions.ORDER.BOOKING.GO_BACK:
        return {};
    default:
        return state;
    }
}

function scrollViewOffsetReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.ORDER.BOOKING.SET_SCROLL_OFFSET:
        return action.scrollViewOffset;
    case Actions.ORDER.BOOKING.GO_BACK:
        return 0;
    default:
        return state;
    }
}

function bookingItemReducer(state = {
    orderNumber: 1,
    playDate: new Date().formatDate('YYYY-MM-DD'),
    totalPrice: 0,      // 订单总价
    unitPrice: 0,       // 单价
}, action)
{
    switch(action.type)
    {
    case Actions.ORDER.BOOKING.BACKUP_USERINFO:
        if(state.userInfo && state.userInfo.ID)
        {
            return state;
        }
        return {
            ...state,
            userInfo: {...action.userInfo},
        };
    case Actions.ORDER.BOOKING.UPDATE_USER_INFO:
        let userInfo = {
            ...state.userInfo,
            ...action.userInfo,
        };
        return {
            ...state,
            userInfo: userInfo,
        };
    case Actions.ORDER.BOOKING.UPDATE_BOOKING_ITEM:
        let _state = {
            ...state,
            ...action.bookingItem,
        };
        let orderDay = _state.orderDay ? _state.orderDay : 1;
        _state.totalPrice = _state.unitPrice * _state.orderNumber * orderDay;
        return {..._state};
    case Actions.ORDER.BOOKING.GET_PRICE_SUCCESS:
        let unitPrice = parseFloat(action.priceItem.DefaultPrice);
        let totalPrice = unitPrice * state.orderNumber;

        return {
            ...state,
            unitPrice,
            totalPrice,
        };
    case Actions.ORDER.BOOKING.GO_BACK:
        return {
            orderNumber: 1,
            playDate: new Date().formatDate('YYYY-MM-DD'),
            totalPrice: 0,
        };
    default:
        return state;
    }
}

function orderDetailItemReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.ORDER.BOOKING.UPDATE_ORDER_DETAIL_DATA:
        return action.orderDetailItem;
    default:
        return state;
    }
}

const bookingReducer = combineReducers({
    price: priceReducer,
    visitor: visitorListReducer,
    scrollViewOffset: scrollViewOffsetReducer,
    bookingItem: bookingItemReducer,
    orderDetailItem:orderDetailItemReducer
});

export default bookingReducer;


