import React, { PureComponent } from 'react';
import { NavBar, Icon, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import { withRouter } from 'react-router';
/**
 * 因为重写了webpack打包脚本（config-overrides.js），造成生成PROD版本的时候运行出现问题。
 */
class LoadingScreen extends PureComponent
{
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.props.history.goBack();
                    }}>
                    { this.props.title ? this.props.title : '' }
                </NavBar>
                <Content>
                    <div style={{height: (window.innerHeight - 45) + 'px', transform: 'translateZ(0)'}}>
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                    </div>
                </Content>
            </div>
        );
    }
}

LoadingScreen = withRouter(LoadingScreen);

export default LoadingScreen;



