import { combineReducers } from 'redux';
import elementMarkerReducer from './elementMarkerReducer';
import mapNavReducer from './mapNavReducer';
import elementMapReducer from './elementMapReducer';
import chooseAddressReducer from './chooseAddressReducer';

const mapReducer = combineReducers({
    elementMarker: elementMarkerReducer,
    mapNav: mapNavReducer,
    elementMap: elementMapReducer,
    chooseAddress: chooseAddressReducer,
});

export default mapReducer;


