import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Modal, ActivityIndicator, DatePicker } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/mine.css';
import { asyncUpdateUserInfoAction, cloneTempUserInfoAction, clearTempUserInfoAction, updateTempUserInfoAction } from '../../store/actions/me/userInfoAction';
// eslint-disable-next-line
import DateUtil from '../../core/utils/DateUtil';

export class UpdateUserInfoScreen extends PureComponent 
{
    constructor(props) 
    {
        super(props);
        this.goBack = this.goBack.bind(this);
        this.save = this.save.bind(this);
        this.onFileChange = this.onFileChange.bind(this);
    }

    componentDidMount()
    {
        if(!this.props.ID)
        {
            this.props.dispatch(cloneTempUserInfoAction());
        }
    }

    render() 
    {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}
                    rightContent={this.renderRightMenu()}>
                    我的信息
                </NavBar>
                <Content style={{ backgroundColor: '#efeff4' }}>
                    <div style={{ marginTop: '15px' }}>
                        {
                            this.renderHeaderImage()
                        }
                        {
                            this.renderItem('昵称:', this.props.Nickname, 0)
                        }
                        {
                            this.renderItem('性别:', this.props.Sex ? '男':'女', 1)
                        }
                        {
                            this.renderDateChooser('生日:', this.props.Birthday, 2)
                        }
                    </div>
                    <div style={{ marginTop: '15px' }}>
                        {
                            this.renderItem('手机:', this.props.PhoneNumber, 3)
                        }
                        {
                            this.renderItem('姓名:', this.props.Name, 4)
                        }
                        {/* {
                            this.renderItem('邮箱:', this.props.Email, 5)
                        } */}
                        {
                            this.renderItem('常住地:', this.props.CityName, 6)
                        }
                    </div>
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="保存中..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderRightMenu() {
        return (
            <div onClick={this.save}>
                保存
            </div>
        );
    }
    renderHeaderImage()
    {
        return (
            <div style={{ backgroundColor: '#ffffff', position: 'relative' }}>
                <div className='userInfoItem'>
                    <div className='userInfoItemLeft'>
                        头像
                    </div>
                    <div className='userInfoItemRight'>
                        <div className="userInfoHeaderIcon" style={{backgroundImage: `url(${this.props.GetUserAvatar})`}}/>
                        <img className='userInfoItemArrow' src={require('../../assets/images/arrow@2x.png')} alt='' />
                    </div>
                </div>
                <div className="userInfoSplitBottom"></div>
                <input className="userInfoFile" type='file'
                    value=''
                    onChange={this.onFileChange}
                    accept="image/*"
                    ref={input => this.fileElement = input} style={{ width:'100%', height:'100%',opacity:'0' }} />
            </div>
        );
    }
    onFileChange()
    {
        let file = this.fileElement.files[0];
        this.headerFile = file;

        let reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = ()=>{
            this.props.dispatch(updateTempUserInfoAction({GetUserAvatar: reader.result}));
        };
    }
    renderItem(leftContent, rightContent, i) {
        return (
            <div onClick={() => this.itemClick(i)} style={{ backgroundColor: '#ffffff' }}>
                <div className='userInfoItem'>
                    <div className='userInfoItemLeft'>
                        {leftContent}
                    </div>
                    <div className='userInfoItemRight'>
                        <span className='userInfoItemLeftContent'>{rightContent}</span>
                        <img className='userInfoItemArrow' src={require('../../assets/images/arrow@2x.png')} alt='' />
                    </div>
                </div>
                <div className="userInfoSplitBottom"></div>
            </div>
        );
    }
    renderDateChooser(leftContent, rightContent, i)
    {
        return (
            <DatePicker
                mode="date"
                minDate={new Date('1900-01-01')}
                maxDate={new Date()}
                title="请选择生日"
                extra="Optional"
                value={new Date(this.props.Birthday)}
                onChange={(date) => this.props.dispatch(updateTempUserInfoAction({Birthday: date.formatDate('YYYY-MM-DD')}))}>
                { this.renderItem(leftContent, rightContent, i) }
            </DatePicker>
        );
    }
    goBack() 
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(clearTempUserInfoAction());
    }
    itemClick(i) 
    {
        const alert = Modal.alert;
        switch (i) {
        case 0:
            this.props.history.push({
                pathname: '/me/updateUserInfoItem',
                state: {
                    key: 'Nickname',
                    value: this.props.Nickname ? this.props.Nickname : '',
                    title: '昵称',
                },
            });
            break;
        case 1:
            alert('修改性别', <div>请选择您的性别</div>, [
                { text: '男', onPress: () => this.props.dispatch(updateTempUserInfoAction({Sex: true})) },
                { text: '女', onPress: () => this.props.dispatch(updateTempUserInfoAction({Sex: false})) },
            ]);
            break;
        case 3:
            alert('提示', '手机号码不可更改', [
                { text: 'OK', style: 'default' },
            ]);
            break;
        case 4:
            this.props.history.push({
                pathname: '/me/updateUserInfoItem',
                state: {
                    key: 'Name',
                    value: this.props.Name ? this.props.Name : '',
                    title: '姓名',
                },
            });
            break;
        case 5:
            this.props.history.push({
                pathname: '/me/updateUserInfoItem',
                state: {
                    key: 'Email',
                    value: this.props.Email ? this.props.Email : '',
                    title: '邮箱',
                },
            });
            break;
        case 6:
            this.props.history.push({
                pathname: '/me/updateUserInfoItem',
                state: {
                    key: 'CityName',
                    value: this.props.CityName ? this.props.CityName : '',
                    title: '常住地',
                },
            });
            break;
        default:
            break;
        }
    }

    save()
    {
        let sex;
        if(this.props.Sex)
        {
            sex = '1';
        }
        else
        {
            sex = '0';
        }
        let kvDict = {
            format: 'json',
            id: this.props.ID,
            nickName: this.props.Nickname,
            name: this.props.Name,
            phoneNumber: this.props.PhoneNumber,
            pwd: this.props.Pwd,
            sex: sex,
            birthday: this.props.Birthday,
            phone: this.props.PhoneNumber,
            cardNo: this.props.CardNo,
            cityName: this.props.CityName,
            email: this.props.Email,
        };
        this.props.dispatch(asyncUpdateUserInfoAction(kvDict, this.headerFile)).then(()=>{
            if(this.props.updateSuccess)
            {
                Modal.alert('提示', '个人信息修改成功！', [
                    { text: 'OK', style: 'default', onPress: () => this.goBack()},
                ]);
            }
            else
            {
                Modal.alert('提示', '修改失败！', [
                    { text: 'OK', style: 'default', onPress: () => this.goBack()},
                ]);
            }
        });
    }
}

const mapStateToProps = function (state) 
{
    return {
        ...state.me.userInfo.tmpUserInfo,
        isFetching: state.me.userInfo.isFetching
    };
};
UpdateUserInfoScreen = connect(mapStateToProps)(UpdateUserInfoScreen);

export default UpdateUserInfoScreen;


