import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    let obj = state[action.code];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.FUN.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state,
            [action.code]: {
                isFetching: true,
            }
        };
    case Actions.FUN.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
                detailItem: action.detailItem
            },
        };
    case Actions.FUN.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
            }
        };
    case Actions.FUN.DETAIL.GO_BACK:
        let _state = {...state};
        delete _state[action.code];
        return {
            ..._state
        };
    case Actions.FUN.DETAIL.SET_DETAIL_SCROLL_OFFSET:
        return {
            ...state,
            [action.code]: {
                ...obj,
                scrollViewOffset: action.scrollViewOffset,
            }
        };
    default:
        return state;
    }
}


