import React from 'react';
import Loadable from 'react-loadable';
import LoadingScreen from './LoadingScreen';

export default function asyncScreen(importScreen, title) 
{
    return Loadable({
        loader: importScreen,
        loading: () => <LoadingScreen title={title}/>,
    });
}


