import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import SysUtil from './core/utils/SysUtil';
import WebDB from './core/utils/WebDB';
import Config from './core/utils/Config';
import createHistory from 'history/createBrowserHistory';
// import createHistory from 'history/createHashHistory';
import { asyncGetUserLocationAction } from './store/actions/location/locationAction';
import { autoLogin, getWXOpenId, setUserInfoAction } from './store/actions/me/userInfoAction';
import { setWindowSizeAction, setConfigItemAction } from './store/actions/nativeAction';
import { setConfigItemAction as setPhotoBrowserConfigItemAction } from './store/actions/photoBrowserAction';
import { asyncGetJourneyMaxDayCountAction } from './store/actions/systemAction';
import * as IConstant from './core/utils/IConstant';
import ErrorBoundary from './views/common/ErrorBoundary';
import Routes from './Routes';
import LocationUtil from './core/utils/LocationUtil';

const history = createHistory({
    basename: '/mobile/yuzhou',
});

// history.listen((location, action) => {
//     // location is an object like window.location
//     console.log(action, location.pathname, location.state);
// });

// window.addEventListener('hashchange',function(event){
//     console.log(event.oldURL + ' , ' + event.newURL);
// });

class App extends PureComponent 
{
    constructor(props)
    {
        super(props);
        this.onNativeMessage = this.onNativeMessage.bind(this);
        this.onIframeMessage = this.onIframeMessage.bind(this);
        this.sendReadyMessage = this.sendReadyMessage.bind(this);

        window.addEventListener('resize', this.onResize.bind(this));
        window.document.addEventListener('message', this.onNativeMessage);
        window.addEventListener('message', this.onIframeMessage, false);
        if (this.props.baiduMapInited)
        {
            this.getUserLocationInfo();
        }
        this.props.dispatch(asyncGetJourneyMaxDayCountAction());
        let appAccount = SysUtil.getUrlParameter('Account');
        let appCode = SysUtil.getUrlParameter('code');
        let isFromNativeApp = SysUtil.getUrlParameter('isFromNativeApp');
        console.log(`process: ${process.env.NODE_ENV}`);
        if(appAccount && appAccount.length > 0 && appCode && appCode.length > 0)
        {
            getWXOpenId(appAccount, appCode, this.props.dispatch);
        }
        else
        {
            // 开发环境时将openid写死。
            if(process.env.NODE_ENV === 'development')
            {
                WebDB.setItem(IConstant.STORAGE_KEY.WX_ACCOUNT, Config.instance.testWeXinAccount);
                WebDB.setItem(IConstant.STORAGE_KEY.WX_USER_OPENID, Config.instance.testWeXinUserOpenId);
                console.log('====请注意：开发环境将微信公众号参数、微信用户参数hardcode了====');
            }
            autoLogin(this.props.dispatch);
        }
        if(isFromNativeApp && isFromNativeApp.length > 0)
        {
            try
            {
                let userInfo = SysUtil.getUrlParameter('userInfo');
                if(userInfo)
                {
                    userInfo = JSON.parse(decodeURIComponent(userInfo));
                    this.props.dispatch(setUserInfoAction(userInfo));
                }
            }
            catch(e){}
            this.props.dispatch(setConfigItemAction({isFromNativeApp}));
            this.sendReadyMessage();
        }
    }
    componentWillUnmount()
    {
        this.timerInterval && clearTimeout(this.timerInterval);
    }
    onResize()
    {
        this.props.dispatch(setWindowSizeAction());
    }
    // 接收来处native app的消息
    onNativeMessage(e)
    {
        let msgObj;
        if(e && e.data && e.data.length > 0)
        {
            msgObj = JSON.parse(e.data);
        }
        if(!msgObj)
        {
            return;
        }
        if(msgObj.type === IConstant.NATIVE_MESSAGE_TYPE.READY)
        {
            this.timerInterval && clearTimeout(this.timerInterval);
            if (this.props.baiduMapInited)
            {
                this.getUserLocationInfo();
            }
            this.props.dispatch(setConfigItemAction({isNativeAppReady: true}));
        }
        // 获取当前浏览器的url
        else if(msgObj.type === IConstant.NATIVE_MESSAGE_TYPE.GET_URL && window.postMessage)
        {
            window.postMessage(JSON.stringify({
                type: IConstant.NATIVE_MESSAGE_TYPE.GET_URL,
                data: {
                    url: window.location.href
                }
            }), '*');
        }
        else if(msgObj.type === IConstant.NATIVE_MESSAGE_TYPE.ELEMANT_MAP_SEARCH)
        {
            this.props.dispatch(setConfigItemAction({elemantMapSearchKeywords: msgObj.data.keywords}));
        }
        else if(msgObj.type === IConstant.NATIVE_MESSAGE_TYPE.ELEMENT_MAP_DATALIST)
        {
            this.props.dispatch(setConfigItemAction({getElementMapDataList: 0}));
        }
        else if(msgObj.type === IConstant.NATIVE_MESSAGE_TYPE.GET_LOCATION)
        {
            //获取定位
            LocationUtil.save(this.props.dispatch, msgObj.data);
        }
        else if(msgObj.type === IConstant.NATIVE_MESSAGE_TYPE.SHARE_NATIVE_USER_INFO && msgObj.data)
        {
            this.props.dispatch(setUserInfoAction(msgObj.data));
            WebDB.setItem(IConstant.STORAGE_KEY.USER_INFO, msgObj.data);
        }
    }
    // 接收来自iframe的消息（photo-browser）
    onIframeMessage(e)
    {
        let msgObj;
        if(e && e.data && e.data.length > 0)
        {
            msgObj = JSON.parse(e.data);
        }
        if(!msgObj)
        {
            return;
        }
        if(msgObj.type === IConstant.PHOTO_BROWSER_MESSAGE_TYPE.PHOTO_BROWSER_LOADING
            || msgObj.type === IConstant.PHOTO_BROWSER_MESSAGE_TYPE.PHOTO_BROWSER_CLOSED
            || msgObj.type === IConstant.PHOTO_BROWSER_MESSAGE_TYPE.PHOTO_BROWSER_OPENED)
        {
            this.props.dispatch(setPhotoBrowserConfigItemAction({photoBrowserStatus: msgObj.type}));
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if (this.props.baiduMapInited && !prevProps.baiduMapInited) 
        {
            this.getUserLocationInfo();
        }
    }
    render() 
    {
        return (
            <ErrorBoundary history={history}>
                <Routes history={history}/>
            </ErrorBoundary>
        );
    }

    getUserLocationInfo()
    {
        this.props.dispatch(asyncGetUserLocationAction(true));
    }

    sendReadyMessage()
    {
        if(window.postMessage)
        {
            window.postMessage(JSON.stringify({
                type: IConstant.NATIVE_MESSAGE_TYPE.READY
            }), '*');
            this.timerInterval = setTimeout(this.sendReadyMessage, IConstant.WEB_VIEW_READY_INTERVAL);
        }
    }
}

const mapStateToProps = function (state, ownProps) {
    return {
        baiduMapInited: state.system.baiduMapInited,
        native: {
            elementMapDataList: state.map.elementMap.dataList,
        }
    };
};

App = connect(mapStateToProps)(App);

export default App;



