import { PureComponent } from 'react';
import * as IConstant from '../../core/utils/IConstant';
import SysUtil from '../../core/utils/SysUtil';

export default class NativeAppScreen extends PureComponent
{
    updateNativeTitle(title)
    {
        if(title && title.length > 0)
        {
            this.sendMessageToNative(IConstant.NATIVE_MESSAGE_TYPE.SET_NAV_BAR_TITLE, {
                title: title
            });
        }
    }
    dial(phoneNumber, isFromNativeApp)
    {
        if(!phoneNumber || phoneNumber.length === 0)
        {
            return;
        }
        if(isFromNativeApp)
        {
            this.sendMessageToNative(IConstant.NATIVE_MESSAGE_TYPE.DIAL, {
                phoneNumber: phoneNumber
            });
        }
        else
        {
            SysUtil.openTel(phoneNumber);
        }
    }
    sendMessageToNative(msgType, msgData)
    {
        if(!window.postMessage)
        {
            return;
        }
        window.postMessage(JSON.stringify({
            type: msgType,
            data: msgData
        }), '*');
    }
}


