import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Modal, PullToRefresh } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/mine.css';
import SysUtil from '../../core/utils/SysUtil';
import * as IConstant from '../../core/utils/IConstant';
import { exitLoginAction , asyncGetUserNumbersAction} from '../../store/actions/me/userInfoAction';

class MineScreen extends PureComponent {

    constructor(props) {
        super(props);
        this.goBack = this.goBack.bind(this);
        this.exit = this.exit.bind(this);
        this.itemClick = this.itemClick.bind(this);
        this.myAttentionListTapped = this.myAttentionListTapped.bind(this);
        this.myFansListTapped = this.myFansListTapped.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
    }
    componentDidMount()
    {
        if(this.props.ID)
        {
            this.onRefresh();
        }
    }
    render() {
        let headImgBg = require('../../assets/images/me/bg.jpg');
        let infroBg = require('../../assets/images/me/infro_bg.png');
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    rightContent={this.renderRightMenu()}
                    onLeftClick={this.goBack}>
                    个人中心
                </NavBar>
                <Content>
                    <PullToRefresh 
                        style={{ height: (window.innerHeight - 45) + 'px', overflow: 'auto' }}
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}>
                        <div>
                            <div className='mineHead' style={{ backgroundImage: `url(${headImgBg})` }}>
                                <div className='mineInfo' style={{ backgroundImage: `url(${infroBg})`, height: (202.0 * window.innerWidth / 640.0) + 'px' }}>
                                    <div className='userImg' style={{ backgroundImage: `url(${this.props.GetUserAvatar})` }} />
                                    <div className='userInfo'>
                                        <div className='userName'>{this.props.Nickname}</div>
                                        <div className='userTips'>
                                            <div className='userNumberButton' onClick={() => this.myAttentionListTapped()} >
                                                <div className='userNumberText'>关注: {this.props.AttentionCount ? this.props.AttentionCount : 0}</div>
                                            </div>
                                            <div className='userNumberButton' onClick={() => this.myFansListTapped()} >
                                                <div className='userNumberText'>粉丝: {this.props.FansCount ? this.props.FansCount : 0}</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div style={{ marginLeft: '30px' }}>
                                {
                                    this.renderItem(require('../../assets/images/me/date.png'), '个人资料', 0)
                                }
                                {
                                    this.renderItem(require('../../assets/images/me/ticket.png'), '我的订单', 1)
                                }
                                {/*{
                                this.renderItem(require('../../assets/images/me/hotel.png'), '抢购订单', 2)
                            }*/}
                                {
                                    this.renderItem(require('../../assets/images/me/hotel.png'), '我的行程', 3)
                                }
                                {/* {
                                this.renderItem(require('../../assets/images/me/hotel.png'), '收货地址管理', 4)
                            } */}
                                {
                                    this.renderItem(require('../../assets/images/me/integral.png'), '我的收藏', 5)
                                }
                                {
                                    this.renderItem(require('../../assets/images/me/footmark.png'), '我的足迹', 6)
                                }
                            </div>
                        </div>
                    </PullToRefresh>
                </Content>
            </div>
        );
    }

    renderItem(img, content, i) {
        return (
            <div onClick={() => this.itemClick(i)}>
                <div className='mineItem'>
                    <div className='mineItemLeft'>
                        <img className='mineItemLeftImg' src={img} alt='' />
                        <span className='mineItemLeftContent'>{content}</span>
                    </div>
                    <img className='mineItemRight' src={require('../../assets/images/arrow@2x.png')} alt='' />
                </div>
                <div style={{ width: '100%', height: '1px', backgroundColor: '#e0e0e0' }}></div>
            </div>
        );
    }

    renderRightMenu() {
        return (
            <div onClick={this.exit}>
                退出
            </div>
        );
    }

    onRefresh()
    {
        this.props.dispatch(asyncGetUserNumbersAction(this.props.ID));
    }
    goBack() {
        this.props.history.goBack();
    }

    exit() {
        const alert = Modal.alert;
        alert('退出', '您确定要退出登录?', [
            { text: '取消' },
            { text: '确定', onPress: () => this.exitLogin() },
        ]);
    }

    exitLogin() {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(exitLoginAction());
    }

    itemClick(i) {
        switch (i) {
        case 0:
            this.props.history.push('/me/updateUserInfo');
            break;
        case 1:
            this.props.history.push('/order/list');
            break;
        case 2:
            break;
        case 3:
            this.props.history.push('/journey/list');
            break;
        // case 4:
        //     this.props.history.push('/me/addressManagement');
        //     break;
        case 5:
            this.props.history.push('/me/collectList');
            break;
        case 6:
            this.props.history.push('/footmark/myList');
            break;
        default:
            break;
        }
    }

    myAttentionListTapped()
    {
        this.props.history.push({
            pathname: '/userProfile/friendList',
            search: `?friendsType=${IConstant.FRIEND_PROFILE_DATA_TYPE.ATTENTIONS}&targetUserId=${this.props.ID}`,
        });
    }

    myFansListTapped()
    {
        this.props.history.push({
            pathname: '/userProfile/friendList',
            search: `?friendsType=${IConstant.FRIEND_PROFILE_DATA_TYPE.FANS}&targetUserId=${this.props.ID}`,
        });
    }
}
const mapStateToProps = function (state) {
    let obj = SysUtil.getUserInfo(state);
    return {
        ...obj,
        isFetching: state.me.isFetching
    };
};
MineScreen = connect(mapStateToProps)(MineScreen);

export default MineScreen;



