import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.WEATHER.GET_WEATHER_REQUEST:
        return {
            isFetching: true,
        };
    case Actions.WEATHER.GET_WEATHER_SUCCESS:
        return {
            isFetching: false,
            data: action.resp
        };
    case Actions.WEATHER.GET_WEATHER_FAILURE:
        return {
            isFetching: false,
        };
    case Actions.WEATHER.GO_BACK:
        return {};
    default:
        return state;
    }
}


