import { combineReducers } from 'redux';
import homeReducer from './homeReducer';
import scenicReducer from './scenic';
import hotelReducer from './hotel';
import newsReducer from './news';
import lineReducer from './line';
import meReducer from './me';
import journeyReducer from './journey';
import ticketPurchaseScreen from './ticketPurchase';
import systemReducer from './systemReducer';
import nativeReducer from './nativeReducer';
import travelNotesReducer from './travel-notes';
import foodReducer from './food';
import specialtyReducer from './specialty';
import funReducer from './fun';
import forumReducer from './forum';
import userNearbyReducer from './near-by';
import mapReducer from './map';
import footmarkReducer from './footmark';
import festivalNewsReducer from './festival-news';
import shoppingReducer from './shopping';
import farmHouseReducer from './farm-house';
import travelAgentReducer from './travel-agent';
import restaurantReducer from './restaurant';
import overviewReducer from './overview';
import trafficReducer from './traffic';
import signReducer from './sign';
import orderReducer from './order';
import weatherReducer from './weather';
import photoBrowserReducer from './photoBrowserReducer';
import commentsReducer from './comments';
import userProfileReducer from './userProfile/userProfileReducer';

const appReducers = combineReducers({
    home: homeReducer,
    scenic: scenicReducer,
    hotel: hotelReducer,
    news: newsReducer,
    festivalNews: festivalNewsReducer,
    line: lineReducer,
    me: meReducer,
    journey: journeyReducer,
    ticketPurchase:ticketPurchaseScreen,
    system: systemReducer,
    native: nativeReducer,
    travelNotes: travelNotesReducer,
    food: foodReducer,
    specialty: specialtyReducer,
    fun: funReducer,
    forum: forumReducer,
    userNearby: userNearbyReducer,
    map: mapReducer,
    footmark: footmarkReducer,
    shopping: shoppingReducer,
    farmHouse: farmHouseReducer,
    travelAgent: travelAgentReducer,
    restaurant: restaurantReducer,
    overview: overviewReducer,
    traffic: trafficReducer,
    sign: signReducer,
    order: orderReducer,
    weather:weatherReducer,
    photoBrowser: photoBrowserReducer,
    comments: commentsReducer,
    userProfile: userProfileReducer
});

export default appReducers;


