import * as IConstant from '../../../core/utils/IConstant';

export function clearChooseAddressAction()
{
    return {
        type: IConstant.Actions.MAP.CHOOSEADDRESS.CLEAR_SELECT_ADDRESS,
    };
}

export function updateChooseAddressAction(selectPoint)
{
    return function(dispatch, getState)
    {
        dispatch(setChooseAddressAction(selectPoint));
        return new Promise((resolve)=>{
            resolve();
        });
    };
    
}

function setChooseAddressAction(selectPoint)
{
    return {
        type: IConstant.Actions.MAP.CHOOSEADDRESS.UPDATE_SELECT_ADDRESS,
        selectPoint
    };
}


