import { Actions } from '../../../core/utils/IConstant';

function ListReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.TICKET_PURCHASE.GET_LIST_REQUEST:
        return {
            ...state,
            isFetching:true
        };
    case Actions.TICKET_PURCHASE.GET_LIST_SUCCESS:
        return {
            ...state,
            dataList:action.result.datas,
            isFetching:false
        };
    case Actions.TICKET_PURCHASE.GET_LIST_FAILURE:
        return {
            ...state,
            isFetching: false
        };  
    case Actions.TICKET_PURCHASE.GO_BACK:
        return {}; 
    default:
        return {
            ...state
        };
    }
}

export default ListReducer;


