import { Actions } from '../../core/utils/IConstant';

export default function photoBrowserReducer(state = {}, action)
{
    if(action.type && action.type.endsWith('_GO_BACK'))
    {
        let hasloadPhotoBrowerIframe = state.hasloadPhotoBrowerIframe;
        return {
            hasloadPhotoBrowerIframe: !!hasloadPhotoBrowerIframe
        };
    }
    switch(action.type)
    {
    case Actions.PHOTO_BROWSER.CLEAR_CONFIG_ITEMS:
        return {};
    case Actions.PHOTO_BROWSER.SET_CONFIG_ITEM:
        return {
            ...state,
            ...action.configItem,
        };
    case Actions.PHOTO_BROWSER.SET_PHOTOBROWER_IFRAME_LOAD:
        return {
            ...state,
            hasloadPhotoBrowerIframe: true
        };
    default:
        return state;
    }
}


