import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function setSelectedTabIndexAction(index)
{
    return {
        type: IConstant.Actions.SPECIALTY.LIST.SET_SELECTED_TAB_INDEX,
        index: index
    };
}

export function asyncGetListAction(orderFiled, orderType, pageIndex, searchKey)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(orderFiled, orderType, pageIndex, searchKey));
        return _fetchList(orderFiled, orderType, pageIndex, searchKey).then((resp)=>{
            dispatch(_getListSuccessAction(orderFiled, orderType, pageIndex, searchKey, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(orderFiled, orderType, error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.SPECIALTY.LIST.GO_BACK,
    };
}

export function asyncDeleteListAction(orderFiled, orderType)
{
    return function(dispatch, getState)
    {
        dispatch(deleteListAction(orderFiled, orderType));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction(orderFiled, orderType)
{
    return {
        type: IConstant.Actions.SPECIALTY.LIST.DELETE,
        orderFiled, 
        orderType
    };
}

export function setKeywordAction(keyword)
{
    return {
        type: IConstant.Actions.SPECIALTY.LIST.SET_KEYWORD,
        keyword: keyword
    };
}

export function setScrollerPositionAction(orderFiled, orderType, offset)
{
    return {
        type: IConstant.Actions.SPECIALTY.LIST.SET_LISTVIEW_OFFSET,
        orderFiled, 
        orderType,
        listViewOffset: offset
    };
}

function _fetchList(orderFiled, orderType, pageIndex, searchKey)
{
    let conditionParams = {
        Order: [{
            OrderField: orderFiled,
            OrderType: orderType,
        }],
        Page: {
            PageIndex: pageIndex,
            PageSize: IConstant.PAGE_SIZE,
        },
        Search: [],
    };
    if (searchKey && searchKey.length > 0)
    {
        let search = {
            AndOR: 'and',
            SearchField: 'CnName,EnName',
            ConditionType: '2',
            ListSearchValue:[{
                ItemValue: searchKey
            }]
        };
        conditionParams.Search.push(search);
    }
    let kvDict = {
        conditionParams: conditionParams,
    };

    return HttpClient.getFromPortal(IConstant.PortalApis.ELEMENTS.LocalProductList, kvDict)
        .then((resp) => resp.json());
}

function _getListRequestAction(orderFiled, orderType, pageIndex, searchKey)
{
    return {
        type: IConstant.Actions.SPECIALTY.LIST.GET_LIST_REQUEST,
        orderFiled, 
        orderType,
        pageIndex,
        keyword: searchKey,
    };
}

function _getListSuccessAction(orderFiled, orderType, pageIndex, searchKey, result)
{
    return {
        type: IConstant.Actions.SPECIALTY.LIST.GET_LIST_SUCCESS,
        orderFiled, 
        orderType,
        pageIndex,
        keyword: searchKey,
        result
    };
}

function _getListFailureAction(orderFiled, orderType, error)
{
    return {
        type: IConstant.Actions.SPECIALTY.LIST.GET_LIST_FAILURE,
        orderFiled, 
        orderType,
        message: error
    };
}


