export default class Config
{
    /**
     * 服务器地址
     */
    // 门户地址
    hostPortal                  = 'http://117.78.52.104:8083';
    // 电商地址
    hostEBusiness               = 'http://117.78.52.104:8084';
    // 电商统一支付平台
    hostEPayment                = 'http://117.78.52.104:8089';
    // 微信后台域名
    // hostWechatServer            = 'http://testyzzxd.xinxiantour.com';
    hostWechatServer            = 'http://117.78.52.104';
    // 创建行程时，如果当前用户在禹州县境内，则使用用户当前位置；如果当前用户不在禹州县境内，则使用默认的位置信息。
    appCityName                 = '禹州';
    // 查询天气用
    weatherCityName             = '禹州';
    // 默认的经纬度
    appCityLatitude             = 34.13911;
    appCityLongitude            = 113.476166;

    // 本地调试开发微信相关的功能时，可以将以下信息hard code（这个是`安顺智慧旅游平台`测试公众号的信息）
    testWeXinAccount            = 'afe66b183fdf485a820e642a711a63ae';   // 每个微信公众号是固定的，可以在手机微信里面进入微信公众号页面，然后分享到网页后查看这个参数
    testWeXinUserOpenId         = 'od_7Bv9v-kVGk6py5sSYBQJgM7Hc';       // 微信用户的id，每个用户是固定的，可以通过`微信开发者工具`查看code参数：https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1455784140
    
    // 电商支付平台微信支付帐号
    wechatPayAccount            = 'PAY_YZ_B001_TEST';
    wechatPaySecret             = 'yz001';

    update(opts)
    {
        for (let k in opts)
        {
            let v = opts[k];
            if (k && v)
            {
                this[k] = v;
            }
        }
    }

    static instance: Config     = new Config();
}



