import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, SearchBar } from 'antd-mobile';
import Content from '../../views/common/Content';
import FarmHouseListView from '../../views/farm-house/FarmHouseListView';
import UIUtil from '../../core/utils/UIUtil';
import { setSelectedTabIndexAction, goBackAction, setKeywordAction } from '../../store/actions/farm-house/listAction';
import '../../assets/css/farm-house.css';
import LeftContentView from '../../views/common/LeftContentView';

/**
 * 乡村游
 */
class FarmHouseListScreen extends PureComponent
{
    tabs = [
        {title: '综合', tabIndex: 0},
        {title: '热度', tabIndex: 1},
        {title: '距离', tabIndex: 2},
        {title: '最新', tabIndex: 3},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }
    render()
    {
        let tabHeight = window.innerHeight - (45 + 44);
        let listViewHeight = window.innerHeight - (45 + 44 + 44);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    leftContent={<LeftContentView
                        ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                        goBackAction={goBackAction}
                        history={this.props.history}
                    />}
                    onLeftClick={() => {
                        this.LeftContentView.goHome();
                    }}>
                    乡村游列表
                </NavBar>
                <Content>
                    <SearchBar placeholder="请输入关键字" 
                        maxLength={20} 
                        value={this.props.keyword}
                        onChange={(val)=>{this.updateKeyword(val);}}
                        onSubmit={(val)=>{
                            this.updateKeyword(val);
                            this.searchByKeyword(val);
                        }}/>
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                            this.searchByKeyword(this.props.keyword, index);
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><FarmHouseListView key={index} 
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                tabIndex={item.tabIndex} 
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    updateKeyword(keyword)
    {
        this.props.dispatch(setKeywordAction(keyword));
    }
    searchByKeyword(keyword, index = undefined)
    {
        index = (index === undefined) ? this.props.selectedTabIndex : index;
        let listView: FarmHouseListView = this.tabs[index].listView;
        if(!listView)
        {
            return;
        }
        listView.searchByKeyword(keyword);
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: FarmHouseListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push(`/farm-house/detail/${item.Code}`);
    }
}

const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.farmHouse.listScreen.selectedTabIndex,
        keyword: state.farmHouse.listScreen.keyword
    };
};

FarmHouseListScreen = connect(mapStateToProps)(FarmHouseListScreen);

export default FarmHouseListScreen;


