import React, { PureComponent } from 'react';

export default class ErrorBoundary extends PureComponent 
{
    constructor(props) 
    {
        super(props);
        this.state = { error: null };
        this.refreshPage = this.refreshPage.bind(this);
        this.goToHome = this.goToHome.bind(this);
    }

    componentDidCatch(error, errorInfo) 
    {
        // Display fallback UI
        this.setState({ error });
        // You can also log the error to an error reporting service
        // logErrorToMyService(error, info);
        // window.Raven.captureException(error, { extra: errorInfo });
        window.fundebug.notifyError(error, errorInfo);
    }

    render() 
    {
        if (this.state.error) 
        {
            //render fallback UI
            return (
                <div className="appCrash">
                    <img src={require('../../assets/images/error.jpg')} alt='' />
                    <div className="appCrashButtons">
                        <div className="appCrashButton" onClick={this.refreshPage}>刷新页面</div>
                        <div className="appCrashButton" onClick={this.goToHome}>返回首页</div>
                    </div>
                </div>
            );
        } 
        else 
        {
            //when there's not an error, render children untouched
            return this.props.children;
        }
    }
    refreshPage()
    {
        window.location.reload();
    }
    goToHome()
    {
        this.props.history.push('/');
        window.location.reload();
    }
}


