import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, SegmentedControl } from 'antd-mobile';
import Content from '../../views/common/Content';
import SpecialtyRecommendListView from '../../views/specialty/SpecialtyRecommendListView';
import {  
    goBackAction
} from '../../store/actions/specialty/recommendListAction';
import '../../assets/css/specialty.css';

class SpecialtyRecommendListScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
        this.showCommonList = this.showCommonList.bind(this);
    }
    render()
    {
        let listViewHeight = window.innerHeight - 45;
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    <SegmentedControl values={['非买不可', '全部特产']} 
                        tintColor="white" 
                        selectedIndex={0}
                        onValueChange={this.showCommonList}/>
                </NavBar>
                <Content>
                    <SpecialtyRecommendListView ref={(listView)=>{
                        if(listView)
                        {
                            this.listView = listView.getWrappedInstance();
                        }
                    }}
                    onItemClick={this.onItemClick}
                    height={listViewHeight}/>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item || !item.GetRelation || !item.GetRelation.Code)
        {
            return;
        }
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        this.props.history.push(`/specialty/detail/${item.GetRelation.Code}`);
    }
    goBack()
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }
    showCommonList()
    {
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        this.props.history.push('/specialty/list?segment=true');
    }
}

SpecialtyRecommendListScreen = connect()(SpecialtyRecommendListScreen);

export default SpecialtyRecommendListScreen;


