import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/order.css';
import SysUtil from '../../core/utils/SysUtil';
import '../../core/utils/DateUtil';
import HttpClient from '../../core/utils/HttpClient';
import * as IConstant from '../../core/utils/IConstant';
import { 
    asyncGetVisitorsAction,
} from '../../store/actions/order/bookingAction';

class OrderBookingVisitorAddScreen extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            idCard: ''
        };
    }
    render() {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.goBack();
                    }}
                    rightContent={
                        <div style={{ display: 'flex', alignItems: 'center', height: '100%' }}>
                            <div style={{ color: 'white' }} onClick={() => this.add()}>
                                添加
                            </div>
                        </div>
                    }>
                    新增游客
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}
                    style={{ backgroundColor: '#f3f3f3' }}>
                    {
                        this.renderPage()
                    }
                </Content>
            </div>
        );
    }
    renderPage() {
        return (
            <div className='bookingVisitorAddBox'>
                <div className='bookingVisitorAddName'>
                    <div className='bookingVisitorAddNameTitle'>姓名</div>
                    <input type='text' placeholder='请填写真实姓名' value={this.state.name} className='bookingVisitorAddNameInput' onChange={(e) => this.valueChange(e, 0)} />
                </div>
                <div className='bookingVisitorAddName'>
                    <div className='bookingVisitorAddNameTitle'>身份证号</div>
                    <input type='text' placeholder='请填写身份证号' value={this.state.idCard} className='bookingVisitorAddNameInput' onChange={(e) => this.valueChange(e, 1)} />
                </div>
            </div>
        );
    }
    add() {
        if (this.state.name === '') {
            Toast.info('请填写真实姓名',1);
            return;
        }

        if (this.state.idCard === '') {
            Toast.info('请填写身份证号',1);
            return;
        }

        if (!SysUtil.ValidateIdcard(this.state.idCard)) {
            Toast.info('身份证号格式不正确',1);
            return;
        }
        let kvDict = {
            ConditionParams :{
                Params: {
                    UserID: this.props.userInfo.ID,
                    UserName: this.state.name,
                    IDCard: this.state.idCard
                }
            }
        };

        HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS, IConstant.PortalApis.VISITOR.AddVisitors, kvDict)
            .then((resp) => resp.json()).then((resp) => {
                if (resp && resp.status === '0') {
                    Toast.info('添加成功!', 1);
                    this.props.dispatch(asyncGetVisitorsAction(this.props.userInfo.ID));
                    this.goBack();
                } else {
                    Toast.info(resp.message, 1);
                }
            });
    }
    goBack() {
        this.props.history.goBack();
    }
    valueChange(e, type) {
        if (type === 0) {
            this.setState({
                name: e.target.value
            });
        } else {
            this.setState({
                idCard: e.target.value
            });
        }
    }
}

const mapStateToProps = function (state, ownProps) {
    return {
        userInfo:SysUtil.getUserInfo(state)
    };
};

OrderBookingVisitorAddScreen = connect(mapStateToProps)(OrderBookingVisitorAddScreen);

export default OrderBookingVisitorAddScreen;


