import React, {  } from 'react';
import { connect } from 'react-redux';
import { NavBar, ActivityIndicator, Carousel } from 'antd-mobile';
import Content from '../../views/common/Content';
import { 
    asyncGetDetailAction, 
    goBackAction, 
} from '../../store/actions/festival-news/detailAction';
import NativeAppScreen from '../common/NativeAppScreen';

import '../../assets/css/festival-news.css';
import * as IConstant from '../../core/utils/IConstant';
import LeftContentView from '../../views/common/LeftContentView';

class FestivalNewsDetailScreen extends NativeAppScreen
{
    componentDidMount()
    {
        this.loadData();
        if(this.props.isNativeAppReady)
        {
            this.sendMessageToNative(IConstant.NATIVE_MESSAGE_TYPE.SHOW_SHARE, {isShowShare: true , url: window.location.href});
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(!prevProps.isNativeAppReady && this.props.isNativeAppReady)
        {
            this.sendMessageToNative(IConstant.NATIVE_MESSAGE_TYPE.SHOW_SHARE, {isShowShare: true , url: window.location.href});
        }
    }
    render()
    {
        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark" 
                            className="navBar"
                            leftContent={<LeftContentView
                                ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                                goBackAction={() => goBackAction(this.props.match.params.code)}
                                history={this.props.history}
                            />}
                            onLeftClick={() => {
                                this.LeftContentView.goHome();
                            }}>
                            详情
                        </NavBar>
                }
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    {
                        this.renderHeaderImages()
                    }
                    {
                        this.renderHeaderView()
                    }
                    {
                        this.renderContentView()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderHeaderImages()
    {
        if(!this.props.detailItem || !this.props.detailItem.GetImage || !this.props.detailItem.GetImage.ImageList || this.props.detailItem.GetImage.ImageList.length === 0)
        {
            return null;
        }
        return (
            <Carousel
                className="scenicDetailHeaderImgSlider"
                autoplay={true}
                dots={true}
                swiping={true}
                infinite
                selectedIndex={0}
                swipeSpeed={35}>
                {
                    this.props.detailItem.GetImage.ImageList.map((item, index)=><div key={index} className="scenicDetailHeaderImg" style={{backgroundImage: `url(${item.MiddleImgSrc})`}}/>)
                }
            </Carousel>
        );
    }
    renderHeaderView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div>
                <div className="newsDetailHeaderRow">
                    <div className="festivalNewsDetailHeaderName">{this.props.detailItem.CNNAME}</div>
                    <div className="festivalNewsOthers">活动时间：{this.props.detailItem.HOLDTIME}</div>
                    <div className="festivalNewsOthers">活动地点：{this.props.detailItem.ADDRESS}</div>
                    <div className="festivalNewsOthers">活动主题：{this.props.detailItem.SUBJECT}</div>
                    <div className="festivalNewsOthers">举办单位：{this.props.detailItem.ORGANIZER}</div>
                </div>
                <div className="newsDetailSplitLine"/>
            </div>
        );
    }
    renderContentView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div className="newsDetailHtml">
                {this.props.detailItem.SUMMARY}
            </div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetDetailAction(this.props.match.params.code));
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.festivalNews.detailScreen[ownProps.match.params.code];
    return {
        ...obj,
        isFromNativeApp: state.native.isFromNativeApp,
        isNativeAppReady: state.native.isNativeAppReady,
    };
};

FestivalNewsDetailScreen = connect(mapStateToProps)(FestivalNewsDetailScreen);

export default FestivalNewsDetailScreen;


