import React, { PureComponent } from 'react';
import { NavBar, Icon } from 'antd-mobile';
import { connect } from 'react-redux';
import Content from '../../views/common/Content';
import ReactDOM from 'react-dom';
import { ActivityIndicator } from 'antd-mobile';
import { asyncGetTicketInfoAction, goBackAction, setScrollerPositionAction } from '../../store/actions/ticketPurchase/infoAction';
import { asyncGetTicketListAction } from '../../store/actions/ticketPurchase/listAction';
import '../../core/utils/DateUtil';
import HttpClient from '../../core/utils/HttpClient';
import '../../assets/css/ticketPurchase.css';

class TicketPurchaseScreen extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            day: '0',
            hour: '00',
            min: '00',
            second: '00'
        };
        this.updateTime = this.updateTime.bind(this);
        this.ShowResult=this.ShowResult.bind(this);
        this.timer=setInterval(this.updateTime, 1000);
    }
    componentDidMount() {
        if (!this.props.ticketInfo.dataInfo || !this.props.purchaseList.dataList) {
            this.loadData();
        }
        this.restoreScrollViewPosition();
    }
    render() {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    门票抢购
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    {
                        this.renderPage()
                    }
                    {
                        this.props.ticketInfo.isFetching || this.props.purchaseList.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }
    renderPage() 
    {
        if (!this.props.ticketInfo.dataInfo || !this.props.purchaseList.dataList) {
            return;
        }
        let banner = require('../../assets/images/ticketPurchase/banner_top.jpg');
        let ticketUrl = HttpClient.HOST_WECHAT_SERVER + this.props.ticketInfo.dataInfo.PanicBuyingActiveImgUrl;

        let beginDate = new Date(this.props.ticketInfo.dataInfo.PanicBuyingBeginTime).formatDate('YYYY-MM-DD');
        let beginTime = new Date(this.props.ticketInfo.dataInfo.PanicBuyingBeginTime).formatDate('HH:mm');

        let startDate = new Date(this.props.ticketInfo.dataInfo.PanicBuyingBeginTime);

        let tag1 = '';
        let tag2 = '';

        if (startDate.getTime() > new Date().getTime()) {
            tag1 = '距开始';
            tag2 = '共计';
        } else {
            tag1 = '距结束';
            tag2 = '剩余';
        }
        return (
            <div className='ticketPurchaseBox'>
                <img className='ticketPurchaseBanner' src={banner} alt='' />
                <div className='ticketPurchaseContent'>
                    <div className='ticketPurchaseContentEndTime'>
                        {tag1}:<span className='ticketPurchaseContentEndTimeSpan'>{this.state.day}</span>天<span className='ticketPurchaseContentEndTimeSpan'>{this.state.hour}</span>时<span className='ticketPurchaseContentEndTimeSpan'>{this.state.min}</span>分<span className='ticketPurchaseContentEndTimeSpan'>{this.state.second}</span>秒
                    </div>
                    <div className='ticketPurchaseContentStartTime'>
                        <div className='daytime'>{beginDate}</div>
                        <div className='hourtime'>{beginTime}开抢</div>
                    </div>
                    <div className='ticketPurchaseContentImgBox'>
                        <img className='ticketimgBg' src={ticketUrl} alt='' />
                        <div className='ticketContent'>
                            <div className='ticketName'>{this.props.ticketInfo.dataInfo.PanicBuyingActiveName}</div>
                            <div className='leftNumber'>{tag2}{this.props.ticketInfo.dataInfo.PanicBuyingProductNum}张</div>
                        </div>
                    </div>
                    <div className='ticketPurchaseContentBuy'>
                        <div className='ticketPrice'>
                            <span className='salePrice'>&yen;{this.props.ticketInfo.dataInfo.PanicBuyingProductPresentPrice}</span><span className='normalPrice'>&yen;{this.props.ticketInfo.dataInfo.PanicBuyingProductOriginalPrice}元</span>
                        </div>
                        <div className='buyBtn' onClick={this.ShowResult}>
                            <span>立即开抢</span>
                        </div>
                    </div>
                </div>
                <div className='ticketPurchaseRole'>
                    <div className='ticketPurchaseRoleTip'>活动规则</div>
                    <div className='ticketPurchaseRoleText' dangerouslySetInnerHTML={{ __html: this.props.ticketInfo.dataInfo.PanicBuyingActiveRule }}></div>
                </div>
                {
                    this.renderList()
                }
            </div>
        );
    }
    renderList() {
        let titImg = require('../../assets/images/ticketPurchase/qgcg_tit.png');
        let bottomImg = require('../../assets/images/ticketPurchase/activyTickets_bottom.jpg');
        let ListBgImg = require('../../assets/images/ticketPurchase/qgcg_list_bg.png');

        return (
            <div className='ticketPurchaseList'>
                <img src={titImg} alt='' className='ticketPurchaseListTip' />
                <div className='ticketPurchaseListContent' style={{ backgroundImage: `url(${ListBgImg})` }}>
                    <ul className='ticketPurchaseListUl'>
                        {
                            this.props.purchaseList.dataList.map((item, index) =>
                                <li className='ticketPurchaseListItem' key={index}>
                                    <img src={item.HeadImgUrl} className='ticketPurchaseItemHead' alt='' />
                                    <p className='ticketPurchaseItemContent'>
                                        {
                                            item.NickName === null
                                                ?
                                                '佚名'
                                                :
                                                item.NickName
                                        }
                                        <span className='ticketPurchaseItemSpan'>{item.Province}&nbsp;{item.City}</span>
                                    </p>
                                </li>
                            )
                        }
                    </ul>
                </div>
                <img src={bottomImg} alt='' className='ticketPurchaseListBottom' />
            </div>
        );
    }
    updateTime() {
        if (this.props.ticketInfo.dataInfo) {

            let startDate = new Date(this.props.ticketInfo.dataInfo.PanicBuyingBeginTime).getTime();
            let endDate = new Date(this.props.ticketInfo.dataInfo.PanicBuyingEndTime).getTime();

            if (startDate > new Date().getTime()) {
                this.caculateTime(startDate);
            } else {
                this.caculateTime(endDate);
            }
        }
    }
    caculateTime(time) {
        let time_distance = time - new Date().getTime();
        // 天
        let int_day = Math.floor(time_distance / 86400000);
        time_distance -= int_day * 86400000;
        // 时
        let int_hour = Math.floor(time_distance / 3600000);
        time_distance -= int_hour * 3600000;
        // 分
        let int_minute = Math.floor(time_distance / 60000);
        time_distance -= int_minute * 60000;
        // 秒 
        let int_second = Math.floor(time_distance / 1000);
        // 时分秒为单数时、前面加零 
        if (int_hour < 10) {
            int_hour = '0' + int_hour;
        }
        if (int_minute < 10) {
            int_minute = '0' + int_minute;
        }
        if (int_second < 10) {
            int_second = '0' + int_second;
        }
        this.setState({
            day: int_day,
            hour: int_hour,
            min: int_minute,
            second: int_second
        });
    }
    restoreScrollViewPosition() {
        if (this.scrollView) {
            if (!isNaN(this.props.ticketInfo.scrollViewOffset)) {
                this.scrollView.scrollToY(this.props.ticketInfo.scrollViewOffset);
            }
            else {
                this.scrollView.scrollToY(0);
            }
        }
    }
    cacheScrollOffset() {
        let offsetY = ReactDOM.findDOMNode(this.scrollView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(offsetY));
    }
    loadData() {
        this.props.dispatch(asyncGetTicketInfoAction()).then(() => {
            this.props.dispatch(asyncGetTicketListAction(this.props.ticketInfo.dataInfo.AccountGuid, this.props.ticketInfo.dataInfo.PanicBuyingActiveGuid));
        });
    }
    ShowResult(){
        this.props.history.push(`${this.props.match.url}/result`);
        clearInterval(this.timer);
        this.cacheScrollOffset();
    }
}

const mapStateToProps = function (state) {
    return {
        ticketInfo: state.ticketPurchase.ticketPurchaseScreen.ticketInfo,
        purchaseList: state.ticketPurchase.ticketPurchaseScreen.purchaseList,
    };
};

TicketPurchaseScreen = connect(mapStateToProps)(TicketPurchaseScreen);

export default TicketPurchaseScreen;


