import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, SegmentedControl } from 'antd-mobile';
import Content from '../../views/common/Content';
import ShoppingRecommendListView from '../../views/shopping/ShoppingRecommendListView';
import {  
    goBackAction
} from '../../store/actions/shopping/recommendListAction';
import '../../assets/css/shopping.css';
import LeftContentView from '../../views/common/LeftContentView';

class ShoppingRecommendListScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.showCommonList = this.showCommonList.bind(this);
    }
    render()
    {
        let listViewHeight = window.innerHeight - 45;
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    leftContent={<LeftContentView
                        ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                        goBackAction={goBackAction}
                        history={this.props.history}
                    />}
                    onLeftClick={() => {
                        this.LeftContentView.goHome();
                    }}>
                    <SegmentedControl values={['非逛不可', '全部购物']} 
                        tintColor="white" 
                        selectedIndex={0}
                        onValueChange={this.showCommonList}/>
                </NavBar>
                <Content>
                    <ShoppingRecommendListView ref={(listView)=>{
                        if(listView)
                        {
                            this.listView = listView.getWrappedInstance();
                        }
                    }}
                    onItemClick={this.onItemClick}
                    height={listViewHeight}/>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item || !item.GetRelation || !item.GetRelation.Code)
        {
            return;
        }
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        this.props.history.push(`/shopping/detail/${item.GetRelation.Code}`);
    }
    goBack()
    {
        this.props.dispatch(goBackAction());
        if(this.state.isMenu){
            this.props.history.replace('/');
        }else{
            this.props.history.goBack();
        }
    }
    showCommonList()
    {
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        this.props.history.push('/shopping/list?segment=true');
    }
}

ShoppingRecommendListScreen = connect()(ShoppingRecommendListScreen);

export default ShoppingRecommendListScreen;


