import * as IConstant from '../../../core/utils/IConstant';
import '../../../core/utils/DateUtil';
const SiteNameList = ['第一站','第二站','第三站','第四站','第五站'];

export function goBackAction()
{
    return {
        type: IConstant.Actions.FOOTMARK.CREATE.GO_BACK,
    };
}

export function asyncUpdateFootmarkItemAndSiteIndexAction(footmarkItem,siteIndex)
{
    return function(dispatch, getState)
    {
        dispatch(updateFootmarkItemAndSiteIndexAction(footmarkItem,siteIndex));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function updateFootmarkItemAndSiteIndexAction(footmarkItem,siteIndex)
{
    let showList = footmarkItem && footmarkItem.GetShowList ? footmarkItem.GetShowList : [];
    let tabs = [];
    for (let i = 0; i < showList.length; i++) {
        tabs.push({
            title: getSiteNameDisplayWithSiteIndex(i)
        });
    }
    return {
        type: IConstant.Actions.FOOTMARK.CREATE.UPDATE_FOOTMARK_ITEM,
        footmarkItem,
        tabs,
        siteIndex
    };
}

function getSiteNameDisplayWithSiteIndex(siteIndex)
{
    return SiteNameList && SiteNameList.length > siteIndex ? SiteNameList[siteIndex] : '';
}

export function updateSiteIndexAction(siteIndex)
{
    return {
        type: IConstant.Actions.FOOTMARK.CREATE.UPDATE_SITE_INDEX,
        siteIndex
    };
}

export function updateSiteItemAction(siteIndex,siteItem)
{
    return {
        type: IConstant.Actions.FOOTMARK.CREATE.UPDATE_SITE_ITEM,
        siteIndex,
        siteItem
    };
}

export function isFetchingChangeAction(isFetching)
{
    return {
        type: IConstant.Actions.FOOTMARK.CREATE.IS_FETCHING_CHANGE,
        isFetching:isFetching
    };
}


