import { Actions } from '../../core/utils/IConstant';

export default function nativeReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.NATIVE.WINDOW_SIZE_CHANGED:
        return {
            ...state,
            windowSize: {...action.windowSize},
        };
    case Actions.NATIVE.SET_CONFIG_ITEM:
        return {
            ...state,
            ...action.configItem,
        };
    default:
        return state;
    }
}


