import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncDeleteListAction, setScrollerPositionAction } from '../../store/actions/shopping/listAction';
import UIUtil from '../../core/utils/UIUtil';
import SysUtil from '../../core/utils/SysUtil';
import EmptyView from '../../views/common/EmptyView';

// 综合、热度、距离、最新
const sortFields: Array = ['TopPX,BusinessType,Sort', 'likeCount', 'distance', 'ModifyTime'];
const sortTypes: Array = ['desc', 'desc', 'asc', 'asc'];

class ShoppingListView extends PureComponent
{
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList)
        {
            this.loadData(this.props.inputKeyword);
        }
        if(this.listView && !isNaN(this.props.listViewOffset))
        {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(this.props.dataList !== prevProps.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : [])});
        }
    }
    render()
    {
        return (
            <div className="scenicListBox" style={{height: this.props.height}}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh(this.props.inputKeyword)}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                        :null
                }
            </div>
        );
    }
    renderListView()
    {
        if(this.props.dataList && this.props.dataList.length > 0)
        {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );
    
            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={()=>this.onRefresh(this.props.inputKeyword)}
                    />}
                    onEndReached={this.loadNextPage}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20}/>
            );
        }
    }
    searchByKeyword(keyword)
    {
        if(keyword === this.props.keyword || (this.isEmptyString(keyword) && this.isEmptyString(this.props.keyword)))
        {
            return;
        }
        this.onRefresh(keyword);
    }
    isEmptyString(str)
    {
        return !str || str.length === 0;
    }
    onRefresh(keyword)
    {
        this.props.dispatch(asyncDeleteListAction(sortFields[this.props.tabIndex], sortTypes[this.props.tabIndex], this.getExtraParams())).then(()=>{
            this.loadData(keyword);
        });
        // this.props.dispatch(deleteListAction(sortFields[this.props.tabIndex], sortTypes[this.props.tabIndex]));
        // setTimeout(()=>{
        //     this.loadData(keyword);
        // }, 50);
    }
    loadNextPage()
    {
        this.loadData();
    }
    renderFooter()
    {
        if(!this.props.isFetching || this.props.hasMore !== true)
        {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID)
    {
        let imgUrl = item.GetImage ? item.GetImage.MiddleImageFrontUrl : '';
        return (
            <div key={rowID} 
                className="scenicListItem"
                onClick={()=>{
                    if(this.props.onItemClick)
                    {
                        this.props.onItemClick(item);
                    }
                }}>
                <div style={{backgroundImage: `url(${imgUrl})`}} className="scenicListItemImg"/>
                <div className="funListItemTextBox">
                    <div className="scenicListItemName">{item.CnName}</div>
                    <div className="funListItemOthers">距离{item.distance != null ? item.distance.toFixed(1) : ''}千米</div>
                    <div className="funListItemOthers">{item.Address}</div>
                </div>
            </div>
        );
    }
    loadData(keyword)
    {
        if(this.props.isFetching)
        {
            return;
        }
        if(this.props.hasMore === false)
        {
            return;
        }

        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        this.props.dispatch(asyncGetListAction(sortFields[this.props.tabIndex], sortTypes[this.props.tabIndex], pageIndex, keyword ? keyword : this.props.keyword, this.getExtraParams()));
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(sortFields[this.props.tabIndex], sortTypes[this.props.tabIndex], offsetY, this.getExtraParams()));
    }
    getExtraParams()
    {
        return {
            storeKey: this.props.storeKey,
            lat: this.props.centerLat,  // 周边
            lng: this.props.centerLng,
        };
    }
}

ShoppingListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

function getReduxStoreKey(ownProps)
{
    if(ownProps.centerLat && ownProps.centerLat)
    {
        return `${sortFields[ownProps.tabIndex]}_${ownProps.centerLat}_${ownProps.centerLng}`;
    }
    return sortFields[ownProps.tabIndex];
}

const mapStateToProps = function(state, ownProps)
{
    let storeItem = (ownProps.storeKey && ownProps.storeKey.length > 0) ? SysUtil.getStoreItemByDotConnector(state, ownProps.storeKey) : state.shopping.listScreen;
    let obj = storeItem.groups[getReduxStoreKey(ownProps)];
    return {
        selectedTabIndex: storeItem.selectedTabIndex,
        ...obj,
        inputKeyword: storeItem.keyword,
    };
};

ShoppingListView = connect(mapStateToProps, null, null, {withRef: true})(ShoppingListView);

export default ShoppingListView;


