import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, SegmentedControl, ActivityIndicator, Toast, ActionSheet } from 'antd-mobile';
import Content from '../../views/common/Content';
import ReactDOM from 'react-dom';
import queryString from 'query-string';
import CSSTransitionGroup from 'react-transition-group/CSSTransitionGroup';
import Config from '../../core/utils/Config';
import {
    goBackAction,
    updateFromToLocationInfo,
    isFetchingChangeAction,
    selectNavTypeAction,
    isPanelShowingChangeAction,
    navSearchRequestAction,
    navSearchSuccessAction,
    navSearchFailureAction,
    navMethodIndexChangeAction,
    mapZoomAndCenterLocationChangeAction
} from '../../store/actions/map/mapNavAction';

import '../../assets/css/map.css';

class MapNavScreen extends PureComponent {
    toLat = '';
    toLng = '';
    fromLat ;
    fromLng ;
    fromIsUserLocation = true; // 
    code = '';
    driveMethods: string[] = ['最少时间', '最短距离', '避开高速'];
    busMethods: string[] = ['最少时间', '最少换乘', '最少步行', '不乘地铁'];
    constructor(props) {
        super(props);
        this.toLat = this.props.match.params.toLat;
        this.toLng = this.props.match.params.toLng;

        let urlSearch = queryString.parse(this.props.location.search);

        this.fromLat = urlSearch.fromLat;
        this.fromLng = urlSearch.fromLng;
        this.fromIsUserLocation = !(this.fromLat && this.fromLng && this.fromLat.length > 0 && this.fromLng.length > 0);
        this.code = this.props.match.params.code;

        this.state = {
            mapHeight: 0,
            panelLeft: 0,
        };

        this.searchCompleted = this.searchCompleted.bind(this);
        this.hidePanel = this.hidePanel.bind(this);
        this.showPanel = this.showPanel.bind(this);
        this.showNavWayChooser = this.showNavWayChooser.bind(this);
        this.changeMapNavType = this.changeMapNavType.bind(this);
    }
    componentDidMount() {
        this.props.dispatch(isFetchingChangeAction(this.code,true));
        this.updateWindowDimensions();
    }

    componentDidUpdate(prevProps, prevState, snapshot)
    {
        let isUpdateLocation = this.props.baiduMapInited && !prevProps.baiduMapInited;
        isUpdateLocation = isUpdateLocation  || (this.fromIsUserLocation && !prevProps.fromLat && this.props.fromLat !== prevProps.fromLat);
        if (isUpdateLocation)
        {
            this.updateFromLocation(this.props);
        }
        //  navType change
        if ( this.props.navType !== prevProps.navType || this.props.navMethodIndex !== prevProps.navMethodIndex)
        {
            this.planLine(this.props);
            this.hidePanel();
        }

        if (this.props.navResults && this.props.isPanelShowing !== prevProps.isPanelShowing)
        {
            let panelResult = document.getElementById('r-result').innerHTML;
            this.setState({ panelResult: panelResult });
        }

        if (this.props.navResults !== prevProps.navResults)
        {
            this.showSearchOverlay(this.props.navResults);
        }
    }

    render() {
        return (
            <div>
                <NavBar mode="dark"
                    className="navBar"
                    icon={<Icon type="left" />}
                    rightContent={
                        <div onClick={() => this.showNavPanelDetails()}>详情</div>
                    }
                    onLeftClick={() => {
                        let code = this.props.match.params.code;
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction(code));
                    }}>
                    <SegmentedControl values={['开车', '公交', '步行']}
                        tintColor="white"
                        selectedIndex={
                            (!this.props.navType || this.props.navType === 'drive' ? 0 : (this.props.navType === 'bus' ? 1 : 2))
                        }
                        onChange={(e)=>this.changeMapNavType(e.nativeEvent.selectedSegmentIndex)}/>
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    <div id="elementMarkerMap" style={{ height: (this.state.mapHeight + 'px') }}></div>
                    <CSSTransitionGroup
                        transitionName="navpanel"
                        transitionAppearTimeout={500}
                        transitionEnterTimeout={500}
                        transitionLeaveTimeout={300}>
                        {
                            this.props.isPanelShowing
                                ?
                                <div key="1" className="navResultPanel" onClick={this.hidePanel}>
                                    <div style={{ marginLeft: (this.state.panelLeft + 'px') ,height:'100%',overflow:'scroll'}}>
                                        <div dangerouslySetInnerHTML={{ __html: this.state.panelResult }}></div>
                                    </div>
                                </div>
                                :
                                null
                        }
                    </CSSTransitionGroup>
                    <div className="distanceTimeBox">
                        <div className="distanceTimeBoxItem">{this.props.totalDistance}</div>
                        <div className="distanceTimeBoxItem">{this.props.totalTime}</div>
                    </div>
                    <div className="navWay" onClick={this.showNavWayChooser}>
                        {
                            this.props.navType === 'walk' ? '' : (
                                this.props.navType === 'drive' ? this.driveMethods[this.props.navMethodIndex] : this.busMethods[this.props.navMethodIndex]
                            )
                        }
                    </div>
                    <div id="r-result" style={{ 'display': 'none' }}></div>
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }

    initMap() {
        this.driveMethodTypes = [window.BMAP_DRIVING_POLICY_LEAST_TIME, window.BMAP_DRIVING_POLICY_LEAST_DISTANCE, window.BMAP_DRIVING_POLICY_AVOID_HIGHWAYS];
        this.busMethodTypes = [window.BMAP_TRANSIT_POLICY_LEAST_TIME, window.BMAP_TRANSIT_POLICY_LEAST_TRANSFER, window.BMAP_TRANSIT_POLICY_LEAST_WALKING, window.BMAP_TRANSIT_POLICY_AVOID_SUBWAYS];

        // 地图标记图片
        this.markerIcon = new window.BMap.Icon(require('../../assets/images/map/common_poi@2x.png'), new window.BMap.Size(30, 30), {
            anchor: new window.BMap.Size(15, 30),
            imageSize: new window.BMap.Size(30, 30)
        });

        this.map = this.map ? this.map : new window.BMap.Map('elementMarkerMap');
        this.map.centerAndZoom(new window.BMap.Point(Config.instance.appCityLongitude, Config.instance.appCityLatitude), 16);
        this.map.addControl(new window.BMap.NavigationControl({ type: window.BMAP_NAVIGATION_CONTROL_SMALL, anchor: window.BMAP_ANCHOR_BOTTOM_RIGHT, enableGeolocation: true, offset: new window.BMap.Size(20, 60) }));

        this.planLine(this.props);

    }

    updateWindowDimensions() {
        const height = window.innerHeight - ReactDOM.findDOMNode(this).parentNode.offsetTop;
        this.setState({ mapHeight: height ,panelLeft: (window.innerWidth / 3.0 )}, () => {
            
            this.updateFromLocation(this.props);
        });
    }

    updateFromLocation(props) {
        if (props.baiduMapInited)
        {
            this.initMap();
        }
        if (props.baiduMapInited && (!this.fromIsUserLocation || (this.fromIsUserLocation && props.fromLat && props.fromLng))) {
            if (this.fromIsUserLocation)
            {
                this.fromLat = props.fromLat;
                this.fromLng = props.fromLng;
            }
            let initNavType = 'drive';
            this.props.dispatch(updateFromToLocationInfo(props.match.params.code, this.fromLat, this.fromLng, this.toLat, this.toLng, initNavType));
        }
    }

    changeMapNavType(val) {
        let navType = (val === 0 ? 'drive' : (val === 1 ? 'bus' : 'walk'));
        this.props.dispatch(selectNavTypeAction(this.code, navType));
    }

    planLine(props) {
        if (!this.toLat || !this.toLng || !this.map || !this.fromLat || !this.fromLng || !props.navType) {
            return;
        }

        this.doPlanLine(props);
    }
    doPlanLine(props) {
        props.dispatch(navSearchRequestAction(this.code));

        let startIcon = new window.BMap.Icon(require('../../assets/images/map/icon_nav_start.png'), new window.BMap.Size(22, 35));
        let endIcon = new window.BMap.Icon(require('../../assets/images/map/icon_nav_end.png'), new window.BMap.Size(22, 35));
        startIcon.imageSize = new window.BMap.Size(22, 35);
        endIcon.imageSize = new window.BMap.Size(22, 35);

        let _from = new window.BMap.Point(this.fromLng, this.fromLat);
        let _to = new window.BMap.Point(this.toLng, this.toLat);

        if (props.navType === 'drive') {
            if (!this.drivingSearch) {
                this.drivingSearch = new window.BMap.DrivingRoute(this.map, {
                    renderOptions: { map: this.map, autoViewport: true, panel: 'r-result' },
                    policy: this.driveMethodTypes[props.navMethodIndex],
                    onSearchComplete: this.searchCompleted
                });
            }
            else {
                this.drivingSearch.setPolicy(this.driveMethodTypes[props.navMethodIndex]);
            }
            //设置起终点图标
            this.drivingSearch.setMarkersSetCallback(function (result) {
                result[0].marker.setIcon(startIcon);
                result[1].marker.setIcon(endIcon);
            });
            this.drivingSearch.search(_from, _to);
        }
        else if (props.navType === 'bus') {
            if (!this.busSearch) {
                this.busSearch = new window.BMap.TransitRoute(this.map, {
                    renderOptions: { map: this.map, panel: 'r-result' },
                    policy: this.busMethodTypes[props.navMethodIndex],
                    onSearchComplete: this.searchCompleted.bind(this)
                });
            }
            else {
                this.busSearch.setPolicy(this.busMethodTypes[props.navMethodIndex]);
            }
            //设置起终点图标
            this.busSearch.setMarkersSetCallback(function (result) {
                result[0].marker.setIcon(startIcon);
                result[1].marker.setIcon(endIcon);
            });
            this.busSearch.search(_from, _to);
        }
        else if (props.navType === 'walk') {
            if (!this.walkSearch) {
                this.walkSearch = new window.BMap.WalkingRoute(this.map, {
                    renderOptions: { map: this.map, autoViewport: true, panel: 'r-result' },
                    onSearchComplete: this.searchCompleted.bind(this)
                });
            }
            //设置起终点图标
            this.walkSearch.setMarkersSetCallback(function (result) {
                result[0].marker.setIcon(startIcon);
                result[1].marker.setIcon(endIcon);
            });
            this.walkSearch.search(_from, _to);
        }
    }
    searchCompleted(results) {
        this.map.clearOverlays();
        let status: any;
        if (this.props.navType === 'drive') {
            status = this.drivingSearch.getStatus();
        }
        else if (this.props.navType === 'bus') {
            status = this.busSearch.getStatus();
        }
        else {
            status = this.walkSearch.getStatus();
        }
        if (status === window.BMAP_STATUS_SUCCESS) {
            let totalTime = '';
            let totalDistance = '';
            let dataIsValid = false;

            let count = results.getNumPlans();
            if (count > 0) {
                // 获取第一条方案
                let plan = results.getPlan(0);
                totalDistance = plan.getDistance(true);
                totalTime = plan.getDuration(true);
                dataIsValid = true;
            }
            this.props.dispatch(navSearchSuccessAction(this.code, results, totalTime, totalDistance, dataIsValid));
        }
        else {
            this.props.dispatch(navSearchFailureAction(this.code,results));
        }
    }

    showSearchOverlay(results)
    {
        this.props.dispatch(isFetchingChangeAction(this.code,false));
        if (!results)
        {
            return;
        }

        let count = results.getNumPlans();
        if (count > 0) {
            // 获取第一条方案
            let plan = results.getPlan(0);
            // 获取方案的驾车线路
            // let route = plan.getRoute(0);
            // 获取每个关键步骤,并输出到页面
            let s = [];
            if (this.props.navType === 'drive' || this.props.navType === 'walk') 
            {
                if(!plan || !plan.getNumRoutes || !plan.getRoute)
                {
                    return;
                }
                for (let j = 0; j < plan.getNumRoutes(); j++) 
                {
                    let route = plan.getRoute(j);
                    if(!route || !route.getNumSteps || !route.getStep)
                    {
                        continue;
                    }
                    for (let i = 0; i < route.getNumSteps(); i++) 
                    {
                        let step = route.getStep(i);
                        if(!step || !step.getDescription)
                        {
                            continue;
                        }
                        s.push((i + 1) + '. ' + step.getDescription());
                    }
                }
            }
            else if (this.props.navType === 'bus') 
            {
                for (let i = 0; i < results.getNumPlans(); i++) 
                {
                    let _p = results.getPlan(i);
                    if(_p && _p.getDescription)
                    {
                        s.push('方案' + (i + 1) + '. ' + _p.getDescription(false));
                    }
                }
            }
            if (this.props.navType === 'bus' && plan && plan.getNumLines) 
            {
                // 绘制线路
                for (let i = 0; i < plan.getNumLines(); i++) {
                    let line = plan.getLine(i);
                    this.map.addOverlay(new window.BMap.Polyline(line.getPath()));
                }
            }
        }
        else
        {
            Toast.fail('抱歉，未检索到路线',2);
        }
    }

    togglePanel() {
        this.props.dispatch(isPanelShowingChangeAction(this.code, !this.props.isPanelShowing));
    }
    showPanel() {
        this.props.dispatch(isPanelShowingChangeAction(this.code, true));
    }
    hidePanel() {
        this.props.dispatch(isPanelShowingChangeAction(this.code, false));
    }
    showNavWayChooser() {
        if (this.props.navType === 'walk') {
            return;
        }
        
        let buttons = this.props.navType === 'drive' ? this.driveMethods : this.busMethods;
        let title = this.props.navType === 'drive' ? '请选择驾车方案' : '请选择公交方案';
        ActionSheet.showActionSheetWithOptions({
            options: buttons,
            title: title
        },
        (buttonIndex)=>{
            if (buttonIndex >= 0)
            {
                this.props.dispatch(navMethodIndexChangeAction(this.code,buttonIndex));
            }
        });
    }

    showNavPanelDetails() {
        this.togglePanel();
    }

    // 保存 地图中心点 和 缩放级
    setMapZoomAndCenterLocation()
    {
        if (!this.map)
        {
            return;
        }
        let centerPoint = this.map.getCenter();
        let mapZoom = this.map.getZoom();
        let centerLat = centerPoint.lat;
        let centerLng = centerPoint.lng;
        this.props.dispatch(mapZoomAndCenterLocationChangeAction(this.code,mapZoom,centerLat,centerLng));
    }
}

const mapStateToProps = function (state, ownProps) {
    let obj = state.map.mapNav[ownProps.match.params.code];
    return {
        ...obj,
        baiduMapInited: state.system.baiduMapInited,
        fromLat: state.system.latitude,
        fromLng: state.system.longitude,
    };
};

MapNavScreen = connect(mapStateToProps)(MapNavScreen);

export default MapNavScreen;


