import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function asyncGetListAction()
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction());
        return _fetchList().then((resp)=>{
            let dataList = (resp && resp.results) ? resp.results : [];
            dispatch(_getListSuccessAction(dataList));
        }).catch((error)=>{
            dispatch(_getListFailureAction(error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.SPECIALTY.RECOMMEND_LIST.GO_BACK,
    };
}

export function asyncDeleteListAction()
{
    return function(dispatch, getState)
    {
        dispatch(deleteListAction());
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction()
{
    return {
        type: IConstant.Actions.SPECIALTY.RECOMMEND_LIST.DELETE,
    };
}

export function setScrollerPositionAction(offset)
{
    return {
        type: IConstant.Actions.SPECIALTY.RECOMMEND_LIST.SET_LISTVIEW_OFFSET,
        listViewOffset: offset
    };
}

function _fetchList(orderFiled, orderType, pageIndex, searchKey)
{
    let kvDict = {
        'ModuleEnName': 'tuijiantechan', 
        'CatalogIdentifier': 'mobile/wanzhuan/mai'
    };
    return HttpClient.getFromPortal(IConstant.PortalApis.ELEMENTS.LocalProductRecommendList, kvDict)
        .then((resp) => resp.json());
}

function _getListRequestAction()
{
    return {
        type: IConstant.Actions.SPECIALTY.RECOMMEND_LIST.GET_LIST_REQUEST,
    };
}

function _getListSuccessAction(dataList)
{
    return {
        type: IConstant.Actions.SPECIALTY.RECOMMEND_LIST.GET_LIST_SUCCESS,
        dataList
    };
}

function _getListFailureAction(error)
{
    return {
        type: IConstant.Actions.SPECIALTY.RECOMMEND_LIST.GET_LIST_FAILURE,
        message: error
    };
}


