import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ActivityIndicator, Modal, TextareaItem, Button, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import { 
    asyncGetDetailAction, 
    asyncSendCommentsAction,
    goBackAction, 
} from '../../store/actions/forum/detailAction';

import '../../assets/css/forum.css';
import SysUtil from '../../core/utils/SysUtil';

const DEFAULT_PLACEHOLDER = '请输入回复内容';

class ForumDetailScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            showCommentsInput: false,
            placeholder: DEFAULT_PLACEHOLDER,
            comments: '',
        };
        this.showCommentsButtonView = this.showCommentsButtonView.bind(this);
        this.sendComments = this.sendComments.bind(this);
    }
    componentDidMount()
    {
        this.loadData();
    }
    render()
    {
        let rightMenu = this.props.contentItem ? <div className="forumDetailRightMenu" onClick={()=>this.showCommentsButtonView()}><img src={require('../../assets/images/commentNav@2x.png')} alt=''/></div> : null;
        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark" 
                            className="navBar"
                            icon={<Icon type="left" />}
                            rightContent={rightMenu}
                            onLeftClick={() => {
                                let code = this.props.match.params.code;
                                let dispatch = this.props.dispatch;
                                this.props.history.goBack();
                                dispatch(goBackAction(code));
                            }}>
                            论坛详情
                        </NavBar>
                }
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    {
                        this.renderHeaderView()
                    }
                    {
                        this.renderContentView()
                    }
                    {
                        this.renderResponseView()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
                <Modal
                    popup
                    visible={this.state.showCommentsInput}
                    onClose={()=>this.setState({showCommentsInput: false})}
                    animationType="slide-up">
                    <TextareaItem
                        ref={(ref)=>this.commentsInputView=ref} 
                        autoHeight
                        placeholder={this.state.placeholder}
                        onChange={(val)=>this.setState({comments: val})}
                        rows={5}
                        count="500"
                    />
                    <Button
                        className="forumDetailSendButton" 
                        type="primary" 
                        onClick={this.sendComments}
                        loading={this.props.isFetching && this.state.comments} 
                        disabled={this.props.isFetching || !this.state.comments}>
                        发送
                    </Button>
                </Modal>
            </div>
        );
    }
    renderHeaderView()
    {
        if(!this.props.contentItem)
        {
            return null;
        }
        return (
            <div>
                <div className="newsDetailHeaderRow">
                    <div className="newsDetailHeaderName">{this.props.contentItem.title}</div>
                    <div className="newsListSecondRow">
                        <div className="forumDetailItemOthers">
                            作者：
                            <div className="forumDetailUserIcon" style={{backgroundImage: `url(${this.props.contentItem.GetUserAvatar})`}}></div>
                            {this.props.contentItem.Nickname}
                        </div>
                        <div style={{flex: 1}}/>
                        <div className="forumDetailItemOthers">时间：{this.props.contentItem.CreateTime}</div>
                    </div>
                </div>
                <div className="newsDetailSplitLine"/>
            </div>
        );
    }
    renderContentView()
    {
        if(!this.props.contentItem)
        {
            return null;
        }
        return (
            <div className="newsDetailHtml" dangerouslySetInnerHTML={{__html: this.props.contentItem.Content}}></div>
        );
    }
    renderResponseView()
    {
        if(!this.props.contentItem)
        {
            return null;
        }
        let commentsCount = this.props.responseList ? this.props.responseList.length : 0;
        return (
            <React.Fragment>
                <div className="forumDetailCommentsViewHeader">
                    <div className="forumDetailCommentsViewHeaderText">评论({ commentsCount })</div>
                    <div style={{flex: 1}}/>
                    <div className="forumDetailCommentsViewHeaderButton" onClick={()=>this.showCommentsButtonView()}>我要评论</div>
                </div>
                {
                    commentsCount <= 0
                        ? null
                        : this.props.responseList.map((item, index) => this.renderResponseItemView(item, index))
                }
            </React.Fragment>
        );
    }
    renderResponseItemView(item, index)
    {
        return (
            <div key={item.ResponseID} className="forumDetailResponseBox">
                <div className="footmarkCommentsViewCommentsItemIcon"
                    style={{ backgroundImage: `url(${item.GetUserAvatar})` }}/>
                <div className="footmarkCommentsViewCommentsItemRight">
                    <div className="footmarkCommentsViewCommentsItemHeaderText">
                        <div className="footmarkCommentsViewCommentsItemNameRow">
                            <div className="footmarkCommentsViewCommentsItemName">
                                { item.Nickname }
                            </div>
                            <div style={{flex: 1}}/>
                            <div className="footmarkCommentsViewCommentsItemNameRowCommentsButton" 
                                onClick={()=>{
                                    this.showCommentsButtonView(`回复@${item.Nickname}`, item);
                                }}>
                                <img src={require('../../assets/images/footmark/com-comment@2x.png')} alt=''/>
                            </div>
                        </div>
                        <div className="footmarkItemHeaderDate">{ item.CreateTime }</div>
                    </div>
                    <div className="footmarkCommentsViewCommentsItemText">
                        {
                            (item.RNickname && item.RNickname.length > 0)
                                ? this.renderReplyHeader(item)
                                : null
                        }
                        <div dangerouslySetInnerHTML={{__html: item.Content}}></div>
                    </div>
                    <div className="footmarkCommentsViewCommentsItemRightSplit"/>
                </div>
            </div>
        );
    }
    renderReplyHeader(item)
    {
        return (
            <div>
                回复 <span className="forumDetailCommentsNotification">@{ item.RNickname }</span>&nbsp;&nbsp;：
            </div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetDetailAction(this.props.match.params.code));
    }
    toLoginPage()
    {
        this.props.history.push('/me/login');
    }
    showCommentsButtonView(placeholder, item)
    {
        if(!this.props.userId)
        {
            this.toLoginPage();
        }
        this.selectedItem = item;
        placeholder = placeholder ? placeholder : DEFAULT_PLACEHOLDER;
        this.setState({ showCommentsInput: true, placeholder }, ()=>{
            if(this.commentsInputView)
            {
                this.commentsInputView.focus();
            }
        });
    }
    sendComments()
    {
        if(!this.state.comments)
        {
            Toast.fail(DEFAULT_PLACEHOLDER);
            this.commentsInputView.focus();
            return;
        }
        let kvDict = {
            tid: this.props.contentItem.ThreadID,
            uid: this.props.userId,
            content: this.state.comments,
            pid: this.selectedItem ? this.selectedItem.ResponseID : ''
        };
        this.props.dispatch(asyncSendCommentsAction(this.props.match.params.code, kvDict)).then(()=>{
            if(this.props.successMessage)
            {
                Toast.success(this.props.successMessage, undefined, undefined, false);
            }
            this.setState({
                showCommentsInput: false,
                comments: '',
            });
            this.selectedItem = undefined;
            this.loadData();
        });
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.forum.detailScreen[ownProps.match.params.code];
    return {
        ...obj,
        userId: SysUtil.getUserId(state),
        isFromNativeApp: state.native.isFromNativeApp
    };
};

ForumDetailScreen = connect(mapStateToProps)(ForumDetailScreen);

export default ForumDetailScreen;



