// 绑定的当前用户信息，方便出现异常日志时，定位异常
// https://docs.fundebug.com/notifier/javascript/customize/metadata.html
export default class ErrorUtil
{
    static setMetaInfo(config)
    {
        if(!config || typeof config !== 'object' || !window.fundebug)
        {
            return;
        }
        let metaData = window.fundebug.metaData;
        metaData = {
            ...metaData,
            ...config,
        };
        window.fundebug.metaData = metaData;
    }
}


