import { Actions } from '../../../core/utils/IConstant';

export default function signInfoReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.SIGN.GET_INFO_REQUEST:
        return {
            ...state,
            isFetching:true
        };
    case Actions.SIGN.GET_INFO_SUCCESS:
        return {
            ...state,
            isFetching:false,
            dataInfo:action.resp.datas
        };
    case Actions.SIGN.GET_INFO_FAILURE:
        return {
            ...state,
            isFetching:false
        };
    case Actions.SIGN.GO_BACK:
        return {};
    default:
        return state;
    }
}


