import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    let footmarkCode = action.footmarkCode;
    if(!footmarkCode)
    {
        return state;
    }
    let obj = state[footmarkCode];
    if(!obj)
    {
        obj = { selectedTabIndex: 1, imageViewVisible: false, };
        state[footmarkCode] = obj;
        state = {...state};
    }
    switch(action.type)
    {
    case Actions.FOOTMARK.DETAIL.SET_SELECTED_TAB_INDEX:
        return {
            ...state, 
            [footmarkCode]: {
                ...obj,
                selectedTabIndex: action.index,
            }
        };
    case Actions.FOOTMARK.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state, 
            [footmarkCode]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.FOOTMARK.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state, 
            [footmarkCode]: {
                ...obj,
                isFetching: false,
                detailItem: action.detailItem,
            }
        };
    case Actions.FOOTMARK.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state, 
            [footmarkCode]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.FOOTMARK.DETAIL.SET_IMAGE_VIEW_VISIBLE:
        return {
            ...state, 
            [footmarkCode]: {
                ...obj,
                imageViewVisible: action.imageViewVisible,
            }
        };
    case Actions.FOOTMARK.DETAIL.SEND_COMMENTS_REQUEST:
        let _obj = {...obj};
        delete _obj.successMessage;
        return {
            ...state,
            [footmarkCode]: {
                ..._obj,
                isFetching: true,
            },
        };
    case Actions.FOOTMARK.DETAIL.SEND_COMMENTS_SUCCESS:
        return {
            ...state,
            [footmarkCode]: {
                ...obj,
                isFetching: false,
                successMessage: '回帖发表成功，等待审核通过后显示回帖内容！',
            },
        };
    case Actions.FOOTMARK.DETAIL.SEND_COMMENTS_FAILURE:
        return {
            ...state,
            [footmarkCode]: {
                ...obj,
                isFetching: false,
            },
        };
    case Actions.FOOTMARK.DETAIL.SEND_REPLYS_REQUEST:
        let _obj_ = {...obj};
        delete _obj_.successMessage;
        return {
            ...state,
            [footmarkCode]: {
                ..._obj_,
                isFetching: true,
            },
        };
    case Actions.FOOTMARK.DETAIL.SEND_REPLYS_SUCCESS:
        return {
            ...state,
            [footmarkCode]: {
                ...obj,
                isFetching: false,
                successMessage: '回帖发表成功，等待审核通过后显示回帖内容！',
            },
        };
    case Actions.FOOTMARK.DETAIL.SEND_REPLYS_FAILURE:
        return {
            ...state,
            [footmarkCode]: {
                ...obj,
                isFetching: false,
            },
        };
    case Actions.FOOTMARK.DETAIL.GO_BACK:
        delete state[footmarkCode];
        return {
            ...state
        };
    default:
        return state;
    }
}


