import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    let obj = state[action.orderNo];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.ORDER.DETAIL.GET_DETAIL_REQUEST:
    case Actions.ORDER.DETAIL.CHECK_STATUS_REQUEST:
        return {
            ...state,
            [action.orderNo]: {
                ...obj,
                isFetching: true,
                orderStatusChanged: false,
            }
        };
    case Actions.ORDER.DETAIL.GET_DETAIL_SUCCESS:
    case Actions.ORDER.DETAIL.CHECK_STATUS_SUCCESS:
        return {
            ...state,
            [action.orderNo]: {
                ...obj,
                isFetching: false,
                detailItem: action.detailItem,
                orderStatusChanged: action.orderStatusChanged
            },
        };
    case Actions.ORDER.DETAIL.GET_DETAIL_FAILURE:
    case Actions.ORDER.DETAIL.CHECK_STATUS_FAILURE:
        return {
            ...state,
            [action.orderNo]: {
                ...obj,
                isFetching: false,
                orderStatusChanged: false,
            }
        };
    case Actions.ORDER.DETAIL.GO_BACK:
    case Actions.COMMENTS.ADD_ORDER_COMMENTS_SUCCESS:
        let _state = {...state};
        delete _state[action.orderNo];
        return {
            ..._state
        };
    case Actions.ORDER.DETAIL.SET_DETAIL_SCROLL_OFFSET:
        return {
            ...state,
            [action.orderNo]: {
                ...obj,
                scrollViewOffset: action.scrollViewOffset,
            }
        };
    case Actions.ORDER.DETAIL.SET_DETAIL_ORDER_OPERATE_LIST:
        return {
            ...state,
            [action.orderNo]: {
                ...obj,
                operateList: action.operateList
            },
        };
    case Actions.ORDER.DETAIL.GET_PAY_PARAMS_REQUEST:
        delete obj.payFailure;
        return {
            ...state,
            [action.orderNo]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.ORDER.DETAIL.GET_PAY_PARAMS_SUCCESS:
        return {
            ...state,
            [action.orderNo]: {
                ...obj,
                isFetching: false,
                payParams: action.payParams
            }
        };
    case Actions.ORDER.DETAIL.GET_PAY_PARAMS_FAILURE:
        return {
            ...state,
            [action.orderNo]: {
                ...obj,
                isFetching: false,
                payFailure: action.payFailure
            }
        };
    default:
        return state;
    }
}


