import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncDeleteListAction, setScrollerPositionAction } from '../../store/actions/order/listAction';
import EmptyView from '../../views/common/EmptyView';
import SysUtil from '../../core/utils/SysUtil';
import UIUtil from '../../core/utils/UIUtil';
import * as IConstant from '../../core/utils/IConstant';

class OrderListView extends PureComponent
{
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList)
        {
            this.loadData(this.props.inputKeyword);
        }
        if(this.listView && !isNaN(this.props.listViewOffset))
        {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(this.props.dataList !== prevProps.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : [])});
        }
    }
    render()
    {
        return (
            <div className="scenicListBox" style={{height: this.props.height}}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh(this.props.inputKeyword)}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 0)
                        ?
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                        :null
                }
            </div>
        );
    }
    renderListView()
    {
        if(this.props.dataList && this.props.dataList.length > 0)
        {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: 1,
                        borderBottom: '0.5px solid #dddddd',
                    }}
                />
            );
    
            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}
                    />}
                    onEndReached={this.loadNextPage}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20}/>
            );
        }
    }
    isEmptyString(str)
    {
        return !str || str.length === 0;
    }
    onRefresh(keyword)
    {
        this.props.dispatch(asyncDeleteListAction(this.props.orderType)).then(()=>{
            this.loadData(keyword);
        });
    }
    loadNextPage()
    {
        this.loadData();
    }
    renderFooter()
    {
        if(!this.props.isFetching || this.props.hasMore !== true)
        {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID)
    {
        if (!item || item === undefined)
        {
            return null;
        }
        let imgUrl = item.GetImage && item.GetImage.ImageFrontUrl  ? item.GetImage.ImageFrontUrl : '';
        return (
            <div key={rowID} 
                className="scenicListItem"
                onClick={()=>{
                    if(this.props.onItemClick)
                    {
                        this.props.onItemClick(item);
                    }
                }}>
                <div style={{backgroundImage: `url(${imgUrl})`}} className="scenicListItemImg"/>
                <div className="scenicListItemTextBox">
                    <div className="orderListItemName">{item.OrderName}</div>
                    <div className="orderListTrafficDayRow">
                        <div className="orderListItemOthers">{this.getOrderNumber(item)} | 共计：<span className="orderListItemPrice">¥{this.getSumPrice(item)}</span></div>
                        <div style={{flex: 1}}/>
                        <div className="orderListItemOthers">{SysUtil.getStatusName(this.getOrderStatus(item))}</div>
                    </div>
                    <div className="orderListItemDate">{this.getDateLabel(item)}</div>
                </div>
            </div>
        );
    }
    loadData(keyword)
    {
        if(this.props.isFetching)
        {
            return;
        }
        if(this.props.hasMore === false)
        {
            return;
        }

        let pageIndex = this.props.pageIndex >= 0 ? (this.props.pageIndex + 1) : 0;
        this.props.dispatch(asyncGetListAction(this.props.userId, this.props.orderType, pageIndex));
    }
    cacheScrollOffset()
    {
        if(this.listView)
        {
            let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
            this.props.dispatch(setScrollerPositionAction(this.props.orderType, offsetY));
        }
    }
    getDateLabel(item)
    {
        if(this.props.orderType === IConstant.ORDER_TYPE.HOTEL)
        {
            return '入住时间:' + new Date(item.PlaySDate).formatDate('YYYY-MM-DD') ;
        }
        else if(this.props.orderType === IConstant.ORDER_TYPE.LINE)
        {
            return '发团时间' + new Date(item.PlaySDate).formatDate('YYYY-MM-DD');
        }
        return '游玩时间:'+ new Date(item.PlayDate).formatDate('YYYY-MM-DD') ;
    }
    getSumPrice(item)
    {
        if(this.props.orderType === IConstant.ORDER_TYPE.HOTEL)
        {
            return item.AmountPrice;
        }
        else if(this.props.orderType === IConstant.ORDER_TYPE.LINE)
        {
            return item.AmountPrice;
        }
        return item.SumPrice;
    }
    getOrderStatus(item)
    {
        if(this.props.orderType === IConstant.ORDER_TYPE.SPECIAL)
        {
            return item.OrderSTATUS;
        }
        return parseInt(item.OrderStatus,10);
    }
    getOrderNumber(item)
    {
        if(this.props.orderType === IConstant.ORDER_TYPE.HOTEL)
        {
            let dayCount = new Date(item.PlayEDate).diffDays(new Date(item.PlaySDate));
            return `${dayCount}天${item.OrderNumber}房`;
        }
        else if(this.props.orderType === IConstant.ORDER_TYPE.TICKET)
        {
            return `${item.OrderNumber}张`;
        }
        else
        {
            return `${item.OrderNumber}张`;
        }
    }
}

OrderListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

const mapStateToProps = function(state, ownProps)
{
    let userId = SysUtil.getUserId(state);
    let obj = state.order.listScreen.groups[ownProps.orderType];
    return {
        selectedTabIndex: state.order.listScreen.selectedTabIndex,
        ...obj,
        userId: userId,
    };
};

OrderListView = connect(mapStateToProps, null, null, {withRef: true})(OrderListView);

export default OrderListView;


