import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import SysUtil from '../../../core/utils/SysUtil';

export function goBackAction(code)
{
    return {
        type: IConstant.Actions.FORUM.DETAIL.GO_BACK,
        code,
    };
}

// 详情
export function asyncGetDetailAction(code)
{
    return function(dispatch, getState)
    {
        let store = getState();
        dispatch(_getDetailRequestAction(code));
        return _fetchDetail(code, store).then((resp)=>{
            let data = (resp.results && resp.results.length > 0) ? resp.results[0] : {};
            dispatch(_getDetailSuccessAction(code, data));
        }).catch((error)=>{
            dispatch(_getDetailFailureAction(code, error));
        });
    };
}

// 添加点评
export function asyncSendCommentsAction(code, kvDict)
{
    return function(dispatch, getState)
    {
        dispatch(_sendCommentsRequestAction(code));
        return _sendComents(code, kvDict).then((resp)=>{
            let data = (resp.results && resp.results.length > 0) ? resp.results[0] : {};
            dispatch(_sendCommentsSuccessAction(code, data));
        }).catch((error)=>{
            dispatch(_sendCommentsFailureAction(code, error));
        });
    };
}

function _sendComents(code, kvDict)
{
    return HttpClient.postToPortal(IConstant.PortalApis.FORUM.ForumThreadResponseInsert, kvDict, undefined, true)
        .then((resp) => resp.json());
}

function _sendCommentsRequestAction(code)
{
    return {
        type: IConstant.Actions.FORUM.DETAIL.SEND_COMMENTS_REQUEST,
        code,
    };
}

function _sendCommentsSuccessAction(code, data)
{
    return {
        type: IConstant.Actions.FORUM.DETAIL.SEND_COMMENTS_SUCCESS,
        code,
    };
}

function _sendCommentsFailureAction(code, error)
{
    return {
        type: IConstant.Actions.FORUM.DETAIL.SEND_COMMENTS_FAILURE,
        code,
        message: error
    };
}

function _fetchDetail(code, store)
{
    let userId = SysUtil.getUserId(store);
    userId = userId ? userId : '';
    let kvDict = {
        id: code,
        PageIndex: 1,
        PageSize: IConstant.ALL_PAGE_SIZE,
        isMgr: 0,
        type: 0,
        uid: userId,
    };
    return HttpClient.getFromPortal(IConstant.PortalApis.FORUM.ForumThreadDetail, kvDict)
        .then((resp) => resp.json());
}

function _getDetailRequestAction(code)
{
    return {
        type: IConstant.Actions.FORUM.DETAIL.GET_DETAIL_REQUEST,
        code,
    };
}

function _getDetailSuccessAction(code, data)
{
    return {
        type: IConstant.Actions.FORUM.DETAIL.GET_DETAIL_SUCCESS,
        code,
        contentItem: (data.Thread && data.Thread.length > 0) ? data.Thread[0] : undefined,
        responseList: data.Response,
    };
}

function _getDetailFailureAction(code, error)
{
    return {
        type: IConstant.Actions.FORUM.DETAIL.GET_DETAIL_FAILURE,
        code,
        message: error
    };
}



