import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import SysUtil from '../../../core/utils/SysUtil';

export function goBackAction()
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.GO_BACK,
    };
}

export function updateUserInfoAction(userInfo)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.UPDATE_USER_INFO,
        userInfo: userInfo,
    };
}

export function updateBookingItemAction(bookingItem)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.UPDATE_BOOKING_ITEM,
        bookingItem,
    };
}

export function asyncBackupUserInfoAction()
{
    return function (dispatch, getState) {
        let state = getState();
        dispatch(_backupUserInfoAction(SysUtil.getUserInfo(state)));
        return new Promise((resolve) => {
            resolve();
        });
    };
}

export function asyncGetPriceAction(productCode, orderType)
{
    return function(dispatch, getState)
    {
        dispatch(_getPriceRequestAction());
        return _fetchPrice(productCode, orderType).then((resp)=>{
            resp = (resp && resp.results) ? resp.results : null;
            dispatch(_getPriceSuccessAction(resp, orderType));
        }).catch((error)=>{
            dispatch(_getPriceFailureAction(error));
        });
    };
}

export function asyncGetVisitorsAction(userId)
{
    return function(dispatch, getState)
    {
        dispatch(_getVisitorsRequestAction());
        return _fetchVisitors(userId).then((resp)=>{
            resp = (resp && resp.results ) ? resp.results : null;
            dispatch(_getVisitorsSuccessAction(resp));
        }).catch((error)=>{
            dispatch(_getVisitorsFailureAction(error));
        });
    };
}

export function updateOrderDetailItemAction(orderDetailItem)
{
    return function (dispatch, getState) {
        dispatch(_setOrderDetailItemAction(orderDetailItem));
        return new Promise((resolve) => {
            resolve();
        });
    };
}

export function _setOrderDetailItemAction(orderDetailItem)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.UPDATE_ORDER_DETAIL_DATA,
        orderDetailItem,
    };
}

function _fetchPrice(productCode, orderType)
{
    let url = HttpClient.HOST_E_BUSINESS;
    let kvDict;
    let portalApi;
    if(orderType === IConstant.ORDER_TYPE.TICKET)
    {
        portalApi = IConstant.PortalApis.ELEMENTS.TicketDayPrice;
        kvDict = {
            ConditionParams: {
                Params: {
                    Code: productCode
                }
            }
        };
    }
    else if(orderType === IConstant.ORDER_TYPE.HOTEL)
    {
        portalApi = IConstant.PortalApis.ELEMENTS.Hotel_GetRoomDayPrice;
        kvDict = {
            ConditionParams: {
                Params:{
                    Code: productCode,
                    StartDate: '',
                    EndDate: ''
                }
            }
        };
    }
    else if(orderType === IConstant.ORDER_TYPE.LINE)
    {
        portalApi = IConstant.PortalApis.ELEMENTS.GetLineDayPrices;
        kvDict = {
            ConditionParams: {
                Params:{ code: productCode }
            }
        };
    }
    if(!url)
    {
        return;
    }
    return HttpClient.postToPortalWithUrl(url,portalApi, kvDict)
        .then((resp) => resp.json());
}

function _fetchVisitors(userId)
{
    let kvDict = {
        ConditionParams: {
            Params: {
                UserID: userId
            }
        }
    };
    return HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS, IConstant.PortalApis.VISITOR.GetVisitorsList, kvDict)
        .then((resp) => resp.json());
}

function _getPriceRequestAction()
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.GET_PRICE_REQUEST,
    };
}

function _getPriceSuccessAction(resp, orderType)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.GET_PRICE_SUCCESS,
        priceItem: resp,
        orderType,
    };
}

function _getPriceFailureAction(error)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.GET_PRICE_FAILURE,
        message: error
    };
}

function _getVisitorsRequestAction()
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.GET_VISITORS_REQUEST,
    };
}

function _getVisitorsSuccessAction(resp)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.GET_VISITORS_SUCCESS,
        dataList: resp,
    };
}

function _getVisitorsFailureAction(error)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.GET_VISITORS_FAILURE,
        message: error
    };
}

function _backupUserInfoAction(userInfo)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.BACKUP_USERINFO,
        userInfo,
    };
}

export function _asyncUpdateSelectVisitorAction(resp)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.UPDATE_SELECT_VISITOR,
        resp
    };
}

export function _asyncUpdateVisitorAction(resp)
{
    return {
        type: IConstant.Actions.ORDER.BOOKING.UPDATE_VISITOR,
        resp
    };
}


