import { combineReducers } from 'redux';
import ticketInfoReducer from './infoReducer';
import purchaseListReducer from './listReducer';

const ticketPurchaseScreenReducer = combineReducers({
    ticketInfo: ticketInfoReducer,
    purchaseList: purchaseListReducer
});

const ticketPurchaseReducer = combineReducers({
    ticketPurchaseScreen: ticketPurchaseScreenReducer,
});

export default ticketPurchaseReducer;


