import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';
import SysUtil from '../../../core/utils/SysUtil';

export function setSelectedGroupIndexAction(index)
{
    return {
        type: IConstant.Actions.FORUM.LIST.SET_SELECTED_GROUP_INDEX,
        index: index
    };
}

export function asyncSetSelectedGroupIndexAction(index)
{
    return function(dispatch, getState)
    {
        dispatch(setSelectedGroupIndexAction(index));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function asyncGetGroupList()
{
    return function(dispatch, getState)
    {
        dispatch(_getGroupListRequestAction());
        return _fetchGroupList().then((resp)=>{
            dispatch(_getGroupListSuccessAction(resp.results));
        }).catch((error)=>{
            dispatch(_getGroupListFailureAction(error));
        });
    };
}

export function asyncGetListAction(sectionID, pageIndex)
{
    return function(dispatch, getState)
    {
        let store = getState();
        dispatch(_getListRequestAction(sectionID, pageIndex));
        return _fetchList(sectionID, pageIndex, store).then((resp)=>{
            dispatch(_getListSuccessAction(sectionID, pageIndex, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(sectionID, error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.FORUM.LIST.GO_BACK,
    };
}

export function asyncDeleteListAction(sectionID)
{
    return function(dispatch, getState)
    {
        dispatch(deleteListAction(sectionID));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction(sectionID)
{
    return {
        type: IConstant.Actions.FORUM.LIST.DELETE,
        sectionID,
    };
}

export function setScrollerPositionAction(sectionID, offset)
{
    return {
        type: IConstant.Actions.FORUM.LIST.SET_LISTVIEW_OFFSET,
        sectionID, 
        listViewOffset: offset
    };
}

function _fetchList(sectionID, pageIndex, store)
{
    let userId = SysUtil.getUserId(store);
    userId = userId ? userId : '';
    let kvDict = {
        SID: sectionID,
        Query: '',
        PageIndex: pageIndex,
        PageSize: IConstant.PAGE_SIZE,
        Order: 'ReplyTime',
        Uid: userId,
    };
    return HttpClient.getFromPortal(IConstant.PortalApis.FORUM.ForumThreadList, kvDict)
        .then((resp) => resp.json());
}

function _fetchGroupList()
{
    return HttpClient.getFromPortal(IConstant.PortalApis.FORUM.ForumSection)
        .then((resp) => resp.json());
}

function _getListRequestAction(sectionID, pageIndex)
{
    return {
        type: IConstant.Actions.FORUM.LIST.GET_LIST_REQUEST,
        sectionID,
        pageIndex,
    };
}

function _getListSuccessAction(sectionID, pageIndex, result)
{
    return {
        type: IConstant.Actions.FORUM.LIST.GET_LIST_SUCCESS,
        sectionID,
        pageIndex,
        result
    };
}

function _getListFailureAction(sectionID, error)
{
    return {
        type: IConstant.Actions.FORUM.LIST.GET_LIST_FAILURE,
        sectionID,
        message: error
    };
}

function _getGroupListRequestAction()
{
    return {
        type: IConstant.Actions.FORUM.LIST.GET_GROUP_LIST_REQUEST,
    };
}

function _getGroupListSuccessAction(groupList)
{
    return {
        type: IConstant.Actions.FORUM.LIST.GET_GROUP_LIST_SUCCESS,
        groupList,
    };
}

function _getGroupListFailureAction(error)
{
    return {
        type: IConstant.Actions.FORUM.LIST.GET_GROUP_LIST_FAILURE,
        message: error
    };
}



