import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import ErrorUtil from '../../../core/utils/ErrorUtil';
import WebDB from '../../../core/utils/WebDB';

// 自动登录
export function autoLogin(dispatch)
{
    let loginFormStr = WebDB.getItem(IConstant.STORAGE_KEY.USER_LOGIN_FORM);
    if(loginFormStr && loginFormStr.length > 0)
    {
        let loginForm = JSON.parse(loginFormStr);
        if(loginForm && loginForm.phone && loginForm.password)
        {
            dispatch(asyncLoginAction(loginForm.phone, loginForm.password));
        }
    }
}

// 设置用户信息（native直接将用户信息传递过来）
export function setUserInfoAction(userInfo)
{
    if(!userInfo)
    {
        return;
    }
    return {
        type: IConstant.Actions.ME.USER_INFO.SET_USER_INFO,
        userInfo: userInfo
    };
}

// 获取微信用户OpenId
export function getWXOpenId(appAccount, appCode, dispatch)
{
    let url = HttpClient.WECHAT_HANDLER;
    let kvDict = {
        type: '1',
        action: '1',
        conditionParm: JSON.stringify({
            AccountGuid: appAccount,
            Code: appCode
        })
    };
    return HttpClient.getData(url, kvDict)
        .then((resp) => resp.json()).then((resp) => {
            if (resp && resp.success && resp.datas) 
            {
                let data = resp.datas;
                if (data.OAuth.err) 
                {
                    console.warn('获取微信用户失败');
                } else 
                {
                    let appOpenId = data.OpenId;
                    WebDB.setItem(IConstant.STORAGE_KEY.WX_USER_OPENID, appOpenId);
                    WebDB.setItem(IConstant.STORAGE_KEY.WX_ACCOUNT, appAccount);
                    console.log('获取微信用户成功');
                    ErrorUtil.setMetaInfo({wechatUser: {...data, appOpenId, appAccount}});
                }
            }
            else
            {
                console.warn('获取微信用户失败');
            }
            autoLogin(dispatch);
        });
}

// 将门户的用户信息同步到微信后台
function _syncAppUserInfoToWXServer(userInfo)
{
    if(!userInfo)
    {
        return;
    }
    let url = HttpClient.WECHAT_HANDLER;
    let kvDict = {
        type: '12',
        action: '2',
        conditionParm: JSON.stringify({
            WapUserGuid: '',
            UserId: userInfo.ID,
            NickName: userInfo.Nickname,
            Name: userInfo.Name,
            Phone: userInfo.PhoneNumber || userInfo.Phone,
            Email: userInfo.Email,
            Sex: userInfo.Sex,
            Birthday: userInfo.Birthday,
            City: userInfo.City,
            CreateTime: userInfo.CreateTime,
            IsDelete: 0,
        }),
    };
    return HttpClient.getData(url, kvDict)
        .then((resp) => resp.json()).then((resp) => {
            if(resp && resp.success && resp.datas)
            {
                console.log('同步用户信息成功');
            }
            else
            {
                console.warn('同步用户信息失败');
            }
        });
}

// 将微信用户信息+APP用户信息 进行绑定
function _bindWXToAppUser(userInfo)
{
    let openId = WebDB.getItem(IConstant.STORAGE_KEY.WX_USER_OPENID);
    if(!openId || openId.length === 0 || !userInfo)
    {
        return;
    }
    let url = HttpClient.WECHAT_HANDLER;
    let kvDict = {
        type: '2',
        action: '9',
        conditionParm: JSON.stringify({
            OpenId: openId,
            UserId: userInfo.ID,
        })
    };
    return HttpClient.getData(url, kvDict)
        .then((resp) => resp.json()).then((resp) => {
            if(resp && resp.success && resp.datas)
            {
                _syncAppUserInfoToWXServer(userInfo);
                console.log('绑定微信用户成功');
            }
            else
            {
                console.warn('绑定微信用户失败');
            }
        });
}

// 登录
export function asyncLoginAction(phone, password)
{
    return function(dispatch, getState)
    {
        dispatch(_loginRequestAction());
        return _fetchLogin(phone, password).then((resp)=>{
            if(resp && resp.status === '0' && resp.results && resp.results.length > 0)
            {
                resp.results = resp.results[0];
                ErrorUtil.setMetaInfo({portalUser: {...resp.results}});
                // 接口没有返回密码，登录成功就将输入框的密码存过来，修改个人信息要用到
                resp.results.Pwd = password;

                // 存储到localstorage
                WebDB.setItem(IConstant.STORAGE_KEY.USER_LOGIN_FORM, {
                    phone,
                    password,
                });
                WebDB.setItem(IConstant.STORAGE_KEY.USER_INFO, resp.results);

                _bindWXToAppUser(resp.results);
            }
            dispatch(_loginSuccessAction(resp));
            
        }).catch((error)=>{
            dispatch(_loginFailureAction(error));
        });
    };
}

function _fetchLogin(phone, password)
{
    let kvDict = {
        phone: phone,
        pwd: password,
    };
    return HttpClient.postToPortal(IConstant.PortalApis.ME.Login, kvDict, null, true)
        .then((resp) => resp.json());
}

function _loginRequestAction(code)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.LOGIN_REQUEST,
        code,
    };
}

function _loginSuccessAction(resp)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.LOGIN_SUCCESS,
        userInfo: resp
    };
}

function _loginFailureAction(error)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.LOGIN_FAILURE,
        message: error
    };
}

//退出登录
export function exitLoginAction()
{
    WebDB.removeItem(IConstant.STORAGE_KEY.USER_LOGIN_FORM);
    WebDB.removeItem(IConstant.STORAGE_KEY.USER_INFO);
    return {
        type: IConstant.Actions.ME.USER_INFO.EXIT_LOGIN,
    };
}

// 修改个人信息
export function asyncUpdateUserInfoAction(kvDict, headerFile)
{
    return function(dispatch, getState)
    {
        dispatch(_updateUserInfoRequestAction());
        return _fetchUpdateUserInfo(kvDict, headerFile).then((resp)=>{
            if(resp.status && resp.status === '0' && resp.results && resp.results.length > 0)
            {
                resp.results = resp.results[0];
                dispatch(_updateUserInfoSuccessAction(resp));
            }
            else{
                dispatch(_updateUserInfoFailureAction(resp.message));
            }
        }).catch((error)=>{
            dispatch(_updateUserInfoFailureAction(error));
        });
    };
}

// 修改个人信息时先clone一份
export function cloneTempUserInfoAction()
{
    return {
        type: IConstant.Actions.ME.USER_INFO.CLONE_TEMP_INFO,
    };
}

export function clearTempUserInfoAction()
{
    return {
        type: IConstant.Actions.ME.USER_INFO.CLEAR_TEMP_INFO,
    };
}

export function updateTempUserInfoAction(configItem)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.UPDATE_TEMP_INFO,
        configItem,
    };
}

function _fetchUpdateUserInfo(kvDict, headerFile)
{
    // return HttpClient.postDataWithFile(url, kvDict, null, true, headerFile ? [headerFile] : headerFile)
    //     .then((resp) => resp.json());
    return HttpClient.postToPortal(IConstant.PortalApis.ME.Update, kvDict, null, true, headerFile ? [headerFile] : headerFile)
        .then((resp) => resp.json());
}

function _updateUserInfoRequestAction(code)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.UPDATE_INFO_REQUEST,
        code,
    };
}

function _updateUserInfoSuccessAction(resp)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.UPDATE_INFO_SUCCESS,
        userInfo: resp
    };
}

function _updateUserInfoFailureAction(error)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.UPDATE_INFO_FAILURE,
        message: error
    };
}

//获取用户个数接口
export function asyncGetUserNumbersAction(userId)
{
    return function(dispatch, getState)
    {
        dispatch(_getUserNumbersRequestAction());
        return _fetchGetUserNumbers(userId).then((resp)=>{
            dispatch(_getUserNumbersSuccessAction(resp && resp.results ? resp.results : null));
        }).catch((error)=>{
            dispatch(_getUserNumbersFailureAction(error));
        });
    };
}

function _getUserNumbersRequestAction()
{
    return {
        type: IConstant.Actions.ME.USER_INFO.GET_NUMBERS_REQUEST,
    };
}

function _fetchGetUserNumbers(userId)
{
    let kvDict = { 'ID': userId };
    return HttpClient.postToPortal(IConstant.PortalApis.ME.GetUserCount,kvDict,null,true)
        .then((resp) => resp.json());
}

function _getUserNumbersSuccessAction(results)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.GET_NUMBERS_SUCCESS,
        results
    };
}

function _getUserNumbersFailureAction(error)
{
    return {
        type: IConstant.Actions.ME.USER_INFO.GET_NUMBERS_FAILURE,
        message: error
    };
}


