import { combineReducers } from 'redux';
import indexReducer from './indexReducer';
import detailReducer from './detailReducer';
import createReducer from './createReducer';
import mapLineReducer from './mapLineReducer';
import editReducer from './editReducer';

import listReducer from './listReducer';

import addPointReducer from './addPointReducer';


const journeyReducer = combineReducers({
    indexScreen: indexReducer,
    detailScreen: detailReducer,
    createScreen: createReducer,
    mapLineScreen: mapLineReducer,
    editScreen: editReducer,

    listScreen: listReducer,

    addPointScreen: addPointReducer,

});

export default journeyReducer;


