import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import OrderListView from '../../views/order/OrderListView';
import UIUtil from '../../core/utils/UIUtil';
import * as IConstant from '../../core/utils/IConstant';
import { setSelectedTabIndexAction, goBackAction } from '../../store/actions/order/listAction';
import '../../assets/css/order.css';

class OrderListScreen extends PureComponent
{
    tabs = [
        {title: '门票', orderType: IConstant.ORDER_TYPE.TICKET},
        {title: '酒店', orderType: IConstant.ORDER_TYPE.HOTEL},
        {title: '线路', orderType: IConstant.ORDER_TYPE.LINE},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
    }
    render()
    {
        let tabHeight = window.innerHeight - 45;
        let listViewHeight = window.innerHeight - (45 + 44);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={this.goBack}>
                    我的订单
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><OrderListView key={index} 
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                orderType={item.orderType} 
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: OrderListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        let orderType = this.tabs[this.props.selectedTabIndex].orderType;
        if(orderType === IConstant.ORDER_TYPE.SPECIAL)
        {
            this.props.history.push(`/specialShop/order-detail/${item.OrderNo}`);
        }
        else
        {
            this.props.history.push({
                pathname: '/order/detail',
                search: `?orderNo=${item.OrderNo}&orderType=${orderType}`,
                state: {basicInfo: item},
            });
        }
    }
    goBack()
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }
}

const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.order.listScreen.selectedTabIndex,
    };
};

OrderListScreen = connect(mapStateToProps)(OrderListScreen);

export default OrderListScreen;


