import React from 'react';
import { connect } from 'react-redux';
import { NavBar } from 'antd-mobile';
import queryString from 'query-string';
import Content from '../../views/common/Content';
import NewsListView from '../../views/news/NewsListView';
import { goBackAction, increaseClickCountAction } from '../../store/actions/news/listAction';
import '../../assets/css/news.css';
import LeftContentView from '../../views/common/LeftContentView';
import NativeAppScreen from '../common/NativeAppScreen';

class CommonNewsListScreen extends NativeAppScreen
{
    newsKeys = {
        'know/union': '快乐联盟',
        'know/ConvenientTourist': '便客空间',
    };
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        let urlSearch = queryString.parse(this.props.location.search);
        this.key = urlSearch.key;
    }
    componentDidMount()
    {
        this.updateNativeTitle(this.newsKeys[this.key]);
    }
    render()
    {
        let listViewHeight = window.innerHeight - (this.props.isFromNativeApp ? 0 : 45);

        return (          
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark"
                            className="navBar"
                            leftContent={<LeftContentView
                                ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                                goBackAction={goBackAction}
                                history={this.props.history}
                            />}
                            onLeftClick={() => {
                                this.LeftContentView.goHome();
                            }}>
                            {this.newsKeys[this.key]}
                        </NavBar>
                }
                <Content>
                    <NewsListView
                        idKey={this.key}
                        ref={(listView) => {
                            if (listView) {
                                this.listView = listView.getWrappedInstance();
                            }
                        }}
                        onItemClick={this.onItemClick}
                        height={listViewHeight} />
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        let dispatch = this.props.dispatch;
        this.props.history.push(`/news/detail/${item.ContentCode}`);
        dispatch(increaseClickCountAction(this.key, item.ContentCode));
    }
}

const mapStateToProps = function(state, ownProps)
{
    return {
        isFromNativeApp: state.native.isFromNativeApp
    };
};

CommonNewsListScreen = connect(mapStateToProps)(CommonNewsListScreen);

export default CommonNewsListScreen;


