import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    let obj = state[action.code];
    switch(action.type)
    {
    case Actions.FORUM.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching: true,
            }
        };
    case Actions.FORUM.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
                contentItem: action.contentItem,
                responseList: action.responseList,
            },
        };
    case Actions.FORUM.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
            }
        };
    case Actions.FORUM.DETAIL.SEND_COMMENTS_REQUEST:
        let _obj = {...obj};
        delete _obj.successMessage;
        return {
            ...state,
            [action.code]: {
                ..._obj,
                isFetching: true,
            },
        };
    case Actions.FORUM.DETAIL.SEND_COMMENTS_SUCCESS:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching: false,
                successMessage: '回帖发表成功，等待审核通过后显示回帖内容！',
            },
        };
    case Actions.FORUM.DETAIL.SEND_COMMENTS_FAILURE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching: false,
            },
        };
    case Actions.FORUM.DETAIL.GO_BACK:
        let _state = {...state};
        delete _state[action.code];
        return {
            ..._state
        };
    default:
        return state;
    }
}



