/**
 * 节庆活动
 */

import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, Icon } from 'antd-mobile';
import Content from '../../views/common/Content';
import FestivalNewsListView from '../../views/news/FestivalNewsListView';
import { setSelectedTabIndexAction, goBackAction } from '../../store/actions/festival-news/listAction';
import '../../assets/css/news.css';
import * as IConstant from '../../core/utils/IConstant';

class FestivalNewsListScreen extends PureComponent
{
    tabs = [
        {title: '节庆活动', idKey: IConstant.FESTIVAL_STYPE_TYPE.FESTIVAL_JIEQINGHUOD},
        {title: '赛事', idKey: IConstant.FESTIVAL_STYPE_TYPE.FESTIVAL_SAISHI},
        {title: '会展', idKey: IConstant.FESTIVAL_STYPE_TYPE.FESTIVAL_HUIZHAN},
        {title: '演出', idKey: IConstant.FESTIVAL_STYPE_TYPE.FESTIVAL_YANCHU},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }
    render()
    {
        let navBarHeight = this.props.isFromNativeApp ? 0 : 45;
        let listViewHeight =this.props.isFromNativeApp? window.innerHeight - 44 : window.innerHeight - (44 + navBarHeight);

        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark"
                            className="navBar"
                            icon={<Icon type="left" />}
                            onLeftClick={() => {
                                let dispatch = this.props.dispatch;
                                this.props.history.goBack();
                                dispatch(goBackAction());
                            }}>
                            节庆活动
                        </NavBar>
                }
                <Content>
                    <Tabs tabs={this.tabs}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><FestivalNewsListView key={index} 
                                idKey={item.idKey}
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: FestivalNewsListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push(`${this.props.match.url}/detail/${item.Code}`);
    }
}

const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.festivalNews.listScreen.selectedTabIndex,
        isFromNativeApp: state.native.isFromNativeApp
    };
};

FestivalNewsListScreen = connect(mapStateToProps)(FestivalNewsListScreen);

export default FestivalNewsListScreen;


