import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator } from 'antd-mobile';
import { asyncGetListAction, asyncDeleteListAction, setScrollerPositionAction } from '../../store/actions/fun/recommendListAction';
import UIUtil from '../../core/utils/UIUtil';

class FunRecommendListView extends PureComponent
{
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList)
        {
            this.loadData();
        }
        if(this.listView && !isNaN(this.props.listViewOffset))
        {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(this.props.dataList !== prevProps.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : [])});
        }
    }
    render()
    {
        return (
            <div className="scenicListBox" style={{height: this.props.height}}>
                {
                    this.renderListView()
                }
                {
                    this.props.isFetching
                        ?
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                        :null
                }
            </div>
        );
    }
    renderListView()
    {
        if(this.props.dataList && this.props.dataList.length >= 0)
        {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: '5px',
                        backgroundColor: '#2d2327',
                    }}
                />
            );
    
            return (
                <ListView
                    ref={(el) => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}
                    />}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20}/>
            );
        }
    }
    isEmptyString(str)
    {
        return !str || str.length === 0;
    }
    onRefresh()
    {
        this.props.dispatch(asyncDeleteListAction()).then(()=>{
            this.loadData();
        });
    }
    renderRow(item, sectionID, rowID)
    {
        let imageUrl = item.GetImage && item.GetImage.MiddleImageFrontUrl ? item.GetImage.MiddleImageFrontUrl : '';
        imageUrl = imageUrl && imageUrl.length > 0 && imageUrl.indexOf('noPicture') < 0 ? imageUrl : (item.GetRelation && item.GetRelation.GetImage && item.GetRelation.GetImage.MiddleImageFrontUrl ? item.GetRelation.GetImage.MiddleImageFrontUrl : '');
        return (
            <div key={rowID} 
                className="scenicRecommendListItemBox"
                onClick={()=>{
                    if(this.props.onItemClick)
                    {
                        this.props.onItemClick(item);
                    }
                }}>
                <div className="scenicRecommendListItemBg"
                    style={{backgroundImage: `url(${imageUrl})`}}>
                    <div className="scenicRecommendListBottom">
                        <div className="hotelRecommendListBottomText">{item.Head}</div>
                    </div>
                </div>
            </div>
        );
    }
    loadData()
    {
        if(this.props.isFetching)
        {
            return;
        }
        this.props.dispatch(asyncGetListAction());
    }
    cacheScrollOffset()
    {
        if(!this.listView)
        {
            return;
        }
        let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(offsetY));
    }
}

FunRecommendListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
};

const mapStateToProps = function(state)
{
    return {
        ...state.fun.recommendListScreen
    };
};

FunRecommendListView = connect(mapStateToProps, null, null, {withRef: true})(FunRecommendListView);

export default FunRecommendListView;


