import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, ActivityIndicator, Carousel, Calendar } from 'antd-mobile';
import { Line, Circle } from 'rc-progress';
import ReactDOM from 'react-dom';
import Content from '../../views/common/Content';
import { 
    asyncGetDetailAction, 
    asyncGetRoomListAction,
    goBackAction, 
    setDetailScrollerPositionAction,
} from '../../store/actions/hotel/detailAction';
// eslint-disable-next-line
import DateUtil from '../../core/utils/DateUtil';
import SysUtil from '../../core/utils/SysUtil';
import * as IConstant from '../../core/utils/IConstant';
import zhCN from 'antd-mobile/lib/calendar/locale/zh_CN';
import PhotoBrowserView from '../../views/photo-brower/PhotoBrowerView';
import ElementOperationView from '../../views/common/ElementOperationView';
import '../../assets/css/hotel.css';
import LeftContentView from '../../views/common/LeftContentView';

class HotelDetailScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.showElementMarkerMap = this.showElementMarkerMap.bind(this);
        this.onDateChooserCancel = this.onDateChooserCancel.bind(this);
        this.onDateChooserConfirm = this.onDateChooserConfirm.bind(this);
        this.showDateChooser = this.showDateChooser.bind(this);
        this.showImageBrowser = this.showImageBrowser.bind(this);
        this.state = {
            Score: 0,
            HealthScore: 0,
            InstallationScore: 0,
            ServiceScore: 0,
            EnvironmentScore: 0,
            index : 1,
            showDatePicker: false,
            imageList:[]
        };
    }
    componentDidMount()
    {
        if(!this.props.detailItem)
        {
            this.loadData();
        }
        else
        {
            this.startScoreAnimation(this.props.detailItem);
        }
        this.restoreScrollViewPosition();
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(prevProps.detailItem !== this.props.detailItem && this.props.detailItem)
        {
            this.startScoreAnimation(this.props.detailItem);
        }
    }
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    leftContent={<LeftContentView
                        ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                        goBackAction={() => goBackAction(this.props.match.params.code)}
                        history={this.props.history}
                    />}
                    onLeftClick={() => {
                        this.LeftContentView.goHome();
                    }}>
                    酒店详情
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    {
                        this.renderHeaderView()
                    }
                    {
                        this.renderElementOperationView()
                    }
                    {
                        this.renderScoreView()
                    }
                    {
                        this.renderDateChooseRow()
                    }
                    {
                        this.renderRoomListView()
                    }
                    {
                        this.renderMapView()
                    }
                    {
                        this.renderBasicViews()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                    <PhotoBrowserView 
                        ref={(view)=>{this.photoBrowserView = view ? view.getWrappedInstance() : this.scrollView;}}
                    />
                </Content>
                <Calendar
                    type="range"
                    title="请选择入离日期"
                    locale={zhCN}
                    visible={this.state.showDatePicker}
                    onCancel={this.onDateChooserCancel}
                    onConfirm={this.onDateChooserConfirm}
                    minDate={new Date()}
                    maxDate={new Date().addDays(365)}/>
            </div>
        );
    }
    renderHeaderView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        let star = SysUtil.getHotelStarCount(this.props.detailItem.HotelStarNo ? this.props.detailItem.HotelStarNo : '0');
        let starImg = require('../../assets/images/hotel/ico_diamond_full@2x.png');
        let starEmptyImg = require('../../assets/images/hotel/ico_diamond_empty@2x.png');
        if(this.props.detailItem.IsCountryRate !== 'False')
        {
            starImg = require('../../assets/images/hotel/ico_star_full@2x.png');
            starEmptyImg = require('../../assets/images/hotel/ico_star_empty@2x.png');
        }
        return (
            <div className="scenicDetailHeader" onClick={this.showImageBrowser}>
                {
                    this.renderHeaderImages()
                }
                <div className="scenicDetailHeaderBottom">
                    <div className="scenicDetailHeaderBottomText">{this.props.detailItem.CnName}</div>
                    <div className="scenicDetailHeaderBottomView">
                        <div className="scenicDetailHeaderBottomStars">
                            {
                                Array(5).fill(0).map((item, index)=><img key={index}
                                    className="scenicDetailHeaderBottomStar"
                                    src={index < star ? starImg : starEmptyImg}
                                    alt=''/>)
                            }
                        </div>
                        <div className="scenicDetailHeaderBottomText">{this.state.index}/{this.props.detailItem.GetImage && this.props.detailItem.GetImage.ImageList ? this.props.detailItem.GetImage.ImageList.length : 1}</div>
                    </div>
                </div>
            </div>
        );
    }
    renderDateChooseRow()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        let startDate = new Date().formatDate('YYYY-MM-DD');
        let endDate = new Date().addDays(1).formatDate('YYYY-MM-DD');
        if(this.props.startDate && this.props.endDate)
        {
            startDate = this.props.startDate;
            endDate = this.props.endDate;
        }
        return (
            <div className="hotelDetailDateRow" onClick={this.showDateChooser}>
                <div className="hotelDetailDateItem">
                    <div className="hotelDetailDateItemLabel">入住时间</div>
                    <div className="hotelDetailDateItemValue">{startDate}</div>
                </div>
                <div className="hotelDetailDateSplit"/>
                <div className="hotelDetailDateItem">
                    <div className="hotelDetailDateItemLabel">退房时间</div>
                    <div className="hotelDetailDateItemValue">{endDate}</div>
                </div>
            </div>
        );
    }
    renderRoomListView()
    {
        if(!this.props.roomList || this.props.roomList.length === 0)
        {
            return null;
        }
        return this.props.roomList.map((roomItem, index)=>this.renderRoomItemView(roomItem, index));
    }
    renderRoomItemView(roomItem, index)
    {
        return (
            <div className="hotelDetailRoomItemRow" key={index}>
                <div className="hotelDetailRoomItemName">{roomItem.CnName}</div>
                <div style={{flex: 1}}/>
                <div className="hotelDetailRoomItemPrice">¥{roomItem.DefaultPrice}</div>
                {
                    roomItem.IsReserve
                        ?
                        <div className="hotelDetailRoomItemBookingButton" onClick={()=>this.bookingRoom(roomItem)}>预订</div>
                        : null
                }
            </div>
        );
    }
    bookingRoom(roomItem)
    {
        if(!this.props.isUserLogined)
        {
            this.props.history.push('/me/login');
            return;
        }
        let hotelCode = this.props.match.params.code;
        this.props.history.push({
            pathname: `/order/booking/${hotelCode}/${roomItem.Code}/${IConstant.ORDER_TYPE.HOTEL}`,
            state: {
                startDate: this.props.startDate,
                endDate: this.props.endDate,
                hotelName:roomItem.HotelName
            }
        });
    }
    renderScoreView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div className="scenicScoreView">
                <div className="scenicScoreCircle">
                    <Circle percent={this.state.Score * 20} strokeWidth="6" trailWidth="6" strokeColor="#42a6dc" trailColor="#f0f0f0"/>
                    <div className="scenicScoreCircleTextBox">
                        <div style={{flex: 1}}/>
                        <div  className="scenicScoreCircleTextSubBox">
                            <div className="scenicScoreCircleText">{this.state.Score.toFixed(1)}</div>
                            <div className="scenicScoreCircleUnit">分</div>
                        </div>
                        <div style={{flex: 1}}/>
                    </div>
                </div>
                <div className="scenicScoreLines">
                    <div className="scenicScoreLine">
                        <div className="scenicScoreLineLabel">卫生</div>
                        <Line className="scenicScoreLineProgress" percent={this.state.HealthScore * 20} strokeWidth="4" trailWidth="4" strokeColor="#42a6dc" trailColor="#f0f0f0" />
                    </div>
                    <div className="scenicScoreLine">
                        <div className="scenicScoreLineLabel">设施</div>
                        <Line className="scenicScoreLineProgress" percent={this.state.InstallationScore * 20} strokeWidth="4" trailWidth="4" strokeColor="#42a6dc" trailColor="#f0f0f0" />
                    </div>
                    <div className="scenicScoreLine">
                        <div className="scenicScoreLineLabel">服务</div>
                        <Line className="scenicScoreLineProgress" percent={this.state.ServiceScore * 20} strokeWidth="4" trailWidth="4" strokeColor="#42a6dc" trailColor="#f0f0f0" />
                    </div>
                    <div className="scenicScoreLine">
                        <div className="scenicScoreLineLabel">环境</div>
                        <Line className="scenicScoreLineProgress" percent={this.state.EnvironmentScore * 20} strokeWidth="4" trailWidth="4" strokeColor="#42a6dc" trailColor="#f0f0f0" />
                    </div>
                </div>
            </div>
        );
    }
    renderHeaderImages()
    {
        if(!this.props.detailItem || !this.props.detailItem.GetImage || !this.props.detailItem.GetImage.ImageList || this.props.detailItem.GetImage.ImageList.length === 0)
        {
            return null;
        }
        return (
            <Carousel
                className="scenicDetailHeaderImgSlider"
                afterChange = {()=>this.imageChage()}
                autoplay={true}
                dots={false}
                swiping={true}
                infinite
                selectedIndex={0}
                swipeSpeed={35}>
                {
                    this.props.detailItem.GetImage.ImageList.map((item, index)=><div key={index} className="scenicDetailHeaderImg" style={{backgroundImage: `url(${item.ImgSrc})`}}/>)
                }
            </Carousel>
        );
    }
    imageChage(){
        if(this.state.index === this.props.detailItem.GetImage.ImageList.length){
            this.setState({index:1});
        }else{
            this.setState({index:this.state.index+1});
        }
    }
    renderMapView()
    {
        if(!this.props.detailItem || !this.props.detailItem.Longitude || !this.props.detailItem.Latitude)
        {
            return null;
        }
        let width = window.innerWidth * 2;
        let imgUrl = `http://api.map.baidu.com/staticimage?width=${width}&height=250&markers=${this.props.detailItem.Longitude},${this.props.detailItem.Latitude}&markerStyles=-1,http://oe72hu540.bkt.clouddn.com/ico_pin_blu.png`;
        return (
            <img className="scenicDetailMapImg"
                src={imgUrl}
                onClick={()=>this.showElementMarkerMap()}
                alt=''/>
        );
    }
    renderBasicViews()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div className="scenicDetailInfoRows">
                <div className="scenicDetailInfoRow">
                    <div className="scenicDetailInfoLabel">地址</div>
                    <div className="scenicDetailInfoValue">{this.props.detailItem.Address}</div>
                </div>
                {
                    (this.props.detailItem.Telephone && this.props.detailItem.Telephone.length > 0)
                        ?
                        <div className="scenicDetailInfoRow">
                            <div className="scenicDetailInfoLabel">电话</div> 
                            <div className="scenicDetailInfoPhoneRow" onClick={()=> SysUtil.openTel(this.props.detailItem.Telephone)}>
                                <div className="scenicDetailInfoValue">{this.props.detailItem.Telephone}</div>
                                <div style={{flex:1}}/>
                                <img className="scenicDetailInfoPhone" src={require('../../assets/images/scenic/ico_phone_ora@2x.png')} alt=''/>
                            </div>
                        </div>
                        :null
                }
                <div className="scenicDetailInfoRow">
                    <div className="scenicDetailInfoLabel">酒店简介</div>
                    <div className="scenicDetailInfoValue scenicDetailMultDesc">{this.props.detailItem.Summary}</div>
                </div>
            </div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetDetailAction(this.props.match.params.code)).then(()=>{
            let startDate = new Date().formatDate('YYYY-MM-DD');
            let endDate = new Date().addDays(1).formatDate('YYYY-MM-DD');
            this.props.dispatch(asyncGetRoomListAction(this.props.match.params.code, startDate, endDate));
        });
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.scrollView).scrollTop;
        this.props.dispatch(setDetailScrollerPositionAction(this.props.match.params.code, offsetY));
    }
    restoreScrollViewPosition()
    {
        if(this.scrollView)
        {
            if(!isNaN(this.props.scrollViewOffset))
            {
                this.scrollView.scrollToY(this.props.scrollViewOffset);
            }
            else
            {
                this.scrollView.scrollToY(0);
            }
        }
    }
    startScoreAnimation(detailItem)
    {
        // 这样进度条可以有动画
        setTimeout(()=>{
            this.setState({
                Score: detailItem.Score ? detailItem.Score : 3.0,
                HealthScore: detailItem.HealthScore ? detailItem.HealthScore : 3.0,
                InstallationScore: detailItem.InstallationScore ? detailItem.InstallationScore : 3.0,
                ServiceScore: detailItem.ServiceScore ? detailItem.ServiceScore : 3.0,
                EnvironmentScore: detailItem.EnvironmentScore ? detailItem.EnvironmentScore: 3.0,
            });
        }, 700);
    }

    showElementMarkerMap()
    {
        this.cacheScrollOffset();
        let detailItem = this.props.detailItem;
        this.props.history.push(`/map/elementMarker/${this.props.match.params.code}/${detailItem.CnName}/${detailItem.Address}/${detailItem.Latitude}/${detailItem.Longitude}`);
    }
    onDateChooserCancel()
    {
        this.setState({showDatePicker: false});
    }
    onDateChooserConfirm(startTime, endTime)
    {
        this.setState({showDatePicker: false});
        if(startTime && endTime)
        {
            this.props.dispatch(asyncGetRoomListAction(this.props.match.params.code, startTime.formatDate('YYYY-MM-DD'), endTime.formatDate('YYYY-MM-DD')));
        }
    }
    showDateChooser()
    {
        this.setState({showDatePicker: true});
    }
    showImageBrowser()
    {
        if(!this.props.detailItem || !this.props.detailItem.GetImage || !this.props.detailItem.GetImage.ImageList || this.props.detailItem.GetImage.ImageList.length === 0)
        {
            return null;
        }

        let imageList = [];
        for (let i = 0; i < this.props.detailItem.GetImage.ImageList.length; i++) {
            let imageItem = this.props.detailItem.GetImage.ImageList[i];
            imageList.push(imageItem.ImgSrc.replace('Small', 'Large'));
        }

        this.photoBrowserView.open(imageList);
    }
    renderElementOperationView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return <ElementOperationView
            code={this.props.match.params.code}
            onNearByTapped={()=>{
                this.props.history.push({
                    pathname: '/me/near-by',
                    state: {
                        centerLat: this.props.detailItem.Latitude,
                        centerLng: this.props.detailItem.Longitude,
                    },
                });
            }}/>;
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.hotel.detailScreen[ownProps.match.params.code];
    return {
        ...obj,
        isUserLogined: SysUtil.isUserLogined(state),
    };
};

HotelDetailScreen = connect(mapStateToProps)(HotelDetailScreen);

export default HotelDetailScreen;


