import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function setSelectedTabIndexAction(index)
{
    return {
        type: IConstant.Actions.FESTIVAL_NEWS.LIST.SET_SELECTED_TAB_INDEX,
        index: index
    };
}

export function asyncGetListAction(key,pageIndex)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(key,pageIndex));
        return _fetchList(key,pageIndex).then((resp)=>{
            dispatch(_getListSuccessAction(key,pageIndex, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(key, error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.FESTIVAL_NEWS.LIST.GO_BACK,
    };
}

export function asyncDeleteListAction(key)
{
    return function(dispatch, getState)
    {
        dispatch(deleteListAction(key));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction(key)
{
    return {
        type: IConstant.Actions.FESTIVAL_NEWS.LIST.DELETE,
        key,
    };
}

export function setScrollerPositionAction(key, offset)
{
    return {
        type: IConstant.Actions.FESTIVAL_NEWS.LIST.SET_LISTVIEW_OFFSET,
        key,
        listViewOffset: offset
    };
}

function _fetchList(key,pageIndex)
{
    let searchValue;
    if (key === IConstant.FESTIVAL_STYPE_TYPE.FESTIVAL_JIEQINGHUOD)
    {
        searchValue = 143001;
    }
    else if (key === IConstant.FESTIVAL_STYPE_TYPE.FESTIVAL_SAISHI)
    {
        searchValue = 143002;
    }
    else if (key === IConstant.FESTIVAL_STYPE_TYPE.FESTIVAL_HUIZHAN)
    {
        searchValue = 143003;
    }
    else if (key === IConstant.FESTIVAL_STYPE_TYPE.FESTIVAL_YANCHU)
    {
        searchValue = 143004;
    }
    let kvDict = {
        conditionParams: {
            Page: {
                PageIndex: pageIndex,
                PageSize: IConstant.PAGE_SIZE
            },
            Search: [{
                SearchField: 'FESTIVALSTYPE',
                ConditionType: '1',
                AndOR: 'and',
                ListSearchValue: [{
                    ItemValue: searchValue ? searchValue : 0
                }]
            }]
        }
    };
    return HttpClient.postToPortal(IConstant.PortalApis.ELEMENTS.FestivalList,kvDict,null,true)
        .then((resp) => resp.json());
}

function _getListRequestAction(key,pageIndex)
{
    return {
        type: IConstant.Actions.FESTIVAL_NEWS.LIST.GET_LIST_REQUEST,
        key,
        pageIndex
    };
}

function _getListSuccessAction(key, pageIndex, result)
{
    return {
        type: IConstant.Actions.FESTIVAL_NEWS.LIST.GET_LIST_SUCCESS,
        pageIndex,
        key,
        result
    };
}

function _getListFailureAction(key, error)
{
    return {
        type: IConstant.Actions.FESTIVAL_NEWS.LIST.GET_LIST_FAILURE,
        key,
        message: error
    };
}


