import { Actions } from '../../../core/utils/IConstant';

export default function createReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.JOURNEY.CREATE.GET_CREATE_REQUEST:
        return {
            ...state,
            isFetching: true,
        };
    case Actions.JOURNEY.CREATE.GET_CREATE_SUCCESS:
        return {
            ...state,
            isFetching:false,
            dataList:action.resp
        };
    case Actions.JOURNEY.CREATE.GET_CREATE_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.JOURNEY.CREATE.GET_MDTYPES_REQUEST:
        return {
            ...state,
            isFetching: true,
        };
    case Actions.JOURNEY.CREATE.GET_MDTYPES_SUCCESS:
        return {
            ...state,
            isFetching:false,
            mdTypes:action.resp
        };
    case Actions.JOURNEY.CREATE.GET_MDTYPES_FAILURE:
        return {
            ...state,
            isFetching: false,
        };
    case Actions.JOURNEY.CREATE.UPDATE_MDTYPES:
        return {
            ...state,
            mdTypes: action.mdTypes
        };
    case Actions.JOURNEY.CREATE.GO_BACK:
        return {};
    default:
        return {
            ...state
        };
    }
}


