import { Actions } from '../../../core/utils/IConstant';

export default function detailReducer(state = {}, action)
{
    let obj = state[action.id];    
    switch(action.type)
    {
    case Actions.TRAVEL_NOTES.DETAIL.GET_DETAIL_REQUEST:
        return {
            ...state,
            [action.id]: {
                ...obj,                
                isFetching: true,
            }
        };
    case Actions.TRAVEL_NOTES.DETAIL.GET_DETAIL_SUCCESS:
        return {
            ...state,
            [action.id]: {
                ...obj,                
                isFetching: false,
                detailItem: action.detailItem,
                detailItems: action.detailItems
            },
        };
    case Actions.TRAVEL_NOTES.DETAIL.GET_DETAIL_FAILURE:
        return {
            ...state,
            [action.id]: {
                ...obj,                
                isFetching: false,
            }
        };
    case Actions.TRAVEL_NOTES.DETAIL.SEND_COMMENT_REQUEST:
        let _obj = {...obj};
        delete _obj.successMessage;
        return {
            ...state,
            [action.id]: {
                ..._obj,
                isFetching: true,
            },
        };
    case Actions.TRAVEL_NOTES.DETAIL.SEND_COMMENT_SUCCESS:
        return {
            ...state,
            [action.id]: {
                ...obj,
                isFetching: false,
                successMessage: '回帖发表成功，等待审核通过后显示回帖内容！',
            },
        };
    case Actions.TRAVEL_NOTES.DETAIL.SEND_COMMENT_FAILURE:
        return {
            ...state,
            [action.id]: {
                ...obj,
                isFetching: false,
            },
        };
    case Actions.TRAVEL_NOTES.DETAIL.GET_COMMENT_REQUEST:
        return {
            ...state,
            [action.id]: {
                ...obj,
            },
        };
    case Actions.TRAVEL_NOTES.DETAIL.GET_COMMENT_SUCCESS:
        return {
            ...state,
            [action.id]: {
                ...obj,
                commentList: action.results,
            },
        };
    case Actions.TRAVEL_NOTES.DETAIL.GET_COMMENT_FAILURE:
        return {
            ...state,
            [action.id]: {
                ...obj,
            },
        };
    case Actions.TRAVEL_NOTES.DETAIL.GO_BACK:
        let _state = {...state};
        delete _state[action.id];
        return {
            ..._state
        };
    default:
        return state;
    }
}


