import { Actions } from '../../core/utils/IConstant';

export function setWindowSizeAction()
{
    return {
        type: Actions.NATIVE.WINDOW_SIZE_CHANGED,
        windowSize: {
            width: window.innerWidth,
            height: window.innerHeight,
        }
    };
}

export function setConfigItemAction(configItem)
{
    return {
        type: Actions.NATIVE.SET_CONFIG_ITEM,
        configItem
    };
}


