import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Button, Toast, Modal, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/me.css';
import HttpClient from '../../core/utils/HttpClient';
import SysUtil from '../../core/utils/SysUtil';
import * as IConstant from '../../core/utils/IConstant';

class FindPasswordAndRegistScreen extends PureComponent {
    type = '0'; //0 重置密码  1 注册
    phoneNumber = '';
    checkCode = '';
    inputCheckCode = '';
    pwd1 = '';
    pwd2 = '';
    constructor(props) {
        super(props);
        this.goBack = this.goBack.bind(this);
        this.handlePhoneNumberChange = this.handlePhoneNumberChange.bind(this);
        this.resetPwd = this.resetPwd.bind(this);
        this.register = this.register.bind(this);
        this.scheduleTimer = this.scheduleTimer.bind(this);
        this.handlePasswordChange1 = this.handlePasswordChange1.bind(this);
        this.handlePasswordChange2 = this.handlePasswordChange2.bind(this);
        this.state = {
            step: '1',
            checkCode: '',
            timeCount: 60,  //计数器
            isTimerTicking: false,
            isFetching: false,
        };
    }

    componentDidMount()
    {
        this.type = this.props.match.params.type;
    }
    handlePhoneNumberChange(event) {
        if (this.state.step === '1') {
            this.phoneNumber = event.target.value;
        } else if (this.state.step === '2') {
            this.inputCheckCode = event.target.value;
        }

    };

    handlePasswordChange1(event) {
        this.pwd1 = event.target.value;
    }

    handlePasswordChange2(event) {
        this.pwd2 = event.target.value;
    }

    render() {
        let title = '重置密码';
        if (this.type === '0') {
            title = '重置密码';
        } else {
            title = '注册';
        }
        let info = '手机号';
        let buttonContent = '下一步，验证手机号码';
        let inputPlaceholder = '请输入手机号';
        if (this.state.step === '1') {
            info = '手机号';
            buttonContent = '下一步，验证手机号码';
            inputPlaceholder = '请输入手机号';
        } else if (this.state.step === '2') {
            info = '验证码';
            buttonContent = '下一步，设置密码';
            inputPlaceholder = '请输入验证码';
        } else if (this.state.step === '3') {
            info = '密码';
            buttonContent = '完成';
            inputPlaceholder = '请输入密码';
        }
        return (
            <div className="whiteTopNavBar">
                <NavBar mode="light"
                    className="navBar"
                    icon={<Icon type="left" />}
                    leftContent='返回'
                    onLeftClick={this.goBack}>
                    {title + this.state.step + '/3'}
                </NavBar>
                <Content>
                    <div className='findPasswordContent'>
                        {
                            this.state.step === '2' ?
                                <div style={{ alignSelf: 'center' }}>验证手机 {this.phoneNumber}</div>
                                : null
                        }
                        {
                            this.state.step === '3' ?
                                this.renderStep3()
                                :
                                (
                                    <div className='findPasswordInput'>
                                        <div style={{position: 'relative'}}>
                                            <span className='findPasswordInputInfo'>{info}</span>
                                            <input
                                                className='findPasswordInputPhone'
                                                style={{width:this.state.step === '2'?'150px':'auto'}}
                                                type='number'
                                                placeholder={inputPlaceholder}
                                                onChange={this.handlePhoneNumberChange}
                                                ref="myTextInput" />
                                        </div>
                                        {
                                            this.state.step === '2' ?
                                                <div
                                                    className='sendCodeButton'
                                                    style={{ color: this.state.isTimerTicking ? '#999999' : '#58c0e7', borderColor: this.state.isTimerTicking ? '#999999' : '#58c0e7' }}
                                                    onClick={() => this.sendCode()}>
                                                    {this.state.isTimerTicking ? '重新获取(' + this.state.timeCount + 's)' : '发送验证码'}
                                                </div>
                                                : null
                                        }
                                    </div>
                                )
                        }
                        <Button style={{ margin: '10px 10px 0px 10px', background: '#58c0e7' }} type="primary" onClick={() => this.submit()}>{buttonContent}</Button>
                    </div>
                    {
                        this.state.isFetching
                            ?
                            <ActivityIndicator
                                toast
                                text="Loading..." />
                            : null
                    }
                </Content>
            </div>
        );
    }

    renderStep3() {
        return (
            <div>
                <div className='findPasswordInput' style={{justifyContent:'flex-start'}}>
                    <span className='findPasswordInputInfo'>密码</span>
                    <input
                        className='findPasswordInputPhone'
                        type='password'
                        placeholder='请输入密码'
                        onChange={this.handlePasswordChange1} />
                </div>
                <div className='findPasswordInput' style={{justifyContent:'flex-start'}}>
                    <span className='findPasswordInputInfo'>确认密码</span>
                    <input
                        className='findPasswordInputPhone'
                        type='password'
                        placeholder='请再次输入密码'
                        onChange={this.handlePasswordChange2} />
                </div>
            </div>
        );
    }
    goBack() {
        this.props.history.goBack();
    }
    submit() {
        if (this.state.step === '1') {
            if (this.phoneNumber.length === 0) {
                Toast.show('请输入手机号', 1);
                return;
            }
            if (this.phoneNumber.length !== 11) {
                Toast.show('请输入11位手机号', 1);
                return;
            }
            //验证手机号是否已注册
            let kvDict = {
                phone: this.phoneNumber,
            };
            this.setState({isFetching: true});
            HttpClient.postToPortal(IConstant.PortalApis.ME.IsRegister, kvDict, null, true).then((resp) => resp.json()).then((resp) => {
                this.setState({isFetching: false});
                if (this.type === '0') { //重置密码
                    if (resp && resp.status === '0' && resp.results && resp.results.length > 0) { //未注册
                        //进入第2步
                        this.setState({
                            step: '2'
                        });
                        this.refs.myTextInput.value = '';
                    } else { //已注册
                        Modal.alert('提示', '您的手机号码未注册', [
                            { text: 'OK', style: 'default' },
                        ]);
                    }
                } else { // 注册
                    if (resp && resp.status === '0' && resp.results && resp.results.length > 0) { //未注册
                        Modal.alert('提示', '您的手机号码已注册', [
                            { text: 'OK', style: 'default' },
                        ]);
                    } else { //已注册
                        //进入第2步
                        this.setState({
                            step: '2'
                        });
                        this.refs.myTextInput.value = '';
                    }
                }
            });
        } else if (this.state.step === '2') {
            if (this.inputCheckCode.length === 0) {
                Toast.show('请输入验证码', 1);
            } else {
                if (this.inputCheckCode === this.checkCode) {
                    //进入第3步
                    this.setState({
                        step: '3'
                    });
                    this.refs.myTextInput.value = '';
                } else {
                    Toast.show('验证码错误！', 1);
                }
            }
        } else if (this.state.step === '3') {
            if (this.pwd1.length === 0) {
                Toast.show('请输入密码', 1);
                return;
            }

            if (this.pwd2.length === 0) {
                Toast.show('请再次输入密码', 1);
                return;
            }

            if (this.pwd1 !== this.pwd2) {
                Toast.show('两次密码输入不一致!', 1);
                return;
            }

            if (this.type === '0') {
                this.resetPwd();
            } else {
                this.register();
            }
        }
    }

    //重置密码
    resetPwd()
    {
        let kvDict = {
            phone: this.phoneNumber,
            pwd: this.pwd1
        };
        this.setState({isFetching: true});
        HttpClient.postToPortal(IConstant.PortalApis.ME.ResetPwd, kvDict, null, true).then((resp) => resp.json()).then((resp) => {
            this.setState({isFetching: false});
            if (resp.status === '0')
            {
                Modal.alert('提示', '密码重置成功！', [
                    { text: 'OK', style: 'default', onPress: () => this.goBack() },
                ]);
            }
            else
            {
                Modal.alert('提示', '密码重置失败', [
                    { text: 'OK', style: 'default' },
                ]);
            }
        });
    }

    //注册
    register()
    {
        let kvDict = {
            phone: this.phoneNumber,
            pwd: this.pwd1,
            email: ''
        };
        this.setState({isFetching: true});
        HttpClient.postToPortal(IConstant.PortalApis.ME.Register,kvDict,null,true).then((resp) => resp.json()).then((resp) => {
            this.setState({isFetching: false});
            if (resp.status === '0') {
                Modal.alert('提示', '注册成功！', [
                    { text: 'OK', style: 'default', onPress: () => this.goBack() },
                ]);
            } else {
                Modal.alert('提示', '注册失败', [
                    { text: 'OK', style: 'default' },
                ]);
            }
        });
    }

    sendCode() {
        if (!this.state.isTimerTicking)
        {
            this.checkCode = SysUtil.RndNum(5);

            let kvDict = {
                phone: this.phoneNumber,
                type: '1',
                code: this.checkCode,
            };
            this.setState({isFetching: true});
            HttpClient.postToPortal(IConstant.PortalApis.ME.SendMessage, kvDict, null, true).then((resp) => resp.json()).then((resp) => {
                this.setState({isFetching: false});
                if (resp.status === '0')
                {
                    Toast.show('验证码已发送', 1);
                    this.setState({
                        timeCount: 60,
                        isTimerTicking: true
                    });
                    this.scheduleTimer();
                }
                else
                {
                    Toast.show('验证码发送失败', 1);
                }
            });
        }
    }

    scheduleTimer() {
        this.setState({ timeCount: (this.state.timeCount - 1) });

        if (this.state.timeCount > 0) {
            setTimeout(this.scheduleTimer, 1000);
        }
        else {
            this.setState({ isTimerTicking: false });
        }
    }

    componentWillUnmount() {
        this.setState({ timeCount: 60, isTimerTicking: false });
    }
}

FindPasswordAndRegistScreen = connect()(FindPasswordAndRegistScreen);

export default FindPasswordAndRegistScreen;


