import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function mineListReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.JOURNEY.INDEX.GET_MINE_LIST_REQUEST:
        return {
            isFetching: true,
            isRefresh:false,
        };
    case Actions.JOURNEY.INDEX.GET_MINE_LIST_SUCCESS:
        return {
            isFetching: false,
            dataList: action.dataList,
            isRefresh:false,
        };
    case Actions.JOURNEY.INDEX.GET_MINE_LIST_FAILURE:
        return {
            isFetching: false,
            isRefresh:false,
        };
    case Actions.JOURNEY.DETAIL.COPY_TO_MINE_SUCCESS:       // 行程拷贝
    case Actions.JOURNEY.INDEX.BACK:
        return {};
    case Actions.JOURNEY.INDEX.SET_LIST_REFRESH:
        return {
            isRefresh:action.isRefresh
        };
    default:
        return {
            ...state
        };
    }
}

function recommendListReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.JOURNEY.INDEX.GET_RECOMMEND_LIST_REQUEST:
        return {
            isFetching: true,
        };
    case Actions.JOURNEY.INDEX.GET_RECOMMEND_LIST_SUCCESS:
        return {
            isFetching: false,
            dataList: action.dataList,
        };
    case Actions.JOURNEY.INDEX.GET_RECOMMEND_LIST_FAILURE:
        return {
            isFetching: false,
        };
    case Actions.JOURNEY.INDEX.GO_BACK:
        return {};
    default:
        return {
            ...state
        };
    }
}

function scrollViewOffsetReducer(state = 0, action)
{
    switch(action.type)
    {
    case Actions.JOURNEY.INDEX.SET_SCROLL_OFFSET:
        return action.scrollViewOffset;
    default:
        return state;
    }
}

const indexReducer = combineReducers({
    mineList: mineListReducer,
    recommendList: recommendListReducer,
    scrollViewOffset: scrollViewOffsetReducer,
});

export default indexReducer;


