import * as IConstant from '../../../core/utils/IConstant';

// 添加订单点评成功
export function addOrderCommentsSuccessAction(orderNo)
{
    return {
        type: IConstant.Actions.COMMENTS.ADD_ORDER_COMMENTS_SUCCESS,
        orderNo,
    };
}


