import * as IConstant from '../../core/utils/IConstant';
import { _getUserLocationSuccessAction, _getUserLocationFailureAction } from '../../store/actions/location/locationAction';
import WebDB from './WebDB';
import SysUtil from './SysUtil';

export default class LocationUtil
{
    static getCurrentUserLocation(state,dispatch,isUpdate)
    {
        let isFromNativeApp = SysUtil.getUrlParameter('isFromNativeApp');
        isFromNativeApp = isFromNativeApp || (state.native && state.native.isFromNativeApp);
        if (isFromNativeApp && window.postMessage)
        {
            //从 native 获取数据
            window.postMessage(JSON.stringify({
                type: IConstant.NATIVE_MESSAGE_TYPE.GET_LOCATION,
                data: {}
            }), '*');
            return;
        }
        //不强制刷新 定位
        if (!isUpdate)
        {
            let locationInfo = SysUtil.getLocation(state);
            LocationUtil.save(dispatch,locationInfo);
            return;
        }
        //定位
        let geolocation = new window.BMap.Geolocation();
        geolocation.getCurrentPosition(function (r) {
            if (this.getStatus() === window.BMAP_STATUS_SUCCESS) {
                console.log('定位成功！');
                let address = r['address'];
                let addressStr: string = '';
                let lat = r.point.lat;
                let lng = r.point.lng;
                if (address) {
                    if (address['province'] && address['province'].length > 0) {
                        addressStr = address['province'];
                    }
                    if (address['city'] && address['city'].length > 0) {
                        addressStr += address['city'];
                    }
                }
                LocationUtil.save(dispatch,{
                    lat,
                    lng,
                    address: addressStr,
                });
            }
            else {
                console.log('定位失败');
                dispatch(_getUserLocationFailureAction('定位失败'));
            }
        }, { enableHighAccuracy: true, timeout: 100 });
    }

    static save(dispatch,info)
    {
        // 存储到localstorage
        WebDB.setItem(IConstant.STORAGE_KEY.USER_LOCATION, info);
        dispatch(_getUserLocationSuccessAction(info.lat, info.lng, info.address ? info.address : ''));
    }
}



