import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, SegmentedControl } from 'antd-mobile';
import Content from '../../views/common/Content';
import FoodRecommendListView from '../../views/food/FoodRecommendListView';
import {  
    goBackAction
} from '../../store/actions/food/recommendListAction';
import '../../assets/css/food.css';
import LeftContentView from '../../views/common/LeftContentView';

class FoodRecommendListScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
        this.showCommonList = this.showCommonList.bind(this);
    }
    render()
    {
        let listViewHeight = window.innerHeight - 45;
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    leftContent={<LeftContentView
                        ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                        goBackAction={goBackAction}
                        history={this.props.history}
                    />}
                    onLeftClick={() => {
                        this.LeftContentView.goHome();
                    }}>
                    <SegmentedControl values={['非吃不可', '全部美食']} 
                        tintColor="white" 
                        selectedIndex={0}
                        onValueChange={this.showCommonList}/>
                </NavBar>
                <Content>
                    <FoodRecommendListView ref={(listView)=>{
                        if(listView)
                        {
                            this.listView = listView.getWrappedInstance();
                        }
                    }}
                    onItemClick={this.onItemClick}
                    height={listViewHeight}/>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item || !item.GetRelation || !item.GetRelation.Code)
        {
            return;
        }
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        this.props.history.push(`/food/detail/${item.GetRelation.Code}`);
    }
    showCommonList()
    {
        if(this.listView)
        {
            this.listView.cacheScrollOffset();
        }
        // this.props.history.push('/food/list?segment=true');
        this.props.history.push('/restaurant/list?segment=true');
    }
}

FoodRecommendListScreen = connect()(FoodRecommendListScreen);

export default FoodRecommendListScreen;


