import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar } from 'antd-mobile';
import Content from '../../views/common/Content';
import TravelNotesListView from '../../views/travel-notes/TravelNotesListView';
import { setSelectedTabIndexAction, goBackAction } from '../../store/actions/travel-notes/listAction';
import '../../assets/css/travel-notes.css';
import LeftContentView from '../../views/common/LeftContentView';

class TravelNotesListScreen extends PureComponent
{
    tabs = [
        {title: '热门游记', sortBy: '1'},
        {title: '精华游记', sortBy: '2'},
        {title: '全部游记', sortBy: '0'},
    ];
    constructor(props)
    {
        super(props);
        this.onItemClick = this.onItemClick.bind(this);
    }
    render()
    {
        let listViewHeight = window.innerHeight - (45 + 44);

        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    leftContent={<LeftContentView
                        ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                        goBackAction={goBackAction}
                        history={this.props.history}
                    />}
                    onLeftClick={() => {
                        this.LeftContentView.goHome();
                    }}>
                    游记
                </NavBar>
                <Content>
                    <Tabs tabs={this.tabs}
                        initialPage={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(index));
                        }}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        {
                            this.tabs.map((item, index)=><TravelNotesListView key={index} 
                                sortBy={item.sortBy}
                                ref={(listView)=>{
                                    if(listView)
                                    {
                                        this.tabs[index].listView = listView.getWrappedInstance();
                                    }
                                }}
                                onItemClick={this.onItemClick}
                                height={listViewHeight}/>)
                        }
                    </Tabs>
                </Content>
            </div>
        );
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView: TravelNotesListView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push(`${this.props.match.url}/detail/${item.ID}`);
    }
}

const mapStateToProps = function(state)
{
    return {
        selectedTabIndex: state.travelNotes.listScreen.selectedTabIndex,
    };
};

TravelNotesListScreen = connect(mapStateToProps)(TravelNotesListScreen);

export default TravelNotesListScreen;


