import TravelNotesDetailScreen from './screens/travel-notes/TravelNotesDetailScreen';
import FoodRecommendListScreen from './screens/food/FoodRecommendListScreen';
import FoodDetailScreen from './screens/food/FoodDetailScreen';
import RestaurantListScreen from './screens/restaurant/RestaurantListScreen';
import RestaurantDetailScreen from './screens/restaurant/RestaurantDetailScreen';
import SpecialtyListScreen from './screens/specialty/SpecialtyListScreen';
import SpecialtyRecommendListScreen from './screens/specialty/SpecialtyRecommendListScreen';
import SpecialtyDetailScreen from './screens/specialty/SpecialtyDetailScreen';
import FunListScreen from './screens/fun/FunListScreen';
import FunRecommendListScreen from './screens/fun/FunRecommendListScreen';
import FunDetailScreen from './screens/fun/FunDetailScreen';
import NearByListScreen from './screens/me/NearByListScreen';
// import ElementMarkerScreen from './screens/map/ElementMarkerScreen';
import MapNavScreen from './screens/map/MapNavScreen';
// import ElementMapScreen from './screens/map/ElementMapScreen';
import LoginScreen from './screens/me/LoginScreen';
import MineScreen from './screens/me/MineScreen';
import UpdateUserInfoScreen from './screens/me/UpdateUserInfoScreen';
import ShoppingRecommendListScreen from './screens/shopping/ShoppingRecommendListScreen';
import ShoppingListScreen from './screens/shopping/ShoppingListScreen';
import ShoppingDetailScreen from './screens/shopping/ShoppingDetailScreen';
import FarmHouseListScreen from './screens/farm-house/FarmHouseListScreen';
import FarmHouseDetailScreen from './screens/farm-house/FarmHouseDetailScreen';
import TravelAgentListScreen from './screens/travel-agent/TravelAgentListScreen';
import TravelAgentDetailScreen from './screens/travel-agent/TravelAgentDetailScreen';
// import OverviewListScreen from './screens/overview/OverviewListScreen';
// import OverviewDetailScreen from './screens/overview/OverviewDetailScreen';
import FindPasswordAndRegistScreen from './screens/me/FindPasswordAndRegistScreen';
// import TrafficListScreen from './screens/traffic/TrafficListScreen';
import SignScreen from './screens/sign/SignScreen';
// import TrafficAirScreen from './screens/traffic/TrafficAirScreen';
// import TrafficTrainScreen from './screens/traffic/TrafficTrainScreen';
// import TrafficBusScreen from './screens/traffic/TrafficBusScreen';
// import TrafficLocalScreen from './screens/traffic/TrafficLocalScreen';

// import JourneyIndexScreen from './screens/journey/JourneyIndexScreen';
// import JourneyDetailScreen from './screens/journey/JourneyDetailScreen';
// import CreateJourneyScreen from './screens/journey/CreateJourneyScreen';
// import JourneyMapLineScreen from './screens/journey/JourneyMapLineScreen';
import JourneyEditScreen from './screens/journey/JourneyEditScreen';
// import JourneyListScreen from './screens/journey/JourneyListScreen';
// import JourneyAddPointScreen from './screens/journey/JourneyAddPointScreen';

// import FootmarkListScreen from './screens/footmark/FootmarkListScreen';
// import FootmarkDetailScreen from './screens/footmark/FootmarkDetailScreen';
import CreateFootmarkScreen from './screens/footmark/CreateFootmarkScreen';

import OrderListScreen from './screens/order/OrderListScreen';
import OrderDetailScreen from './screens/order/OrderDetailScreen';
import OrderBookingScreen from './screens/order/OrderBookingScreen';
import OrderBookingVisitorListScreen from './screens/order/OrderBookingVisitorListScreen';
import UpdateUserInfoItemScreen from './screens/me/UpdateUserInfoItemScreen';
import OrderBookingVisitorAddScreen from './screens/order/OrderBookingVisitorAddScreen';
import WeatherScreen from './screens/weather/WeatherScreen';
import OrderCommentsScreen from './screens/order/OrderCommentsScreen';
import CommentsListScreen from './screens/comments/CommentsListScreen';
import CollectListScreen from './screens/me/CollectListScreen';
import ForumListScreen from './screens/forum/ForumListScreen';
import ForumDetailScreen from './screens/forum/ForumDetailScreen';
import FriendListScreen from './screens/userProfile/FriendListScreen';
import FriendProfileScreen from './screens/userProfile/FriendProfileScreen';
import FriendInfoScreen from './screens/userProfile/FriendInfoScreen';
import PostForumScreen from './screens/forum/PostForumScreen';

// 异步组件
import './assets/css/traffic.css';
import './assets/css/overview.css';
import './assets/css/scenic.css';
import './assets/css/map.css';
import './assets/css/footmark.css';
import './assets/css/journey.css';

const AsyncOverviewListScreen = asyncScreen(
    () => import('./screens/overview/OverviewListScreen'),
    '禹州概览'
);
const AsyncOverviewDetailScreen = asyncScreen(
    () => import('./screens/overview/OverviewDetailScreen'),
);
const AsyncScenicListScreen = asyncScreen(
    () => import('./screens/scenic/ScenicListScreen'),
    '景区列表'
);
const AsyncScenicDetailScreen = asyncScreen(
    () => import('./screens/scenic/ScenicDetailScreen'),
    '景区详情',
);
const AsyncSceneryListScreen = asyncScreen(
    () => import('./screens/scenic/SceneryListScreen'),
    '景点列表',
);
const AsyncSceneryDetailScreen = asyncScreen(
    () => import('./screens/scenic/SceneryDetailScreen'),
    '景点详情',
);
const AsyncScenicRecommendListScreen = asyncScreen(
    () => import('./screens/scenic/ScenicRecommendListScreen'),
    '非游不可',
);
const AsyncElementMapScreen = asyncScreen(
    () => import('./screens/map/ElementMapScreen')
);
const AsyncSNJElementMapScreen = asyncScreen(
    () => import('./screens/map/SNJElementMapScreen')
);
const AsyncElementMarkerScreen = asyncScreen(
    () => import('./screens/map/ElementMarkerScreen')
);
const AsyncChooseAddressScreen = asyncScreen(
    () => import('./screens/map/ChooseAddressScreen')
);

// 足迹
const AsyncFootmarkListScreen = asyncScreen(
    () => import('./screens/footmark/FootmarkListScreen'),
    '足迹'
);
const AsyncFootmarkDetailScreen = asyncScreen(
    () => import('./screens/footmark/FootmarkDetailScreen'),
    '足迹'
);
const AsyncMyFootmarkListScreen = asyncScreen(
    () => import('./screens/footmark/MyFootmarkListScreen'),
    '我的足迹'
);

// const AsyncCreateFootmarkScreen = asyncScreen(
//     () => import('./screens/footmark/CreateFootmarkScreen'),
//     '发布足迹'
// );

// 行程
const AsyncJourneyIndexScreen = asyncScreen(
    () => import('./screens/journey/JourneyIndexScreen'),
    '我的行程'
);
const AsyncJourneyDetailScreen = asyncScreen(
    () => import('./screens/journey/JourneyDetailScreen'),
    '行程详情'
);
const AsyncCreateJourneyScreen = asyncScreen(
    () => import('./screens/journey/CreateJourneyScreen'),
    '创建行程'
);
const AsyncJourneyMapLineScreen = asyncScreen(
    () => import('./screens/journey/JourneyMapLineScreen'),
    '行程地图'
);
// const AsyncJourneyEditScreen = asyncScreen(
//     () => import('./screens/journey/JourneyEditScreen'),
//     '编辑行程'
// );
const AsyncJourneyListScreen = asyncScreen(
    () => import('./screens/journey/JourneyListScreen'),
    '我的行程'
);
const AsyncJourneyAddPointScreen = asyncScreen(
    () => import('./screens/journey/JourneyAddPointScreen'),
    '新增兴趣点'
);

let Routes = (props)=><Router history={props.history}>
    <Switch>
        <Route exact path="/" component={HomeScreen}/>
        <Route exact path="/scenic/list" component={AsyncScenicListScreen}/>
        <Route exact path="/scenic/recommend-list" component={AsyncScenicRecommendListScreen}/>
        <Route exact path="/scenic/detail/:code" component={AsyncScenicDetailScreen}/>
        <Route exact path="/scenic/scenery-list/:code" component={AsyncSceneryListScreen}/>
        <Route exact path="/scenic/scenery-detail/:scenicCode/:sceneryCode" component={AsyncSceneryDetailScreen}/>
        <Route exact path="/hotel/list" component={HotelListScreen}/>
        <Route exact path="/hotel/detail/:code" component={HotelDetailScreen}/>
        <Route exact path="/hotel/recommend-list" component={HotelRecommendListScreen}/>
        <Route exact path="/news" component={NewsListScreen}/>
        <Route exact path="/common-news" component={CommonNewsListScreen}/>
        <Route exact path="/news/detail/:code" component={NewsDetailScreen}/>
        <Route exact path="/festival-news" component={FestivalNewsListScreen}/>
        <Route exact path="/festival-news/detail/:code" component={FestivalNewsDetailScreen}/>
        <Route exact path="/line" component={LineListScreen}/>
        <Route exact path="/line/detail/:code" component={LineDetailScreen}/>
        <Route exact path="/ticketPurchase" component={TicketPurchaseScreen} />
        <Route exact path="/ticketPurchase/result" component={PayResultScreen} />
        <Route exact path="/travel-notes" component={TravelNotesListScreen}/>
        <Route exact path="/travel-notes/detail/:id" component={TravelNotesDetailScreen}/>
        <Route exact path="/food/recommend-list" component={FoodRecommendListScreen}/>
        <Route exact path="/food/detail/:code" component={FoodDetailScreen}/>
        <Route exact path="/restaurant/list" component={RestaurantListScreen}/>
        <Route exact path="/restaurant/detail/:code" component={RestaurantDetailScreen}/>
        <Route exact path="/specialty/list" component={SpecialtyListScreen}/>
        <Route exact path="/specialty/recommend-list" component={SpecialtyRecommendListScreen}/>
        <Route exact path="/specialty/detail/:code" component={SpecialtyDetailScreen}/>
        <Route exact path="/fun/list" component={FunListScreen}/>
        <Route exact path="/fun/recommend-list" component={FunRecommendListScreen}/>
        <Route exact path="/fun/detail/:code" component={FunDetailScreen}/>
        <Route exact path="/me/near-by" component={NearByListScreen}/>
        <Route exact path="/map/elementMarker/:code/:name/:address/:lat/:lng" component={AsyncElementMarkerScreen}/>
        <Route exact path="/me/login" component={LoginScreen}/>
        <Route exact path="/map/mapNav/:code/:toLat/:toLng" component={MapNavScreen}/>
        <Route exact path="/map/elementMap" component={AsyncElementMapScreen}/>
        <Route exact path="/map/snjelementMap" component={AsyncSNJElementMapScreen}/>
        <Route exact path="/map/chooseAddress/:address/:lat/:lng" component={AsyncChooseAddressScreen}/>
        
        <Route exact path="/me/mine" component={MineScreen}/>
        <Route exact path="/me/updateUserInfo" component={UpdateUserInfoScreen}/>
        <Route exact path="/me/updateUserInfoItem" component={UpdateUserInfoItemScreen}/>
        <Route exact path="/me/findPasswordAndRegist/:type" component={FindPasswordAndRegistScreen}/>
        <Route exact path="/me/collectList" component={CollectListScreen}/>
        
        <Route exact path="/shopping/list" component={ShoppingListScreen}/>
        <Route exact path="/shopping/recommend-list" component={ShoppingRecommendListScreen}/>
        <Route exact path="/shopping/detail/:code" component={ShoppingDetailScreen}/>
        <Route exact path="/farm-house/list" component={FarmHouseListScreen}/>
        <Route exact path="/farm-house/detail/:code" component={FarmHouseDetailScreen}/>
        <Route exact path="/travel-agent/list" component={TravelAgentListScreen}/>
        <Route exact path="/travel-agent/detail/:code" component={TravelAgentDetailScreen}/>
        <Route exact path="/overview" component={AsyncOverviewListScreen}/>
        <Route exact path="/overview/detail/:index" component={AsyncOverviewDetailScreen}/>
        <Route exact path="/traffic" component={AsyncTrafficListScreen}/>
        <Route exact path="/traffic/air" component={AsyncTrafficAirScreen}/>
        <Route exact path="/traffic/train" component={AsyncTrafficTrainScreen}/>
        <Route exact path="/traffic/bus" component={AsyncTrafficBusScreen}/>
        {/* <Route exact path="/traffic/local" component={AsyncTrafficLocalScreen}/>                     */}
        <Route exact path="/sign" component={SignScreen}/>
        <Route exact path="/journey" component={AsyncJourneyIndexScreen}/>
        <Route exact path="/journey/detail/:journeyId" component={AsyncJourneyDetailScreen}/>
        <Route exact path="/journey/create" component={AsyncCreateJourneyScreen}/>
        <Route exact path="/journey/mapLine" component={AsyncJourneyMapLineScreen}/>
        <Route exact path="/journey/edit" component={JourneyEditScreen}/>
        <Route exact path="/journey/list" component={AsyncJourneyListScreen} />
        <Route exact path="/journey/addPoint" component={AsyncJourneyAddPointScreen}/>

        
        <Route exact path="/footmark/list" component={AsyncFootmarkListScreen}/>
        <Route exact path="/footmark/detail" component={AsyncFootmarkDetailScreen}/>
        <Route exact path="/footmark/create" component={CreateFootmarkScreen}/>
        <Route exact path="/footmark/myList" component={AsyncMyFootmarkListScreen}/>

        <Route exact path="/order/list" component={OrderListScreen} />
        <Route exact path="/order/detail" component={OrderDetailScreen}/>
        {/* shopCode: 景区code，productCode: 门票code，orderType: 1:门票，2:酒店, 3:线路 */}
        <Route exact path="/order/booking/:shopCode/:productCode/:orderType" component={OrderBookingScreen} />
        <Route exact path="/order/comments/:shopCode/:orderNo/:orderType" component={OrderCommentsScreen} />
        <Route exact path="/order/booking/visitor" component={OrderBookingVisitorListScreen}/>
        <Route exact path="/order/booking/visitor/add" component={OrderBookingVisitorAddScreen}/>
        <Route exact path="/weather" component={WeatherScreen}/>
        <Route exact path="/comments/:code" component={CommentsListScreen}/>

        <Route exact path="/forum/list" component={ForumListScreen}/>
        <Route exact path="/forum/detail/:code" component={ForumDetailScreen}/>
        <Route exact path="/forum/post" component={PostForumScreen}/>

        <Route exact path="/userProfile/friendList" component={FriendListScreen}/>
        <Route exact path="/userProfile/friendProfile" component={FriendProfileScreen}/>
        <Route exact path="/userProfile/friendInfo" component={FriendInfoScreen}/>
        
        {/* 如果 url 不存在，则重定向到根目录 */}
        <Redirect to="/"/>
    </Switch>
</Router>;

export default Routes;


