import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';
import SysUtil from '../../../core/utils/SysUtil';

export function setSelectedTabIndexAction(index)
{
    return {
        type: IConstant.Actions.TRAVEL_AGENT.LIST.SET_SELECTED_TAB_INDEX,
        index: index
    };
}

export function asyncGetListAction(orderFiled, orderType, pageIndex, searchKey, extraParams)
{
    return function(dispatch, getState)
    {
        dispatch(_getListRequestAction(orderFiled, orderType, pageIndex, searchKey, extraParams));
        return _fetchList(orderFiled, orderType, pageIndex, searchKey, extraParams).then((resp)=>{
            dispatch(_getListSuccessAction(orderFiled, orderType, pageIndex, searchKey, extraParams, resp));
        }).catch((error)=>{
            dispatch(_getListFailureAction(orderFiled, orderType, extraParams, error));
        });
    };
}

export function goBackAction()
{
    return {
        type: IConstant.Actions.TRAVEL_AGENT.LIST.GO_BACK,
    };
}

export function asyncDeleteListAction(orderFiled, orderType, extraParams)
{
    return function(dispatch, getState)
    {
        dispatch(deleteListAction(orderFiled, orderType, extraParams));
        return new Promise((resolve)=>{
            resolve();
        });
    };
}

export function deleteListAction(orderFiled, orderType, extraParams)
{
    return {
        type: IConstant.Actions.TRAVEL_AGENT.LIST.DELETE,
        orderFiled, 
        orderType,
        extraParams,
    };
}

export function setKeywordAction(keyword)
{
    return {
        type: IConstant.Actions.TRAVEL_AGENT.LIST.SET_KEYWORD,
        keyword: keyword
    };
}

export function setScrollerPositionAction(orderFiled, orderType, offset, extraParams)
{
    return {
        type: IConstant.Actions.TRAVEL_AGENT.LIST.SET_LISTVIEW_OFFSET,
        orderFiled, 
        orderType,
        listViewOffset: offset,
        extraParams,
    };
}

function _fetchList(orderFiled, orderType, pageIndex, searchKey, extraParams)
{
    let conditionParams = {
        Order: orderFiled.split(',').map((_orderFiled, index)=>{
            return {
                OrderField: _orderFiled,
                OrderType: orderType,
            };
        }),
        Page: {
            PageIndex: pageIndex,
            PageSize: IConstant.PAGE_SIZE,
        },
        Search: [],
    };
    if (searchKey && searchKey.length > 0)
    {
        let search = {
            AndOR: 'and',
            SearchField: 'cnName',
            ConditionType: '1',
            ListSearchValue:[{
                ItemValue: searchKey
            }]
        };
        conditionParams.Search.push(search);
    }
    let location = SysUtil.getLocation();
    let kvDict = {
        type: 'app',
        conditionParams: conditionParams,
        lat: location.lat,
        lng: location.lng,
    };
    // let locationInfo:UserLocationInfoModel = await UserLocationInfoModel.sharedInstance();
    // if (locationInfo && locationInfo.isValidAdress())
    // {
    //     kvDict.lat = locationInfo.lat;
    //     kvDict.lng = locationInfo.lng;
    // }
    // // 查看某个六大类元素周边的信息
    // if (this.props.centerLocation && this.props.centerLocation.lat > 0 && this.props.centerLocation.lng > 0)
    // {
    //     kvDict.lat = this.props.centerLocation.lat;
    //     kvDict.lng = this.props.centerLocation.lng;
    // }
    return HttpClient.getFromPortal(IConstant.PortalApis.ELEMENTS.TravelAgentList, kvDict)
        .then((resp) => resp.json());
}

function _getListRequestAction(orderFiled, orderType, pageIndex, searchKey, extraParams)
{
    return {
        type: IConstant.Actions.TRAVEL_AGENT.LIST.GET_LIST_REQUEST,
        orderFiled, 
        orderType,
        pageIndex,
        keyword: searchKey,
        extraParams,
    };
}

function _getListSuccessAction(orderFiled, orderType, pageIndex, searchKey, extraParams, result)
{
    return {
        type: IConstant.Actions.TRAVEL_AGENT.LIST.GET_LIST_SUCCESS,
        orderFiled, 
        orderType,
        pageIndex,
        keyword: searchKey,
        extraParams,
        result,
    };
}

function _getListFailureAction(orderFiled, orderType, extraParams, error)
{
    return {
        type: IConstant.Actions.TRAVEL_AGENT.LIST.GET_LIST_FAILURE,
        orderFiled, 
        orderType,
        extraParams,
        message: error,
    };
}


