import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';
import '../../../core/utils/DateUtil';

export function goBackAction() {
    return {
        type: IConstant.Actions.TICKET_PURCHASE.GO_BACK,
    };
}

export function asyncGetTicketInfoAction() {
    return function (dispatch, getState) {
        dispatch(_getTicketInfoRequestAction());
        return _fetchTicketInfo().then((resp) => {
            dispatch(_getTicketInfoSuccessAction(resp));
        }).catch((error) => {
            dispatch(_getTicketInfoFailureAction(error));
        });
    };
}

export function _getTicketInfoRequestAction() {
    return {
        type: IConstant.Actions.TICKET_PURCHASE.GET_INFO_REQUEST,
    };
}

export function _fetchTicketInfo() {
    let url = HttpClient.WECHAT_HANDLER;
    let kvDict = {
        type: '34',
        action: '1',
        conditionParm: JSON.stringify({
            AccountGuid: 'b760689cb63a4a92834d6f14173bc293',
            OpenId: ''
        })
    };

    return HttpClient.getData(url, kvDict).then((resp) => resp.json());
}

export function _getTicketInfoSuccessAction(result) {
    return {
        type: IConstant.Actions.TICKET_PURCHASE.GET_INFO_SUCCESS,
        result
    };
}

export function _getTicketInfoFailureAction(error) {
    return {
        type: IConstant.Actions.TICKET_PURCHASE.GET_INFO_FAILURE,
        error,
    };
}

export function setScrollerPositionAction(offset) {
    return {
        type: IConstant.Actions.TICKET_PURCHASE.SET_SCROLL_OFFSET,
        scrollViewOffset: offset
    };
}


