import { combineReducers } from 'redux';
import { Actions } from '../../../core/utils/IConstant';

function groupReducer(state = {}, action)
{
    let obj = state[action.code];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.COMMENTS.LIST.GET_LIST_REQUEST:
        return {
            ...state, 
            [action.code]: {
                ...obj,
                isFetching: true,
                pageIndex: action.pageIndex,
            }
        };
    case Actions.COMMENTS.LIST.GET_LIST_SUCCESS:
        let dataList = action.pageIndex === 1 ? [] : obj.dataList;
        dataList = dataList ? dataList : [];
        if(action.result)
        {
            dataList = dataList.concat(action.result);
        }
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching: false,
                dataList: dataList,
                total: action.result.TotalCount,
                pageIndex: action.pageIndex,
                hasMore: (action.result.TotalCount > dataList.length)
            }
        };
    case Actions.COMMENTS.LIST.GET_LIST_FAILURE:
        return {
            ...state,
            [action.code]: {
                ...obj,
                isFetching: false,
            }
        };
    case Actions.COMMENTS.LIST.GO_BACK:
        let _state = { ...state };
        delete _state[action.code];
        return _state;
    case Actions.COMMENTS.LIST.DELETE:
        return {
            ...state,
            [action.code]: {
                isFetching: false,
                dataList: [],
            }
        };
    case Actions.COMMENTS.LIST.SET_LISTVIEW_OFFSET:
        return {
            ...state, 
            [action.code]: {
                ...obj,
                listViewOffset: action.listViewOffset
            }
        };
    default:
        return state;
    }
}

const listReducer = combineReducers({
    groups: groupReducer,
});

export default listReducer;


