import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Tabs, Icon, Toast, Modal } from 'antd-mobile';
import Content from '../../views/common/Content';
import PhotoBrowserView from '../../views/photo-brower/PhotoBrowerView';
import UIUtil from '../../core/utils/UIUtil';
import * as IConstant from '../../core/utils/IConstant';
import { goBackAction,setSelectedTabIndexAction,asyncFollowAction,asyncGetFriendsInfoAction ,asyncGetNumbersInfoAction, asyncGetUserIsFollowAction } from '../../store/actions/userProfile/userProfileAction';
import queryString from 'query-string';
import FriendListView from '../../views/userProfile/FriendListView';
import FootmarkListView from '../../views/footmark/FootmarkListView';
import '../../assets/css/userProfile.css';
import SysUtil from '../../core/utils/SysUtil';

class FriendProfileScreen extends PureComponent
{
    tabs = [
        {title: '足迹', type: IConstant.FRIEND_PROFILE_DATA_TYPE.FOOTMARK},
        {title: '关注', type: IConstant.FRIEND_PROFILE_DATA_TYPE.ATTENTIONS},
        {title: '粉丝', type: IConstant.FRIEND_PROFILE_DATA_TYPE.FANS}
    ];
    constructor(props)
    {
        super(props);
        this.goBack = this.goBack.bind(this);
        this.renderTabView = this.renderTabView.bind(this);
        this.targetInfoButtonTapped = this.targetInfoButtonTapped.bind(this);
        this.followButtonTapped = this.followButtonTapped.bind(this);

        this.onFootmarkItemClick = this.onFootmarkItemClick.bind(this);
        this.onLineClick = this.onLineClick.bind(this);
        this.toLoginPage = this.toLoginPage.bind(this);
        this.onSiteImageItemClick = this.onSiteImageItemClick.bind(this);
        this.myFootmarkEditClicking = this.myFootmarkEditClicking.bind(this);
        this.friendProfileClicking = this.friendProfileClicking.bind(this);
        this.onFriendItemClick = this.onFriendItemClick.bind(this);
    }
    componentDidMount()
    {
        this.updateData();
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if (this.props.targetUserId !== prevProps.targetUserId && this.props.targetUserId)
        {
            this.updateData();
            for(let i = 0; i < this.tabs.length; i++)
            {
                let listView = this.tabs[i].listView;
                if(listView)
                {
                    listView.forceUpdate();
                }
            }
        }
        //如果该访客视角 为用户自己的, 操作粉丝/关注列表后, 清空了numbersInfo
        if (this.props.targetUserInfo && this.props.numbersInfo !== prevProps.numbersInfo && !this.props.numbersInfo && this.props.targetUserId) {
            this.props.dispatch(asyncGetNumbersInfoAction(this.props.targetUserId, this.props.userId));
        }
    }
    updateData()
    {
        if(!this.props.targetUserInfo)
        {
            this.props.dispatch(asyncGetFriendsInfoAction(this.props.targetUserId,this.props.userId));
            this.props.dispatch(asyncGetUserIsFollowAction(this.props.targetUserId,this.props.userId));
        }
        if (!this.props.numbersInfo)
        {
            this.props.dispatch(asyncGetNumbersInfoAction(this.props.targetUserId,this.props.userId));
        }
    }
    render()
    {
        let tabHeight = window.innerHeight - (200 - 45);
        let listViewHeight = window.innerHeight - 200;

        return (
            <div className="friendProfileScreenView" >
                <div className="friendProfileNavBar" >
                    <div className="navButton" onClick={() => this.goBack()} >
                        <Icon type="left" color='#fff' />
                    </div>
                    <div className="centerText" >{this.props.targetUserInfo ? (this.props.targetUserInfo.Nickname ? this.props.targetUserInfo.Nickname : this.props.targetUserInfo.Name) : ''}</div>
                    <div className="navButton"></div>
                </div>
                <img className="topBg" src={require('../../assets/images/userProfile/my_bg@2x.png')} alt='' />
                <div className="headerView" >
                    {
                        !this.props.isMine ?
                            <div className="infoButton" onClick={this.targetInfoButtonTapped} >
                                <img className="infoImg" src={require('../../assets/images/userProfile/ico_info@2x.png')} alt='' />
                            </div>
                            :
                            null
                    }
                    <div style={{backgroundImage: `url(${this.props.targetUserInfo ? this.props.targetUserInfo.GetUserAvatar : ''})`}} className="friendProfileItemImg"/>
                    {
                        this.renderFollowView()
                    }
                </div>
                <Content style={{ marginTop: '-51px',backgroundColor:'transparent',height: tabHeight + 'px' }} >
                    <Tabs tabs={this.tabs}
                        ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                        initialPage={this.props.selectedTabIndex}
                        page={this.props.selectedTabIndex}
                        prerenderingSiblingsNumber={0}
                        tabBarUnderlineStyle={{borderColor: '#42a1da', borderWidth: '2px'}}
                        onChange={(tab, index) => { 
                            this.props.dispatch(setSelectedTabIndexAction(this.props.targetUserId,this.props.userId,index));  
                        }}
                        renderTab={tab => this.renderTabView(tab)}
                        onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                        <FootmarkListView 
                            footmarkType={4}
                            targetUserId={this.props.targetUserId}
                            ref={(listView)=>{
                                if(listView)
                                {
                                    this.tabs[0].listView = listView.getWrappedInstance();
                                }
                            }}
                            onItemClick={this.onFootmarkItemClick}
                            onLineClick={this.onLineClick}
                            onSiteImageItemClick={this.onSiteImageItemClick}
                            toLoginPage={this.toLoginPage}
                            myFootmarkEditClicking={this.myFootmarkEditClicking}
                            friendProfileClicking={this.friendProfileClicking}
                            height={listViewHeight}/>
                        <FriendListView
                            friendsType={IConstant.FRIEND_PROFILE_DATA_TYPE.ATTENTIONS}
                            targetUserId={this.props.targetUserId}
                            onItemClick={this.onFriendItemClick}
                            toLoginPage={this.toLoginPage}
                            height={listViewHeight}
                            ref={(listView)=>{
                                if(listView)
                                {
                                    this.tabs[1].listView = listView.getWrappedInstance();
                                }
                            }} />
                        <FriendListView
                            friendsType={IConstant.FRIEND_PROFILE_DATA_TYPE.FANS}
                            targetUserId={this.props.targetUserId}
                            onItemClick={this.onFriendItemClick}
                            toLoginPage={this.toLoginPage}
                            height={listViewHeight}
                            ref={(listView)=>{
                                if(listView)
                                {
                                    this.tabs[2].listView = listView.getWrappedInstance();
                                }
                            }} />
                    </Tabs>
                    <PhotoBrowserView 
                        ref={(view)=>{this.photoBrowserView = view ? view.getWrappedInstance() : undefined;}}
                    />
                </Content>
            </div>
        );
    }
    renderFollowView()
    {
        if ( this.props.isMine)
        {
            return null;
        }
        else
        {
            return (<div className="infoButton" onClick={this.followButtonTapped} >
                {
                    this.props.isFollowed ?
                        <img className="infoImg" src={require('../../assets/images/userProfile/ico_followed@2x.png')} alt='' />
                        :
                        <img className="infoImg" src={require('../../assets/images/userProfile/ico_follow@2x.png')} alt='' />
                }
            </div>);
        }
    }
    renderTabView(tab)
    {
        let count = 0;
        if (this.props.numbersInfo)
        {
            if (tab.type === IConstant.FRIEND_PROFILE_DATA_TYPE.FOOTMARK)
            {
                count = this.props.numbersInfo.FootCount;
            }
            else if( tab.type === IConstant.FRIEND_PROFILE_DATA_TYPE.ATTENTIONS )
            {
                count = this.props.numbersInfo.AttentionCount;
            }
            else if( tab.type === IConstant.FRIEND_PROFILE_DATA_TYPE.FANS )
            {
                count = this.props.numbersInfo.FansCount;
            }
        }
        return (<div className="tabView" >
            <div className="countText" >{count}</div>
            <div className="nameText" >{tab.title}</div>
        </div>);
    }
    goBack()
    {
        let dispatch = this.props.dispatch;
        dispatch(goBackAction(this.props.targetUserId,this.props.userId));
        this.props.history.goBack();
    }
    targetInfoButtonTapped()
    {
        this.props.history.push({
            pathname: '/userProfile/friendInfo',
            search: `?targetUserId=${this.props.targetUserId}`,
        });
    }
    followButtonTapped()
    {
        if(isNaN(this.props.userId))
        {
            this.toLoginPage();
            return;
        }
        this.props.dispatch(asyncFollowAction(this.props.targetUserId,this.props.userId, this.props.isFollowed)).then(()=>{
            Toast.success(this.props.isFollowed ? '关注成功!' : '取消关注成功!', undefined, undefined, false);
        });
    }
    onSiteImageItemClick(item, siteIndex, imageIndex)
    {
        let imageList = [];
        let selectedIndex = 0;
        for(let i = 0; i < item.GetShowList.length; i++)
        {
            let siteItem = item.GetShowList[i];
            if(siteItem && siteItem.GetImage && siteItem.GetImage.ImageList && siteItem.GetImage.ImageList.length > 0)
            {
                for(let j = 0; j < siteItem.GetImage.ImageList.length; j++)
                {
                    let imageItem = siteItem.GetImage.ImageList[j];
                    if(siteIndex === i && imageIndex === j)
                    {
                        selectedIndex = imageList.length;
                    }
                    imageList.push(imageItem.BigImgSrc);
                }
            }
        }
        if(this.photoBrowserView)
        {
            this.photoBrowserView.open(imageList, selectedIndex);
        }
    }
    onLineClick(item)
    {
        this.onFootmarkItemClick(item, 0);
    }
    onFootmarkItemClick(item, selectedTabIndex = 1)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.tabs.length; i++)
        {
            let listView = this.tabs[i].listView;
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push({
            pathname: '/footmark/detail',
            state: {
                item,
                selectedTabIndex,
            }
        });
    }
    toLoginPage()
    {
        this.props.history.push('/me/login');
    }
    myFootmarkEditClicking(item)
    {
        if(item.Status === 1){
            Modal.alert('提示', '该足迹已审核通过，再次编辑将重新审核！', [
                { text: '取消', style: 'default' },
                { text: '确定', onPress: () => this.createFootMark(item)},
            ]);
        } else {
            this.createFootMark(item);
        }
    }

    createFootMark(item){
        this.props.history.push({
            pathname: '/footmark/create', 
            state:{
                isEdit: true,
                footmarkItem: item
            }
        });
    }

    onFriendItemClick(item)
    {
        this.props.history.push({
            pathname: '/userProfile/friendProfile',
            search: `?targetUserId=${item.UserID}`,
        });
    }
    friendProfileClicking(item)
    {
        //不重复进入,targetUserId不变,只刷新了数据,界面没有其它变动
    }
}

const mapStateToProps = function(state, ownProps)
{
    let searchParams = queryString.parse(ownProps.location.search);
    let userId = SysUtil.getUserId(state);
    let profileName = searchParams.targetUserId + '_' + userId;
    let profileObj = state.userProfile[profileName] ? state.userProfile[profileName] : {};
    let targetUserInfo = profileObj[IConstant.FRIEND_PROFILE_DATA_TYPE.TARGETINFO] ? profileObj[IConstant.FRIEND_PROFILE_DATA_TYPE.TARGETINFO] : null;
    let numbersInfo = profileObj[IConstant.FRIEND_PROFILE_DATA_TYPE.NUMBERSINFO] ? profileObj[IConstant.FRIEND_PROFILE_DATA_TYPE.NUMBERSINFO] : null;
    let selectedTabIndex = profileObj.selectedTabIndex ? profileObj.selectedTabIndex : 0; 
    let isFollowed = profileObj.isFollowed ? profileObj.isFollowed : false;
    return {
        selectedTabIndex: selectedTabIndex,
        targetUserInfo: targetUserInfo,
        targetUserId: searchParams.targetUserId,
        numbersInfo: numbersInfo,
        userId,
        isFollowed,
        isMine: (searchParams.targetUserId === (userId + ''))
    };
};

FriendProfileScreen = connect(mapStateToProps)(FriendProfileScreen);

export default FriendProfileScreen;


