import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/order.css';
import SysUtil from '../../core/utils/SysUtil';
import HttpClient from '../../core/utils/HttpClient';
import * as IConstant from '../../core/utils/IConstant';
import {_asyncUpdateSelectVisitorAction, asyncGetVisitorsAction} from '../../store/actions/order/bookingAction';

class OrderBookingVisitorListScreen extends PureComponent
{
    
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {       
                        this.confirm();
                    }}
                    rightContent={
                        <div style={{ display: 'flex', alignItems: 'center', height: '100%' }}>
                            <div style={{ color: 'white' }} onClick={() => this.confirm()}>
                                确定
                            </div>
                        </div>
                    }>
                    选择游客
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}} 
                    style={{backgroundColor: '#f3f3f3'}}>
                    {
                        this.renderHeader()
                    }
                    {
                        this.renderList()
                    }
                </Content>
            </div>
        );
    }
    renderHeader(){
        return(
            <div className='bookingVisitorListHeader'>
                <div className='bookingVisitorListHeaderTip'>
                    选择完毕，请点击右上角的确定按钮。
                </div>
                <div className='bookingVisitorListHeaderAdd' onClick={()=>this.showAdd()}>
                    新增游客
                </div>
            </div>
        );
    }   
    renderList(){
        if(!this.props.dataList){
            return;
        }
        let unchecked=require('../../assets/images/order/icon_order_unchecked.png');
        let checked=require('../../assets/images/order/icon_order_checked.png');

        return(
            <div className='bookingVisitorListContent'>
                {
                    this.props.dataList.map((item,index)=>
                        item.ID !== ''
                            ?
                            <div key={index} className='bookingVisitorListItem' onClick={() => this.onCheckChange(item)}>
                                {
                                    this.ifVisitorChecked(item)
                                        ?
                                        <img src={checked} alt='' className='bookingVisitorListItemIcon' />
                                        :
                                        <img src={unchecked} alt='' className='bookingVisitorListItemIcon' />
                                }
                                <div className='bookingVisitorListItemContent'>
                                    <div className='bookingVisitorListItemContentName'>{item.UserName}</div>
                                    <div>
                                        身份证号
                                        <span className='bookingVisitorListItemContentIdCard'>{item.IDCard}</span>
                                    </div>
                                </div>
                                <img src={require('../../assets/images/order/delete.png')} alt='' className='bookingVisitorListItemIcon' onClick={(e) => this.deleteVisitor(e, item)} />
                            </div>
                            :
                            null
                    )
                }
            </div>
        );
    }
    confirm(){
        let index = 0;
        this.props.selectDataList.forEach((_item) => {
            if (_item.UserName !== '') {
                index++;
            }
        });
        if(index === 0){
            Toast.info('请选择游客!');
            return;
        }
        this.goBack();
    }
    goBack(){
        this.props.history.goBack();
    }
    showAdd(){
        this.props.history.push('/order/booking/visitor/add');
    }
    //游客是否被选中
    ifVisitorChecked(item){
        for(let i=0;i<this.props.selectDataList.length;i++){
            if (this.props.selectDataList[i].UserName === item.UserName && this.props.selectDataList[i].IDCard === item.IDCard) {
                return true;
            }
        }
        return false;
    }
    onCheckChange(item){
        if(this.ifVisitorChecked(item)){
            let selectDataList=this.props.selectDataList;
            for (let i = 0; i < selectDataList.length; i++) {
                if (item.UserName === selectDataList[i].UserName && item.IDCard === selectDataList[i].IDCard) {
                    selectDataList.splice(i, 1);
                    selectDataList[selectDataList.length] = {
                        ID: '',
                        UserName: '',
                        IDCard: ''
                    };
                    break;
                }
            }
            this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
        }else{
            let index = 0;
            this.props.selectDataList.forEach((_item) => {
                if (_item.UserName !== '') {
                    index++;
                }
            });
            if (index === this.props.orderNumber) {
                Toast.info('已选择完毕!');
            } else {
                let selectDataList = this.props.selectDataList;
                selectDataList[index] = {...item};
                this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
            }
        }
    }
    deleteVisitor(e,item){
        e.stopPropagation();
        let kvDict = {
            ConditionParams: {
                Params: {
                    id: item.ID
                }
            }
        };
        HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS, IConstant.PortalApis.VISITOR.DeleteVisitors, kvDict)
            .then((resp) => resp.json()).then((resp) => {
                if (resp && resp.status === '0') {
                    let selectDataList = this.props.selectDataList;
                    for (let i = 0; i < selectDataList.length; i++) {
                        if (item.UserName === selectDataList[i].UserName && item.IDCard === selectDataList[i].IDCard) {
                            selectDataList.splice(i, 1);
                            selectDataList[selectDataList.length] = {
                                ID: '',
                                UserName: '',
                                IDCard: ''
                            };
                            break;
                        }
                    }
                    this.props.dispatch(asyncGetVisitorsAction(this.props.userId)).then(() => {
                        this.props.dispatch(_asyncUpdateSelectVisitorAction(selectDataList));
                    });
                }
            });
    }
}

const mapStateToProps = function(state, ownProps)
{
    return {
        ...state.order.bookingScreen.visitor,
        orderNumber:state.order.bookingScreen.bookingItem.orderNumber,
        userId: SysUtil.getUserId(state),
    };
};

OrderBookingVisitorListScreen = connect(mapStateToProps)(OrderBookingVisitorListScreen);

export default OrderBookingVisitorListScreen;


