import * as IConstant from '../../../core/utils/IConstant';
// import WebDB from '../../../core/utils/WebDB';
import LocationUtil from '../../../core/utils/LocationUtil';

export function asyncGetUserLocationAction(isUpdate)
{
    return function(dispatch, getState)
    {
        dispatch(_getUserLocationRequestAction());
        LocationUtil.getCurrentUserLocation(getState,dispatch,isUpdate);
    };
}

function _getUserLocationRequestAction()
{
    return {
        type: IConstant.Actions.SYSTEM.GET_USER_LOCATION_REQUEST,
    };
}

export function _getUserLocationSuccessAction(lat,lng,addressStr)
{
    return {
        type: IConstant.Actions.SYSTEM.GET_USER_LOCATION_SUCCESS,
        lat: lat,
        lng: lng,
        address: addressStr
    };
}

export function _getUserLocationFailureAction( error)
{
    return {
        type: IConstant.Actions.SYSTEM.GET_USER_LOCATION_FAILURE,
        message: error
    };
}


