import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Tabs, NavBar, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import LeftContentView from '../../views/common/LeftContentView';
import ForumListView from '../../views/forum/ForumListView';
import UIUtil from '../../core/utils/UIUtil';
import { asyncSetSelectedGroupIndexAction, goBackAction, asyncGetGroupList } from '../../store/actions/forum/listAction';
import '../../assets/css/forum.css';
import SysUtil from '../../core/utils/SysUtil';

class ForumListScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.listViewList = [];
        this.onItemClick = this.onItemClick.bind(this);
        this.goBack = this.goBack.bind(this);
        this.postForumTapped = this.postForumTapped.bind(this);
    }
    componentDidMount()
    {
        if(!this.props.groups.dataList)
        {
            this.loadData();
        }
    }
    render()
    {
        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark"
                            className="navBar"
                            leftContent={<LeftContentView
                                ref={(view) => { this.leftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                                goBackAction={goBackAction}
                                history={this.props.history}
                            />}
                            onLeftClick={() => {
                                this.leftContentView.goHome();
                            }}
                            rightContent={<div onClick={this.postForumTapped} class="forumList_rightButton" >
                                <img className="forumListScreen_rightIcon" src={require('../../assets/images/add_public_account@2x.png')} alt='' />
                            </div>}
                        >
                            论坛
                        </NavBar>
                }
                <Content>
                    {
                        this.renderTabsView()
                    }
                    {
                        this.props.groups.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderTabsView()
    {
        if(!this.props.groups.dataList || this.props.groups.dataList.length === 0)
        {
            return null;
        }
        let tabHeight = window.innerHeight - 45;
        let listViewHeight = window.innerHeight - (45 + 44);
        let tabs = this.props.groups.dataList;
        tabs.forEach((item, index)=>item.title = item.SectionName);
        return (
            <Tabs tabs={tabs}
                ref={(ref)=>UIUtil.updateComponentHeight(ref, tabHeight)}
                initialPage={this.props.groups.selectedIndex}
                prerenderingSiblingsNumber={0}
                onChange={(tab, index) => { 
                    this.props.dispatch(asyncSetSelectedGroupIndexAction(index)).then(()=>{
                        this.searchDataList(index);
                    });
                }}
                onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}>
                {
                    tabs.map((item, index)=><ForumListView key={index} 
                        ref={(listView)=>{
                            if(listView)
                            {
                                this.listViewList[index] = listView.getWrappedInstance();
                            }
                        }}
                        onItemClick={this.onItemClick}
                        sectionID={item.SectionID} 
                        height={listViewHeight}/>)
                }
            </Tabs>
        );
    }
    searchDataList(index = undefined)
    {
        index = (index === undefined) ? this.props.groups.selectedIndex : index;
        let listView: ForumListView = this.listViewList[index];
        if(!listView)
        {
            return;
        }
        listView.searchDataList();
    }
    onItemClick(item)
    {
        if(!item)
        {
            return;
        }
        for(let i = 0; i < this.listViewList.length; i++)
        {
            let listView: ForumListView = this.listViewList[i];
            if(listView)
            {
                listView.cacheScrollOffset();
            }
        }
        this.props.history.push(`/forum/detail/${item.ThreadID}`);
    }
    goBack()
    {
        let dispatch = this.props.dispatch;
        this.props.history.goBack();
        dispatch(goBackAction());
    }
    loadData()
    {
        this.props.dispatch(asyncGetGroupList());
    }
    postForumTapped()
    {
        if (!this.props.userId)
        {
            this.props.history.push('/me/login');
            return;
        }
        this.props.history.push('/forum/post');
    }
}

const mapStateToProps = function(state)
{
    return {
        ...state.forum.listScreen,
        userId: SysUtil.getUserId(state),
        isFromNativeApp: state.native.isFromNativeApp
    };
};

ForumListScreen = connect(mapStateToProps)(ForumListScreen);

export default ForumListScreen;



