import * as IConstant from '../../../core/utils/IConstant';

export function goBackAction(code)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.GO_BACK,
        code:code,
    };
}

export function updateFromToLocationInfo(code,fromLat,fromLng,toLat,toLng,navType)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.UPDATE_FROM_TO_LOCATION,
        code:code,
        fromLat:fromLat,
        fromLng:fromLng,
        toLat:toLat,
        toLng:toLng,
        navType:navType
    };
}

export function isFetchingChangeAction(code,isFetching)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.IS_FETCHING_CHANGE,
        code:code,
        isFetching:isFetching
    };
}

export function selectNavTypeAction(code,navType)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.SELECT_NAV_TYPE,
        code:code,
        navType:navType
    };
}

export function isPanelShowingChangeAction(code,isPanelShowing)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.IS_PANEL_SHOWING_CHANGE,
        code:code,
        isPanelShowing: isPanelShowing,
    };
}

export function navSearchRequestAction(code)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.NAV_SEARCH_REQUEST,
        code:code,
    };
}

export function navSearchSuccessAction(code ,results,totalTime,totalDistance,dataIsValid)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.NAV_SEARCH_SUCCESS,
        code:code,
        results: results,
        totalTime: totalTime,
        totalDistance: totalDistance,
        dataIsValid: dataIsValid
    };
}

export function navSearchFailureAction(code,results)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.NAV_SEARCH_FAILURE,
        code:code,
        results: results
    };
}

export function navMethodIndexChangeAction(code,navMethodIndex)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.NAV_METHOD_INDEX_CHANGE,
        code:code,
        navMethodIndex:navMethodIndex
    };
}

export function mapZoomAndCenterLocationChangeAction(code,mapZoom,centerLat,centerLng)
{
    return function(dispatch, getState)
    {
        dispatch(_mapZoomAndCenterLocationChangeAction(code,mapZoom,centerLat,centerLng));
        return new Promise((resolve)=>{
            resolve();
        });
    };
    
}

function _mapZoomAndCenterLocationChangeAction(code,mapZoom,centerLat,centerLng)
{
    return {
        type: IConstant.Actions.MAP.MAPNAV.MAPZOOM_AND_CENTERLOCATION_CHANGE,
        code: code,
        mapZoom:mapZoom,
        centerLat: centerLat,
        centerLng: centerLng
    };
}



