import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

class FootmarkItemView extends PureComponent
{
    render()
    {
        return (
            <div className="footmarkItem"
                style={this.props.style}
                onClick={()=>{
                    if(this.props.onItemClick)
                    {
                        this.props.onItemClick(this.props.item);
                    }
                }}>
                {
                    this.renderLeftView()
                }
                <div className="footmarkItemLeftRightSplit"/>
                {
                    this.renderRightView()
                }
            </div>
        );
    }
    renderFirstSiteView()
    {
        if(!this.props.item.GetShowList || this.props.item.GetShowList.length === 0)
        {
            return null;
        }
        return this.renderSiteItem(this.props.item.GetShowList[0], 0);
    }
    renderOtherSiteViews()
    {
        if(!this.props.item.GetShowList || this.props.item.GetShowList.length <= 1 || !this.props.item.isExpand)
        {
            return null;
        }
        return this.props.item.GetShowList.slice(1).map((item, index)=>this.renderSiteItem(item, index));
    }
    renderSiteItem(item, index)
    {
        let date = item.CreateTime;
        if(date)
        {
            let index = date.indexOf(':');
            if(index !== -1)
            {
                date = date.substr(0, index) + '时';
            }
        }
        let address = item.Address;
        if(address)
        {
            if(address.startsWith('中国'))
            {
                address = address.substr('中国'.length);
            }
        }
        return (
            <div className="footmarkItemSiteItem" key={index}>
                {
                    this.renderSiteItemImageBox(item)
                }
                <div className="footmarkItemSiteItemContent">
                    { item.Content }
                </div>
                <div className="footmarkItemSiteItemAddress">
                    <img src={require('../../assets/images/footmark/footmark_position@2x.png')} alt=''/>
                    { address }&nbsp;&nbsp;{ date }
                </div>
            </div>
        );
    }
    renderSiteItemImageBox(item)
    {
        if(!item.GetImage || !item.GetImage.ImageList || item.GetImage.ImageList.length === 0)
        {
            return null;
        }
        let siteIndex = this.props.item.GetShowList.indexOf(item);
        let len = item.GetImage.ImageList.length;
        let MAX_IMAGE_COUNT = 4;
        if(len < MAX_IMAGE_COUNT)
        {
            return (
                <div className="footmarkItemImageBox">
                    {
                        this.renderSiteItemImageRow(siteIndex, item.GetImage.ImageList)
                    }
                </div>
            );
        }
        else
        {
            return (
                <div className="footmarkItemImageBox">
                    {
                        this.renderSiteItemImageRow(siteIndex, item.GetImage.ImageList.slice(0, 2))
                    }
                    {
                        this.renderSiteItemImageRow(siteIndex, item.GetImage.ImageList.slice(2, 4))
                    }
                </div>
            );
        }
    }
    renderSiteItemImageRow(siteIndex, imageList)
    {
        let siteItem = this.props.item.GetShowList[siteIndex];
        let len = imageList.length;
        let hmargin = 5;
        let imgBoxWidth = (window.innerWidth - 1) * 21 / 25.0 - 20;
        let imgWidth = (imgBoxWidth - (len - 1) * hmargin) / len;
        let imgHeight = imgWidth * 0.75;
        return (
            <div className="footmarkItemImageRow">
                {
                    imageList.map((image, index)=><div key={index}
                        className="footmarkItemImage" 
                        style={{
                            width: imgWidth + 'px', 
                            height: imgHeight + 'px',
                            backgroundImage: `url(${image.MiddleImgSrc})`
                        }}
                        onClick={(e)=>{
                            e.stopPropagation();
                            e.nativeEvent.stopImmediatePropagation();
                            let imageIndex = siteItem.GetImage.ImageList.indexOf(image);
                            if(this.props.onSiteImageItemClick)
                            {
                                this.props.onSiteImageItemClick(this.props.item, siteIndex, imageIndex);
                            }
                        }}/>)
                }
            </div>
        );
    }
    renderLeftView()
    {
        let praiseIcon = this.props.item.IsLike === 1 ? require('../../assets/images/footmark/praise-on@2x.png') : require('../../assets/images/footmark/praise@2x.png');
        let collectIcon = this.props.item.IsCollection === 1 ? require('../../assets/images/footmark/collect-on@2x.png') : require('../../assets/images/footmark/collect@2x.png');
        return (
            <div className="footmarkItemLeft">
                <div className="footmarkItemLeftButton"
                    onClick={(e)=>{
                        e.stopPropagation();
                        e.nativeEvent.stopImmediatePropagation();
                        if(this.props.onLineClick)
                        {
                            this.props.onLineClick(this.props.item);
                        }
                    }}>
                    <img src={require('../../assets/images/footmark/footmark-route@2x.png')} alt=''/>
                    线路
                </div>
                <div className="footmarkLeftButtonSplit"/>
                {
                    this.props.item.Status === 1 ?
                        <div className="footmarkItemLeftButton"
                            onClick={(e)=>{
                                e.stopPropagation();
                                e.nativeEvent.stopImmediatePropagation();
                                if(this.props.onPraiseButtonTapped)
                                {
                                    this.props.onPraiseButtonTapped();
                                }
                            }}>
                            <img src={praiseIcon} alt=''/>
                            {this.props.item.LikeCount}
                        </div>
                        : null
                }
                <div className="footmarkLeftButtonSplit"/>
                {
                    this.props.isMine
                        ?
                        <div className="footmarkItemLeftButton" onClick={(e)=>{
                            e.stopPropagation();
                            e.nativeEvent.stopImmediatePropagation();
                            if(this.props.onDeleteButtonTapped)
                            {
                                this.props.onDeleteButtonTapped();
                            }
                        }}>
                            <img src={require('../../assets/images/footmark/footmark_ico-delete@2x.png')} alt=''/>
                            删除
                        </div>
                        :
                        <div className="footmarkItemLeftButton"
                            onClick={(e)=>{
                                e.stopPropagation();
                                e.nativeEvent.stopImmediatePropagation();
                                if(this.props.onCollectButtonTapped)
                                {
                                    this.props.onCollectButtonTapped();
                                }
                            }}>
                            <img src={collectIcon} alt=''/>
                            收藏
                        </div>
                }
            </div>
        );
    }
    renderRightView()
    {
        return (
            <div className="footmarkItemRight">
                <div style={{width: '10px'}}/>
                <div style={{flex: 1}}>
                    {
                        this.renderHeaderView()
                    }
                    {
                        this.renderStartTimeView()
                    }
                    {
                        this.renderFirstSiteView()
                    }
                    {
                        this.renderOtherSiteViews()
                    }
                    {
                        this.renderCommentsView()
                    }
                    <div style={{height: '10px'}}/>
                </div>
            </div>
        );
    }
    renderHeaderView()
    {
        let datetime = this.props.item.CreateTime;
        if(datetime && datetime.length > 0)
        {
            let index = datetime.lastIndexOf(':');
            if(index > -1)
            {
                datetime = datetime.substr(0, index);
            }
        }
        return (
            <div>
                <div className="footmarkItemHeader">
                    <div className="footmarkItemHeaderItem footmarkItemHeaderLeftItem">
                        <div className="footmarkItemHeaderIcon" onClick={(e) => {
                            e.stopPropagation();
                            e.nativeEvent.stopImmediatePropagation();
                            if (this.props.friendProfileClicking)
                            {
                                this.props.friendProfileClicking(this.props.item);
                            }
                        }}
                        style={{ backgroundImage: `url(${this.props.item.GetUserAvatar})` }} />
                        <div className="footmarkItemHeaderNameDate">
                            <div className="footmarkItemHeaderName">{ this.props.item.NickName }</div>
                            <div className="footmarkItemHeaderDate">{ datetime }</div>
                        </div>
                    </div>
                    <div className="footmarkItemHeaderItem footmarkItemHeaderRightItem">
                        <div style={{flex: 1}}/>
                        {
                            this.renderStatusView()
                        }
                        {
                            this.renderEditView()
                        }
                        {
                            this.renderExpandView()
                        }
                    </div>
                </div>
                {
                    this.renderHeaderTitleView()
                }
            </div>
        );
    }
    renderStartTimeView()
    {
        if(!this.props.item.GetShowList || this.props.item.GetShowList.length === 0)
        {
            return null;
        }
        let startTime = this.props.item.GetShowList[0].CreateTime;
        if(!startTime || startTime.length === 0)
        {
            return null;
        }
        let index = startTime.indexOf(' ');
        if(index !== -1)
        {
            startTime = startTime.substr(0, index);
        }
        return (
            <div className="footmarkItemStartTime">
                <img src={require('../../assets/images/footmark/start-time@2x.png')} alt=''/>
                出发时间：{startTime}
            </div>
        );
    }
    renderStatusView()
    {
        if(!this.props.isMine)
        {
            return null;
        }
        if(this.props.item.Status === 0 || this.props.item.Status === 2)
        {
            let text = (this.props.item.Status === 0 ? '待审核' : '拒绝');
            return (
                <div className="footmarkItemStatusText">
                    { text }
                </div>
            );
        }
        return null;
    }
    renderEditView()
    {
        if(!this.props.isMine)
        {
            return null;
        }
        return (
            <div className="footmarkItemEditIcon" onClick={(e) => {
                e.stopPropagation();
                e.nativeEvent.stopImmediatePropagation();
                this.props.myFootmarkEditClicking(this.props.item);
            }} >
                <img src={require('../../assets/images/footmark/edit@2x.png')} alt=''/>
            </div>
        );
    }
    renderExpandView()
    {
        if(!this.props.item.GetShowList || this.props.item.GetShowList.length <= 1)
        {
            return null;
        }
        return (
            <div className="footmarkItemExpand"
                onClick={(e)=>{
                    e.stopPropagation();
                    e.nativeEvent.stopImmediatePropagation();
                    if(this.props.toggleExpand)
                    {
                        this.props.toggleExpand();
                    }
                }}>
                { this.props.item.isExpand === true ? '收起' : '展开' }
                <img src={require('../../assets/images/footmark/open@2x.png')} alt=''/>
            </div>
        );
    }
    renderHeaderTitleView()
    {
        if(!this.props.item.Title || this.props.item.Title.length === 0)
        {
            return null;
        }
        return (
            <div className="footmarkItemHeaderTitle">
                { this.props.item.Title }
            </div>
        );
    }
    renderCommentsView()
    {
        if(!this.props.item.GetCommentList || this.props.item.GetCommentList.length === 0)
        {
            return null;
        }
        return (
            <div className="footmarkItemCommentsBox">
                {
                    this.props.item.GetCommentList.slice(0, 5).map((item, index)=>this.renderCommentsItemView(item, index))
                }
            </div>
        );
    }
    renderCommentsItemView(item, index)
    {
        return (
            <div className="footmarkItemComments" style={index === 0 ? null : {marginTop: '10px'}} key={index}>
                <span className="footmarkItemCommentsName">{ item.NickName }</span>
                <span className="footmarkItemCommentsContent">&nbsp;:&nbsp;{ item.Content }</span>
            </div>
        );
    }
}

FootmarkItemView.propTypes = {
    // 展开、收起
    toggleExpand: PropTypes.func,
    // 点赞
    onPraiseButtonTapped: PropTypes.func,
    // 收藏
    onCollectButtonTapped: PropTypes.func,
    //我的行程编辑
    myFootmarkEditClicking: PropTypes.func,
    //访客视角
    friendProfileClicking: PropTypes.func,
    //删除
    onDeleteButtonTapped: PropTypes.func,
};

export default FootmarkItemView;


