import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, ActivityIndicator, Modal, TextareaItem, Button, Toast } from 'antd-mobile';
import Content from '../../views/common/Content';
import { 
    asyncGetDetailAction, 
    goBackAction,
    asyncSendCommentsAction,
    asyncGetCommentsAction
} from '../../store/actions/travel-notes/detailAction';
import LeftContentView from '../../views/common/LeftContentView';
import SysUtil from '../../core/utils/SysUtil';
import * as IConstant from '../../core/utils/IConstant';

const DEFAULT_PLACEHOLDER = '请输入回复内容';

class TravelNotesDetailScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.state = {
            showCommentsInput: false,
            placeholder: DEFAULT_PLACEHOLDER,
            comments: '',
        };
        this.showCommentsButtonView = this.showCommentsButtonView.bind(this);
        this.sendComments = this.sendComments.bind(this);
    }

    componentDidMount()
    {
        this.loadData();
        this.loadCommentList();
    }
    render()
    {

        let rightMenu = this.props.detailItem ? <div className="forumDetailRightMenu" onClick={()=>this.showCommentsButtonView()}><img src={require('../../assets/images/commentNav@2x.png')} alt=''/></div> : null;        
        return (
            <div className={this.props.isFromNativeApp ? 'isFromNativeApp' : null}>
                {
                    this.props.isFromNativeApp
                        ? null
                        :
                        <NavBar mode="dark" 
                            className="navBar"
                            leftContent={<LeftContentView
                                ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                                goBackAction={() => goBackAction(this.props.match.params.id)}
                                history={this.props.history}
                            />}
                            rightContent={rightMenu}
                            onLeftClick={() => {
                                this.LeftContentView.goHome();
                            }}>
                            详情
                        </NavBar>
                }
                <Content ref={(view)=>{this.scrollView = view ? view.getWrappedInstance() : this.scrollView;}}>
                    {
                        this.renderHeaderView()
                    }
                    {
                        this.renderContentView()
                    }
                    {
                        this.renderResponseView()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
                <Modal
                    popup
                    visible={this.state.showCommentsInput}
                    onClose={()=>this.setState({showCommentsInput: false})}
                    animationType="slide-up">
                    <TextareaItem
                        ref={(ref)=>this.commentsInputView=ref} 
                        autoHeight
                        placeholder={this.state.placeholder}
                        onChange={(val)=>this.setState({comments: val})}
                        rows={5}
                        count="500"
                    />
                    <Button
                        className="forumDetailSendButton" 
                        type="primary" 
                        onClick={this.sendComments}
                        loading={this.props.isFetching && this.state.comments} 
                        disabled={this.props.isFetching || !this.state.comments}>
                        发送
                    </Button>
                </Modal>
            </div>
        );
    }
    renderHeaderView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div>
                <div className="newsDetailHeaderRow">
                    <div className="newsDetailHeaderName">{this.props.detailItem.title}</div>
                    <div className="newsListSecondRow">
                        <div className="newsListItemOthers">作者：{this.props.detailItem.Nickname}</div>
                        <div style={{flex: 1}}/>
                        <div className="newsListItemOthers">时间：{this.props.detailItem.CreateDate}</div>
                    </div>
                </div>
                <div className="newsDetailSplitLine"/>
            </div>
        );
    }
    renderContentView()
    {
        if(!this.props.detailItems || this.props.detailItems.length === 0)
        {
            return null;
        }
        return (
            <div className="newsDetailHtml">
                {
                    this.props.detailItems.map((item,index)=>this.renderItemView(item, index))
                }
            </div>
        );
        // return (
        //     <div className="newsDetailHtml" dangerouslySetInnerHTML={{__html: this.props.detailItem.mp_content}}></div>
        // );
    }
    renderItemView(item, index)
    {
        if(item.ContentType === 0)
        {
            return (
                <div key={index} className="travelNotesDetailHeader">
                    { item.Content }
                </div>
            );
        }
        else if(item.ContentType === 2)
        {
            if(item.GetIMAGE)
            {
                return (
                    <img key={index} className="travelNotesDetailImage" src={item.GetIMAGE.MiddleImageFrontUrl} alt=''/>
                );
            }
        }
        else
        {
            return (
                <div key={index} className="travelNotesDetailContent" dangerouslySetInnerHTML={{__html: item.Content}}/>
            );
        }
    }
    renderResponseView()
    {
        if(!this.props.detailItems)
        {
            return null;
        }
        let commentsCount = this.props.commentList ? this.props.commentList.length : 0;
        return (
            <React.Fragment>
                <div className="forumDetailCommentsViewHeader">
                    <div className="forumDetailCommentsViewHeaderText">评论({ commentsCount })</div>
                    <div style={{flex: 1}}/>
                    <div className="forumDetailCommentsViewHeaderButton" onClick={()=>this.showCommentsButtonView()}>我要评论</div>
                </div>
                {
                    commentsCount <= 0
                        ? null
                        : this.props.commentList.map((item, index) => this.renderResponseItemView(item, index))
                }
            </React.Fragment>
        );
    }
    renderResponseItemView(item, index)
    {
        return (
            <div key={item.CommentID} className="forumDetailResponseBox">
                <div className="footmarkCommentsViewCommentsItemIcon"
                    style={{ backgroundImage: `url(${item.GetAvatar ? item.GetAvatar.MiddleImageFrontUrl : ''})` }}/>
                <div className="footmarkCommentsViewCommentsItemRight">
                    <div className="footmarkCommentsViewCommentsItemHeaderText">
                        <div className="footmarkCommentsViewCommentsItemNameRow">
                            <div className="footmarkCommentsViewCommentsItemName">
                                { item.Nickname }
                            </div>
                            <div style={{flex: 1}}/>
                            <div className="footmarkCommentsViewCommentsItemNameRowCommentsButton" 
                                onClick={()=>{
                                    this.showCommentsButtonView(`回复@${item.Nickname}`, item);
                                }}>
                                <img src={require('../../assets/images/footmark/com-comment@2x.png')} alt=''/>
                            </div>
                        </div>
                        <div className="footmarkItemHeaderDate">{ item.CreateTime }</div>
                    </div>
                    <div className="footmarkCommentsViewCommentsItemText">
                        {
                            (item.HFC && item.HFC.length > 0)
                                ? this.renderReplyHeader(item)
                                : null
                        }
                        <div dangerouslySetInnerHTML={{__html: item.Content}}></div>
                    </div>
                    <div className="footmarkCommentsViewCommentsItemRightSplit"/>
                </div>
            </div>
        );
    }
    renderReplyHeader(item)
    {
        return (
            <div>
                回复 <span className="forumDetailCommentsNotification">@{ item.HFN }</span>&nbsp;&nbsp;：
            </div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetDetailAction(this.props.match.params.id));
    }
    loadCommentList()
    {
        let kvDict = {
            id: this.props.match.params.id,
            table: 'web_sns_note',
            pagesize: IConstant.ALL_PAGE_SIZE,
            pageindex: 1
        };
        this.props.dispatch(asyncGetCommentsAction(this.props.match.params.id, kvDict));
    }
    showCommentsButtonView(placeholder, item)
    {
        if(!this.props.userId)
        {
            this.toLoginPage();
        }
        this.selectedItem = item;
        placeholder = placeholder ? placeholder : DEFAULT_PLACEHOLDER;
        this.setState({ showCommentsInput: true, placeholder }, ()=>{
            if(this.commentsInputView)
            {
                this.commentsInputView.focus();
            }
        });
    }
    sendComments()
    {
        if(!this.state.comments)
        {
            Toast.fail(DEFAULT_PLACEHOLDER);
            this.commentsInputView.focus();
            return;
        }
        let kvDict = {
            id: this.props.match.params.id,
            table: 'web_sns_note',
            uid: this.props.userId,
            content: this.state.comments,
            pid: this.selectedItem ? this.selectedItem.CommentID : ''
        };
        this.props.dispatch(asyncSendCommentsAction(this.props.match.params.id, kvDict)).then(()=>{
            if(this.props.successMessage)
            {
                Toast.success(this.props.successMessage, undefined, undefined, false);
            }
            this.setState({
                showCommentsInput: false,
                comments: '',
            });
            this.selectedItem = undefined;
            this.loadCommentList();
        });
    }
    toLoginPage()
    {
        this.props.history.push('/me/login');
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.travelNotes.detailScreen[ownProps.match.params.id];
    return {
        ...obj,
        userId: SysUtil.getUserId(state),      
        isFromNativeApp: state.native.isFromNativeApp,  
    };
};

TravelNotesDetailScreen = connect(mapStateToProps)(TravelNotesDetailScreen);

export default TravelNotesDetailScreen;


