import { Actions } from '../../../core/utils/IConstant';

export default function chooseAddressReducer(state = {}, action)
{
    switch(action.type)
    {
    case Actions.MAP.CHOOSEADDRESS.CLEAR_SELECT_ADDRESS:
        return {
        };
    case Actions.MAP.CHOOSEADDRESS.UPDATE_SELECT_ADDRESS:
        return {
            ...state,
            selectPoint: action.selectPoint
        };
    default:
        return state;
    }
}


