import * as IConstant from '../../../core/utils/IConstant';
import HttpClient from '../../../core/utils/HttpClient';

export function goBackAction(code)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GO_BACK,
        code,
    };
}

// 酒店详情
export function asyncGetDetailAction(code)
{
    return function(dispatch, getState)
    {
        dispatch(_getDetailRequestAction(code));
        return _fetchDetail(code).then((resp)=>{
            resp = (resp && resp.results && resp.results.length > 0) ? resp.results[0] : undefined;
            dispatch(_getDetailSuccessAction(code, resp));
        }).catch((error)=>{
            dispatch(_getDetailFailureAction(code, error));
        });
    };
}

export function asyncGetRoomListAction(code, startDate, endDate)
{
    return function(dispatch, getState)
    {
        dispatch(_getRoomListRequestAction(code));
        return _fetchRoomList(code, startDate, endDate).then((resp)=>{
            dispatch(_getRoomListSuccessAction(code, startDate, endDate, resp ? resp.results : null));
        }).catch((error)=>{
            dispatch(_getRoomListFailureAction(code, error));
        });
    };
}

// 酒店详情页面滚动条位置
export function setDetailScrollerPositionAction(code, offset)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.SET_DETAIL_SCROLL_OFFSET,
        code,
        scrollViewOffset: offset,
    };
}

function _fetchDetail(code)
{
    let kvDict = {
        ConditionParams: {
            Params:{ Code: code }
        }
    };
    return HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS , IConstant.PortalApis.ELEMENTS.HotelDetail, kvDict)
        .then((resp) => resp.json());
}

function _fetchRoomList(code, startDate, endDate)
{
    let kvDict = {
        ConditionParams: {
            Params:{
                code: code,
                StartDate: startDate,
                EndDate: endDate
            }
        }
    };
    return HttpClient.postToPortalWithUrl(HttpClient.HOST_E_BUSINESS , IConstant.PortalApis.ELEMENTS.HotelRoom, kvDict)
        .then((resp) => resp.json());
}

function _getDetailRequestAction(code)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_DETAIL_REQUEST,
        code,
    };
}

function _getDetailSuccessAction(code, resp)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_DETAIL_SUCCESS,
        code,
        detailItem: resp
    };
}

function _getDetailFailureAction(code, error)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_DETAIL_FAILURE,
        code,
        message: error
    };
}

function _getRoomListRequestAction(code, startDate, endDate)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_ROOM_LIST_REQUEST,
        code,
        data: {
            roomList: [],
            startDate, 
            endDate,
        }
    };
}

function _getRoomListSuccessAction(code, startDate, endDate, resp)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_ROOM_LIST_SUCCESS,
        code,
        data: {
            roomList: resp,
            startDate, 
            endDate,
        },
    };
}

function _getRoomListFailureAction(code, error)
{
    return {
        type: IConstant.Actions.HOTEL.DETAIL.GET_ROOM_LIST_FAILURE,
        code,
        message: error
    };
}


