import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import { ListView, PullToRefresh, ActivityIndicator, Toast, Modal } from 'antd-mobile';
import FootmarkItemView from './FootmarkItemView';
import { 
    asyncGetListAction, 
    asyncDeleteListAction, 
    setScrollerPositionAction, 
    toggleExpandAction, 
    asyncPraiseAction,
    asyncCollectAction,
} from '../../store/actions/footmark/listAction';
import SysUtil from '../../core/utils/SysUtil';
import UIUtil from '../../core/utils/UIUtil';
import '../../assets/css/footmark.css';
import EmptyView from '../../views/common/EmptyView';
import * as IConstant from '../../core/utils/IConstant';
import HttpClient from '../../core/utils/HttpClient';

class FootmarkListView extends PureComponent
{
    // 0:最新；1:推荐；2:收藏；3:我的; 4: 访客视角-足迹, 传targetUserId
    constructor(props)
    {
        super(props);

        this.renderRow = this.renderRow.bind(this);
        this.onRefresh = this.onRefresh.bind(this);
        this.loadNextPage = this.loadNextPage.bind(this);
        this.renderFooter = this.renderFooter.bind(this);
        
        const dataSource = new ListView.DataSource({
            rowHasChanged: (row1, row2) => row1 !== row2,
            sectionHeaderHasChanged: (s1, s2) => s1 !== s2,
        });

        this.state = {
            dataSource: dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : []),
        };
    }
    componentDidMount()
    {
        // 处理返回
        if(!this.props.dataList)
        {
            this.loadData(this.props.footmarkType);
        }
        if(this.listView && !isNaN(this.props.listViewOffset))
        {
            this.listView.scrollTo(0, this.props.listViewOffset);
        }
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(prevProps.targetUserId !== this.props.targetUserId || !this.props.dataList)
        {
            // this.loadData(this.props.footmarkType);
            this.onRefresh();
        }
        if(this.props.dataList !== prevProps.dataList)
        {
            this.setState({dataSource: this.state.dataSource.cloneWithRows(this.props.dataList ? this.props.dataList : [])});
        }
    }
    render()
    {
        return (
            <div className="scenicListBox" style={{height: this.props.height}}>
                {
                    this.renderListView()
                }
                {
                    !this.props.isFetching && (!this.props.dataList || this.props.dataList.length === 0)
                        ?
                        <EmptyView style={{height: this.props.height}} onClick={()=>this.onRefresh(this.props.inputKeyword)}/>
                        :
                        null
                }
                {
                    (this.props.isFetching && this.props.pageIndex === 1)
                        ?
                        <ActivityIndicator 
                            toast
                            text="Loading..." />
                        :null
                }
            </div>
        );
    }
    renderListView()
    {
        if(this.props.dataList && this.props.dataList.length > 0)
        {
            const separator = (sectionID, rowID) => (
                <div
                    key={`${sectionID}-${rowID}`}
                    style={{
                        height: '8px',
                        backgroundColor: '#eeeef6',
                    }}
                />
            );
    
            return (
                <ListView
                    ref={el => {
                        this.listView = el;
                        UIUtil.fixListViewHeightIssue(el, this.props.height);
                    }}
                    dataSource={this.state.dataSource}
                    renderRow={this.renderRow}
                    renderSeparator={separator}
                    style={{
                        height: this.props.height,
                        overflow: 'auto',
                    }}
                    pullToRefresh={<PullToRefresh
                        refreshing={this.props.isFetching}
                        onRefresh={this.onRefresh}
                    />}
                    onEndReached={this.loadNextPage}
                    renderFooter={this.renderFooter}
                    initialListSize={this.props.dataList.length}
                    pageSize={20}
                    scrollRenderAheadDistance={500}
                    scrollEventThrottle={50}
                    onEndReachedThreshold={20}/>
            );
        }
    }
    isEmptyString(str)
    {
        return !str || str.length === 0;
    }
    onRefresh()
    {
        let targetUserId = this.props.footmarkType === 4 ? this.props.targetUserId : this.props.userId;
        this.props.dispatch(asyncDeleteListAction(this.props.footmarkType, targetUserId,this.props.userId)).then(()=>{
            this.loadData();
        });
    }
    loadNextPage()
    {
        this.loadData();
    }
    renderFooter()
    {
        if(!this.props.isFetching || this.props.hasMore !== true)
        {
            return null;
        }
        return (
            <div className="loadingMore">
                <ActivityIndicator
                    text="Loading..."
                />
            </div>
        );
    }
    renderRow(item, sectionID, rowID)
    {
        let index = parseInt(rowID, 10);
        let isFirst = index === 0;
        let isMine = this.props.footmarkType === 3 || ( this.props.footmarkType === 4 && this.props.userId  === this.props.targetUserId); 
        return <FootmarkItemView 
            key={rowID} 
            item={item} 
            style={ isFirst ? {marginTop: '6px'} : null } 
            isMine={isMine}
            toggleExpand={()=>this.toggleExpand(index)}
            onPraiseButtonTapped={()=>this.onPraiseButtonTapped(item, index)}
            onCollectButtonTapped={()=>this.onCollectButtonTapped(item, index)}
            onDeleteButtonTapped={()=>this.onDeleteButtonTapped(item, index)}
            onItemClick={this.props.onItemClick}
            onLineClick={this.props.onLineClick}
            onSiteImageItemClick={this.props.onSiteImageItemClick}
            myFootmarkEditClicking={this.props.myFootmarkEditClicking}
            friendProfileClicking={this.props.friendProfileClicking} />;
    }
    onPraiseButtonTapped(item, index)
    {
        if(isNaN(this.props.userId))
        {
            this.props.toLoginPage();
            return;
        }
        this.props.dispatch(asyncPraiseAction(this.props.footmarkType,this.props.targetUserId ,this.props.userId, item.Code, item.IsLike === 1 ? 0 : 1, index)).then(()=>{
            Toast.success(item.IsLike === 1 ? '取消点赞成功' : '点赞成功', undefined, undefined, false);
        });
    }
    onCollectButtonTapped(item, index)
    {
        if(isNaN(this.props.userId))
        {
            this.props.toLoginPage();
            return;
        }
        this.props.dispatch(asyncCollectAction(this.props.footmarkType,this.props.targetUserId, this.props.userId, item.Code, item.IsCollection === 1 ? 0 : 1, index)).then(()=>{
            Toast.success(item.IsCollection === 1 ? '取消收藏成功' : '收藏成功', undefined, undefined, false);
        });
    }
    onDeleteButtonTapped(item, index)
    {
        if(isNaN(this.props.userId))
        {
            this.props.toLoginPage();
            return;
        }
        Modal.alert('提示','确定删除这条足迹吗？',[
            { text: '取消', style: 'default' },
            { text: '确定', onPress: () => {
                let jsonDict = {
                    'Code':item.Code,
                    'UserID':this.props.userId,
                };
                this.setState({ isLoading: true });
                HttpClient.postToPortal(IConstant.PortalApis.FOOTMARK.DELETE, jsonDict,null,true)
                    .then((resp) => resp.json())
                    .then((respJson) =>
                    {
                        if (respJson.status === '0'){
                            Toast.success('删除成功');
                            let targetUserId = this.props.footmarkType === 4 ? this.props.targetUserId : this.props.userId;                            
                            this.props.dispatch(asyncGetListAction(1, this.props.footmarkType, targetUserId, this.props.userId));
                        } else {
                            Toast.fail('删除失败');
                        }
                    })
                    .catch((error) => {
                        console.error(error);
                        Toast.fail('操作失败');                        
                    });
            }},
        ]);
    }
    toggleExpand(index)
    {
        let targetUserId = this.props.footmarkType === 4 ? this.props.targetUserId : this.props.userId;
        this.props.dispatch(toggleExpandAction(this.props.footmarkType, targetUserId, this.props.userId, index));
    }
    loadData()
    {
        if(this.props.isFetching)
        {
            return;
        }
        if(this.props.hasMore === false)
        {
            return;
        }

        let pageIndex = this.props.pageIndex ? (this.props.pageIndex + 1) : 1;
        let targetUserId = this.props.footmarkType === 4 ? this.props.targetUserId : this.props.userId;
        this.props.dispatch(asyncGetListAction(pageIndex, this.props.footmarkType, targetUserId,this.props.userId));
    }
    cacheScrollOffset()
    {
        if (this.listView)
        {
            let offsetY = ReactDOM.findDOMNode(this.listView).scrollTop;
            let targetUserId = this.props.footmarkType === 4 ? this.props.targetUserId : this.props.userId;
            this.props.dispatch(setScrollerPositionAction(this.props.footmarkType, targetUserId,this.props.userId, offsetY));
        }
    }
}

FootmarkListView.propTypes = {
    // 列表项点击事件
    onItemClick: PropTypes.func,
    // 0:最新；1:推荐；2:收藏；3:我的; 4: 访客视角-足迹, 传targetUserId
    footmarkType: PropTypes.number,
    toLoginPage: PropTypes.func,
    //编辑
    myFootmarkEditClicking: PropTypes.func,
    //访客视角
    friendProfileClicking: PropTypes.func,
};

const mapStateToProps = function(state, ownProps)
{
    let userId = SysUtil.getUserId(state);
    let obj = state.footmark.listScreen.groups[ownProps.footmarkType + '_' + userId];
    if (ownProps.footmarkType === 4)
    {
        let profileName = ownProps.targetUserId + '_' + userId;
        let profileObj = state.userProfile[profileName] ? state.userProfile[profileName] : {};
        obj = profileObj[IConstant.FRIEND_PROFILE_DATA_TYPE.FOOTMARK];
    }
    return {
        ...obj,
        userId,
        targetUserId: ownProps.targetUserId
    };
};

FootmarkListView = connect(mapStateToProps, null, null, {withRef: true})(FootmarkListView);

export default FootmarkListView;


