import { Actions } from '../../../core/utils/IConstant';

export default function elementMarkerReducer(state = {}, action)
{
    let obj = state[action.code];
    obj = obj ? obj : {};
    switch(action.type)
    {
    case Actions.MAP.ELEMENTMARKER.GO_BACK:
        let _state = {...state};
        delete _state[action.code];
        return {
            ..._state
        };
    case Actions.MAP.ELEMENTMARKER.MAPZOOM_AND_CENTERLOCATION_CHANGE:
        
        return {
            ...state,
            [action.code]: {
                ...obj,
                mapZoom: action.mapZoom,
                centerLat: action.centerLat,
                centerLng: action.centerLng
            }
        };
        
    default:
        return state;
    }
}


