import asyncScreen from './common/AsyncScreen';

export const AsyncTrafficListScreen = asyncScreen(
    () => import('./traffic/TrafficListScreen'),
    '交通指南'
);

export const AsyncTrafficAirScreen = asyncScreen(
    () => import('./traffic/TrafficAirScreen'),
    '航空'
);

export const AsyncTrafficTrainScreen = asyncScreen(
    () => import('./traffic/TrafficTrainScreen'),
    '火车'
);

export const AsyncTrafficBusScreen = asyncScreen(
    () => import('./traffic/TrafficBusScreen'),
    '客运'
);

