import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { NavBar, ActivityIndicator, Carousel } from 'antd-mobile';
import { Line, Circle } from 'rc-progress';
import ReactDOM from 'react-dom';
import Content from '../../views/common/Content';
import * as IConstant from '../../core/utils/IConstant';
import SysUtil from '../../core/utils/SysUtil';
import { 
    asyncGetDetailAction, 
    goBackAction, 
    setDetailScrollerPositionAction,
} from '../../store/actions/line/detailAction';

import '../../assets/css/line.css';
import PhotoBrowserView from '../../views/photo-brower/PhotoBrowerView';
import ElementOperationView from '../../views/common/ElementOperationView';
import LeftContentView from '../../views/common/LeftContentView';

class LineDetailScreen extends PureComponent
{
    constructor(props)
    {
        super(props);
        this.showImageBrowser = this.showImageBrowser.bind(this);
        this.onNearByTapped = this.onNearByTapped.bind(this);
        this.state = {
            Score: 0,
            RouteScore: 0,
            TrafficScore: 0,
            StayScore: 0,
            GuideScore: 0,
            index : 1,
            showImageBrowser:false,
            imageList:[]
        };
    }
    componentDidMount()
    {
        if(!this.props.detailItem)
        {
            this.loadData();
        }
        else
        {
            this.startScoreAnimation(this.props.detailItem);
        }
        this.restoreScrollViewPosition();
    }
    componentDidUpdate(prevProps, prevState, snapshot)
    {
        if(prevProps.detailItem !== this.props.detailItem && this.props.detailItem)
        {
            this.startScoreAnimation(this.props.detailItem);
        }
    }
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    leftContent={<LeftContentView
                        ref={(view) => { this.LeftContentView = view ? view.getWrappedInstance() : this.scrollView; }}
                        goBackAction={() => goBackAction(this.props.match.params.code)}
                        history={this.props.history}
                    />}
                    onLeftClick={() => {
                        this.LeftContentView.goHome();
                    }}>
                    线路详情
                </NavBar>
                <Content ref={(view)=>{
                    this.scrollView = view ? view.getWrappedInstance() : this.scrollView;
                }}>
                    {
                        this.renderHeaderView()
                    }
                    {
                        this.renderElementOperationView()
                    }
                    {
                        this.renderScoreView()
                    }
                    {
                        this.renderLineListView()
                    }
                    {
                        this.renderBasicViews()
                    }
                    {
                        this.renderLineArranges()
                    }
                    {
                        this.renderOtherInfoViews()
                    }
                    {
                        this.props.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                    <PhotoBrowserView 
                        ref={(view)=>{this.photoBrowserView = view ? view.getWrappedInstance() : this.photoBrowserView;}}
                    />
                </Content>
            </div>
        );
    }
    renderLineListView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return this.renderLineItemView();
    }
    renderLineItemView()
    {
        return (
            <div className="hotelDetailRoomItemRow" >
                <div className="hotelDetailRoomItemName">发团日期：{new Date().addDays(1).formatDate('YYYY-MM-DD')}</div>
                <div style={{flex: 1}}/>
                <div className="hotelDetailRoomItemPrice">¥{this.props.detailItem.GetLinesDayPrice.DefaultPrice}</div>
                {
                    this.props.detailItem.IsReserve
                        ? <div className="hotelDetailRoomItemBookingButton" onClick={()=>this.showBooking()}>预订</div>
                        : null
                }
            </div>
        );
    }
    showBooking()
    {
        if(!this.props.isUserLogined)
        {
            this.props.history.push('/me/login');
            return;
        }
        let lineCode = this.props.match.params.code;
        let code = this.props.detailItem.TravelCode;
        this.props.history.push({
            pathname: `/order/booking/${code}/${lineCode}/${IConstant.ORDER_TYPE.LINE}`,
            state: {
                lineName: this.props.detailItem.CnName,
            },
        });
    }
    renderHeaderView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div className="scenicDetailHeader" onClick={this.showImageBrowser}>
                {
                    this.renderHeaderImages()
                }
                <div className="scenicDetailHeaderBottom">
                    <div className="scenicDetailHeaderBottomText">{this.props.detailItem.CnName}</div>
                    <div className="lineDetailHeaderBottomText">{this.state.index}/{this.props.detailItem.GetImage  && this.props.detailItem.GetImage.ImageList ? this.props.detailItem.GetImage.ImageList.length : 1}</div>
                </div>
            </div>
        );
    }
    renderElementOperationView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return <ElementOperationView
            entityType ={IConstant.EntityType.EntityType_Route}
            code={this.props.match.params.code}
            onNearByTapped={this.onNearByTapped}/>;
    }
    renderScoreView()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div className="scenicScoreView">
                <div className="scenicScoreCircle">
                    <Circle percent={this.state.Score * 20} strokeWidth="6" trailWidth="6" strokeColor="#42a6dc" trailColor="#f0f0f0"/>
                    <div className="scenicScoreCircleTextBox">
                        <div style={{flex: 1}}/>
                        <div  className="scenicScoreCircleTextSubBox">
                            <div className="scenicScoreCircleText">{this.state.Score.toFixed(1)}</div>
                            <div className="scenicScoreCircleUnit">分</div>
                        </div>
                        <div style={{flex: 1}}/>
                    </div>
                </div>
                <div className="scenicScoreLines">
                    <div className="scenicScoreLine">
                        <div className="scenicScoreLineLabel">行程</div>
                        <Line className="scenicScoreLineProgress" percent={this.state.RouteScore * 20} strokeWidth="4" trailWidth="4" strokeColor="#42a6dc" trailColor="#f0f0f0" />
                    </div>
                    <div className="scenicScoreLine">
                        <div className="scenicScoreLineLabel">交通</div>
                        <Line className="scenicScoreLineProgress" percent={this.state.TrafficScore * 20} strokeWidth="4" trailWidth="4" strokeColor="#42a6dc" trailColor="#f0f0f0" />
                    </div>
                    <div className="scenicScoreLine">
                        <div className="scenicScoreLineLabel">住宿</div>
                        <Line className="scenicScoreLineProgress" percent={this.state.StayScore * 20} strokeWidth="4" trailWidth="4" strokeColor="#42a6dc" trailColor="#f0f0f0" />
                    </div>
                    <div className="scenicScoreLine">
                        <div className="scenicScoreLineLabel">导游</div>
                        <Line className="scenicScoreLineProgress" percent={this.state.GuideScore * 20} strokeWidth="4" trailWidth="4" strokeColor="#42a6dc" trailColor="#f0f0f0" />
                    </div>
                </div>
            </div>
        );
    }
    renderHeaderImages()
    {
        if(!this.props.detailItem || !this.props.detailItem.GetImage || !this.props.detailItem.GetImage.ImageList || this.props.detailItem.GetImage.ImageList.length === 0)
        {
            return null;
        }
        return (
            <Carousel
                className="scenicDetailHeaderImgSlider"
                afterChange = {()=>this.imageChage()}
                autoplay={true}
                dots={false}
                swiping={true}
                infinite
                selectedIndex={0}
                swipeSpeed={35}>
                {
                    this.props.detailItem.GetImage.ImageList.map((item, index)=><div key={index} className="scenicDetailHeaderImg" style={{backgroundImage: `url(${item.ImgSrc})`}}/>)
                }
            </Carousel>
        );
    }
    imageChage(){
        if(this.state.index === this.props.detailItem.GetImage.ImageList.length){
            this.setState({index:1});
        }else{
            this.setState({index:this.state.index+1});
        }
    }
    renderBasicViews()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div className="scenicDetailInfoRows">
                <div className="scenicDetailInfoRow">
                    <div className="scenicDetailInfoLabel">行程简介</div>
                    <div className="scenicDetailInfoValue scenicDetailMultDesc">{this.props.detailItem.Characterystic}</div>
                </div>
            </div>
        );
    }
    renderLineArranges()
    {
        if(!this.props.detailItem 
            || !this.props.detailItem.GetRelationLineDaysList
            || !this.props.detailItem.GetRelationLineDaysList.length === 0)
        {
            return null;
        }
        return (
            <div>
                <div className="lineDetailSplitLine"/>
                <div className="lineDetailArrangeListBox">
                    <div className="lineDetailInfoLabel">行程安排</div>
                    {
                        this.props.detailItem.GetRelationLineDaysList.map((item, index)=>this.renderLineArrangeItem(item, index))
                    }
                </div>
            </div>
        );
    }
    renderLineArrangeItem(item, index)
    {
        return (
            <div key={index}>
                <div className="lineArrangeItemDay">第{item.Days}天</div>
                <div className="lineArrangeItemDayBox">
                    {
                        this.renderLineDaysItemList(item.LineDaysList)
                    }
                </div>
            </div>
        );
    }
    renderLineDaysItemList(list)
    {
        if(!list || list.length === 0)
        {
            return null;
        }
        return list.map((item, index)=>this.renderLineDaysItem(item, index));
    }
    renderLineDaysItem(item, index)
    {
        return (
            <div key={index}>
                <div className="lineArrangeItemSpace"/>
                <div className="lineArrangeItemDayTravelTimeBox">
                    <div className="lineArrangeItemDayTravelTimeIcon"/>
                    <div className="lineArrangeItemDayTravelTime">{item.TravelTime}&nbsp;{item.Title}</div>
                </div>
                <div className="lineArrangeItemSpace"/>
                <div className="lineArrangeItemDayDetails lineArrangeItemDayLongDetails">{item.Itinerary}</div>
                <div className="lineArrangeItemSpace"/>
            </div>
        );
    }
    renderOtherInfoViews()
    {
        if(!this.props.detailItem)
        {
            return null;
        }
        return (
            <div ref={(view)=>{this.otherInfoView = view;}}>
                <div className="lineDetailOtherViewsSpace"/>
                <div className="scenicDetailInfoRows">
                    {
                        (this.props.detailItem.UpTime || this.props.detailItem.DownTime)
                            ?
                            this.renderOtherInfoItemView('有效日期', this.props.detailItem.UpTime + '至' + this.props.detailItem.DownTime)
                            :null
                    }
                    {
                        this.renderOtherInfoItemView('预订限制', this.props.detailItem.ReserveLimit)
                    }
                    {
                        this.renderOtherInfoItemView('费用包含', this.props.detailItem.Feeincludes)
                    }
                    {
                        this.renderOtherInfoItemView('费用不包含', this.props.detailItem.Notincluded)
                    }
                    {
                        this.renderOtherInfoItemView('出行须知', this.props.detailItem.TravelNotes)
                    }
                    {
                        this.renderOtherInfoItemView('安全须知', this.props.detailItem.SaftHint)
                    }
                    {
                        this.renderOtherInfoItemView('温馨提示', this.props.detailItem.ReminderBZ)
                    }
                </div>
            </div>
        );
    }
    renderOtherInfoItemView(label, value)
    {
        if(!value || value.length === 0)
        {
            return null;
        }
        return (
            <div className="scenicDetailInfoRow">
                <div className="scenicDetailInfoLabel">{label}</div>
                <div className="scenicDetailInfoValue scenicDetailMultDesc" dangerouslySetInnerHTML={{__html: value.replace(/\n/g, '<br/>')}}/>
            </div>
        );
    }
    loadData()
    {
        this.props.dispatch(asyncGetDetailAction(this.props.match.params.code));
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.scrollView).scrollTop;
        this.props.dispatch(setDetailScrollerPositionAction(this.props.match.params.code, offsetY));
    }
    restoreScrollViewPosition()
    {
        if(this.scrollView)
        {
            if(!isNaN(this.props.scrollViewOffset))
            {
                this.scrollView.scrollToY(this.props.scrollViewOffset);
            }
            else
            {
                this.scrollView.scrollToY(0);
            }
        }
    }
    startScoreAnimation(detailItem)
    {
        // 这样进度条可以有动画
        setTimeout(()=>{
            this.setState({
                Score: detailItem.Score ? detailItem.Score : 3.0,
                RouteScore: detailItem.RouteScore ? detailItem.RouteScore : 3.0,
                TrafficScore: detailItem.TrafficScore ? detailItem.TrafficScore : 3.0,
                StayScore: detailItem.StayScore ? detailItem.StayScore : 3.0,
                GuideScore: detailItem.GuideScore ? detailItem.GuideScore: 3.0,
            });
        }, 700);
    }

    showImageBrowser()
    {
        if(!this.props.detailItem || !this.props.detailItem.GetImage || !this.props.detailItem.GetImage.ImageList || this.props.detailItem.GetImage.ImageList.length === 0)
        {
            return null;
        }

        let imageList = [];
        for (let i = 0; i < this.props.detailItem.GetImage.ImageList.length; i++)
        {
            let imageItem = this.props.detailItem.GetImage.ImageList[i];
            imageList.push(imageItem.ImgSrc.replace('Small','Large'));
        }

        this.photoBrowserView.open(imageList);
    }
    onNearByTapped()
    {
        if (this.scrollView && this.otherInfoView)
        {
            let otherInfoHeight = this.otherInfoView.scrollHeight;
            let moveHeight = ReactDOM.findDOMNode(this.scrollView).scrollHeight - otherInfoHeight;
            // //计算 预定须知的位置
            this.scrollView.scrollToY(moveHeight);
        }
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.line.detailScreen[ownProps.match.params.code];
    return {
        ...obj,
        isUserLogined: SysUtil.isUserLogined(state),
    };
};

LineDetailScreen = connect(mapStateToProps)(LineDetailScreen);

export default LineDetailScreen;


